var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"International"},[_c('div',{staticClass:"container-fluid"},[_c('div',{staticClass:"row",staticStyle:{"position":"relative"}},[_vm._m(0),_vm._m(1),_c('div',{staticClass:"inner-banger"},[_c('nav',{attrs:{"aria-label":"breadcrumb"}},[_c('ol',{staticClass:"breadcrumb"},[_c('li',{staticClass:"breadcrumb-item"},[_c('router-link',{attrs:{"to":"/"}},[_vm._v("TOP")])],1),_c('li',{staticClass:"breadcrumb-item active",attrs:{"aria-current":"page"}},[_vm._v(" 全國女人國際徵信 ")])])])])])]),_c('section',{staticStyle:{"background":"#f5f4f0"}},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-10 col-md-12 col-sm-12"},[_c('div',{staticClass:"frame mt-4"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s1",attrs:{"id":"s1","name":"s1"}},[_vm._v("全球國際徵信新聞")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-1",modifiers:{"accordion-1":true}}],attrs:{"block":"","variant":""}},[_vm._v("地球村的世界無遠弗屆")])],1),_c('b-collapse',{attrs:{"id":"accordion-1","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 生活在這個地球村的世界無遠弗屆，跨國性婚姻、企業已成為生活常態，於是乎各種危機處理的徵信服務也必須隨之國際化；有鑒於此，環球女人徵信社國際徵信調查部除了承辦一般徵信徵信業務之外，更能解決各種大陸蒐證、國內外商業徵信等等國際性案件，希望藉由我們徵信過於過際事務的專業，為委託人解決國內外危機處理問題，環球女人徵信社是全球華人徵信業中的最愛。"),_c('br'),_c('br'),_vm._v(" 人生往往會遭遇許多問題，許多在台灣會發生的問題，同樣的也會在國際華人間發生；例如台商大陸包二奶、國際華人商業徵信等等，都是許多人常常必須面對的事情，因此我們的徵信業務擴增至大陸蒐證、國際商業徵信等更專業的危機處理領域，希望藉由我們優秀的國際徵信團隊，協助解決各種國際性危機處理問題！"),_c('br'),_c('br'),_vm._v(" 各種國內外的危機處理問題，都讓我們的專業徵信來協助您解決；像是法律諮詢、信用調查、國際糾紛等等危機處理方面的問題，您是否希望能夠有專業徵信的協助卻又擔心語言、溝通、風俗民情的不同呢？環球女人徵信社國際徵信調查部有精通國際危機處理業務的徵信，無論是大陸蒐證，或者是各種國際商業徵信等等國際徵信業務，我們都能提供給您絕佳的危機處理協助。"),_c('br'),_c('br'),_vm._v(" 當您在外努力衝刺時，環球女人徵信社國際徵信調查部及我們的專業徵信們可以給您優質的協助，專業的大陸蒐證，或者國際性的商業徵信、危機處理等徵信業務，讓您沒有後顧之憂的盡情揮灑您的專業。"),_c('br'),_c('br'),_vm._v(" 環球女人徵信社國際徵信調查部有專業的大陸蒐證、國內外商業徵信、危機處理等等國際徵信服務。許多人為了追求更大利潤，於是紛紛前往其他藍海之地開拓市場，於是所面臨的各種危機處理問題也更加複雜困難；環球女人徵信社國際徵信調查部了解徵信業務需要配合社會趨勢，也因此惟有國際化的專業徵信服務，才能夠進一步協助委託人解決各種國際性商業徵信、大陸蒐證、危機處理等徵信需求。"),_c('br'),_c('br'),_vm._v(" 環球女人徵信社國際徵信調查部不斷培訓專業徵信，要求我們的徵信人員務必精益求精，提供百分百的完美服務。當您面臨各種國內外商業徵信、外遇抓姦、大陸蒐證等等各種危機處理問題時，都讓我們專業的國際徵信專員來協助您順利解決，環球女人徵信社絕對是全球華人徵信業中最佳選擇！ ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-2",modifiers:{"accordion-2":true}}],attrs:{"block":"","variant":""}},[_vm._v("雷曼兄弟破產 台股地雷再現 美國第4大金融機構破產 非同小可。")])],1),_c('b-collapse',{attrs:{"id":"accordion-2","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("文．羅崇貴")]),_c('p',[_vm._v(" 人算不如天算，美國又有1間大型金融機構倒閉，台股受到牽連，很多持有雷曼兄弟債券等商品的台灣公司被迫踩到地雷，台股難逃一陣殺盤。"),_c('br'),_c('br'),_vm._v(" 美國第4大金融機構Lehman Brothers(雷曼兄弟)在台北時間9月15日正午，正式向外界宣告破產，這間美國第4大投資銀行倒閉事件，讓美國華爾街「金融海嘯」又掀起一陣浪潮，雷曼兄弟倒閉，美國財政部不接管，全球股市勢必再掀波瀾。"),_c('br'),_c('br'),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("雷曼危機 全球金融海嘯再起")]),_c('br'),_vm._v(" 雷曼兄弟的倒閉事件，牽連到美林證券與AIG保險集團，還好美林證券與美國銀行(Bank of America)展開合併的談判，應該可以避開這次由雷曼兄弟引爆的金融危機，但是，AIG發生籌資困難的問題，引起持有相關投資的金融機構一片緊張，更傳出AIG準備向美聯儲融資400億美元。"),_c('br'),_c('br'),_vm._v(" 這一連串的金融危機，已經讓美國金融機構出現了大團結，包括美國銀行(Bank of America)、巴克萊(Barclays)、花旗(Citibank)、瑞士信貸(Credit Suisse)、德意志銀行(Deutsche Bank)、高盛(Goldman Sachs)、摩根大通(JPMorgan Chase)、美林(Merrill Lynch)、摩根士丹利(Morgan Stanley) 和瑞銀(UBS)等10家商業和投資銀行組成的銀行團，每家銀行分配出資70億美元，共同建立一套總額700億美元的擔保借貸工具，以協助降低全球金融市場可能面臨的信貸減損衝擊。"),_c('br'),_c('br'),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("全球財經單位 嚴密監控引爆的風險")]),_c('br'),_vm._v(" 不只美國金融機構的自救，歐洲金融產業也繃緊神經，歐洲央行(ECB)與英國央行(BOE)9月15日相繼宣布，將密切觀察雷曼兄弟宣布申請破產保護後的衝擊，並已備妥於必要時採取措施。根據彭博社報導，亞洲財經首長表示，也正在評估美經濟情勢的最新發展，日本央行首席新聞官杉本芳浩表示：「我們正密切監控亞洲市場走勢，試圖瞭解這些發展，將如何衝擊日本市場」。泰國財長蘇拉朋在曼谷接受訪問時表示：「部分人士說雷曼也許不是最後一家，可能還有其他家接踵而來。我們將密切監控市場。」"),_c('br'),_c('br'),_vm._v(" 新加坡政府投資公司副主席陳慶炎在日內瓦指出：「假如房價衰退遠甚於預期，大型金融機構恐喪失償債能力，信用緊縮將更嚴重，經濟成長恐大幅減弱。」伴隨著房價挫跌的惡性通縮循環，金融機構倒閉與經濟成長疲軟將緊接而來。"),_c('br'),_c('br'),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("謠傳美國可能出現財務危機的公司")]),_c('br'),_vm._v(" 前美國聯準會(Fed)主席葛林斯潘(Alan Greenspan)表示，美國信貸危機已經形成「百年僅見」的金融危機，並可能會有更多大型金融機構面臨倒閉。他說：「我不相信這個百年僅見的嚴重金融危機不會為全球房市帶來衝擊，而且我認為這正在發生。」他預期未來可能有更多銀行倒閉，而監管機關不應嘗試保護每一家面臨倒閉問題的機構。"),_c('br'),_c('br'),_vm._v(" 在葛林斯潘的談話中，他預期會有更多的美國銀行面臨倒閉風險，是哪些金融機構又遭到點名呢？新華網引述英國《獨立報》報導，首當其衝的會是美國華盛頓互惠銀行(Washington Mutual)，它是全美最大存款和貸款銀行，但多月來面臨嚴重資金短缺。"),_c('br'),_c('br'),_vm._v(" 其次，全球最大保險商和金融服務公司美國國際集團(AIG)，由穩健的保險業務進軍金融業務，今年巨額虧損卻高達130億美元。華爾街第3大投資銀行美林銀行也受傷慘重，本來也在被指名會發生財務問題的名單之內，但是，美林宣布與美國銀行的合併談判，暫時讓市場把它排除在「可能倒閉名單」之外。"),_c('br'),_c('br'),_vm._v(" 另外，香港《文匯報》引述瑞士《SonntagsZeitung》周刊報導，指瑞銀(UBS)可能也有風險，消息指出，UBS仍受次貸危機所擾，單是次貸和證券化-A貸款已分別要各撥備達10億美元，債券保險又要撥備20億美元，最後還要為學生貸款撥備10億美元。UBS之前已累計為次貸資產撥備425億美元，第2季損失亦達3.58億瑞郎，但該行總裁表示UBS處於風暴過後之勢，他相信明年將會恢復獲利，再加上瑞銀加入全球10家銀行，撥700億美元的援助金融產業計畫，這樣的說法尚待證實。"),_c('br'),_c('br'),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("救經濟 美國政府沒「招」了")]),_c('br'),_vm._v(" 全美最大儲蓄貸款銀行Washingtson Mutual傳出房貸相關的壞帳高達190億美元，可能將會被逼迫售出全美2300間分行來籌資應急，預估第3季應付虧損的準備金將需要45億美元。"),_c('br'),_c('br'),_vm._v(" 面對一連串的金融危機，美國政府跟台灣馬政府面對的經濟狀況一樣，顯得有些「束手無策」，美國聯準會(Fed)為了維持金融市場秩序，不但狂砍利率，且持續擴大挹注市場資金。到了最近，財政部也站出來宣布暫時收編房利美(Fannie Mae)與房地美(Freddie Mac)，但是幾乎大多數的美國分析師都在納悶，為何目前實行的諸多措施，仍未能有效的紓緩金融系統所面臨的壓力？這次，美國政府決定不救雷曼兄弟，市場認為：「政府已經沒招數了！」這樣的情境，似乎與馬政府想要救台股，卻碰上一連串國際經濟危機一樣的無奈。"),_c('br'),_c('br'),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("留意殺融資跌破6000點")]),_c('br'),_vm._v(" 這樣的國際金融風暴一定會牽動到國際股市重挫，大眾證券表示，參考97年金融風暴台股走勢，最低跌破10年線800多點之後，才觸底強彈，依這樣的歷史經驗換算，台股不排除有可能下探約5700多點，接近時，可積極增加持股。不過，9月16日台股開盤繼續重挫，6000點整數立刻跌破，台股大盤加權股價指數出現「5」字頭之後，5700點已經不是市場認為會止跌的位置。"),_c('br'),_c('br'),_vm._v(" 無論台股如何重挫，這時候應避開外資持股高的大型股，一來這些大型股跌深，儘管股價會在價值顯現下具備支撐，再殺有限，但是，被外資當成提款機的股票都是這些大型權值股，賣壓無法在短時間之內停止；二來外資大筆出脫時候，政府基金也搞不過外資，因此建議進場標的排除掉大型股。"),_c('br'),_c('br'),_vm._v(" 除了避開外資賣超股之外，目前台股融資維持率在130％關鍵水位，一旦有風吹草動，就可能引動龐大的停損賣壓，在底部仍無法確定下，短線上投資人要避開高融資個股。以融資餘額角度來看，整體融資戶虧損達3成，就幾乎可研判已來到股市低點，截至8月底止，融資戶的虧損已達27％，因此，現在已是殺融資的末段，消化賣壓後有望止跌，不過，短時間之內還需要謹慎觀望。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-3",modifiers:{"accordion-3":true}}],attrs:{"block":"","variant":""}},[_vm._v("損失慘重 中國散戶走上街頭")])],1),_c('b-collapse',{attrs:{"id":"accordion-3","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 去年中國股市大漲，讓很多賺錢的中產和藍領階級笑開懷，今年中國股市暴跌，讓這些人的財富嚴重縮水和家庭破碎，甚至一度上街頭抗議。"),_c('br'),_c('br'),_vm._v(" 過去十一個月，中國股市暴跌六十五％，數千萬股民怨氣沖天，讓非常重視穩定的中國政府感到焦慮，北京當局公布史無前例的救市計畫，讓上週五中國股市勁揚，但由於很多散戶已經損失慘重，在重回宛如賭場的股市之前，可能會三思而行。"),_c('br'),_c('br'),_vm._v(" 在房地產公司擔任司機的張表示：「政府救股市很好，但我怕股市尚未觸底，只要股民看到股市下跌，就會嚇到撤離資金。」"),_c('br'),_c('br'),_vm._v(" 中國股市在不到兩年時間暴漲逾兩倍，數千萬對投資所知有限的股民，爭相把所剩不多的閒錢投入股市，但從去年十月創下歷史高點以後，中國股市已回檔至二○○六年底的水準。"),_c('br'),_c('br'),_vm._v(" 上海海通證券分析師張齊表示：「中產階級的夢想已被埋葬，一輩子的儲蓄像煙般消失，往前看，很多家庭將遠離股市。」"),_c('br'),_c('br'),_vm._v(" 張齊表示，中國政府的救市計畫，包括用政府資金買上市股票，為股市交易十八年來最大膽的舉動，可能讓股市上漲好幾週。"),_c('br'),_c('br'),_vm._v(" 過去幾年中國股市大漲，使得炒股變成全民運動，網路聊天室貼滿投資秘訣，很多股民甚至把投資基金當成買彩券。"),_c('br'),_c('br'),_vm._v(" 股民瘋狂炒股使得Ａ股股價，比在香港上市的Ｈ股要高出一倍，中國政府也吹噓世界市值前十大公司中，有五家位於中國。"),_c('br'),_c('br'),_vm._v(" 若干中國官員警告股市泡沫，但很多股民認為，股價跟不上中國經濟自一九九○年初以來的飛躍成長，股市大漲誘使很多民眾搬出存款轉進股市，也提供企業必要的資金，但今年以來景氣突然翻轉，危及中國社會穩定。"),_c('br'),_c('br'),_vm._v(" 知名的部落格網站如club.stock.sohu.com張貼抗議字眼，並邀請網友加入，今年六月一小群憤怒的股民在上海證交所外抗議，這在中國大城市相當罕見。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-4",modifiers:{"accordion-4":true}}],attrs:{"block":"","variant":""}},[_vm._v("今年第12家 美Ameribank宣布倒閉")])],1),_c('b-collapse',{attrs:{"id":"accordion-4","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("自由時報編譯陳柏誠／綜合外電報導")]),_c('p',[_vm._v(" 擁有一○二年歷史、位在西維吉尼亞州的Ameribank宣布倒閉，並將其所有存款分別出售給該州Pioneer Community銀行以及俄亥俄州The Citizens Savings銀行。這是全美今年第十二家倒閉的銀行，也是美國爆發至少二十年來最嚴重的銀行危機下的最新受害者。"),_c('br'),_c('br'),_vm._v(" 這是西維吉尼亞州自1999年下令關閉First National Bank of Keystone以來的最新一起銀行倒閉事件。諷刺的是，Ameribank正是九年前Keystone倒閉時，吸收該行一．三五億美元存款的銀行。"),_c('br'),_c('br'),_vm._v(" 聯邦存款保險公司（ＦＤＩＣ )表示，Ameribank存款總額截至6月底為一．○二億美元，擁有資產一．一五億美元。聯邦監管機構─美國儲蓄機構管理局（ＯＴＳ )表示，Ameribank今年第二季連續第四季出現淨損及資金流失。ＯＴＳ指出，Ameribank在針對中低收入戶的建築修復貸款業務方面過度追求成長，導致該行資本嚴重不足。"),_c('br'),_c('br'),_vm._v(" Ameribank西維吉尼亞州五家分行的存款將轉給Pioneer Community銀行，The Citizens Savings銀行則將接收該行俄亥俄州三家分行的存款。這兩家銀行同時將買下大約二三○○萬美元Ameribank的資產。"),_c('br'),_c('br'),_vm._v(" 被指定接管該銀行的ＦＤＩＣ，將承接剩餘的股權，日後再作處分。因Ameribank倒閉，ＦＤＩＣ存款保險的支出預估將達四二○○萬美元。"),_c('br'),_c('br'),_vm._v(" ＦＤＩＣ為八四五一家金融機構作保，上月將認為瀕臨倒閉的銀行家數提升到一一七家，創下2003年中以來最高數量，該機構第一季底的預估只有九十家。"),_c('br'),_c('br'),_vm._v(" 去年共有三家金融機構倒閉。"),_c('br'),_c('br'),_vm._v(" 今年迄今最大的銀行倒閉事件，是位在加州Pasadena的IndyMac銀行，資產規模達三二○億美元，於7月11日勒令倒閉。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-5",modifiers:{"accordion-5":true}}],attrs:{"block":"","variant":""}},[_vm._v("金融帝國崩塌 美元資產燙手")])],1),_c('b-collapse',{attrs:{"id":"accordion-5","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("編譯盧永山／特稿")]),_c('p',[_vm._v(" 美國政府快速拯救金融業的行動，可能使美國金融帝國的形象蒙塵，因為這些行動會暴露美國經濟的弱點，使外國投資人對美元資產退避三舍。"),_c('br'),_c('br'),_vm._v(" 美國政府的救市行動可能使明年度預算赤字再增數百億美元，也引發質疑，美國是否仍會奉行自由市場經濟。"),_c('br'),_c('br'),_vm._v(" 美信用評等轉弱"),_c('br'),_c('br'),_vm._v(" 標準普爾主權評等經理錢伯斯表示：「美國的信用評等變弱，因金融機構接二連三出狀況，已對美國政府形成真正的風險。」"),_c('br'),_c('br'),_vm._v(" 結果便是，外國投資人在對美國政府和經濟提供資金之際，可能要求更高的報酬率，在借貸成本升高，加上美元貶值下，美國民眾的生活水準將會降低。"),_c('br'),_c('br'),_vm._v(" 截至目前為止，美國因為經濟規模、軍事實力和金融市場的深度，在國際投資人心中享有特殊地位，美元在二次世界大戰後取代英鎊成為全球主要的外匯存底貨幣，使美國能自由地向外借貸，逐漸推高貿易赤字，這讓美國民眾以為，美國是獨一無二的，註定成為全球政治和經濟領導者。"),_c('br'),_c('br'),_vm._v(" 但美國不能再把這個特殊地位視為理所當然。自十年前歐元問世後，歐元已和美元在國際貿易與投資上競爭主要貨幣的地位，中國等新興市場的快速成長，也削弱美國對全球資金的吸引力。"),_c('br'),_c('br'),_vm._v(" 普林斯頓大學教授李奇表示，這是當前金融危機如此危險的原因，這場金融動盪已造成美國政府信心危機，美國在政治和金融上的雙重強勢之說，如今已備受考驗。"),_c('br'),_c('br'),_vm._v(" 華府Kalorama Partners執行長皮特表示，美國政府的救市行動雖可向外國投資人宣示，美國不接受股市宛如自由落體的狂跌，但也會凸顯出美國金融體系的極度脆弱。"),_c('br'),_c('br'),_vm._v(" 諾貝爾經濟學獎得主史帝格里茲表示，美國政府隨機紓困金融業，可能使外國投資人不願投資美國，因為他們不知道哪家銀行會倒閉，哪些會活下來。"),_c('br'),_c('br'),_vm._v(" 史帝格里茲表示：「過去我們相信美國政府會依法行事，現在似乎要仰賴行政自由裁量，哪些銀行獲得紓困，端賴保羅森或柏南克的個人裁量。」 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-6",modifiers:{"accordion-6":true}}],attrs:{"block":"","variant":""}},[_vm._v("美股展望經濟數據難起色 未來仍劇烈波動")])],1),_c('b-collapse',{attrs:{"id":"accordion-6","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("〔編譯盧永山／綜合21日外電報導〕")]),_c('p',[_vm._v(" 上週美國政府端出救股市的大菜，激勵股市暴漲，在大菜的細節公布後，投資人將重新關注經濟數據，預料數據將難有起色，美股未來仍將呈劇烈波動。"),_c('br'),_c('br'),_vm._v(" 德意志銀行美國股票集團主管費茲帕屈克表示：「這是股市尋求的強心針，本週投資人將搞清楚整體救市細節，然後重新檢視經濟數據。」"),_c('br'),_c('br'),_vm._v(" 上週美股波動劇烈，週三波動指數創六年來最高，週四紐約證交所的交易股數暴增至42億股，創紀錄新高。"),_c('br'),_c('br'),_vm._v(" 定量分析服務公司資深副總裁陶爾表示：「就歷史標準而言，股市波動相當劇烈，反映出市場的不確定，股市波動必須和緩下來，下週政府公布救市細節後，讓我們看看市場如何反應。」"),_c('br'),_c('br'),_vm._v(" 陶爾表示：「我們似乎度過最近的風暴，若有人說接下來股市會漲二或三個月，我會同意，但這不是衰退的結束，經濟仍將疲軟不振。」不少分析師預估，美國第四季經濟成長將轉為負數，而且到明年下半年都難以復甦，信用緊縮惡化了經濟展望。"),_c('br'),_c('br'),_vm._v(" 聯準會主席柏南克週三將在國會聯合經濟委員會發表經濟展望報告，此外，本週還將公布幾項經濟數據。"),_c('br'),_c('br'),_vm._v(" 週三美國房地產經紀人協會將公布8月成屋銷售數據，預料將較7月下跌1.2%。"),_c('br'),_c('br'),_vm._v(" 週四美國商務部將公布8月耐久財訂單，預料將下跌1.3%，主要受到飛機和汽車部門訂單下滑拖累。另外，週四也將公布8月新屋銷售數據預料將下跌1%，新屋庫存料將維持高檔。"),_c('br'),_c('br'),_vm._v(" 週五將公布上週初次申領失業救濟金人數。"),_c('br'),_c('br'),_vm._v(" 費茲帕屈克表示：「當投資人了解政府的救市行動後，將重新檢視經濟放緩的情況，股市將得面對此一問題。」 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-7",modifiers:{"accordion-7":true}}],attrs:{"block":"","variant":""}},[_vm._v("中國奶製品 全面禁止進口")])],1),_c('b-collapse',{attrs:{"id":"accordion-7","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("記者王昶閔、楊雅民、羅添斌、謝文華／綜合報導")]),_c('p',[_vm._v(" 繼中國奶粉被檢出含毒物三聚氰胺後，昨天植物性蛋白類的「奶精」也發現毒物蹤跡！這是全球首度發現中國製植物性蛋白含三聚氰胺，衛生署除對中國提出強烈譴責，鑑於中國奶製產品問題叢出，即日起禁止所有中國奶粉、奶製產品進口！"),_c('br'),_c('br'),_vm._v(" 奶精為何含毒？衛生署副署長宋晏仁推測，奶精主成分是玉米漿（為植物性蛋白）與少許酪蛋白鈉，酪蛋白鈉萃取自牛奶，可能是牛奶受到污染所致；台大食品科技研究所教授葉安義則認為，這不太可能經由酪蛋白鈉間接污染，比較可能是直接添加三聚氰胺。"),_c('br'),_c('br'),_vm._v(" 而中國海協會副秘書長王小兵昨率領十九名四川地震重建規劃專家來訪，對中國毒奶粉銷台及台灣廠商求償問題，他說：「此行只談震災重建，不談毒奶粉。」"),_c('br'),_c('br'),_vm._v(" 金車主動通報 12萬箱產品回收"),_c('br'),_c('br'),_vm._v(" 金車公司昨主動通報衛生署，該公司八種添加中國奶精產品受到三聚氰胺污染，包括：「曼特寧風味三合一咖啡」、「藍山風味三合一咖啡」、「焦糖瑪其朵三合一咖啡」、「曼特寧二合一咖啡」、「阿拉比卡三合一咖啡」、「法式香草三合一咖啡」、「奶茶三合一咖啡」與「快煮湯雞茸玉米濃湯」，即日起十二萬箱、兩千一百六十萬包沖泡式咖啡下架回收，部分產品外銷到德國，也將循管道告知該國。"),_c('br'),_c('br'),_vm._v(" 衛生署食品衛生處處長蕭東銘表示，金車去年十二月十二日以來，自中國山東省都慶公司進口四批、共七十噸奶精，鑑於中國奶粉風暴擴大，主動將奶精原料樣本送往食品工業發展研究所檢驗，發現第四批產品受到污染。相對於廠商主動送檢的負責態度，消基會秘書長游開雄則批評，此事件凸顯衛生署對中國進口奶製品的安全與否，根本沒有全盤掌握！"),_c('br'),_c('br'),_vm._v(" 根據財政部關稅總局統計，自二○○三年開放中國奶製品進口來台後，進口量年年成長，去年進口量已達一萬五千九百七十七公噸，今年一至八月已進口八千零八十七公噸，進口量僅次於紐西蘭和澳洲。在毒奶粉、毒奶精事件發生後，消費者也人心惶惶，擔心往後還有那些中國奶製品是含毒的「未爆彈」，會陸續爆發出來。"),_c('br'),_c('br'),_vm._v(" 中國還有那些未爆彈 消費者憂"),_c('br'),_c('br'),_vm._v(" 衛生署則建議，家中食品若內含中國奶粉、奶製品、植物性蛋白類添加物，最好暫時停止食用，也要求所有製造三合一咖啡、湯類等相關業者，只要製造原料有用到奶精，都應主動送驗；衛生署也將針對全國相關食品工廠的原料進行全面抽查。"),_c('br'),_c('br'),_vm._v(" 因衛生署宣布禁止所有中國奶粉、奶製產品進口，有高達二十支奶粉從中國進口的台灣雀巢表示，將配合政府措施，會從其它國家的雀巢工廠進口同樣商品替代。"),_c('br'),_c('br'),_vm._v(" 根據業者估計，台灣一年奶粉市場規模約一百五十億元，目前雀巢在台灣成人奶粉市場市占率約四成、兒童奶粉市場約一成。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-8",modifiers:{"accordion-8":true}}],attrs:{"block":"","variant":""}},[_vm._v("低價電腦概念股 外資看好")])],1),_c('b-collapse',{attrs:{"id":"accordion-8","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 宏碁（2353）執行長暨總經理蘭奇（Gianfranco Lanci）日前在匈牙利舉行的全球記者會中宣示，三年內打敗惠普成為全球筆記型電腦一哥，對於未來三年個人電腦市場景氣更是樂觀，外資近期也將焦點放在筆記型電腦族群。"),_c('br'),_c('br'),_vm._v(" 低價電腦概念成為筆記型電腦族群在熊市中的護身符，包括宏碁、華碩（2357）、新普（6121）、廣達（2382）等都是外資第四季首選科技類股。"),_c('br'),_c('br'),_vm._v(" 相較於仁寶（2324）因商用市場景氣低迷，下修今年出貨目標10%，宏碁截至目前，對今年表現信心十足，尤其針對消費市場推出的低價電腦Aspire One反應熱烈，針對宏碁今年表現，蘭奇表示，2008年宏碁營收將接近200億美元，個人電腦（PC）銷售量將超越3,000萬台，未來每年出貨量將保持25%至30%的年成長率，2011年營收可達到300億美元。"),_c('br'),_c('br'),_vm._v(" 外資巴黎證券、高盛證券、里昂、瑞銀近來均喊進宏碁，列為科技類股首選個股，目標價上看70元至72元之間，看好理由為低價電腦興起，宏碁Aspire One熱銷；但摩根大通對於蘭奇的談話，看法相對保守，摩根大通指出，針對蘭奇提出今年200億美元營收目標，摩根大通的預估值少了8%，雖然宏碁對前景樂觀，但第四季電腦景氣未明，尤其各大電腦品牌在歐洲將反映近期美元兌歐元強勁升值而調漲價格，包括惠普和宏碁皆有意第四季在英國漲價5%至10%，對於第四季出貨相對不利，因此對宏碁重申中立評等，目標價56元，主要觀察重點在於宏碁在歐洲市場佔有率消長情況。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-9",modifiers:{"accordion-9":true}}],attrs:{"block":"","variant":""}},[_vm._v("IT競爭力全球第二 資訊專家提警訊?")])],1),_c('b-collapse',{attrs:{"id":"accordion-9","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("公共電視 更新日期:2008/09/22")]),_c('p',[_vm._v(" 資訊科技產品製造王國的台灣，這幾年來因為世界工廠轉移到中國，台灣產業積極尋求轉型，去年專利申請量獨步全球，平均2000人就擁有一項專利，再加上台灣私人企業不吝於研發投資，每百人就有高達512美元的研發經費，在亞洲僅次於日本，讓台灣在經濟學人調查的、2008年全球IT產業競爭，拿下全球第二的好成績，不過長期觀察國際資訊產業市場的外貿協會認為，台灣不能因此自滿。"),_c('br'),_c('br'),_vm._v(" 失去製造的優勢，台灣廠商朝著價值鏈的兩端、品牌和創新發展，資策會認為，生產電腦的宏基電腦和智慧型手機的宏達電，的確創造了台灣資訊產業發展的典範，而人才將是主導未來勝場的決定關鍵。"),_c('br'),_c('br'),_vm._v(" 雖然台灣IT產業整體表現亮眼，不過台灣在商業環境的得分卻有衰退趨勢，兩岸關係和台灣政局的穩定性，不僅左右了外資在台經營的意願，也會影響產業競爭力。記者陳姝君馬台興台北報導。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-10",modifiers:{"accordion-10":true}}],attrs:{"block":"","variant":""}},[_vm._v("全球經濟影響 星今年經濟成長可能低於4%?")])],1),_c('b-collapse',{attrs:{"id":"accordion-10","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 新加坡貿工部長林勛強今天表示，在全球經濟受到美國金融市場劇烈震盪影響，變得更具挑戰性之下，新加坡今年全年經濟成長，可能會低於百分之四。"),_c('br'),_c('br'),_vm._v(" 林勛強出席一項商業論壇後受訪時做上述表示。"),_c('br'),_c('br'),_vm._v(" 林勛強指出，新加坡在出口需求放緩之下，今年稍晚的經濟成長將會放緩，並可能延續到明年。"),_c('br'),_c('br'),_vm._v(" 林勛強說，上述狀況已經影響了新加坡的經濟成長前景，我們正在思考新加坡的經濟成長可能在百分之四到五的低端，可能接近百分之四，甚至跌破百分之四，這將取決於"),_c('span',{attrs:{"id":"iCliCK_SafeGuard"}},[_c('span',{attrs:{"id":"word4"}},[_c('span',{attrs:{"id":"iCliCK_SafeGuard"}},[_c('span',{attrs:{"id":"word4","onmouseover":"iCliCKAction('over', 4, event, this);return true;","onclick":"iCliCKAction('click', 4, event, this);return true;","onmouseout":"iCliCKAction('out', 4, event, this);return true;"}},[_vm._v("未來")])])])]),_vm._v("數週或數月的金融危機發展狀況。"),_c('br'),_c('br'),_vm._v(" 新加坡政府八月才把全年經濟成長預測，從百分之四到六調降至百分之四到五。"),_c('br'),_c('br'),_vm._v(" 但同時公佈的多份國際財經預測機構的報告都認為，新加坡今年全年經濟成長率可能為百分之四或更低。"),_c('br'),_c('br'),_vm._v(" 尤其是新加坡政府日前公佈八月份的非石油出口比去年同期下滑百分之十三點八，經濟分析師都認為，新加坡第三季陷入技術性衰退的風險增加。"),_c('br'),_c('br'),_vm._v(" 財政部長尚達曼昨天指出，新加坡不排除出現技術性衰退，可能連續兩個季度的經濟出現萎縮。"),_c('br'),_c('br'),_vm._v(" 但林勛強不願評論尚達曼的談話，僅強調新加坡勞動市場才是最重要的，對新加坡人來說，最重要的是保住工作，而新加坡今年前六個月的就業狀況良好，政府創造了許多新工作。 ")])])],1)],1)],1)],1)])])])])])])])}
