                                    <li class="number">女人在家待的時間過久，想外出工作先生不准許</li>
                                    <li class="number">家務無人分擔，時間久了對自己認知為（傭人）而對另一半感到不滿</li>
                                    <li class="number">雙方爭執後，無法良性溝通，長時間積壓無法釋放</li>
                                    <li class="number">另一半長時間工作，無共同興趣、嗜好，感情無法契合</li>
                                    <li class="number">結婚因不孕而有傳宗接代壓力，往往外遇離婚收場</li>
                                    <li class="number">孩子教育方式不能苟同，引起爭執，責怪女方，老婆覺得委屈</li>
                                    <li class="number">先生有暴力傾向，吵架時會動手打人或丟東西</li>
                                    <li class="number">種族不同，家族排擠引起夫妻不合，往往都會外遇</li>
                                    <li class="number">老婆是女強人，過於強勢，老公較內斂、樸實，有的女人會再去尋求身份地位與她相當的男人，創造新的未來</li>
                                    <li class="number" style="margin-bottom:0px;">舊情復燃</li>
                                </ul>
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-7 variant=""
                        >女人外遇是否都很難挽回？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-7"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <p>
                                女人外遇是最麻煩且最無法挽回的！<br><br>
                                相對於男人外遇大多是肉體外遇，女人卻是精神外遇先開始才會到肉體外遇，而女人變心比男人還快、還冷血！如果女人真的外遇，我想男人要先想看看-   到底是什麼讓你們的感情變淡，或者讓她覺得被忽略？覺得你不再呵護她，在乎她的感受，把她捧在心上？<br><br>
                                先想想在日常生活中有哪些地方忽視她、或虧待她，是否平常忙於工作沒在乎她的感受？如果真的是因你平常忽略，讓她不得不出去找慰藉，趕快趁這時侯彌補吧！<br><br>
                                送個小禮物、開口說愛她，甜言蜜語都不可少！不要怕肉麻，如果你真想挽回，就趕快去做吧！接著就是做些讓她感動的事，邊觀察她對外遇對象是真的動心，還是一時的慰藉。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                </div>
              </div>
            </div>
            <div class="frame">
              <div
                data-bs-spy="scroll"
                data-bs-target="#list-example"
                data-bs-offset="0"
                class="scrollspy-example"
                tabindex="0"
              >
                <h2 id="s2" name="s2" ref="s2">外遇法律</h2>
                <div class="accordion" role="tablist">
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-11 variant=""
                        >通姦所生之子女，應從何人之姓?</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-11"
                      visible
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <p>
                                民法第一千零五十九條規定：「子女從父姓。但母無兄弟，約定其子女從母姓者，從其約定。贅夫之子女從母姓。但約定其子女從父姓者，從其約定。」通姦所生之子女為非婚生子女，關於非婚生子女之從姓，可分別討論如下：<br><br>
                                （一）未受認領、準正前：司法院三十三年院字第二七七三號解釋認為：非婚生子女在未經其生父認領前，與其生父既無父與子女之親屬關係，而其生母之關係，則依民法第一千零六十五條第二項之規定，視為婚生子女，無須認領。由民法第一千零五十九條本旨推之，自應解為從母姓。<br><br>
                                （二）已經認領、準正而成為婚生子女者： <br />
                                非婚生子女經認領〈民法第一千零六十五條〉、準正〈民法第一千零六十四條〉者，應類推適用民法第一千零五十九條之規定，使其從父姓為宜，依此見解，子女若尚未報出生戶口，即為生父認領時，可直接從父姓報出生戶口。反之，子女已從母姓報出生戶口，始由父認領時，子女當然改從父姓。惟若生母方面有「母無兄弟」之情形者，仍得依生父及生母之約定使該子女仍保留母姓，此為解釋上當然之結果。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-22 variant=""
                        >告訴後能否只對配偶或相姦人撤回</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-22"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <p>
                                刑事訴訟法第二百三十八條第一項規定：「告訴乃論之罪，告訴人於第一審辯論終結前，得撤回其告訴。」又通姦罪，對於配偶提出告訴者，效力固然及於相姦人；但「對於配偶撤回告訴者，其效力不及於相姦人」（刑事訴訟法第二百三十九條但書）。所以，可單獨撤回對配偶的告訴，只告相姦者。<br><br>
                                但，反面解釋，若只對相姦人撤回告訴，其效力則及於配偶。(等同撤回對配偶之告訴)
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-33 variant=""
                        >通姦罪能否只告配偶或只告相姦人</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-33"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <p>
                                刑事訴訟法第二百三十九條本文規定，告訴乃論之罪，對於共犯之一人告訴或撤回告訴者，其效力及於其他共犯，稱之為「告訴之主觀不可分原則」。<br><br>
                                所以假如提起告訴，不能只告配偶或只告與其相姦之人，這是因為告訴係對於犯罪事實為之，並非對於特定之犯人為之，依照法條明文，告訴之主觀不可分，僅適用於「告訴」及「撤回告訴」兩種情形，但實務將其擴張至其他情形，承認所謂「不得告訴之主觀不可分」及「不得自訴之主觀不可分」。<br><br>
                                前者，如甲、乙犯通姦罪，甲之妻丙對甲為縱容、宥恕而不告訴者（刑法第二百四十五條第二項），依實務見解，丙對乙亦不得告訴。後者，如甲、乙犯通姦罪，甲之妻丙對甲不得自訴者（刑事訴訟法第三百二十一條），依實務見解，丙對乙亦不得自訴。<br><br>
                                但刑事訴訟法給予告訴人，對配偶單獨撤回告訴的權利，也就是老婆可在提出通姦告訴後，可單獨對老公撤回告訴，只剩下第三者是被告，告訴一但撤回不得再提告訴。所以造成不是兩個一起被告，就是只剩第三者被告，所以想要當通姦的第三者，還是先了解法律好自為之。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block v-b-toggle.accordion-44 variant=""
                        >我國刑法對於通姦罪有何規定?</b-button
                    >
                    </b-card-header>
                    <b-collapse
                    id="accordion-44"
                    accordion="my-accordion"
                    role="tabpanel"
                    >
                    <b-card-body>
                        <b-card-text>
                            <p>
                                （一）刑法第二百三十九條規定：「有配偶而與人通姦，處一年以下有期徒刑。其相姦者，亦同。」所謂&quot;通姦&quot;係指婚姻關係外，男女雙方都有意思同意發生的姦淫行為，又稱和姦，所以別於強姦。本罪之特別要件有二：一則本罪主體須為現有配偶之人，二則須有與他人和姦之行為。又因本罪為即成犯（有配偶之人與他人有姦淫行為時即已成罪），故以在通姦時有配偶為前提條件；如果在通姦後其婚姻關係因離婚或依法撤銷，也不影響通姦罪之成立。<br><br>
                                （二）本條後段規定&quot;相姦罪&quot;。乃謂認識與自己通姦者為有配偶之人，而與之和姦之犯罪。本罪主體不以有配偶為必要，但未滿十四歲之女子，因無責任能力，所以不得為本罪主體；反為刑法第二百二十七條第一項與幼童性交罪之被害人。另十四歲以上未滿十六歲之子女則為刑法第二百二十七條第三項之被害人，亦不得為本罪之主體。所以行為人與此等人（上述幼童）相姦者應成立與幼童性交罪，不成立通姦罪。
                            </p>
                        </b-card-text>
                    </b-card-body>
                    </b-collapse>
                </b-card>
                <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block v-b-toggle.accordion-55 variant=""
                        >配偶與人發生一夜情是否構成通姦</b-button
                    >
                    </b-card-header>
                    <b-collapse
                    id="accordion-55"
                    accordion="my-accordion"
                    role="tabpanel"
                    >
                    <b-card-body>
                        <b-card-text>
                            <p>
                                通姦罪是在處罰有配偶與人通姦之行為，相姦人並不限於良家婦女，即使是娼妓或是二奶，亦不阻卻構成要件的成立。所以配偶花錢嫖妓將構成通姦。亦即通姦罪之成立與是否花錢無關。<br><br>
                                惟值得注意的是，對未滿十六歲之男女為性交或猥褻之行為<br />
                                刑法認定未滿十六歲的人對於性不具有自主能力，因此只要與十六歲以下的人發生性關係，不管當事人的意願如何，都構成犯罪行為。<br><br>
                                倘若性行為的發生違反了當事人的意願，那當然構成刑法第二百二十一條的強制性交罪；若當事人未滿十四歲，根據刑法第二百二十二條，必須加重刑罰。<br><br>
                                如果是在兩情相悅的情況下呢？刑法並不認定那是兩情相悅。<br />
                                根據刑法第二百二十七條，對未滿十四歲者為性交，處三年以上十年以下有期徒刑，對十四歲以上十六歲以下之人為性交，處七年以下有期徒刑。上述刑罰男女皆適用，但是未滿十八歲者犯之屬告訴乃論，而且可以減輕或免除其刑；因此常會這種情形出現：父母發現小孩偷嚐禁果後，雙方家長互告對方小孩強暴，後又因達成和解而撤回告訴。
                            </p>
                        </b-card-text>
                    </b-card-body>
                    </b-collapse>
                </b-card>
                <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block v-b-toggle.accordion-66 variant=""
                        >成立通姦罪一定要抓姦在床?</b-button
                    >
                    </b-card-header>
                    <b-collapse
                    id="accordion-66"
                    accordion="my-accordion"
                    role="tabpanel"
                    >
                    <b-card-body>
                        <b-card-text>
                            <p>
                                絕大部分犯罪往往都是在事後才被發覺，但我們不能因此說它就不是犯罪，問題是如何去認定與界定。<br><br>
                                所以刑事訴訟法第一百五十四條規定：「犯罪事實應依證據認定之，無證據不得推定其犯罪事實。」這裡的證據係指證據能力，也就是證據資格。要取得「證據能力」須經過嚴格的證明程序，包括法定證據方法（被告、證人、文書、鑑定、勘驗）和法定調查程序，刑事訴訟法對此訂有詳細的規定。於具備證據能力後則有所謂證明力的問題，法官對此採自由心證原則，即關於如何評價證據之證據價值（證明力）的原則。<br><br>
                                至於有罪之判決，必須證明至何等程序，最高法院曾謂：認定犯罪事實所憑之證據，雖不以直接證據為限，間接證據亦包括在內；但無論其為直接或間接證據，其為訴訟上之證明，必須達於一般人均可得確信其為真實的程度，而無合理之懷疑存在時，始得據為被告有罪之認定。<br><br>
                                所以通姦罪即使不是捉姦在床，但若從其他證據方法中，（例如：目擊者看到通姦人進出賓館，加上鑑定犯罪人做愛後擦拭的衛生紙上的精液之DNA等。）能達得到上述之有罪判決之確定程度者，仍可成立通姦罪。
                            </p>
                        </b-card-text>
                    </b-card-body>
                    </b-collapse>
                </b-card>
                </div>
              </div>
            </div>
            <div class="frame">
              <div
                data-bs-spy="scroll"
                data-bs-target="#list-example"
                data-bs-offset="0"
                class="scrollspy-example"
                tabindex="0"
              >
                <h2 id="s3" name="s3" ref="s3">外遇故事</h2>
                <div class="accordion" role="tablist">
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-1a variant=""
                        >外遇為何永遠斷不了？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-1a"
                      visible
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            我先生斷斷續續的外遇已經好幾年<br />
                            都是跟同一個女人<br />
                            那女人沒結婚，也沒交男朋友<br />
                            只要先生去，她都不會拒絕<br />
                            就這樣傷害了我很多年<br />
                            中間我們分分合合<br />
                            他跟我保證下次不會再犯了<br />
                            但是在私底下卻一再的背叛我<br />
                            我發現後，他向我承認 <br />
                            他去找她，想看她過的如何?<br> 
                            那女人用 msn 跟我說：我先生想跟她在一起，叫她等他恢復單身<br />
                            而那女人也跟我說：他若單身她不會拒絕他的<br />
                            後來我問他，他說只是想而已，也不一定真的如此做啊!<br />
                            我覺得好像沒讓我先生跟那女人再一起他不會甘心似的!<br />
                            我常常覺得他心裡是不是有毛病啊 ? 他自己說他自己也很矛盾<br />
                            其實我心裡真的是很痛，該如何處理這爛攤子呢?<br />
                            離婚是否真是最好的選擇?<br />
                            <br />
                            妳會痛，其實第三者也會痛，因為女人總是受的傷害多。<br />
                            因為妳和第三者的縱容，所以妳的先生肆無忌憚的，一而再再而三的游移在妳們之間。<br />
                            妳告對方又如何 ? 這只會助長他們之間的革命感情，就算關完出來也還會在一起。<br />
                            更甚者，也許妳的丈夫另找其他的女人<br />
                            對妳和對現在妳口中的第三者都是一樣的。
                            我只能跟妳說：大多數會花心的人都是狗改不了吃屎的。<br />
                            傳統社會都告訴我們：第三者是狐狸精，第三者不要臉；<br />
                            但那個坐擁三妻四妾的男人呢?!<br />
                            是不是都被社會病態的半遮掩半誇耀著他的厲害與大享齊人之福?!<br>
                            妳老公都叫第三者等他了，妳都不防範以後的事嗎？<br />
                            至少先讓自己經濟獨立，有養活自己的能力。<br />
                            要堅定相信自己。希望妳捫心自問，到底還愛不愛妳的丈夫，能忍受他的行為嗎?<br />
                            這不是成全不成全別人的問題，是成全妳自己，讓妳自己的心得到自由，凡事都在妳的一念之間，與其去恨那個第三者，不如恨妳的丈夫，是他的優柔寡斷害了你們三個人，讓妳們在情海裡反覆煎熬！<br>
                            感情的事真的不能說誰對誰錯，但是有婚姻也該有些分寸，如果妳先生，完全離不開那個女的，不如妳讓自己&quot;自由&quot;吧！<br />
                            與其等人來愛，不如多愛自己，自己要過什麼日子，妳自己可以掌握的，不該等著人家來給妳些什麼。多為自己想吧！<br>
                            你是為了小孩留在他身邊，還是為了賭氣，是真心無怨無悔的付出嗎?<br />
                            女人不要為難自己，若是你真的不想離婚，就為自己找些喜歡的事來分散注意力，放在他身上的比率少了，就不會那麼痛了!<br>
                            對於目前，離婚是最好的選擇。<br />
                            若你希望等到老年時，成為老來伴。<br />
                            此刻，你必須忍受心痛的感覺，直到他倦鳥歸巢!<br />
                            你回想愛他哪一點？值得你為他如此犧牲？<br />
                            若開心比難過少，痛苦一定比快樂多，就讓愛隨風而逝吧！
                            他得到他想要的，你得到自由。也許他會後悔，但你不必對他的決定負責。<br />
                            而你，卻必須對自己的人生負責啊！<br />
                            所有不幸福的人生，都是被自己耽誤的。<br />
                            人面對自己的選擇不能一再的後悔，想清楚再去做！
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-2a variant=""
                        >我的外遇與婚姻</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-2a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            我93年6月經朋友介紹認識老公（大我一歲），當時因為剛失戀受傷很重（交往2年半，前男友跟他的前女友跑了），所以陸續考驗了周圍追我的7個人半年左右，最後只剩老公還在撐，而當時因為他的外型不是我喜歡的（屬矮胖167公分75公斤，前男友是瘦高179公分70公斤），但因為個性很好，對我也很好，所以我猶豫了很久，加上朋友鼓吹我給他機會試試看，說他是好老公型的，所以94年2月開始試著跟他交往，由於他的個性好、老實、大方讓我感動之外，加上當時周圍的同事、朋友一個個結婚，而我也感染了那份氣息（想說交往1年趕快結比較不會有風險），結果在6月陪朋友看婚紗時就一時衝動一起訂了，10月初拍照、10底訂婚，然後95年3月底結婚。<br><br>
                            期間曾因新房裝潢、神桌及小姑等問題爭執，加上他的個性比較被動，以及婚前症候群（總覺得他太矮自己不是那麼愛他...）曾經想延後婚期....，然後5月中去了歐洲17天照理說應該很浪漫，又因為小姑LV禮物等問題起爭執，17天都沒有親蜜....，後來心裡一直有疙瘩，覺得自己不是那麼喜歡他，拖到7月才去結婚登記（本來還不想登記的）...結果印象中8月～10月都沒有親密（他靠過來我就逃開...），當時就在想這個婚是不是結得太衝動了？<br><br>
                            然後11月1次朋友的邀約中，我認識了我的外遇（高帥175公分70公斤），因為小我5歲，所以原本不敢多想，但沒想到在一次次的見面中我們竟彼此吸引（他並不知道我已婚）...最後在12月玩出火了，然後在95年2月中時，他從另一位朋友口中得知其實我已婚，他很生氣跟前女友復合.....而我因為跟老公也一直沒有親密關係，他也隱隱約約知道外遇（但不知道詳情，只知道他年紀小，不太可能有未來），所以跟老公協議離婚（老公一開始不答應，後來同意簽字登記但還是住在一起，半年考驗，若以後和好有小孩再登記結婚....所以現在家人都還不知道...）<br><br>
                            由於我已經登記離婚了，而4月時外遇跟女友因為某原因分手了，所以他希望我能趕快處理離婚搬出來，跟他在一起....但我竟然猶豫了，我發現我居然沒有勇氣跟家人講，面對同事、朋友（只有些熟的知道）....因為他才26歲，1個月4萬左右，而我31歲了，雖然我有車、積蓄、1個月6萬，跟他在一起不會有經濟問題（他做室內設計，我可以協助他投資房屋），但就是覺得有點不安穩；但我老公有車、有房（尚有房貸）、1個月9萬，對我又好1個月會給我1.5萬零用錢（現在當然沒有，以後若生小孩在給），我其實可以不用想那麼多選老公就好了，但重點卻在我跟他到現在已經1年2個月沒有親密關係了....可見我對他多麼沒感覺，如果為了生小孩而生，我又覺得太勉強自己...我若沒有真心愛老公，對他也不公平...而且以後要是有了小孩，我又外遇怎辦？<br><br>
                            唉..大家看到這一定覺得我太貪心了，但這突然變成了麵包和愛情的選擇.....我很想選愛情，但又覺得未來不保障（他太年輕有變數，雖然他說有想到我們的未來...會接CASE賺錢...），且老公這麼好的人我又不忍辜負；但選了老公又覺得愛情很可貴，失去他我會很傷心...
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-3a variant=""
                        >如果老婆外遇, 除了離婚就別無他法了嗎?</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-3a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            溝通！溝通！真的是一個很重要的橋樑~~<br />
                            夫妻倆人把話說清楚.了解原因~~<br />
                            婚姻不是兒戲~~要好好經營...<br />
                            也許   外遇只是一段插曲 ( 提醒你們調整彼此相處的方式 )~~不要輕易放棄婚姻~<br><br>
                            不要急著想要如何處理，而要先反省一下。
                            <ul class="mt-2" style="padding-left:18px;margin-bottom:0px;">
                              <li class="number">婚後做丈夫的還有像婚前那樣浪漫、有情調嗎？（多久沒有去餐廳、電影院了？）</li>
                              <li class="number">做丈夫的有那些壞習慣（不要說婚前就已經知道了），為何沒有為家人而修正？</li>
                              <li class="number">是否有那些不良嗜好？（不要說只是小事喔，別人眼中是大事）</li>
                              <li class="number">收入是否能支付所有開銷？（若不能，做老婆的當然要另想辦法囉）</li>
                              <li class="number">做丈夫的是否能滿足妻子的性慾（通常男人都會說“當然有”，其實不盡然喔）</li>
                              <li class="number">婚後做丈夫的是否有長進（隔壁的升官了，你是否有晉升？）是否有去進修課程？</li>
                              <li class="number">做丈夫的作息時間是否正常？</li>
                            </ul>
                            <br>
                            通常一個女人會有紅杏出牆的原因：<br />
                            <ul  class="mt-2" style="padding-left:18px;margin-bottom:0px;">
                              <li class="number">一時被情所迷惑。（通常女人都是覺得自己沒被關心呵護，心靈空虛，才會如此。此時最怕人財兩失，所以要注意財務）</li>
                              <li class="number">對丈夫、家庭已經很厭倦（上述的6個原因所造成），或性慾的需求。</li>
                            </ul>
                            <br>
                            對策：<br>
                            <ul class="mt-2" style="padding-left:18px;margin-bottom:0px;">
                              <li class="number">來個二度蜜月之類的計畫。當然是秘密計畫，但只是不小心被妻子發覺你有這個計畫。</li>
                              <li class="number">有意無意地透露從電視、報章雜誌看到（增進情趣）的方法，再試著去實行，包括做愛的方式、場合（偶爾做車床族也不錯，只要不被拍就好）。</li>
                              <li class="number" style="margin-bottom:0px;">除了有必要的工作時間外，盡量都留在家陪老婆，或者帶老婆出去玩（此時就要有共同的話題了，做丈夫的要去事先準備）</li>
                            </ul>
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-4a variant=""
                        >「台灣世界展望會」會長夫人-楊雀面對外遇的心路歷程</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-4a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h4>康健雜誌 76 期 文 / 黃惠鈴</h4>
                          <p>
                            楊雀＋杜明翰 ~ 讓我們再牽一次手<br />
                            一場生命中難以承受之重，卻讓他們重生，再次牽手相伴。<br>                             
                            有對夫妻幾乎從不爭吵，即使有孩子，每星期兩人還會刻意抽一天單獨約會，是外人稱羨的幸福佳偶，但結婚 15   年後的某一天，太太突然發現先生從婚後第 9 年起有了外遇。<br>
                            如果事情發生在你身上，你覺得你們彼此還能破鏡重圓，執手偕老嗎？<br />
                            楊雀 和她 先生，台灣世界展望會會長杜明翰就做到了。<br>
                            約 9 年前，當時在台灣微軟擔任副總的杜明翰，主動向楊雀坦承與公司女同事長達 6 年外遇，楊雀悲痛欲絕。<br>
                            她在學校當輔導老師，常帶一些媽媽團體，熟知美滿婚姻的幾大要素，不敢相信《外遇》這種事竟然會發生在自己身上？<br>
                            「他跟我說他有外遇時，讓我覺得最痛的，不是他有外遇，而是我的信心整個被擊垮。結婚 15   年來，我努力扮演好各種角色，那一剎那，我心想：我的生命到底怎麼了？」<br>
                            眨著雙明眸大眼，楊雀以低沈的嗓音透露，當時她第一個念頭是想死，其次是離婚。<br />
                            楊雀 7   歲時，爸爸外遇，排行老三的她從小聽媽媽說「男人不是好東西」、「女人要莊敬自強」。<br>
                            父母感情不睦，她心中暗許日後不跟媽媽拿同樣的劇本。<br />
                            她認為媽媽因為沒受什麼教育才吃苦，所以在窮困的經濟狀況下，從小她就工讀，一路向上唸到碩士，之後並跟大學時代即相識的杜明翰結婚。<br>
                            她自認為婚姻經營得還不錯，是眾人口中的好太太、好媽媽、好媳婦，不懂為什麼一路努力，到頭來卻跟她媽媽命運相同，也遭遇丈夫外遇？<br>
                            所以當她 40 歲那年，聽到杜明翰的告白，她脫口就說：<br /><br>
                            「男人真不是好東西！」說完，連自己都嚇一跳。<br>
                            她痛哭三天三夜，完全不看杜明翰一眼，她不相信最親近的丈夫怎麼會背叛她，為什麼她篤信的上帝要這樣待她？<br />
                            不過，也因為虔誠信仰宗教，她在悲傷中一直祈求，也相信神會帶領她，所以經歷三天三夜的痛苦後，第四天清晨她決定重建婚姻。<br>
                            當天恰巧是西洋情人節，兩人決定把這天訂為另一個結婚紀念日，代表兩人關係的新生。 <br>
                            但重建之路是很辛苦的，特別是事發後至少一、兩年內，而且楊雀也認識第三者，情緒更強烈。<br>
                            有時只是看到杜明翰的護照、瞥見路上的旅館，都會勾起她不舒服的感覺。<br><br>
                            比犯人更辛苦的獄卒<br />
                            事發後一個月，有一天楊雀開車時想起這件事，情緒再度激動，整個車在路上蛇行，非常危險，她趕緊停靠路邊，在車上嚎啕大哭，覺得眼前的生命之路如此難，怎能走得下去？<br>
                            自己都已經那麼委曲求全了，上帝還要她怎樣？<br />
                            難道不計對方的錯而饒恕她，這豈不便宜了對方？<br>
                            痛苦不已的她在車上想起聖經的故事，腦海中突然有個圖像：<br /><br>
                            她跟杜明翰都被關在監牢裡。<br>
                            因為不饒恕對方，等於也把自己一輩子關在牢房，如同獄卒看守犯人。<br />
                            何況獄卒比犯人辛苦，犯人在牢裡可以吃喝睡，獄卒卻必須全神貫注緊盯犯人，怎麼自由？除非把犯人放了。<br>
                            她猛然醒悟：把饒恕想成便宜對方，只是蛋糕上的糖衣，真正的內涵是──釋放自己！<br>
                            饒恕是為了自己！<br>
                            那天回家後，她提筆寫了封信給第三者。<br />
                            信中最後一段內容是：「耶穌愛你，我也愛你，我們夫妻願意一輩子成為妳的好朋友。」這段偉大的話，其實楊雀以「咬牙切齒」的心情寫下。<br>
                            既然這麼難，為什麼要勉強？<br />
                            受訪時她笑道，她知道那是該做的事─   不是為第三者，也不是為杜明翰，而是為了她自己，她不要被關在監牢裡！<br>
                            「饒恕跟對方改不改過無關，而是你不再因對方犯的錯而受罰」，楊雀說：「他跟我講有外遇，我覺得被捅了一刀。<br />
                            但是人在恨裡，就會把別人傷害你的事實，變成錄影帶、錄音帶，沒事就會再放一遍、聽一遍，就像拿一把刀在自己的胸口上再捅一刀，即使那個事實已經過去了。<br>
                            人家只捅你一刀，但到最後你會怪別人捅你千瘡百孔，坦白講，這不公道，其實九百九十九刀，都是自己捅上去的，不斷自我傷害。」<br>
                            當她把信寄出去後，她發現，原本心中第三者巨大的身影，頓時減了一半，儘管第三者後來並沒有回信。 <br>
                            要補償，就不可能有愛<br />
                            她也知道，如果要重建婚姻，絕不能一直把焦點放在外遇，特別是杜明翰已結束出軌，再去蒐集更多過去的證據，無疑與重建之路背道而馳。<br>
                            「我不問，其實也是在幫助自己，」楊雀說。   當陷於低潮的情緒中，她不質問杜明翰跟第三者之間更多的細節，只會很傷心地問杜明翰：「你為什麼要這樣對我？」然後要求杜明翰不用跟她講道理，但一定要在一旁陪伴她、抱抱她。<br>
                            另外，她在重建婚姻之初就跟杜明翰說好，選擇留在婚姻，不是要他做什麼補償。<br />
                            因為學輔導的她深知，兩人如果要繼續走下去，她絕不能擺高姿態，抱持「你犯錯後要回來，我倒要看看你做了哪些來彌補」的受害者心理，因為這麼做，只會讓杜明翰滿懷罪惡，無益婚姻。<br>
                            「就算真的補償，人會累，而且在補償的心理下，愛不可能出來，那是贖罪。<br />
                            結果等罪贖完了，對方可能還是會出走。」楊雀說。<br>
                            楊雀的寬恕態度，在杜明翰口中是「很重要的禮物」，因為：<br /><br>
                            「如果一個曾經犯過錯的人永遠在罪惡感當中，就永遠不健康。」<br />
                            事隔多年，戴著眼鏡長相斯文的杜明翰感性地說，被外遇者受的傷害不是外遇者補償就可撫平的。<br>
                            但當楊雀給出這個禮物，他很自然地回應，兩人之間就會產生信任感和連結，「那是一種重新建造的愛情，不是補償，」杜明翰認為。<br>
                            所以，在重建過程中，他學習體諒、學習忍耐、學習陪伴，即使一開始因為楊雀的情緒起伏而有點沮喪，一再覺得兩人互動又回到原點，但慢慢漸入佳境。<br>
                            第三者只是冰山一角<br>
                            與寬恕之路同時並進的，是整理、回顧自我之路。他們至少花一、兩年時間，好好整理自己，甚至上些自我成長、生命回溯的課程。<br>
                            面對外遇衝擊時，楊雀慶幸當下她問對了問題，她的生命到底怎麼了？<br />
                            逼使她面對自己。<br>
                            尤其面對外遇，與其把焦點放在第三者，更不如積極地重新找回自我。<br />
                            「外遇一般都在解決第三者的問題，那只是冰山一角，」楊雀說。<br>
                            雖然她長期做輔導工作，但大部份時間幫別人釐清問題，反而很少真的看自己。<br />
                            面臨生命至今最大的打擊，讓她有機會省思。<br>
                            她發現自己有個根深柢固的信念─必須要夠好，別人才會愛她，所以總在扮演別人期待的角色。<br>
                            另外，「愛」的關係應有「給」有「受」，但她發現她給的很自在，因為那是她建立自尊的來源；卻受得不自在，因為自覺不配。<br>
                            她也想起婚姻中的一些瑣碎，從中看到自我貶低的心態。<br />
                            例如，有天傍晚她從學校下課返家後，急忙脫掉高跟鞋、套裝，趕進廚房煮飯。<br />
                            隔一會兒，難得能早下班的杜明翰也回到家，踱步到廚房問了句：<br /><br>
                            「楊雀，狗餵了沒？」她聽了，差點沒把手中的刀鏟丟過去，生氣回道：「人都沒餵，還餵狗？」杜明翰被罵得落荒而逃。<br>
                            為什麼人家問餵狗沒，好意幫忙做家事，自己會那麼生氣？<br />
                            楊雀在整理心情的過程中捫心自問，領悟到原來因為從小家庭環境不如人，心中早有「人不如狗」、自卑的心理按鈕。<br>
                            一聽到先生回家先詢問狗，沒問候人，馬上無意識地覺得對方真是瞧不起人，她的地位不如狗。於是她為童年的失落好好地哭，也對過去重新解讀跟反應，重新改寫自己的人生劇本。<br>
                            「這真的是人生很重要的功課：去察覺你內在的東西是什麼，……當人願意面對自己最脆弱的部份，其實人就自由了。」<br>
                            楊雀認為，除非人願意回頭面對自己的問題，遇見真正的自己，否則生命無解。<br>
                            透過一點一滴整理生命的枝枝節節，她原本以為杜明翰欠她很多、對不起她，但到後來，當她願意面對自己內部根深柢固的結時，更能跳脫受害者與迫害者的關係。<br>
                            沒有委曲求全，而是發自內心，她對杜明翰說：<br />
                            「誰欠誰，算不清！.........難為你了，前 15   年娶了一個聖人，不是一個女人。」<br>
                            巨人終於消失<br>
                            脫掉一些面具後，兩個人開始有「原來婚姻生活可以這樣過」的感覺，發現彼此更多可愛之處。<br>
                            可是當楊雀覺得自己已恢復得差不多時，老天又給她另一個試煉。<br>
                            那是她寫信給第三者隔兩年多後，杜明翰因公司辦兩天的員工旅遊，要帶她跟孩子一起去，不過杜明翰也事先告訴她，第三者會跟他們坐同一輛遊覽車。<br>
                            楊雀原本以為自己可以承受，可是出遊當天一見到第三者：「整個新仇舊恨又上來。」<br>
                            一到目的地的旅館，她就開始大哭，又問上帝她在重建婚姻的前兩年最常問的一句話：「此路甚難，誰能走？」原來她以為事情都過去了，其實不然。<br>
                            隔天回程他們與第三者又坐同一部車。<br>
                            心情低落的楊雀埋頭猛看書，卻正巧看到書上，有篇故事描述一個曾受迫害的女子，多年後在異地公開演講「寬恕」的主題時，赫然見到當年凌虐她的敵人。<br>
                            當敵人向她伸出手時，她原本想應付了事，勉強握住敵人的手後，竟然克服心中障礙，激動地抱住對方，兩人在愛裡獲得救贖，並進一步領悟饒恕的真意。<br>
                            這篇故事觸動了楊雀，她知道這是她的契機。<br>
                            她在書後的空白頁寫下「我真的在耶穌裡全心全意原諒你，耶穌愛你，我也愛你……」的話語並撕下，往前走向第三者坐的位子。<br>
                            「那是我一輩子走過最艱難的路，真的叫咬緊牙根，含著眼淚，但我知道跟她無關。」楊雀清楚內心仍存著未理清的情緒，她必須勇敢往前。<br>
                            她走到對方的座位旁，輕輕拍拍正閉眼休息的第三者，遞出寫好的紙條，對方的眼眶立即泛紅。她問第三者，「可不可以談一下？」對方往內挪，讓楊雀坐下。<br>
                            接著，楊雀強迫自己做件事：去拉對方的手。<br>
                            兩人的手相握的那一剎那，她發現她看到的，是一個也需要別人疼愛的平凡女子。<br />
                            「這對我來說很重要，巨人消失了！」楊雀回憶。而她給自己的煎熬也消失。<br>
                            那次接觸大概 20 分鐘，第三者不久後離職，楊雀和她沒再聯絡。<br />
                            隔了快 1   年，春天時節，楊雀在服務的學校收到那女孩寄來的信，除了表達歉意，也感謝楊雀在生命遭逢艱難時，還一再原諒她，給她很大的勇氣重新面對人生。<br>
                            當楊雀看到信，她知道因外遇事件引起的糾葛仇恨，終於可以劃下句點。<br />
                            她至今還保留著這封信，視為上天給她的禮物。<br>
                            「你聽來會覺得怎麼這麼充滿神蹟神妙？<br />
                            但其實很多所謂的神蹟，不是真的很玄妙，而是人明明知道怎樣做比較好，卻不願意去做。」楊雀說。<br>
                            她常想像獄卒與犯人的圖像，她明白釋放的路是必走之途，即使外遇的人不回頭，藉由釋放，也能給自己一個公道的對待，一生不用纏繞在對方的身上。<br>
                            「真正到最後的關鍵點，還是要找回真正的自我，」楊雀說。<br />
                            走過這場桃色風暴，杜明翰與楊雀的感情更堅。<br>
                            杜明翰一年多前從企業界退出，獻身於非營利組織「台灣世界展望會」，為各地需要幫助的人奔走。而楊雀幾年前也放棄教職，現在每星期至少有兩天到展望會作義工。<br>
                            「我們現在站在同一個根基上，彼此信賴，一起承受生命之恩，那種親密戰友的感覺很棒！」杜明翰說。<br>
                            而楊雀更說，這幾年她跟杜明翰之間的相處很真實，<br />
                            可以自在地在對方面前作自己， 「這是人生很大的禮物！」
                          </p>
                        </b-card-text>
                      </b-card-body>
