                      <b-card-body>
                        <b-card-text>
                          <p>
                            角逐台北市建商公會理事長一年半，賴正鎰卻在昨天(14日)突然宣布退選，外傳是受到最近偷拍黃晴曉案和自己疑似婚外情事件有關，也有傳聞表示，他的退出可能是知道自己會輸。因為他的退選，目前建商公會理事長可望由呼聲最高的麗寶董事長吳寶田出任。<br><br>
                            虎視眈眈，一刻也不敢鬆懈，台北市建商公會改選理事長，光是警力加保全就出動了百來位戒備，從一樓大廳數十位，向下延伸，地下一樓會場外人數最多，至少40名警察在休息室standby，靠近會場的走道則有便衣加刑警8名、保全4名，這麼大陣仗戒備，今天選舉卻意外和平收場，就是因為候選人之一的賴正鎰突然宣布退選。<br><br>
                            建商公會現任理事長蔡竹雄表示，「鄉林建設賴理事長，本身做法跟台北市的做法有不一樣，我們是希望以和平圓滿來完成這個事情(選舉)，因為做這個理事長是要有倫理道德的，倫理道德就是起碼，你到台北市，你至少要做理事之後，再做別的。」<br><br>
                            大聲唱名，台北市建商公會改選理事長，會員代表擠在計票板旁等著看結果，麗寶董事長吳寶田則是和其他人邊閒聊，看起來心情相當不錯的他，這回呼聲最高，可望拿下三分之二席次，高票當選理事長，他的勁敵賴正鎰則是從頭到尾沒有現身。<br><br>
                            吳寶田表示，「很感謝我們賴正鎰先生，在(選舉)前的一刻，他棄選我們台北市公會理事長，所以我在這裡也感謝他。」<br><br>
                            因為偷拍黃晴曉事件，且自己還被平面媒體爆料疑似婚外情，讓賴正鎰心情大受影響，選擇放棄爭了一年半的台北市建築公會理事長頭銜，對好勝心強的賴正鎰來說，這是第一次，可以想像他現在的心情恐怕不太好受。
                          </p>
