                        <b-card-text>
                          <p>
                            布希和第一夫人蘿拉的婚姻亮起了紅燈？最近美國小報爆料，布希和蘿拉關係持續惡化，兩人不但已經貌合神離，布希甚至有外遇，對象就是國務卿萊斯。<br />
                            <br />
                            2004年布希提名萊斯擔任國務卿時，在萊斯臉頰上輕輕一吻，讓外界紛紛猜測起兩人的關係，且布希對萊斯言聽計從已是眾所周知之事，日前布希在聯合國開會時想上廁所，還傳紙條通知萊斯，後來只見布希起身，萊斯立刻補位，兩人的默契，可見一斑。<br />
                            <br />
                            布希與萊斯的曖昧關係，蘿拉當然是看在眼底，據傳她還因此和布希大吵一架，最後離家出走，搬到華府的「五月花」旅館小住。<br />
                            <br />
                            小報報導指出，雖然布希跟蘿拉早已失合，但為了國家形象，兩人才刻意在布希任內維持第一家庭和樂融融的模樣，不過兩人只打算撐過美國大選，等到布希明年一月卸任後，布希跟蘿拉就要離婚，屆時蘿拉能領到高達約六億台幣的贍養費。
