                          <h4>中國時報／葉宜欣／綜合報導 </h4>
                          <p>
                            今年三月離婚的廣末涼子，當初報載離婚原因是前夫岡澤高宏花心又愛亂花錢，不過岡澤也跳出來反指廣末有外遇，和瑛太搞婚外情。<br><br>
