                        <b-card-text>
                          <h4>中廣新聞網〔夏明珠報導〕</h4>
                          <p>
                            洋基隊巨砲A-Rod宣佈他的婚姻已經無法挽回，不過他宣稱那和謠傳中他的外遇沒有關係。<br><br>
                            A-Rod今天委託律師向邁阿密法院提請離婚，佛羅里達採行的是無過失離婚，意思是要求離婚的一方，不需要證明配偶犯錯，只要聲明兩人無法繼續共同生活，就可以獲得離婚判決。儘管如此，A-Rod宣稱他的妻子辛西亞說他外遇，完全是沒有根據的指控。不過A-Rod承認，對他們的兩個女兒而言，辛西亞是個好母親。<br><br>
                            辛西亞七月七號以A-Rod對婚姻不忠的理由，訴請離婚，雖然，離婚訴狀中沒有提到A-Rod外遇對象的名字，不過全世界都已經知道，A-Rod的緋聞對象就是流行樂界的大姊大瑪丹娜。儘管兩人都否認有私情，不過媒體似乎未審先判，紐約一家八卦報紙給A-Rod取了個外號，叫他出軌Rod。<br><br>
                            A-Rod向邁阿密法院提出的離婚申請，表明他願意繼續支付兩個女兒的全部生活費，並且希望共同擔負撫養的責任，至於他目前所住價值一千兩百萬美元的豪宅、汽車和其他財產，A-Rod說，他和辛西亞在2002年簽訂的婚前協議中，都有載明財產分配原則，據報導，辛西亞要房子、要車、要贍養費、要保險、還要許多律師拒絕透露的私人物品。不過A-Rod說，超出婚前協議的需索，他都不同意。<br><br>
                            A-Rod今年才和洋基簽下價值兩億七千五百萬美元的十年長約，他的生涯全壘打數已經累積到541支，這一季到目前為止打擊率三成28。
