                        <b-card-text>
                          <p>
                            性欲低落的男人需要補充睪固酮，來增加欲望，不過，補充過量，卻可能補出現問題！一名女變男的變性人持續補充睪固酮，男性第二性徵確實比較明顯，但隨之而來的欲望大增，導致外遇不斷，不斷偷吃，與女友之間關係生變。<br><br>
                            娘娘腔想變man 脾氣變暴躁<br><br>
                            另外，有些娘娘腔的男生，以為補充睪固酮，就可以讓自己變得更MAN，其實不然。新店耕莘醫院精神科暨心理衛生中心主任楊聰財表示，娘娘腔與睪固酮無關，就算補充太多，只能讓聲音變低沈，脾氣暴躁一點，但個性還是一樣娘，偶爾還是會不小心比出了蓮花指。<br><br>
                            該名女變男的變性人年紀約30歲，在完成變性手術之後，持續補充男性荷爾蒙，不僅長出了鬍子，聲音也變低沈了，從外表看確實與一般男人沒有兩樣。<br><br>
                            變成男人之後的她，卻也犯了全天下男人會犯的錯誤，那就是外遇！該名變性人向醫師表示，沒有施打男性荷爾蒙之前，對另一半很忠實，沒想到補充睪固酮之後，性欲高漲，甚至有些侵略性。<br><br>
                            成男人“才知男人為何好色”<br><br>
