                        <b-card-text>
                            <p>
                                婚姻可不是兒戲，因此法律上認定具有一定的行為能力的人方可具有行使結婚的行為，民法規定結婚法定年齡為：<br>
                                <ul class="mt-2">
                                    <li class="number">男性滿十八歲</li>
                                    <li class="number">女性滿十六歲</li>
                                </ul>
                                未成年者需經由法定代理人同意。 <br />
                                第九百七十四條 未成年人訂定婚約，應得法定代理人之同意。
                            </p>
                        </b-card-text>
                    </b-card-body>
                    </b-collapse>
                </b-card>
                <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block v-b-toggle.accordion-55 variant=""
                        >離婚協議書簽立後，他方不願至戶政機&nbsp;關共同辦理離婚登記，如何處理?</b-button
                    >
                    </b-card-header>
                    <b-collapse
                    id="accordion-55"
                    accordion="my-accordion"
                    role="tabpanel"
                    >
                    <b-card-body>
                        <b-card-text>
                          <p>
                            凡具有民法第一千零五十二條第一款至第十款之事由者，夫妻之一方可以請求判決離婚，檢視一下您是否有符合的情況，如果沒有，那很難訴請離婚，分居並不包括在內。不過，民國七十四年六月五日修正民法親屬編時加入第二款「如有難以維持婚姻之重大事由者亦可請求判決離婚」。<br><br>
                            有一些婦女有使用這一款規定訴請離婚，以目前正處於「事實分居」，實際上難以維持婚姻為理由訴請離婚，為應有證據的取得，例如：戶籍謄本、鄰里長的書面證明，子女、鄰居和丈夫談話之證明等，至於分居滿多久才可以，這要看法官認定了，認定的原則在於婚姻已無實質意義、雙方均無意挽回，以致感情日趨冷漠，形同陌路，婚姻基礎顯然已經動搖、難以維持婚姻等。
                          </p>
                        </b-card-text>
                    </b-card-body>
                    </b-collapse>
                </b-card>
                <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block v-b-toggle.accordion-66 variant=""
                        >誰可以當離婚證人?</b-button
                    >
                    </b-card-header>
                    <b-collapse
                    id="accordion-66"
                    accordion="my-accordion"
                    role="tabpanel"
                    >
                    <b-card-body>
                        <b-card-text>
                          <p>
                            證人，有確實的知曉夫妻雙方有離婚意願的二位成年的證人（親朋好友亦可當任證人），在合法的程序中，證人需要親自在離婚協議書上簽名蓋章，最保障的做好是四人面對面一起簽名蓋章。<br><br>
                            注意！如果僅一位代表二人簽名作證，或者一人拿了其他二個人的身份證簽名蓋章，或者證人並未真正詢問過夫妻雙方當事人的離意願時，即證人是無效的』，不但有偽造私文書罪，同時即使雙方已經辦了離婚程序，還可以到法院訴請離婚無效。<br><br>
                            另外，請不要隨意尋找在報章雜誌上刊登的「包離婚」服務，因為這而的證人最容易發生問題了。您可於離婚協議書上規定「雙方應有至戶政事務所登記之責」，雖然這規定的適用性需考慮，但是可防當事人反悔。
                          </p>
                        </b-card-text>
                    </b-card-body>
                    </b-collapse>
                </b-card>
                </div>
              </div>
            </div>
            <div class="frame">
              <div
                data-bs-spy="scroll"
                data-bs-target="#list-example"
                data-bs-offset="0"
                class="scrollspy-example"
                tabindex="0"
              >
                <h2 id="s3" name="s3" ref="s3">離婚故事</h2>
                <div class="accordion" role="tablist">
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-1a variant=""
                        >他選擇網友&nbsp;要和我離婚</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-1a"
                      visible
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h4>(聯合報 美樂／自由業／北縣三重）</h4>
                          <p>
                            <span style="font-weight:bold;">離婚進行曲!?</span><br>
                            老公第一次上網，是因為失業在家，我教他在家裏上網找工作、找資料。<br>
                            後來半夜發現他都流連於聊天室中。<br>
                            他說他只是順便看看大家都在聊些什麼，沒什麼大不了的，他又不會沈迷其中。<br>
                            不久他的行動電話有女網友的電話。<br>
                            他說他只是留著又沒有打，他說我不尊重他，不給他自由。我以為他失業了，時間多，壓力大，等找到了工作，一忙碌就會和網上的網友疏遠了。<br>
                            想不到他在網站上成立了一個家族，當起了族長，還辦了網聚，行動電話上的網友電話越來越多。每當我和小孩就寢的半夜三更，就是他活躍在聊天室的時間。<br>
                            打開電腦，帶起耳機，那是我進不去的世界。有時半夜醒來，他還守在電腦前，看我一靠近就馬上跳出畫面。<br>
                            他說他沒有妨礙我們，也沒有吵到我們睡眠，只是想有自己的世界。<br>
                            我知道他是在跟女網友聊天。<br>
                            他說她們Call他，他也不能不回。<br>
                            我說要聊天可以，到大的聊天室公開和大家聊呀。我只要求他不要一對一和那些女網友聊天，他說他做不到。<br>
                            廣大的網友，由聊天室介入我的家庭，我輸給了聊天室。<br>
                            別人家一次只要面對一個第三者，我卻要面對一大群。家庭輸給了聊天室，我敗給了網友。<br>
                            如果可以重來一次，我不會再教老公上網。<br>
                            後註：現在，他選擇了那些網友，準備要和我離婚??
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-2a variant=""
                        >浪漫的離婚</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-2a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h4>中國時報／圓（台北市）</h4>
                          <p>
                            和允中結婚的那一年，我們都還年輕，對於婚姻有著太多與眾不同的想法，例如在家不必做飯不生孩子、個人仍保有隱私的空間和時間等等。事後想來，正是這份反傳統的婚姻觀，拆散我和允中的婚姻。<br><br>
                            允中成長在一個傳統的基督教家庭裡，父親是牧師。他從小就立志要像父親一樣當牧師為上帝傳福音。雖然我也是一個受過浸水禮的基督徒，但是我的宗教信仰並不像允中那樣滲進生命裡面。<br><br>
                            所以，儘管我們有著共同的宗教信仰，但是我們的生活哲學卻是南轅北轍。為了愛我，他做了很多的犧牲和讓步，我並不知道，我在婚姻裡的腳步是凌亂的，是不知輕重的，是傷害的。<br><br>
                            我們結婚的時候，允中已經開始在做傳道的工作，結婚八年後，允中順利當上牧師，然而我卻依然沒有準備好當師母。<br><br>
                            在這八年當中，我們談過許多次離婚的事，但是基於不願影響他父親牧師的清譽，亦不願他的牧師前途因為離婚而受到影響，所以我們也就一直擱置著沒有將離婚這件事落實。<br><br>
                            在八年的婚姻途中，雖然我們各懷所思，但還不至於影響彼此相敬如賓的生活。在他當了牧師之後，我們夫妻的關係顯著的名存實亡，越來越像多年老友，而不再像是夫妻。<br><br>
                            是九月吧！一個星期天的晚上，允中溫和地問我：「你明天忙嗎？」我也很客氣地回答：「不忙，有事嗎？」允中說：「明天星期一，如果你不忙的話，我們就去把手續辦了吧！」我平淡地說：「好吧，反正遲早要辦。不過，是不是要寫個什麼協議書之類的文件啊」？<br><br>
                            於是我就拿起紙筆，允中口述，我執筆，因為沒有孩子，所以可寫的內容十分簡單，我們邊寫邊商議房子賣了之後，所得一人一半，除此之外我們沒有其他可以約定的事了。寫完之後，並立即請了兩個好友當見證人，四個人用完印，算是大功告成。<br><br>
                            第二天下午，我和允中有說有笑的走進戶政事務所，辦事的職員看完了協議書之後望著我和允中問：「你們兩位是當事人嗎？」我們一同回答：「是。」他又問：「這張協議書是誰寫的？」我們又一同回答說：「是我們寫的」。<br><br>
                            辦事員請我們稍等一下，當他再回來的時候，戶口名簿上已經註明了我們離婚的紀錄。<br><br>
                            五分鐘就可以解決的事，為什麼等12年？沒有人可以說得清楚。不過我和他都很高興這件事終於做完了。當天我們還手牽著手去吃了一頓法國大餐以資慶祝。<br><br>
                            離婚五年之後，我把以前共有的房子賣了，我如約的將所得的一半匯進他的帳戶，他也找到了一位理想的師母，並生下了一個很可愛的小壯丁。<br><br>
                            我怎麼知道的呢？因為那年我和我的再婚的先生到他家去作過客，還給了他小胖兒子一個紅包呢！
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-3a variant=""
                        >做了奶奶才離婚有想法</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-3a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h4>(中國時報／蕭碧瓊／台中）</h4>
                          <p>
                            <span style="font-weight:bold;">獨居時代的來臨!?</span><br>
                            遠嫁臺灣，每年過著空中飛人的生活，半年臺灣半年大陸，不免對政府法令不滿，心情也隨之不好。
                            初次回大陸娘家，聽到的第一則新聞是張老師離婚了。張老師一家住在娘家隔壁二十多年，一向家庭和睦，現在也子孫滿堂了，怎麼會這樣呢？<br />
                            <br />
                            「五十多歲離婚，不知在想些什麼？」這是媽媽談起時的口氣。「做奶奶了還離婚，丟她兒女的臉。」這是鄰居們的評價。<br />
                            <br />
                            印象中，張老師的先生，好像除了看書，餘外沒什麼愛好，跟他太太活潑開朗的個性，形成鮮明的對比。<br />
                            <br />
                            以前學生時代，時常聽說張老師夫妻管教小孩是出了名地認真，所以她的三個小孩也不負眾望地陸續接受高等教育，成為有用之材。<br />
                            <br />
                            我一直很納悶：家庭幸福，為什麼要離婚？直到有天遇到她女兒，才知道理由很簡單：父母都希望有個人空間。
                            她說，過去為了照顧家庭，父母親不得不放棄很多個人愛好，齊心協力照顧她們兄妹三人；現在他們兄妹全都在外地工作，家中二老的行為摩擦越來越大；母親喜歡熱鬧，父親則偏愛清靜，與其等矛盾加劇，不如趁早分開。現在父母像朋友般偶爾串串門子，兒女們也不覺得有什麼不好。<br />
                            <br />
                            聽到張老師的故事，再想想像我這樣夫妻分居兩地，過著獨居生活，也許是一種享受吧。
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-4a variant=""
                        >設計一個高EQ的離婚</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-4a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h4>中國時報／西嬋（新店市）</h4>
                          <p>
                            <span style="font-weight:bold;">女人話題!?</span><br>
                            我與前夫相戀二年後公證結婚，婚後一起前往美國C校唸書。從一下飛機開始，以前所察覺不到的差異便漸漸顯現出來，也因此愈來愈多的摩擦無法避免地產生。
                            我們三天一小吵，五天一大吵，大吵後會有二個月的冷戰期。同在一個屋簷下，卻經常把彼此都當隱形人。久了之後，竟也習慣這樣的生活而不覺得有任何不便。<br />
                            <br />
                            就在剛到美國後不久，我們發生了很嚴重的爭吵。說來可笑，我的前夫常開玩笑地把穿了一天的臭襪子丟到我身上，我在忍無可忍的情況下，把襪子塞在他正在喝的果汁裡，他馬上賞我一巴掌。
                            表面上我默默地接受這一巴掌，可是心裡卻有了新的決定。
                            雖然我和他仍是過著大小吵不斷的日子，但是由於我有更長遠的打算，所以並不覺得日子難過。<br />
                            <br />
                            <br />
                            終於，我大學畢業了。我拿到了很多研究所的入學許可，當然也包括C校。就在選擇時，我詢問他對我未來動向的期望。
                            他說「無所謂」（他是個很驕傲的人，絕不說出慰留的話）。
                            義無反顧地，我選擇了離他最遠的V校。
                            我請他用搬家車載我到V校，從C到V，我們開車開了四天。這一路上，他不斷地藉故對我發脾氣，一字經、三字經不時出現，可是，我忍住了羞辱，就為了那美麗的夢想。<br />
                            <br />
                            待他回C後二個月，有一天，我和他通電話。
                            我告訴他，到V之後這二個月，是我認識他以來最快樂的日子。
                            我並告訴他在我研究所畢業之前，不希望與他見面，因為我好不容易可以過二年沒有他的快樂日子（當時並沒有想過離開他）。其實他在電話中也常和我生氣，可是一點也不會影響我的情緒。由於我裝了來電顯示器，有時候，我還選擇不接他的電話呢！我的所有情緒由自己掌控。<br />
                            <br />
                            他因為我不再和他嘔氣，而漸漸在乎我的存在。但是此時的我，卻已漸漸不在乎他的存在了。有一次，他因為我隔天有考試，無法和他聊天而生氣（不給他面子）。我不受影響，且快樂地過了半年後（期間都沒有聯絡），打電話向他提出離婚的要求。
                            驕傲的他，口頭上馬上答應了，可是勸戒我：「別人看待離婚女人的特殊眼光，而且再婚不易。」我告訴他：「離開一個男人，並不是為了找另一個男人。沒有男人的生活，絕對比和不對的男人生活來得愉快。」<br />
                            <br />
                            不論如何，我積極地尋找律師，隔州辦理離婚，由於他不全力配合，這個婚離了二年才離成。
                            在這二年裡，我收拾起所有的脾氣、情緒，不敢和他有任何衝突，過去我已忍耐三年和他一起生活，再多個二年就可以自由、快樂，絕對值得。<br />
                            <br />
                            當時由於我為自己設定了一個美夢（離他遠遠地，過二年沒他的日子）。
                            為了美夢，我埋藏了三年的痛苦，結局是美夢成真，而且擁有更多的希望，離婚後，我竟驚覺「原來天空是那麼美」。
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                </div>
              </div>
            </div>
            <div class="frame">
              <div
                data-bs-spy="scroll"
                data-bs-target="#list-example"
                data-bs-offset="0"
                class="scrollspy-example"
                tabindex="0"
              >
                <h2 id="s4" name="s4" ref="s4">離婚新聞</h2>
                <div class="accordion" role="tablist">
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-11a variant=""
                        >李國超和前妻離婚的事&nbsp;女兒不願提？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-11a"
                      visible
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            藝人李國超和前妻離婚兩年，育有一女李尤。<br><br>
                            離婚時，李國超一直不願意告訴當年僅7歲的女兒，父母離異的事實，直到去年過年，他和前妻協議過後才向女兒坦言，「爸爸和媽咪已經離婚。」<br><br>
                            沒想到李尤只說，「大過年的，不提這個。」女兒早熟的回答令李國超感到驚訝，自此之後也不再提起這件事。<br><br>
