a',{directives:[{name:"scroll-to",rawName:"v-scroll-to",value:({ el: '#s5', offset: -145 }),expression:"{ el: '#s5', offset: -145 }"}],staticClass:"list-group-item list-group-item-action Item5",on:{"click":function($event){return _vm.selectItem('Item5')}}},[_vm._v("性騷擾防治及處理")]),_c('a',{directives:[{name:"scroll-to",rawName:"v-scroll-to",value:({ el: '#s6', offset: -145 }),expression:"{ el: '#s6', offset: -145 }"}],staticClass:"list-group-item list-group-item-action Item6",on:{"click":function($event){return _vm.selectItem('Item6')}}},[_vm._v("徵信案例")]),_c('a',{directives:[{name:"scroll-to",rawName:"v-scroll-to",value:({ el: '#s7', offset: -145 }),expression:"{ el: '#s7', offset: -145 }"}],staticClass:"list-group-item list-group-item-action Item7",on:{"click":function($event){return _vm.selectItem('Item7')}}},[_vm._v("法律問題")])])]),_c('div',{staticClass:"col-lg-8 col-md-8 col-sm-12"},[_c('div',{staticClass:"frame mt-4"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s1",attrs:{"id":"s1","name":"s1"}},[_vm._v("離婚通姦法律")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-1",modifiers:{"accordion-1":true}}],attrs:{"block":"","variant":""}},[_vm._v("離婚前要做的功課~")])],1),_c('b-collapse',{attrs:{"id":"accordion-1","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 結婚是人生大事，離婚也是。"),_c('br'),_c('br'),_vm._v(" 婦女新知基金會志工督導-方麗群，時常接到民眾來電諮詢離婚問題，態度不是傷心就是氣憤，但是除非是家庭暴力，方麗群總是勸他們不要在激動之下做任何決定，「與其將來後悔或麻煩不斷，不如現在冷靜思索再決定，為自己爭取更好的籌碼。」"),_c('br'),_c('br'),_vm._v(" 深思熟慮"),_c('br'),_c('br'),_vm._v(" 「先問自己『我到底要什麼？』」律師尤美女說，法律並不能解決感情的問題，一定要自己想清楚，婚姻是不是無法挽回了？這個人還值得你愛嗎？如果不值得，還留戀什麼？如果值得，為什麼不去爭取？"),_c('br'),_c('br'),_vm._v(" 想清楚、有了決定，才有策略。尤美女說，如果決定不離，那麼夫妻雙方就需好好檢視婚姻出了什麼問題，想辦法解決，或者請教婚姻諮商專家；如果決定要離，或者婚姻已無法挽回，暫時不要讓配偶知道自己有離婚念頭，先評估："),_c('br'),_c('br'),_vm._v(" 1. 雙方的財務狀況，自己能不能在資產方面獲得更大保障？"),_c('br'),_c('br'),_vm._v(" 2. 自己是否符合判決離婚十大要件中的任一項，目前掌握多少有利證據？如果沒有證據，就要開始蒐證。"),_c('br'),_c('br'),_vm._v(" 如果是家庭主婦，就得尋找離婚後的經濟來源、生活重心，「很多事要思考、安排，不冷靜怎麼行？」尤美女說。"),_c('br'),_c('br'),_vm._v(" 談離婚協議"),_c('br'),_c('br'),_vm._v(" 離婚有兩種方式：協議離婚及判決離婚。當雙方都有離婚意願，而且對離婚條件達成共識，就可以辦理協議離婚。"),_c('br'),_c('br'),_vm._v(" 法律並沒有規定離婚協議一定要寫哪些內容。婦女新知基金會法案部主任黃嘉韻說，文具行買到的離婚協議制式範本通常太簡略、別人的離婚協議內容也未必符合自己的需要，可以拿來參考，但不建議直接套用，還是要考慮本身的條件（如有無子女、經濟條件等）來寫。"),_c('br'),_c('br'),_vm._v(" 「凡是你們在乎的、可能引起爭議的事，都要談清楚，寫在離婚協議上，」律師尤美女說。協議內容建議包括："),_c('br'),_c('br'),_vm._v(" 子女監護權"),_c('br'),_c('br'),_vm._v(" 離婚是夫妻關係的結束，但親子關係仍然存在，未成年（未滿20歲）子女的生活照顧、財產及身份上的代理權及同意權（如銀行開戶、遷戶口），都需由有監護權的父母行使。"),_c('br'),_c('br'),_vm._v(" 子女監護權可以協議由父母其中一方取得，也可以父母共同監護。但婦女新知基金會志工督導方麗群建議最好不要共同監護，因為如果離婚後一方到外地工作，或根本不負責任，但各種學校文件、銀行資料都需要雙方簽名才有效，會非常麻煩。"),_c('br'),_c('br'),_vm._v(" 子女監護權往往是夫妻討論離婚時僵持不下的主因，尤美女指出。"),_c('br'),_c('br'),_vm._v(" 她建議雙方還是先協議離婚，到戶政機關辦理離婚登記後，雙方再請法院酌定子女監護權及探視權，但風險是法院未必把監護權判給自己；雙方必須提出自己較適合擔任監護人的證據，並由社工訪視，再由法院依「子女的最佳利益」決定監護權歸屬。"),_c('br'),_c('br'),_vm._v(" 方麗群說，不管夫妻關係最後走到什麼境地，孩子都是無辜的，要多想「怎樣做對孩子最好」，「不要把子女當成報復對方的籌碼，或者刺探對方離婚後生活的工具，這樣對孩子是很不健全的成長環境，」她提醒。"),_c('br'),_c('br'),_vm._v(" 她也建議，如果對方條件的確比自己好，監護權不妨讓給對方，還是可以藉探視維繫感情，「把自己離婚後的生活打理好，也很重要。」"),_c('br'),_c('br'),_vm._v(" 子女探視權"),_c('br'),_c('br'),_vm._v(" 沒有監護權的一方有探視子女的權利。離婚協議上最好寫明探視時間（例如每月第一、三個週六的上午九點到翌日晚上八點）、地點及接送方式、寒暑假能否出國、可否有他人陪同等。"),_c('br'),_c('br'),_vm._v(" 尤美女建議最好不要寫「隨時可以探視」。「隨時探視等於不能探視，」她說，常看到一方想要「隨時」去探望子女，結果被百般阻撓、刁難，甚至不開門也不接電話；也有人想藉隨時探視子女，窺探對方離婚後的生活，「不如先寫清楚探視細節，讓對方無法推託或藉機打擾。」"),_c('br'),_c('br'),_vm._v(" 子女扶養費"),_c('br'),_c('br'),_vm._v(" 不論哪一方得到監護權，父母雙方都仍然有義務扶養未成年子女、支付扶養費。"),_c('br'),_c('br'),_vm._v(" 扶養費可以依雙方收入多少來協議，另外也要談清楚交付時間、有無寬限期、交付方式（匯入戶頭比當面交付好，因為可留下匯款紀錄）等。"),_c('br'),_c('br'),_vm._v(" 常見的狀況是，離婚後一方不按時支付子女扶養費。這時另一方可以向法院提出民事訴訟，勝訴後再拿判決到法院民事強制執行處，請求法院執行對方名下的財產，包括動產、不動產，或薪資債權等。"),_c('br'),_c('br'),_vm._v(" 要預防這種麻煩，如果對方財力夠，可以請對方離婚時一次付清扶養費。"),_c('br'),_c('br'),_vm._v(" 尤美女則建議可在離婚協議上約定「若有一期未付，其後之期間視為亦已到期」，如果將來一方沒有按時支付，另一方可以一次請求未支付的總金額。"),_c('br'),_c('br'),_vm._v(" 例如，陳氏夫婦約定陳先生離婚後每月要付扶養費一萬元，但後來他常藉故拖延；陳家小孩現在13歲，距20歲成年還有84個月，陳太太可以要求陳先生一次付清10,000元×84個月＝840,000元，若對方仍不付，可以向法院起訴請求，也可以將離婚協議的這部份辦理公證，約定可逕受強制執行，即可不需經過訴訟。"),_c('br'),_c('br'),_vm._v(" 不過，道高一尺，魔高一丈。尤美女說，有些人離婚後存心不想撫養子女，於是採取「焦土政策」：辭去工作、轉移名下財產，讓法院無法強制執行。「遇到這種人，另一方只能自立自強了，」她說。"),_c('br'),_c('br'),_vm._v(" 財產"),_c('br'),_c('br'),_vm._v(" 台灣多數夫妻都沒有約定財產制度，因此自動適用「法定財產制」，夫妻各自名下的財產各自擁有，可以自由處置，債務也各自負擔。"),_c('br'),_c('br'),_vm._v(" 離婚時，夫妻可以自行協議如何分配財產，例如有外遇的一方為了表達歉意，想給對方較多財產，只要雙方達成共識就可以，法律不會介入。"),_c('br'),_c('br'),_vm._v(" 但如果雙方對財產分配無法達成協議，就要向法院請求剩餘財產分配。"),_c('br'),_c('br'),_vm._v(" 尤美女說，很多人誤以為離婚時自己一定能分到對方一半的財產，其實正確地說，應該是「婚後財產少的一方，可以要求財產多的一方給付差額的一半」，以保障經濟弱勢的一方，稱為「剩餘財產分配請求權」，必須在離婚後5年內、知道對方有剩餘財產的兩年內提出請求，超過期限就不能請求了。"),_c('br'),_c('br'),_vm._v(" 例如陳氏夫婦離婚時，陳先生全部的財產扣掉婚前財產、債務、贈與、繼承、慰撫金，最後剩100萬，而陳太太剩50萬，則陳太太可向先生要求25萬（[100－50]÷2＝25）。"),_c('br'),_c('br'),_vm._v(" 如果陳先生的財產計算出來是負債50萬，而太太有50萬，那麼陳太太不必負擔丈夫的債務，陳先生的財產以零計算，只能向太太要求25萬（[50－0]÷2＝25）。"),_c('br'),_c('br'),_vm._v(" 如果陳氏夫婦婚後一起努力買了兩棟房子，雖然都登記在先生名下，但離婚時陳太太仍可以要求分配財產。"),_c('br'),_c('br'),_vm._v(" 行使「剩餘財產分配請求權」時，可能遇到的問題是：對方有沒有財產？財產在哪裡？"),_c('br'),_c('br'),_vm._v(" 尤美女說，如果一方刻意將資產轉移到海外，那麼即使法院去調稅捐資料也查不到（因海外資產若不申報，就不易查到），因此只能就查得到的部份請求分配，有可能財產少的那一方反而要支付多的一方。"),_c('br'),_c('br'),_vm._v(" 不論是請求夫妻剩餘財產差額分配、子女扶養費、贍養費，為避免對方脫產，將來就算勝訴，對方也沒有財產可以執行，可以在起訴前或訴訟中，向法院聲請假扣押，但聲請人必須負擔擔保金（不超過請求金額的十分之一）。"),_c('br'),_c('br'),_vm._v(" 配偶如果已將資產轉到他人（如外遇對象）名下，這時一方可主張對方惡意處分婚後財產，要求追加計算離婚前五年的財產，這就屬於法庭上的攻防了。"),_c('br'),_c('br'),_vm._v(" 尤美女說，有些太太或先生很會賺錢，抱怨另一半扯後腿，無助家庭經濟，離婚時卻要分他財產，很不公平。她建議，一方如能證明另一方無助家庭經濟，可請求法官酌減或不分財產，如無法證明，原則上離婚前五年內的財產，對方都可以要求分配剩餘財產。"),_c('br'),_c('br'),_vm._v(" 如果雙方協議一定數額離婚，擔心日後他方又至法院請求分配剩餘財產，尤美女建議可以在離婚協議書上註明「夫妻雙方均不再依民法第1030-1條請求剩餘財產之分配」。"),_c('br'),_c('br'),_vm._v(" 保證人等責任"),_c('br'),_c('br'),_vm._v(" 如果是配偶銀行貸款的連帶保證人，離婚前務必要求配偶換保人；如果擔任配偶公司的負責人或股東，也要盡快解除職務，否則以後可能衍生難以預料的法律責任。"),_c('br'),_c('br'),_vm._v(" 住所"),_c('br'),_c('br'),_vm._v(" 離婚後誰搬出去，誰留在原住所？"),_c('br'),_c('br'),_vm._v(" 家中物品分配"),_c('br'),_c('br'),_vm._v(" 尤美女曾有一對當事人為了電鍋該歸誰而吵得不可開交；所以家中大小物品如何分配，也要說清楚寫明白。"),_c('br'),_c('br'),_vm._v(" 其他一切雙方認為重要的事"),_c('br'),_c('br'),_vm._v(" 判決離婚"),_c('br'),_c('br'),_vm._v(" 當雙方無法對離婚條件達成共識，或一方根本不肯離婚，就要上法院訴請離婚了。"),_c('br'),_c('br'),_vm._v(" 民法規定可以裁判離婚的要件請參考：離婚的十大要素"),_c('br'),_c('br'),_vm._v(" 只要符合其中一項，就可以請求法院判決離婚。"),_c('br'),_c('br'),_vm._v(" 現在日漸增多的離婚理由是「有前項以外之重大事由，難以維持婚姻」，但是否成立，需由法院判斷，而且必須是自己不是過錯較多的一方。"),_c('br'),_c('br'),_vm._v(" 例如，王先生打太太、趙太太外遇，造成婚姻失和，王先生、趙太太都是明顯造成婚姻破裂的主要可歸責者，所以他們都不能請求判決離婚，他們的配偶才可以。如果夫妻兩人都有責任，法院會衡量責任程度，責任較輕的一方才能請求離婚。"),_c('br'),_c('br'),_vm._v(" 讓證據說話"),_c('br'),_c('br'),_vm._v(" 刑案鑑識專家李昌鈺常說「證據會說話」，法律界也有「舉證之所在，敗訴之所在」的名言。要打贏離婚官司，證據愈充足，勝算愈大。"),_c('br'),_c('br'),_vm._v(" 例如，因家庭暴力而以「不堪同居虐待」訴請離婚，可以蒐集的證據包括：驗傷單、照片、可以證明對方施虐的錄音帶、被撕毀的衣物、悔過書、人證（子女、親友）等；如果是精神虐待（如語言暴力、限制行動等），可蒐集錄音帶、請親友及鄰居作證。"),_c('br'),_c('br'),_vm._v(" 「雖然證據強弱由法官決定，但累積多一點證據，就有一定份量，」尤美女說。"),_c('br'),_c('br'),_vm._v(" 她說，近年常見夫妻因外遇而引起語言或肢體暴力，但要用「配偶通姦」訴請離婚通常勝算較小，因為證據（證明對方與外遇對象發生性行為）不易取得，因此可以搭配「其他重大事由」（例如兩人個性不合、時常爭吵、離家出走，證明婚姻已難以維持）訴請離婚。"),_c('br'),_c('br'),_vm._v(" 「協議離婚」比「請求判決離婚」好"),_c('br'),_c('br'),_vm._v(" 婦女新知基金會志工督導方麗群建議，有意訴請離婚的人可先去法院家事法庭旁聽、做筆記，看看離婚官司的場面，法官會問什麼問題、要哪些證據、雙方如何為自己辯解，先有所準備，將來上法庭才不會怯場。"),_c('br'),_c('br'),_vm._v(" 不過，她建議還是先考慮協議離婚，不得已時才請求判決離婚。"),_c('br'),_c('br'),_vm._v(" 「把『情』放在第一位，不要一開始就用『法』箝制對方，」她說，尤其是當對方有過失時，通常心懷愧疚，願意答應對配偶較有利的條件，可是如果配偶一開始就撂下一句「法院見」，只會激怒對方也撕破臉；更何況，請求法院判決離婚不一定勝訴，法院的判決也未必比當初協議的條件更好。"),_c('br'),_c('br'),_vm._v(" 方麗群也看過很多父母打離婚官司要孩子出庭作證的案例，「讓孩子看到父母反目成仇、惡言相向，對他們的傷害非常大，」她說，站在保護子女的立場，夫妻也該先考慮協議離婚，「都是成年人了，應該坐下來談，好聚好散。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-2",modifiers:{"accordion-2":true}}],attrs:{"block":"","variant":""}},[_vm._v("什麼狀況下可請求離婚?")])],1),_c('b-collapse',{attrs:{"id":"accordion-2","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 若夫妻兩人無法好聚好散，有一方堅持不肯離婚，但一方已不想維持婚姻了，那麼還有另外一種離婚方式判決離婚。根據民法第一千零五十二條第一項，夫妻之一方，有下列情形之一者，他方得向法院請求離婚： "),_c('ul',[_c('li',{staticClass:"number"},[_vm._v("重婚者")]),_c('li',{staticClass:"number"},[_vm._v("與配偶以外之人合意性交。(此條修正後，適用於男女/男男/女女之間)")]),_c('li',{staticClass:"number"},[_vm._v("夫妻之ㄧ方受他方不堪同居之虐待者")]),_c('li',{staticClass:"number"},[_vm._v("夫妻之ㄧ方對於他方之直系尊親屬為虐待或受他方之直系尊親屬之虐待致不堪為共同生活者")]),_c('li',{staticClass:"number"},[_vm._v("夫妻之ㄧ方以惡意遺棄他方在繼續狀態中者")]),_c('li',{staticClass:"number"},[_vm._v("夫妻之ㄧ方意圖殺害他方者")]),_c('li',{staticClass:"number"},[_vm._v("有不治之惡疾者")]),_c('li',{staticClass:"number"},[_vm._v("有重大不治之精神病者")]),_c('li',{staticClass:"number"},[_vm._v("生死不明已逾三年者")]),_c('li',{staticClass:"number"},[_vm._v(" 因故意犯罪，經判處有期徒刑逾六個月確定。(因故意犯罪的意圖，推測日後極有可能再犯，於此保障他方配偶得訴請離婚) ")])]),_vm._v(" 有前項以外之重大事由難以維持婚姻者，夫妻之ㄧ方得請求離婚！ "),_c('br'),_vm._v(" 但其事由應由夫妻之ㄧ方負責者，僅他方得請求離婚。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-3",modifiers:{"accordion-3":true}}],attrs:{"block":"","variant":""}},[_vm._v("何謂協議離婚?")])],1),_c('b-collapse',{attrs:{"id":"accordion-3","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 協議離婚在法律上稱之為「兩願離婚」或「合意離婚」，協議離婚是指雙方都有意願要結束婚姻的情形下，協議離婚。 如果要離婚的當事人未成年，雖然想要與另一方協議離婚，一定要經由法定代理人（通常是父母）的同意才可喔，否則離婚行為是無效的！ 或您還沒至戶政事務所登記結婚，而想要辦理離婚的話----依法結婚而未辦理結婚戶籍登記，已有婚姻的效力了，一定要到戶政機關先辦理結婚登記，然後同時辦理離婚登記，這是必需要的手續喔！否則離婚也算是無效的。"),_c('br'),_c('br'),_vm._v(" (1) 書面資料，就是所謂的『離婚協議書』，載明了雙方的離婚意願及註明在婚姻關係中所有的財產及子女監護歸屬（針對二十歲以下未成年子女）、子女教養費用的給付、子女探視的時間起迄、贍養費及其他特定的約定等事宜清楚的詳列在協議上，清楚詳列對彼此都是一種保障。離婚協議書並沒有範本，需要依造雙方的協議來訂定，需以清楚、詳細為原則。"),_c('br'),_c('br'),_vm._v(" (2) 證人，有確實的知曉夫妻雙方有離婚意願的二位成年的證人（親朋好友亦可當任證人），在合法的程序中，證人需要親自在離婚協議書上簽名蓋章，最保障的做好是四人面對面一起簽名蓋章。 "),_c('br'),_vm._v(" 注意！如果僅一位代表二人簽名作證，或者一人拿了其他二個人的身份證簽名蓋章，或者證人並未真正詢問過夫妻雙方當事人的離意願時，即證人是無效的』，不但有偽造私文書罪，同時即使雙方已經辦了離婚程序，還可以到法院訴請離婚無效。"),_c('br'),_c('br'),_vm._v(" (3) 登記，依戶籍法規定離婚為登記制，雙方必須在簽妥離婚協議書，攜帶身份證、戶口名簿等資料，一起在戶籍所在地之戶政事務所辦理離婚登記，登記完成後離婚才生效力；若沒有完成登記的程序，就不算是完成離婚手續。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-4",modifiers:{"accordion-4":true}}],attrs:{"block":"","variant":""}},[_vm._v("如何擬定離婚協議書的內容 ?")])],1),_c('b-collapse',{attrs:{"id":"accordion-4","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 協議離婚的方式，根據現行的民法，離婚制度分為二種：兩願離婚與判決離婚。倘若夫妻感情不睦，夫妻都體認到婚姻再繼續下去已經沒有意義，那麼好聚好散，兩人可以兩願離婚的方式結束婚姻。根據民法第一千零五十條，兩願離婚應以書面為之，有兩人以上證人之簽名並應向戶政機關為離婚之登記。"),_c('br'),_c('br'),_vm._v(" 那離婚協議書的內容該怎麼寫呢？法律對此並無規定；為了避免離婚後在法律上還牽扯不清，建議妳將離婚後可能發生的法律關係變化瞭解清楚後，如剩餘財產分配的問題、子女監護權的問題，再與另一方討論並作成協議。"),_c('br'),_c('br'),_vm._v(" 另外，有很多夫妻會因子女監護權或者財產分配問題無法達成協議，而遲遲不肯離婚。其實，您可以去做婚姻協商；或者雙方先去辦離婚，再就小孩歸誰監護問題，向法院提出酌定子女監護權之訴訟，法官會依子女最佳利益作出判決。"),_c('br'),_c('br'),_vm._v(" 離婚協議書通常約定的內容有："),_c('br'),_c('br'),_c('ul',{staticStyle:{"margin-bottom":"0px"}},[_c('li',{staticClass:"number"},[_vm._v("子女監護權誰擁有")]),_c('li',{staticClass:"number"},[_vm._v("子女生活費用誰來付")]),_c('li',{staticClass:"number"},[_vm._v("協議探視子女之時間")]),_c('li',{staticClass:"number"},[_vm._v("夫妻財產之分配")]),_c('li',{staticClass:"number"},[_vm._v("債務之載明(債權人或債務人)")]),_c('li',{staticClass:"number",staticStyle:{"margin-bottom":"0px"}},[_vm._v("請求對方付贍養費 + 子女撫養費(或是要求精神賠償)")])])])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-5",modifiers:{"accordion-5":true}}],attrs:{"block":"","variant":""}},[_vm._v("如何辦理離婚程序?")])],1),_c('b-collapse',{attrs:{"id":"accordion-5","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 離婚程序-a. 協議離婚"),_c('br'),_vm._v(" 雙方簽署書面文件，並有兩名證人簽名 --> 持相關證明文件到戶政機關，辦理離婚登記。"),_c('br'),_c('br'),_vm._v(" 離婚程序-b. 裁判離婚"),_c('br'),_vm._v(" 根據民法1052條規定，向法院提出 --> 持相關證明文件到戶政機關，辦理離婚登記。"),_c('br'),_c('br'),_vm._v(" 離婚登記手續"),_c('br'),_c('br'),_vm._v(" 申請人"),_c('br'),_vm._v(" 1. 協議離婚：當事人雙方。"),_c('br'),_vm._v(" 2. 判決離婚：當事人之一方（雙方均不申請時可由利害關係人-申請）。"),_c('br'),_vm._v(" 3. 受委託人（協議離婚具有正當理由經戶政事務所核准者）。"),_c('br'),_c('br'),_vm._v(" 應附書表及證件"),_c('br'),_c('br'),_vm._v(" 1. 離婚協議書或法院判決及判決確定證明書或經法院核定之調解離婚書。"),_c('br'),_c('br'),_vm._v(" 2. 協議離婚：必須雙方同時到場申報，以登記日為離婚生效日期。"),_c('br'),_c('br'),_vm._v(" 攜帶離婚協議書、當事人之戶口名簿、國民身分證、印章、二個月內二吋相片三張。"),_c('br'),_c('br'),_vm._v(" 3. 判決離婚：由當事人之一方到場辦理，攜帶法院判決及確定證明書、戶口名簿、國民身分證、印章、二個月內二吋相片三張。（並提供另一方之資料以便通報）。"),_c('br'),_c('br'),_vm._v(" 4. 大陸離婚者："),_c('br'),_vm._v(" (1) 協議離婚：大陸地區婚姻登記機關核發之離婚證，並經海基會驗證。"),_c('br'),_vm._v(" (2) 法院民事調解離婚：大陸法院民事調解書及收受該民事調解書送達憑證，並經海基會驗證。"),_c('br'),_vm._v(" (3) 法院民事判決離婚：大陸法院民事判決書及確定證明書，經海基會驗證並經我國法院裁定認可"),_c('br'),_vm._v(" (4) 如委託代辦時，委託書亦經海基會驗證。"),_c('br'),_c('br'),_vm._v(" 5. 國外離婚者：書約須經當地我駐外單位驗證，有加註「符合行為地法」以行為地所規定之離婚生效日為準，若未加註「符合行為地法」須依我民法所規定之方式離婚，以完成戶籍登記為生效日。如係外文者，並翻譯中文經法院或民間公證人認證。如委託代辦時，授權或委託書亦應驗證"),_c('br'),_c('br'),_vm._v(" 6. 當事人之戶口名簿、國民身分證、印章、二個月內二吋相片各三張（換領身分證）。"),_c('br'),_c('br'),_vm._v(" 7. 協議離婚必須雙方同時到場申報，以登記日期為離婚生效日期。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-6",modifiers:{"accordion-6":true}}],attrs:{"block":"","variant":""}},[_vm._v("離婚約定支付贍養費之一方違約而不給付，如何請求?")])],1),_c('b-collapse',{attrs:{"id":"accordion-6","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 第一千零五十七條夫妻之一方，因離婚而生活陷於困難致無法維持生活者，得向他方請求贍養費。"),_c('br'),_vm._v(" 有左列情形之一者，視為生活陷於困難："),_c('br'),_c('ul',[_c('li',[_vm._v("夫妻之一方於離婚後，因照顧共同生活之未成年子女，致不能期待其從事足以維生之工作時。")]),_c('li',[_vm._v("夫妻之一方，因年老、疾病、傷殘或精神耗弱致不能期待其從事足以維生之工作時。")]),_c('li',[_vm._v("夫妻之一方因結婚、育兒或從事家務勞動而中斷工作，致不能期待其於離婚時立即從事足以維生之工作時。")])]),_vm._v(" 第一千零五十七條之一依前條規定負擔贍養義務者，得依其財產收入、營業收益情況及其他扶養義務之負擔支付贍養費。"),_c('br'),_vm._v(" 第一千零五十七之二有左列情形之一，足認對於贍養義務人顯不公平者，不發生贍養費請求權："),_c('br'),_c('ul',[_c('li',[_vm._v("結婚期間在一年以下者。")]),_c('li',[_vm._v("贍養權利人對於他方或其直系親屬犯罪而受徒刑之宣告者。")]),_c('li',[_vm._v("贍養權利人因自己輕率而造成困難者。")]),_c('li',[_vm._v("其他重大之事由。")])]),_vm._v(" 第一千零五十七之三贍養費支付之數額，依婚姻關係存續中之生活程度定之。"),_c('br'),_vm._v(" 法院應審酌左列各款情形，決定贍養費之數額："),_c('br'),_c('ul',[_c('li',[_vm._v("雙方之財產、年齡、身體及健康狀況。")]),_c('li',[_vm._v("雙方之社會地位與生活水準。")]),_c('li',[_vm._v("贍養義務人應負扶養義務之人數。")]),_c('li',[_vm._v("造成婚姻破裂之責任因素。")]),_c('li',[_vm._v("結婚之久暫及對家庭之貢獻。")]),_c('li',[_vm._v("其他影響贍養費支付之重大事由。")])]),_vm._v(" 贍養費義務人應一次支付贍養費，但法院得依贍養義務人之聲請命其領擔保定期給付之。"),_c('br'),_vm._v(" 第一千零五十七條之四贍養費請求權因贍養權利人再婚或死亡而消滅。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-7",modifiers:{"accordion-7":true}}],attrs:{"block":"","variant":""}},[_vm._v("兩願離婚後夫或妻自己之債務，應由誰負責償還?")])],1),_c('b-collapse',{attrs:{"id":"accordion-7","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 夫或妻在婚前欠的債及婚姻關係存續中所負的債務，都由夫或妻個人並就共同財產（任一方因管理自己名下之特有財產所生之債務或超越日常家務代理權之權限所負之債務，需由該方之特有財產支付）負清償之責，所以並不是夫債妻還或妻債夫還的觀念，換句話說雙方是獨立的、有行為能力的個體，個人之負債，另一方沒有償還之必要。"),_c('br'),_c('br'),_vm._v(" 夫或妻婚後任一方之債務，除非一方替另一方所開支票背書或做保人時，當債務人還不出錢時，背書或當保人之一方則有清償債務之責任，這是債務關係並非源於婚姻關係而來，不管是何種財產制度，需要替配偶負連帶責任的機率很小。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-8",modifiers:{"accordion-8":true}}],attrs:{"block":"","variant":""}},[_vm._v("離婚後，能否約定子女改從母姓?")])],1),_c('b-collapse',{attrs:{"id":"accordion-8","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 子女跟誰姓？根據民法第一千零五十九條，子女原則上從父姓。除非妻子無兄弟，夫妻才可約定子女的姓氏。如果是在招贅婚，子女原則上從母姓，但可任意約定子女從父姓。"),_c('br'),_c('br'),_vm._v(" 依現行民法之規定，夫妻離婚由母取得子女之監護權，子女尚無法從母姓。因子女姓氏係依父母婚姻狀態來決定，民法１０５９條規定，普通婚姻從父姓，但母無兄弟可約定從母姓，贅婚從母姓，不因離婚取得監護權而改變。同上述問題，較為特殊狀況，倘母具有原住民身分，依原住民身分法第四條之規定，子女可從母姓取得原住民身分，但若子女原已具有原住民身分，即無法適用該規定從母姓。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-9",modifiers:{"accordion-9":true}}],attrs:{"block":"","variant":""}},[_vm._v("未滿二十歲之未成年，如何辦理協議離婚?")])],1),_c('b-collapse',{attrs:{"id":"accordion-9","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 婚姻可不是兒戲，因此法律上認定具有一定的行為能力的人方可具有行使結婚的行為，民法規定結婚法定年齡為："),_c('br'),_c('ul',{staticClass:"mt-2"},[_c('li',{staticClass:"number"},[_vm._v("男性滿十八歲")]),_c('li',{staticClass:"number"},[_vm._v("女性滿十六歲")])]),_vm._v(" 未成年者需經由法定代理人同意。 "),_c('br'),_vm._v(" 第九百七十四條 未成年人訂定婚約，應得法定代理人之同意。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-10",modifiers:{"accordion-10":true}}],attrs:{"block":"","variant":""}},[_vm._v("離婚協議書簽立後，他方不願至戶政機 關共同辦理離婚登記，如何處理?")])],1),_c('b-collapse',{attrs:{"id":"accordion-10","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 凡具有民法第一千零五十二條第一款至第十款之事由者，夫妻之一方可以請求判決離婚，檢視一下您是否有符合的情況，如果沒有，那很難訴請離婚，分居並不包括在內。不過，民國七十四年六月五日修正民法親屬編時加入第二款「如有難以維持婚姻之重大事由者亦可請求判決離婚」。"),_c('br'),_c('br'),_vm._v(" 有一些婦女有使用這一款規定訴請離婚，以目前正處於「事實分居」，實際上難以維持婚姻為理由訴請離婚，為應有證據的取得，例如：戶籍謄本、鄰里長的書面證明，子女、鄰居和丈夫談話之證明等，至於分居滿多久才可以，這要看法官認定了，認定的原則在於婚姻已無實質意義、雙方均無意挽回，以致感情日趨冷漠，形同陌路，婚姻基礎顯然已經動搖、難以維持婚姻等。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-1b",modifiers:{"accordion-1b":true}}],attrs:{"block":"","variant":""}},[_vm._v("捉姦的定義是什麼? 怎樣才算妨礙家庭?")])],1),_c('b-collapse',{attrs:{"id":"accordion-1b","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 通姦的定義是與有婚姻者的人有性行為，也就是說要出軌的配偶要和其他人間有姦淫的行為存在，才構成通姦。在台灣通姦尚未除罪化，所以若配偶與他人通姦，則可進行捉姦。"),_c('br'),_c('br'),_vm._v(" 捉姦是證據的問題，你要讓法官相信被告兩人間有發生姦淫的行為。"),_c('br'),_vm._v(" 所以捉姦最好是要有拍照或者是有證人看到兩個人正在從事性行為，或者有錄影也可以《法庭上已有案例：就算配偶為了捉姦而裝設針孔攝影機等，還是予以採證，請參見：妻子裝針孔捉姦雖非法取證，法官仍判通姦成立 》，加上捉姦時有照片或者蒐證影帶拍到兩個人沒穿衣服也可以，反正就是要讓法官確信兩人之間確有發生性關係就可以了。"),_c('br'),_c('br'),_vm._v(" 妨礙家庭在刑法上正確的說法為\"妨害婚姻及家庭罪\"，除了上述的通姦之外，誘拐有配偶之人離家、同居或者誘拐未成年人離家等，都屬於妨害家庭，依法都可提起告訴。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-2b",modifiers:{"accordion-2b":true}}],attrs:{"block":"","variant":""}},[_vm._v("成立通姦罪一定要? 成立通姦罪一定要抓姦在床?")])],1),_c('b-collapse',{attrs:{"id":"accordion-2b","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 這個問題就像問殺人罪是否一定要當場看到白刀子進，紅刀子出，有人死了才會成立？或是小偷當場被活逮才會成立竊盜罪一樣，當然不是。"),_c('br'),_c('br'),_vm._v(" 因為前述情形都是犯罪在實施中或實施後，即時發覺之現行犯（刑事訴訟法第八十八條第二項），然而絕大部分犯罪往往都是在事後才被發覺，但我們不能因此說它就不是犯罪，問題是如何去認定的問題。"),_c('br'),_c('br'),_vm._v(" 所以刑事訴訟法第一百五十四條規定：「犯罪事實應依證據認定之，無證據不得推定其犯罪事實。」這裡的證據係指證據能力，也就是證據資格。要取得「證據能力」須經過嚴格的證明程序，包括法定證據方法（被告、證人、文書、鑑定、勘驗）和法定調查程序，刑事訴訟法對此訂有詳細的規定。於具備證據能力後則有所謂證明力的問題，法律對此採自由心證原則，即關於如何評價證據之證據價值（證明力）的原則。"),_c('br'),_c('br'),_vm._v(" 至於有罪之判決，必須證明至何等程序，最高法院曾謂：認定犯罪事實所憑之證據，雖不以直接證據為限，間接證據亦包括在內；但無論其為直接或間接證據，其為訴訟上之證明，必須達於一般人均可得確信其為真實的程度，而無合理之懷疑存在時，始得據為被告有罪之認定（最高法院七六年台上四九八六號判例，八十七年台上一五四二號判例）。"),_c('br'),_c('br'),_vm._v(" 所以通姦罪即使不是抓姦在床 ，但若從其他證據方法中，（例如目擊者看到通姦人進出賓館，鑑定犯罪人遺留物上之DNA等。）能達得到上述之有罪判決之確定程度者，仍可成立通姦罪 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-3b",modifiers:{"accordion-3b":true}}],attrs:{"block":"","variant":""}},[_vm._v("我國刑法對於通姦罪有何規定?")])],1),_c('b-collapse',{attrs:{"id":"accordion-3b","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" （一）刑法第二百三十九條規定：「有配偶而與人通姦，處一年以下有期徒刑。其相姦者，亦同。」所謂通姦係指婚姻關係外男女雙方意思和致之姦淫行為，又稱和姦，所以別於強姦（最高法院十七年第十月十三日決議）。本罪之特別要件有二：一則本罪主體須為現有配偶之人，二則須有與他人和姦之行為。又因本罪為即成犯（有配偶之人與他人為姦淫行為時即已成罪），故以在通姦時有配偶為前提條件；至如於通姦後其婚姻關係因離婚或依法撤銷，亦不影響本罪之成立。 "),_c('br'),_c('br'),_vm._v(" （二）本條後段規定相姦罪。乃謂認識與自己通姦者為有配偶之人，而與之和姦之犯罪。本罪主體不以有配偶為必要，但未滿十四歲之女子，因無責任能力，所以不得為本罪主體；反為刑法第二百二十七條第一項與幼童性交罪之被害人。另十四歲以上未滿十六歲之子女則為刑法第二百二十七條第三項之被害人，亦不得為本罪之主體。所以行為人與此等人（上述幼童）相姦者應成立與幼童性交罪，不成立通姦罪。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-4b",modifiers:{"accordion-4b":true}}],attrs:{"block":"","variant":""}},[_vm._v("何種情形，不得提出通姦罪之告訴?")])],1),_c('b-collapse',{attrs:{"id":"accordion-4b","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 刑法第二百四十五條第二項規定：「第二百三十九條之罪，配偶縱容或宥恕者，不得告訴。」"),_c('br'),_c('br'),_vm._v(" 所謂縱容是指放縱或容許，乃事前所為之表示；宥恕則指原宥或寬恕，乃事後所為。實務對此曾表示：縱容配偶與人通姦，告訴權即已喪失，不能因嗣後翻悔而回復。又所謂縱容，但有容許其配偶與人通姦之行為即足，至相姦之人，原不必經其容許（司法院二十五年第一六○五號解釋）。"),_c('br'),_c('br'),_vm._v(" 所以，「如甲、乙明知彼此都是有配偶之人，仍發生姦淫行為，被甲的配偶丙查獲，甲懇求丙宥恕以上通姦的行為，經丙同意以甲應月付生活費二萬元為條件予以宥恕。甲允諾後即置之不理，丙怒極乃告訴甲、乙通姦行為。此時，依刑法第二百四十五第二項規定宥恕配偶與人通姦即不得告訴，此為配偶告訴權之限制，換言之對於配偶之通姦行為，若事後宥恕，一經有宥恕之表示告訴權即已喪失，不能因嗣後反悔而回復即喪失其告訴權，因此丙對於二人均不得再告訴。甲不付月費應僅得循民事途徑救濟，而不得再行告訴。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-5b",modifiers:{"accordion-5b":true}}],attrs:{"block":"","variant":""}},[_vm._v("通姦罪能否只告配偶或只告與其相姦之人?")])],1),_c('b-collapse',{attrs:{"id":"accordion-5b","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 刑事訴訟法第二百三十九條本文規定，告訴乃論之罪，對於共犯之一人告訴或撤回告訴者，其效力及於其他共犯，稱之為「告訴之主觀不可分原則」。"),_c('br'),_c('br'),_vm._v(" 所以假如提起告訴，不能只告配偶或只告與其相姦之人，這是因為告訴係對於犯罪事實為之，並非對於特定之犯人為之，依照法條明文，告訴之主觀不可分，僅適用於「告訴」及「撤回告訴」兩種情形，但實務將其擴張至其他情形，承認所謂「不得告訴之主觀不可分」及「不得自訴之主觀不可分」。前者，如甲、乙犯通姦罪，甲之妻丙對甲為縱容、宥恕而不告訴者（刑法第二百四十五條第二項），依實務見解，丙對乙亦不得告訴。後者，如甲、乙犯通姦罪，甲之妻丙對甲不得自訴者（刑事訴訟法第三百二十一條），依實務見解，丙對乙亦不得自訴。"),_c('br'),_c('br'),_vm._v(" 但刑事訴訟法給予告訴人，對配偶單獨撤回告訴的權利，也就是老婆可在提出通姦告訴後，可單獨對老公撤回告訴，只剩下第三者是被告，告訴一但撤回不得再提告訴。所以造成不是兩個一起被告，就是只剩第三者被告，所以想要當通姦的第三者，還是先了解法律好自為之。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-6b",modifiers:{"accordion-6b":true}}],attrs:{"block":"","variant":""}},[_vm._v("告訴後能否撤回? 能否只對配偶或相姦人一人撤回?")])],1),_c('b-collapse',{attrs:{"id":"accordion-6b","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 刑事訴訟法第二百三十八條第一項規定：「告訴乃論之罪，告訴人於第一審辯論終結前，得撤回其告訴。」又通姦罪，對於配偶提出告訴者，效力固然及於相姦人；但「對於配偶撤回告訴者，其效力不及於相姦人」（刑事訴訟法第二百三十九條但書）。所以，可單獨撤回對配偶的告訴，只告相姦者。"),_c('br'),_c('br'),_vm._v(" 但，反面解釋，若只對相姦人撤回告訴，其效力則及於配偶。(等同撤回對配偶之告訴) ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-7b",modifiers:{"accordion-7b":true}}],attrs:{"block":"","variant":""}},[_vm._v("通姦所生之子女，應從何人之姓?")])],1),_c('b-collapse',{attrs:{"id":"accordion-7b","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 民法第一千零五十九條規定：「子女從父姓。但母無兄弟，約定其子女從母姓者，從其約定。贅夫之子女從母姓。但約定其子女從父姓者，從其約定。」通姦所生之子女為非婚生子女，關於非婚生子女之從姓，可分別討論如下："),_c('br'),_c('br'),_vm._v(" （一）未受認領、準正前：司法院三十三年院字第二七七三號解釋認為：非婚生子女在未經其生父認領前，與其生父既無父與子女之親屬關係，而其生母之關係，則依民法第一千零六十五條第二項之規定，視為婚生子女，無須認領。由民法第一千零五十九條本旨推之，自應解為從母姓。"),_c('br'),_c('br'),_vm._v(" （二）已經認領、準正而成為婚生子女者："),_c('br'),_c('br'),_vm._v(" 非婚生子女經認領〈民法第一千零六十五條〉、準正〈民法第一千零六十四條〉者，應類推適用民法第一千零五十九條之規定，使其從父姓為宜，依此見解，子女若尚未報出生戶口，即為生父認領時，可直接從父姓報出生戶口。反之，子女已從母姓報出生戶口，始由父認領時，子女當然改從父姓。惟若生母方面有「母無兄弟」之情形者，仍得依生父及生母之約定使該子女仍保留母姓，此為解釋上當然之結果。 ")])])],1)],1)],1)],1)])]),_c('div',{staticClass:"frame"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s2",attrs:{"id":"s2","name":"s2"}},[_vm._v("家暴及保護令法律")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-11",modifiers:{"accordion-11":true}}],attrs:{"block":"","variant":""}},[_vm._v("什麼情況下，社會局可強制帶走兒童並且安置於寄養家庭?")])],1),_c('b-collapse',{attrs:{"id":"accordion-11","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 依據 少年法 第 九 條"),_c('br'),_vm._v(" 少年因家庭發生重大變故，致無法生活於其家庭者，其父母、養父母或監護人得申請當地主管機關安置或輔導。"),_c('br'),_c('br'),_vm._v(" 少年之父母、養父母或監護人有下列情形之一或有事實足認有下列各款情形之虞者，當地主管機關關對少年予以適當之保護與安置："),_c('br'),_vm._v(" 一、虐待。"),_c('br'),_vm._v(" 二、惡意遺棄。"),_c('br'),_vm._v(" 三、押賣。"),_c('br'),_vm._v(" 四、強迫、引誘從事不正當之職業行為。"),_c('br'),_vm._v(" 五、其他濫用親權行為。"),_c('br'),_vm._v(" 前二項少年之安置，當地主管機關得辦理家庭寄養或設機構收容教養之，並得酌收必要之費用。"),_c('br'),_c('br'),_vm._v(" 主管機關、機構負責人或個人依前三項之規定，安置、輔導、保護、寄養、收容、教養少年之期間，對少年有監護權。"),_c('br'),_c('br'),_vm._v(" 少年之父母離婚者，法院得依職權、少年本人、其父母、檢察官或主管機關之聲請，為少年之利益，酌定適當之監護人，不受民法第一千零九十四條之限制，並得命其父母支付相當費用。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-22",modifiers:{"accordion-22":true}}],attrs:{"block":"","variant":""}},[_vm._v("什麼是家庭暴力?")])],1),_c('b-collapse',{attrs:{"id":"accordion-22","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" ●遭暴力攻擊時，可以採取以下方式保護自己："),_c('br'),_c('br'),_vm._v(" 以兩手臂去抵擋。尤其施暴者攻擊頭部時，更應以手臂保護頭部，因頭皮較不易驗出傷痕，而手臂比頭部容易驗出傷痕，以手臂保護頭部時，可以讓攻擊點落在手臂上。"),_c('br'),_vm._v(" 大聲喊叫以引起家人或鄰居注意，除了可以協助報警外，日後也可出庭作證。"),_c('br'),_vm._v(" 現場錄音：如果有機會，可以將現場施暴過程錄音存證，將有助於法庭上使用。"),_c('br'),_c('br'),_vm._v(" ●暴力攻擊結束後，應採取以下方式以保護自己："),_c('br'),_c('br'),_vm._v(" 現場應拍照存證。尤其若施暴者有毀損物品或撕爛我們的衣服時，都應拍照，以供未來法庭上使用。受傷處應拍照，可與驗傷單比對作為證物。無論傷勢的大小，都應到醫院或診所接受醫師檢查，更應告知醫師，是遭受家庭暴力，並開立驗傷單（開立乙種驗傷單即可）。"),_c('br'),_vm._v(" 立即告知親友，讓親友知悉事件過程，除可以舒解遭受暴力虐待後之情緒外，將來也可以出庭作證。到派出所要求備案，無論將來是否走上法庭，都要堅持備案，將人、事、時、地、物清楚地要求派出所記錄下來。如果員警拒絕備案時，妳應該把他的姓名及警章號碼記下，再投訴市警局督察室。"),_c('br'),_c('br'),_vm._v(" 如果施暴者向妳認錯、道歉時，應立即要求施暴者書立悔過書，將施暴事件記錄下來，並由施暴者親筆簽名蓋章。如果妳要離家時，應到安全的地方，如警察局、親人或朋友家居住，也可打婦女保護專線113尋求暫時庇護。如果妳是已婚者，在離家後最好避免到男性友人家留宿，以免對方指稱妳通姦，影響到自己的權利。"),_c('br'),_c('br'),_vm._v(" 孩子如果也有危險性或年齡小需要母親照顧時，可以將子女一起帶出，不要將子女單獨留下。離家後，一定要將戶籍遷到可以收到信件的地方，以避免受暴力離家後，反而遭施暴者提出不履行同居之訴訟，又因戶籍地仍與施暴者在一起無法收到法院信件，而未能出庭反駁，致遭敗訴。如果孩子有見到暴力過程時，應先將孩子的證詞錄音下來，以免日後孩子遭受壓力或害怕親人判罪而不願作證或做不實之證詞。如果偕同子女離家時，子女的學區可以透過社會局的協助辦理轉學，毌需擔心學籍的問題。"),_c('br'),_c('br'),_vm._v(" ●在生命可能遭受危害時刻，受虐婦女應帶那些必要東西離家？"),_c('br'),_c('br'),_vm._v(" 當暴力的嚴重程度可能危害到受虐婦女生命安危時，離開施虐者，是必須要考量的。"),_c('br'),_vm._v(" 離家時需隨身攜帶的物件有："),_c('br'),_c('br'),_vm._v(" ◎身份證 ◎健保卡 ◎零錢 ◎換洗衣物 ◎聯絡電話 ◎提款卡或是信用卡。"),_c('br'),_c('br'),_vm._v(" 平時，可以將這些物件裝在可隨身攜帶的小袋子中放好，以備不時之需。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-33",modifiers:{"accordion-33":true}}],attrs:{"block":"","variant":""}},[_vm._v("遇到家暴情況發生，該如何處理?")])],1),_c('b-collapse',{attrs:{"id":"accordion-33","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 家暴受害者雖然可以申請保護令或其它單位協助，但暴力發生時，還是得先作好以下自保的防範措施："),_c('br'),_c('br'),_c('ul',{staticStyle:{"margin-bottom":"0px"}},[_c('li',[_vm._v("受到身體的虐待，如被打、被刀械槍枝威脅、被捆綁或不實指控時，千萬要先保護自己的身體，不要逞強反抗。")]),_c('li',[_vm._v("被施暴者辱罵、諷刺、言語威脅或不實指控時，不要企圖回嘴、反諷或辯解，以免火上加油。")]),_c('li',[_vm._v("發現施暴者在喝酒、嗑藥或情緒焦躁時，即要小心應對。")]),_c('li',[_vm._v("家暴發生時，要儘快設法向外求救，讓鄰居或親友能及時趕到。")]),_c('li',[_vm._v("受虐後要報警、驗傷(甲種驗傷單)、做筆錄、保留證物(如-驗傷單、凶器)，以便向有關單位提出告訴。")])]),_c('br'),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("家暴受害者該如何走出家暴陰影，重新迎向人生的光明面 ?")]),_c('br'),_vm._v(" 長期在家庭暴力中生活的人，無論大人或小孩，身心一定都已遭受非常大的摧殘。身體的傷害也許有痊癒的一天，但心靈的痛，卻需要很長時間的治療。"),_c('br'),_vm._v(" 提醒正在承受家暴的人，試試下列的方法，協助自己走出家暴的陰影："),_c('br'),_c('br'),_c('ul',{staticStyle:{"margin-bottom":"0px"}},[_c('li',[_vm._v("利用家暴求助管道來申請協助及諮詢。")]),_c('li',[_vm._v("向法院申請保護令。")]),_c('li',[_vm._v("積極爭取就業、就學機會，尋求可以獨立自主的機會。")]),_c('li',[_vm._v("嘗試擴展自己的生活圈、多交朋友，不要害怕說出自己的遭遇。")]),_c('li',[_vm._v("多看書、聽演講或自己去當志工，不但可以發現人生的光明面，還可以增加自己面對事實的勇氣。")]),_c('li',[_vm._v("不要感到羞恥、氣餒、被隔離，也不要因此退縮、易怒、衝動，而是應該積極面對，並且更努力學習、更充實自己的生活，才能逃離困境，迎向人生的光明面。")])])]),_c('br'),_c('h5',{staticStyle:{"color":"rgb(152, 75, 75)"}},[_vm._v("家庭暴力求助管道")]),_c('ul',{staticStyle:{"margin-bottom":"0px"}},[_c('li',[_vm._v("24小時家暴專線113或0800-024-995")]),_c('li',[_vm._v("台北市家暴防治中心02-27229543")]),_c('li',[_vm._v("台北縣婦幼中心02-29679665")]),_c('li',[_vm._v("桃園縣婦幼中心03-3336106")]),_c('li',[_vm._v("台中市家扶中心04-23162085")]),_c('li',[_vm._v("台南市家暴暨性侵害防治中心06-3901601")]),_c('li',[_vm._v("高雄市家暴防治中心07-7262085")]),_c('li',[_vm._v("外籍配偶保護諮詢0800-088-885")]),_c('li',[_vm._v("男性關懷0800-013-999")]),_c('li',[_vm._v("內政部家暴防治委員會02-89127331")])])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-44",modifiers:{"accordion-44":true}}],attrs:{"block":"","variant":""}},[_vm._v("家暴的施暴者有什麼法律刑責?")])],1),_c('b-collapse',{attrs:{"id":"accordion-44","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 一、當被害人遭受家庭暴力之急迫危險時，可申請緊急性暫時保護令。"),_c('br'),_c('br'),_vm._v(" 二、當被害人遭受家庭暴力，如有未達急迫危險情況，但確有安全上之現實考量時，可向法院申請一般性暫時保護令。"),_c('br'),_c('br'),_vm._v(" 三、當被害人遭受家庭暴力，有必要聲請保護令時，可向法院申請通常保護令。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-55",modifiers:{"accordion-55":true}}],attrs:{"block":"","variant":""}},[_vm._v("誰可以申請保護令?")])],1),_c('b-collapse',{attrs:{"id":"accordion-55","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 被害人、檢察官、警察機關、家庭暴力暨性侵害防治中心均可申請保護令，被害人為未成年人、身心障礙者或因故難以委任代理人者，其法定代理人、三親等以內之血親或姻親，得為其向法院申請，但緊急性暫時保護令被害人並不能擔任申請人。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-66",modifiers:{"accordion-66":true}}],attrs:{"block":"","variant":""}},[_vm._v("如何申請保護令?")])],1),_c('b-collapse',{attrs:{"id":"accordion-66","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" ◎被害人、檢察官、警察機關、家庭暴力暨性侵害防治中心均可申請保護令。"),_c('br'),_c('br'),_vm._v(" ◎被害人為未成年人、身心障礙者或因故難以委任代理人者，其法定代理人、三親等以內之血親或姻親，得為其向法院申請，但緊急性暫時保護令被害人並不能擔任申請人。"),_c('br'),_c('br'),_vm._v(" ◎只有緊急暫時保護令由檢察官、警察機關、或直轄市、縣（市）主管機關向法院申請，最快在四小時內核發。一般暫時保護令、通常保護令則由被害人直接填寫聲請書狀向法院聲請。"),_c('br'),_c('br'),_vm._v(" 一、聲請緊急性暫時保護令，由檢察官、警察機關或直轄市、縣（市）主管 機關，以言詞、書面或電信傳真方式向法院聲請。"),_c('br'),_c('br'),_vm._v(" 二、聲請一般性暫時保護令應填具「民事（一般性）暫時保護令聲請書狀」， 向被害人之住居所地、相對人之住居所地或家庭暴力發生地之法院聲請。"),_c('br'),_c('br'),_vm._v(" 三、聲請通常保護令應填具「民事通常護令聲請書狀」，向被害人之住居所 地、相對人之住居所地或家庭暴力發生地之法院聲請。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-77",modifiers:{"accordion-77":true}}],attrs:{"block":"","variant":""}},[_vm._v("保護令的種類及內容是什麼?")])],1),_c('b-collapse',{attrs:{"id":"accordion-77","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('ul',{staticStyle:{"margin-bottom":"0px"}},[_c('li',[_vm._v("禁止施暴者對於受虐者或其特定家庭成員實施家庭暴力。")]),_c('li',[_vm._v("禁止施暴者直接或問接對於受虐者為騷擾、接觸、通話或其他非必要之聯絡行為。")]),_c('li',[_vm._v("命施暴者遷出受虐者之住居所，必要時並得禁止施暴者就該不動產為處分行為或為其他假處分。")]),_c('li',[_vm._v("命施暴者遠離下列場所特定距離：受虐者之住居所、學校、工作場所或其他受虐者或其特定家庭成員經常出入之特定場所。")]),_c('li',[_vm._v("定汽、機車及其他個人生活上、職業上或教育上必需品之使用權，必要時並得命交付之。")]),_c('li',[_vm._v("定暫時對未成年子女權利義務之行使及負擔由當事人之一方或雙方共同任之、行使或負擔之內容及方法，必要時並得命交付子女。")]),_c('li',[_vm._v("定施暴者對未成年子女會面交往之方式，必要時並得禁止會面交往。")]),_c('li',[_vm._v("命施暴者給付受虐者住居所之租金或受虐者及其未成年子女之扶養費或其他緊急資助。")]),_c('li',[_vm._v("命施暴者交付受虐者或特定家庭成員之醫療、輔導、庇護所或財物損害等費用。")]),_c('li',[_vm._v("命施暴者完成戒癮治療、精神治療、心理輔導或其他治療、輔導。")]),_c('li',[_vm._v("命施暴者負擔相當之律師費。")]),_c('li',[_vm._v("命其他保護受虐者及其家庭成員之必要命令")])])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-88",modifiers:{"accordion-88":true}}],attrs:{"block":"","variant":""}},[_vm._v("違反保護令罪的刑罰")])],1),_c('b-collapse',{attrs:{"id":"accordion-88","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 觸犯家暴罪者，則依所犯行為相關的「刑法」論罪，至於違反保護令者，可處三年以下徒刑、拘役或併科新台幣十萬元罰金。 ")])])],1)],1)],1)],1)])]),_c('div',{staticClass:"frame"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s3",attrs:{"id":"s3","name":"s3"}},[_vm._v("智慧財產與偽造文書")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-1a",modifiers:{"accordion-1a":true}}],attrs:{"block":"","variant":""}},[_vm._v("智慧財產到底是什麼?")])],1),_c('b-collapse',{attrs:{"id":"accordion-1a","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 「智慧財產」含專利權、著作權、商標權、積體電路電路佈局權、營業秘密及其他無形智慧資產。"),_c('br'),_c('br'),_vm._v(" 【智慧財產權】"),_c('br'),_c('br'),_vm._v(" 智慧財產（Intellectual Property）是指由人類的智慧活動而產生出來的成果，常附著於有形的媒體，其價值不在媒體本身，而在於人們透過媒體所表達出來概念的欣賞，或利用此一概念創造有形產品的更高附加價值；而佔有和支配智慧財產的法律地位，即為智慧財產權（Intellectual Property Rights,簡稱IPR）。智慧財產權的範圍十分廣泛，包括："),_c('br'),_c('br'),_vm._v(" 一、保護人類智慧結晶的創造成果如專利、植物新品種、微生物菌種、積體電路電路佈局、營業秘密及工業設計等；"),_c('br'),_c('br'),_vm._v(" 二、表彰人類創造發明所發展出來的產業識別標記如商標、標章等；"),_c('br'),_c('br'),_vm._v(" 三、表彰人類精神創作的著作權如著作人格權與著作財產權 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-2a",modifiers:{"accordion-2a":true}}],attrs:{"block":"","variant":""}},[_vm._v("怎樣申請『智慧財產權』?")])],1),_c('b-collapse',{attrs:{"id":"accordion-2a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 智慧財產權法指的是『著作權法』、『專利法』、『商標法』及『營業秘密』。"),_c('br'),_c('br'),_vm._v(" ●『著作權法』，其分為\"著作產財權\"與\"著作人格權\"。"),_c('br'),_c('br'),_vm._v(" 首先，著作權於著作完成即享有之，因此是不需要申請的，無論是所謂著作人格權及著作財產權，前者為該著作姓名表示及公開發表等權利，代表的是完成著作的那個\"人\"，後者則為使用(copy)該著作\"物\"的權利。"),_c('br'),_c('br'),_vm._v(" 著作權登記制度廢止後，著作權人與其他一般私權之權利人相同，對其權利之存在自負舉證責任，故著作權人應保留其創作過程、發行及其他與權利有關事項之資料，作為證明自身權利之依據，日後發生私權爭執時，由法院依權利人提出之事證，加以認定。故著作人為證明其權利，於著作完成時，可採取下列方式為未來之舉證責任作準備："),_c('br'),_c('br'),_vm._v(" 一、 音樂著作："),_c('br'),_vm._v(" 可向相關之音樂著作權仲介團體為存證。按新的著作權仲介團體條例已於八十六年十一月五日由總統公布施行，且已有三個音樂著作之團體經主管機關設立，各仲介團體本可自行辦理著作權登錄存證工作。著作人可繳交著作申請登錄，而社會大眾亦可向仲介團體申請查閱，獲得著作相關資訊。遇有訴訟時，該登錄資料即可作為證據。"),_c('br'),_c('br'),_vm._v(" 二、 語文著作："),_c('br'),_vm._v(" 著作人可向中央圖書館之「中華民國國際標準書號中心」填寫出版品預行編目申請單及國際標準書號申請單，以取得編目號碼及國際書碼(ISBN)登錄編號，並於出版後送存著作於中央圖書館。"),_c('br'),_c('br'),_vm._v(" 三、 電腦程式創作："),_c('br'),_vm._v(" 於獨立研發撰碼過程中所留下之完整文件紀錄乃是重要之判斷參考，此種紀錄應包含各種開會紀錄、備忘錄、筆記(note)、工作日誌(logbook)、邏輯圖(logic diagrams)、草稿(draft)、草圖(drawings)、可行性研究、系統分析、進度表(schedule)、資料結構(data structure)、資料流程圖(data flow diagram)、邏輯演繹法(algorithm)、及禁止接觸有著作權之軟體之告示文件，創意活動(creative activity)之照片或錄音帶等。"),_c('br'),_c('br'),_vm._v(" 四、 至法院辦理認證："),_c('br'),_vm._v(" 按公證法第四十六條以下即係有關法院公證處認證私證書之規定，著作人可持其著作至法院公證處辦理著作之認證，惟該項認證僅係確定當事人之真正，對於著作是否確係申請認證人所作，該認證書並無推定之效力。又新修正之公證法於民國九十年四月施行後，著作人亦可向民間之公證人申請辦理認證。此類之認證，可適用於各類型之著作。"),_c('br'),_c('br'),_vm._v(" 五、 向得受理國人著作權登記之外國申請著作權登記："),_c('br'),_vm._v(" 宜注意外國政府機構所發給之證明書，仍為私文書，於提出於法院之前，須經我國駐外單位為認證。"),_c('br'),_c('br'),_vm._v(" 六、 請律師認證："),_c('br'),_vm._v(" 此認證之方式不拘泥於形式，律師可出具證明書，證明著作之內容及完成之時間，亦可僅在著作上蓋上日期戮記，並以備份留存律師事務所。"),_c('br'),_c('br'),_vm._v(" 七、 保留創作過程所需之一切文件，作為訴訟上之證據方法："),_c('br'),_vm._v(" 例如， 美術著作創作過程中所繪製之各階段草圖，劇本創作中之增刪之草稿，創作過程中開會紀錄等。前項記錄如係以電腦進行，亦須於不同階段列印存檔。如係雇用或聘任他人創作而以雇用人或出資人為著作人，亦須保留契約文件及受雇人創作過程之相關資料。"),_c('br'),_c('br'),_vm._v(" 按保存創作證據之方法不一而足，甚且有建議可對自已或第三人發存證信函以證明創作行為者，亦堪供參考。值得注意的是，上述仲介團體或律師之認證、美日等國之著作權登記證明、及著作權創作過程文件等，形式上屬私文書之性質，依民事訴訟法第三百五十七條規定，應由舉證人證明為文書之真正。且縱形式上能證明真正，該文書之實質內容亦僅提供法官參考以獲得心證而已，並無證據法上推定之效力，故無法僅憑該文件之內容即發生舉證責任轉換之效果。至於公證處所發之認證書，其認證效力僅及於該認證人之簽名及作成認證之時間，有關著作之內容是否為該著作人所創作，則不在認證範圍內，故仍由法院認定之，亦無證據推定之效果。至於「中華民國國際標準書號中心」所發之編目號碼及國際書碼財團法人資訊工業策進會之著作登錄之效力亦只是證明申請人曾在該時間為申請或登錄而已，至於是否為該著作人所創作，亦無推定之效果。"),_c('br'),_c('br'),_vm._v(" ● 若是專利申請則需向經濟部智慧財產局提出申請。"),_c('br'),_c('br'),_vm._v(" ● 如何申請商標註冊呢 ?"),_c('br'),_c('br'),_vm._v(" 申請商標註冊應檢附下列文件、規費，向經濟部智慧財產局申請："),_c('br'),_vm._v(" (一) 智慧財產局統一印製之商標、服務標章、證明標章或團體標章註冊申請書。"),_c('br'),_vm._v(" (二) 規費有以下幾種：申請商標註冊（指定使用商品在20種以下者）為新台幣四○○○元；指定使用商品在21種以上60種以下者為新台幣六○○○元；指定使用商品在61種以上者為新台幣一○○○○元。申請服務標章註冊為新台幣四○○○元。申請團體標章或證明標章為新台幣四○○○元。"),_c('br'),_vm._v(" (三) 申請人營業之相關事證（如申請防護商標或防護服務標章則毋需檢附）。"),_c('br'),_vm._v(" (四) 商標圖樣十五張，如為彩色者，應另附黑白圖樣三張，其長及寬不得超過五公分。"),_c('br'),_vm._v(" (五) 申請聯合或防護商標（或服務標章）註冊者，應另附正商標（或服務標章）註冊證或審定書影本乙份。"),_c('br'),_vm._v(" (六) 如委任商標代理人者，須檢附委任書。"),_c('br'),_c('br'),_vm._v(" 商標註冊我國係採先註冊主義，申請商標註冊以郵寄方式辦理者，係以郵戳日期為其申請日。若發生兩申請者同日時，由各申請人協議讓歸一人專用，不能達成協議時，以抽籤方式決定（參照商標法第三十六條）。"),_c('br'),_c('br'),_vm._v(" 註冊商標係指審定商標，自公告之日起滿三個月無人異議，或異議不成立確定後，予以註冊之商標，註冊日為公告期滿之次日。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-3a",modifiers:{"accordion-3a":true}}],attrs:{"block":"","variant":""}},[_vm._v("何謂著作權?")])],1),_c('b-collapse',{attrs:{"id":"accordion-3a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 在人類社會裡，文明的發展與延續，必須藉由許多人的發明、創作才能完成。精神方面的創作，尤其是文學、科學、藝術或其他學術領域的作品更是文明資產的一部分，我們稱他們為著作，為了保障這些著作創作者的權益，由國家制定法律予以保護，法律所規定的這些權利，就叫做著作權。"),_c('br'),_c('br'),_vm._v(" 當創作者完成一項著作時，就這項著作立即享有著作權，而受到著作權法的保護。因此著作權是在著作完成的時候立即發生的權利，也就是說著作人享有著作權，不須要經由任何程序，當然也不必登記。"),_c('br'),_c('br'),_vm._v(" 著作權包括了著作人格權與著作財產權兩個部分。著作人格權，是用來保護著作人的名譽、聲望及其人格利益的，因為和著作人的人格無法分離，所以不可以讓與或繼承。著作人格權包括有公開發表權、姓名表示權及禁止別人不當竄改著作，以致損害著作人名譽之禁止不當改變權等三種權利。"),_c('br'),_c('br'),_vm._v(" 著作財產權，主要是賦予著作人即創作著作的人財產上的權利，使他獲得實質上的經濟利益，促使作者繼續從事創作活動，精進創作的質與量，豐富文化內容。著作財產權的主要內容，包括重製權、公開口述權、公開播送權、公開上映權、公開演出權、公開展示權、改作權、編輯權及出租權，一共有九種權利。"),_c('br'),_c('br'),_vm._v(" 為了尊重著作人的權益，利用別人的著作時，不但不能侵害到著作人格權，還應得到著作財產權人的同意或授權。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-4a",modifiers:{"accordion-4a":true}}],attrs:{"block":"","variant":""}},[_vm._v("何謂公開發表權?")])],1),_c('b-collapse',{attrs:{"id":"accordion-4a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 公開發表權是『著作人格權』的一種，就是說著作人有權決定他的著作要不要對外發表，如果要發表的話，決定什麼時候發表，以及用什麼方式來發表。"),_c('br'),_c('br'),_vm._v(" 假如有人未經著作人的同意，擅自把別人未曾對外公開發表的作品，發表出來，就侵害了著作人的公開發表權，要負違反著作權法的刑事責任，以及賠償著作人損害的民事責任。"),_c('br'),_c('br'),_vm._v(" 舉例來說，有一個男生寫了一封情書，給隔壁班的女生，被那個女生的同學發現了，就把這封情書貼在公佈欄裡，讓大家奇文共賞，這個行為當然沒有先取得情書作者的同意，這就違反了著作人的意思。像這樣未經寫信人的同意，擅自公開發表別人所寫的書信，就是侵害公開發表權的一種很典型的例子。"),_c('br'),_c('br'),_vm._v("   有幾種情形可以推定著作人同意公開發表他的著作。"),_c('br'),_c('br'),_vm._v(" 第一種情形是就未曾發表的著作，著作人把著作財產權讓給別人，或者授權別人利用這個著作，而由於受讓人或被授權人使用著作的行為，造成這個著作被公開發表的結果。"),_c('br'),_c('br'),_vm._v(" 第二種情形是尚未公開發表的美術著作或攝影著作的著作人，把作品讓給別人，而受讓人把他取得的作品公開展覽。還有一種情形是碩士、博士論文的作者，取得學位的時候，論文依法必須送給圖書館，讓大眾借閱。"),_c('br'),_c('br'),_vm._v("   又雇用人或出資人依著作權法規定，從著作完成時起，就取得著作財產權時，由於著作財產權人的使用行為，造成著作被發表的結果，則視為著作人同意公開發表。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-5a",modifiers:{"accordion-5a":true}}],attrs:{"block":"","variant":""}},[_vm._v("偽造文書的定義")])],1),_c('b-collapse',{attrs:{"id":"accordion-5a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 文書為一切意思表示紀錄證明，而刑法處罰偽造文書之主旨，乃在於保護文書之實質的真正。故不僅作成文書之名義人須處於虛捏或假冒，即文書之內容亦必出於偽構，始負偽造之責任，偵辦偽造文書案件，通常皆以此作為基礎，惟其適用，極易滋生誤解，其緣由在對偽造文書罪之定義以及犯罪構成要件未能瞭解所致。"),_c('br'),_c('br'),_vm._v(" 文書之特質如下："),_c('br'),_c('br'),_vm._v(" 一、文書乃表示意思之方式：文書既在表明一定之意思必以一定之方法表示之。通常不外以文字、象形、符號等三種方式為之。"),_c('br'),_c('br'),_vm._v(" 二、文書須有相當之持久性，故必須將其內容記載於體物上：若以水將文自書寫於竹木之上，隨時可以消失於無形者，自不得謂為文書。"),_c('br'),_c('br'),_vm._v(" 三、文書為表現足以證明法律上之權利義務或事實，或足以產生法律上權利義務關係或事實之意思表示。"),_c('br'),_c('br'),_vm._v(" 四、偽造文書罪採有形偽造與實質主義：關於偽造文書之方法有二種，即有形偽造與無形偽造。前者係指必須冒用他人名義所作成之文書，方謂之偽造。如以自己名義作成之文書，縱其內容不實，亦不得謂之偽造；而無形偽造為凡以自己名義作成之文書，其內容不實者，亦謂之偽造。"),_c('br'),_c('br'),_vm._v("   就有形偽造而言，除文書名義人非真正制作人外，是否尚需以文書之內容真實與否，以為犯罪之構成之要件，理論上相當分歧有形式主義與實質主義之分。然我國刑法對於偽造文書罪，均以足生損害於公眾或他人為要件，若僅具偽造之形式，而實質上無生損害之虞者，即不能成立偽造文書罪，故採實質主義。此點非常重要，一般偵辦案件常對「 是否足生損害之虞」，判斷錯誤，故偵辦偽造文書罪應該特別注意此特質。"),_c('br'),_c('br'),_vm._v(" 五、偽造文書除身分犯外，以偽造他人之文書為要件：刑法偽造文書罪，除公務員或從事業務之人，就其職務上或業務上所作成之文書為不實之記載，方始構成偽造之罪。其它公私文書，必須無制作權人，利用他人明義制作者，方成立本罪。故如普通人以自己名義所作成之文書，縱其內容記載不實，亦不成立偽造文書罪。"),_c('br'),_c('br'),_vm._v(" 六、偽造文書罪必須足生損害於公眾或他人，且以足生損害之虞即可，亦即以偽造之文書足生損害於公眾或他人之危險即行成立，並不以實際發生損害之事實為要件，亦 即公眾或他人所受法律保護之利益，因此有遭受損害之虞即是。又所謂損害亦不以有經濟價值為限，如於物之所有人之使用、收益、處分權有所損害，亦屬之。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-6a",modifiers:{"accordion-6a":true}}],attrs:{"block":"","variant":""}},[_vm._v("偽造文書會怎麼判刑?")])],1),_c('b-collapse',{attrs:{"id":"accordion-6a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 偽造文書罪之基本性質是對於公共信用的侵害加以處罰，保護公共信用與法律交往上的安全，其主要規範之法條：刑法第二百十條規定「偽造、變造私文書，足以生損害於公眾或他人者，處五年以下有期徒刑。」、刑法第二百十一條「偽造、變造公文書，足以生損害於公眾或他人者，處一年以上七年以下有期徒刑。」及刑法第二百一十二條「偽造、變造護照、旅券、免許證、特許證及關於品行、能力服務或其他相類之證書、介紹書，足以生損害於公眾或他人者，處一年以下有期徒刑、拘役或三百元以下罰金」本罪之構成要件有下列三項："),_c('br'),_c('br'),_vm._v(" 一、須為文書：所謂「文書」就是記載思想表示之有體物而言，它具有思想之內容且明示或可得而之做成名義人，而能在法律交往與經濟交易中充當適格而明確之證明（註一）。「文書」又可分為私文書、公文書及刑法第二百十二條所列之特種文書。依刑法第十條第三項規定「稱公文書者，謂公務員職務上制作之文書。」即指公務人員基於其職務，所作之文書而言，例如：戶口名簿、駕照、公立醫院制作之醫療單據；而私文書即指非公務員制作之文書或公務員非依職權所制作之文書，例如：私立醫院制作之醫療單據、簽帳單、個人信件；另特種文書則係指刑法第二百十二條列舉之護照、旅券、免許證、特許證等以及關於品行、能力服務或其他相類之證書、介紹書，例如：汽車牌照、身分證、大學畢業證書等均是。"),_c('br'),_c('br'),_vm._v(" 二、有偽造、變造之行為：所謂「偽造」係指無制作權者制作之虛偽文書而言，亦即就真實文書之內容加以竄改而導致其本質改變者或假冒、捏造他人名義制作之文書；而所謂「變造」之行為是指無權修改文書內容之人，擅自更改其內容而言，亦即就真實文書之內容加以竄改而並無導致其本質改變者即是。"),_c('br'),_c('br'),_vm._v(" 三、足以生損害於公眾或他人：所謂足生損害，不僅以實際上發生損害為必要，亦須有足以生損害之虞者。若僅具有偽造、變造之形式，而實際上並不足以生損害之虞者，不構成偽造、變造文書罪。（八十四年台上字第二四五七號參照）"),_c('br'),_c('br'),_vm._v(" 第 212 條 偽造、變造護照、旅券、免許證、特許證及關於品行、能力服務或其他相類之證書、介紹書，足以生損害於公眾或他人者，處一年以下有期徒刑、拘役或三百元以下罰金。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-7a",modifiers:{"accordion-7a":true}}],attrs:{"block":"","variant":""}},[_vm._v("著作財產權的保護期間為多久?")])],1),_c('b-collapse',{attrs:{"id":"accordion-7a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 偽造文書罪之基本性質是對於公共信用的侵害加以處罰，保護公共信用與法律交往上的安全，其主要規範之法條：刑法第二百十條規定「偽造、變造私文書，足以生損害於公眾或他人者，處五年以下有期徒刑。」、刑法第二百十一條「偽造、變造公文書，足以生損害於公眾或他人者，處一年以上七年以下有期徒刑。」及刑法第二百一十二條「偽造、變造護照、旅券、免許證、特許證及關於品行、能力服務或其他相類之證書、介紹書，足以生損害於公眾或他人者，處一年以下有期徒刑、拘役或三百元以下罰金」本罪之構成要件有下列三項："),_c('br'),_c('br'),_vm._v(" 一、須為文書：所謂「文書」就是記載思想表示之有體物而言，它具有思想之內容且明示或可得而之做成名義人，而能在法律交往與經濟交易中充當適格而明確之證明（註一）。「文書」又可分為私文書、公文書及刑法第二百十二條所列之特種文書。依刑法第十條第三項規定「稱公文書者，謂公務員職務上制作之文書。」即指公務人員基於其職務，所作之文書而言，例如：戶口名簿、駕照、公立醫院制作之醫療單據；而私文書即指非公務員制作之文書或公務員非依職權所制作之文書，例如：私立醫院制作之醫療單據、簽帳單、個人信件；另特種文書則係指刑法第二百十二條列舉之護照、旅券、免許證、特許證等以及關於品行、能力服務或其他相類之證書、介紹書，例如：汽車牌照、身分證、大學畢業證書等均是。"),_c('br'),_c('br'),_vm._v(" 二、有偽造、變造之行為：所謂「偽造」係指無制作權者制作之虛偽文書而言，亦即就真實文書之內容加以竄改而導致其本質改變者或假冒、捏造他人名義制作之文書；而所謂「變造」之行為是指無權修改文書內容之人，擅自更改其內容而言，亦即就真實文書之內容加以竄改而並無導致其本質改變者即是。"),_c('br'),_c('br'),_vm._v(" 三、足以生損害於公眾或他人：所謂足生損害，不僅以實際上發生損害為必要，亦須有足以生損害之虞者。若僅具有偽造、變造之形式，而實際上並不足以生損害之虞者，不構成偽造、變造文書罪。（八十四年台上字第二四五七號參照）"),_c('br'),_c('br'),_vm._v(" 第 212 條 偽造、變造護照、旅券、免許證、特許證及關於品行、能力服務或其他相類之證書、介紹書，足以生損害於公眾或他人者，處一年以下有期徒刑、拘役或三百元以下罰金。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-8a",modifiers:{"accordion-8a":true}}],attrs:{"block":"","variant":""}},[_vm._v("產品要外銷國外，是否須在當地申請專利?")])],1),_c('b-collapse',{attrs:{"id":"accordion-8a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 專利是屬地主義，必須要在當地取得專利權才會獲得保護。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-9a",modifiers:{"accordion-9a":true}}],attrs:{"block":"","variant":""}},[_vm._v("什麼情形算侵犯著作人格權? 什麼情形算侵犯著作財產權呢?")])],1),_c('b-collapse',{attrs:{"id":"accordion-9a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 1. 首先，著作權於著作完成即享有之，因此是不需要申請的，無論是所謂著作人格權及著作財產權，前者為該著作姓名表示及公開發表等權利，代表的是完成著作的那個\"人\"，後者則為使用(copy)該著作\"物\"的權利，因此權利歸屬的重點在於舉證著作\"物與完成著作\"人\""),_c('br'),_c('br'),_vm._v(" 2. 舉個簡單的例子，假設甲於某年某月某日完成的一幅畫(假設無受僱於他人之情形)，甲即刻起則同時擁有著作人格權與著作財產權，今天假設若乙冒用甲的名義署名參加比賽，即謂乙侵犯甲的著作人格權；而假設丙未經某甲同意，任意大量複製其畫作販賣圖利，則丙侵犯了甲的著作財產權。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-10a",modifiers:{"accordion-10a":true}}],attrs:{"block":"","variant":""}},[_vm._v("到底何謂侵權行為?")])],1),_c('b-collapse',{attrs:{"id":"accordion-10a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 侵權行為意義：因故意或過失，不法侵害他人之權利者負損害賠償之責任。"),_c('br'),_c('br'),_vm._v(" 客觀要件："),_c('br'),_vm._v(" 1. 需有加害行為"),_c('br'),_vm._v(" 2. 行為需不法"),_c('br'),_vm._v(" 3. 需侵害權利或利益"),_c('br'),_vm._v(" 4. 致生損害"),_c('br'),_vm._v(" 5. 損害與結果間有因果關係"),_c('br'),_c('br'),_vm._v(" 主觀要件："),_c('br'),_vm._v(" 1. 需有責任能力"),_c('br'),_vm._v(" 2. 需為故意或過失"),_c('br'),_c('br'),_vm._v(" 效力："),_c('br'),_vm._v(" 被害日起10年內應主張，發現侵害後2年間不行使消滅，但為不當得利於15年內依不當得利請求反還。"),_c('br'),_c('br'),_vm._v(" 加害人因侵權行為取得債權者，雖因時效消滅仍得拒絕履行，如：以詐欺脅迫開立之借據。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-11a",modifiers:{"accordion-11a":true}}],attrs:{"block":"","variant":""}},[_vm._v("哪些行為違反了智慧財產權?")])],1),_c('b-collapse',{attrs:{"id":"accordion-11a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("資料來源:http://www.wtuc.edu.tw/copyright/page02.htm")]),_c('p',[_vm._v(" 一、有人抄襲了我的心血，該怎麼辦？"),_c('br'),_vm._v(" 不論學生、上班族或是Soho族，都有可能遇到自己的辛苦創作，在未經同意的情形下而遭他人利用。如果他人的利用是合法、合理的使用，就沒有侵害著作權的問題；反之，如果他人沒有合理使用的情形，也未經著作權人的同意就使用別人的著作，那就是侵害著作權的違法行為。著作權法所稱的「著作」，包括文學、科學、藝術或其他學術範圍的創作，共分為十一類，它們都受到著作權的保護，包括：語文著作，音樂著作，戲劇、舞蹈著作、美術著作、攝影著作、圖形著作、視聽著作、錄音著作、建築著作、電腦程式著作及表演等。"),_c('br'),_c('br'),_vm._v(" 當著作權受到侵害時，著作權人可以依著作權法第六章「權利侵害之救濟」的規定要求侵害人負民事上損害賠償責任；也可依照著作權法第七章「罰則」的規定，訴請法院對侵害人論罪科刑。不論是民事上的救濟或刑事上的追訴均由司法機關受理訴訟後，依法裁判，若不服司法機關之判決，可依民事訴訟法或刑事訴訟法規定之程序提起上訴，以資救濟。所以，有人抄了您的心血，不法侵害您的著作權時，不要猶豫，要勇於依法主張權利，這樣，尊重著作權的觀念，才可以在我們的社會落實生根。"),_c('br'),_c('br'),_vm._v(" 二、暫時性重製，都算是觸法嗎？"),_c('br'),_vm._v(" 所謂「暫時性重製」是指我們使用電腦網路或影音光碟機來觀賞影片、聆聽音樂、閱讀文章、觀看圖片時，這些影片、音樂、文字、圖片等影像或聲音，都是先透過機器之作用而「重製儲存」在電腦或影音光碟機內部的隨機取存記憶體(RAM)內後，影像再顯示在電視螢幕上，聲音則是利用音響設備而播放，當關機的同時這些資訊也就消失了，這種情形就是一種「暫時性重製」的現象。 那我們在日常生活中使用電腦、影音光碟來觀賞影片、聆聽音樂、傳遞資訊等著作權保護的著作時，是否會侵犯別人的著作權呢？其實大家不必過度擔心，因為著作權法雖然保護著作人的權益，也同時兼顧社會公共利益，因此特別規定，除了電腦程式以外，網路中繼性傳輸，或使用合法著作，屬技術操作過程中必要的過渡性、附帶性而不具獨立經濟意義的暫時性重製，例如，網路瀏覽、快速存取或其他為達成傳輸功能之電腦或機械本身技術上所不可避免的暫時性重製，或者影碟、程式、文字、圖片的取得來源是合法的，當您使用電腦和影音光碟機時利用該等著作或資訊所造成的暫時性重製的狀況，都不屬於著作人的重製權範圍，不必獲得同意。此外，著作權法也訂定了許多合理使用的規定，來限制著作人的權利，讓一般人在合理範圍內可以使用他人著作也不必獲得著作權人的授權。所以，縱使是在著作人重製權範圍的暫時性重製，仍有適用合理使用的空間。"),_c('br'),_c('br'),_vm._v(" 三、上班族小心！應避免觸犯「營業秘密法」。"),_c('br'),_vm._v(" 所謂的營業秘密，係指方法、技術、製程、配方、程式、設計或其他可用於生產、銷售或經營之資訊，例如：行銷策略、定價資訊、員工薪資、客戶名單等，這些資訊必須符合：（一）需具有秘密性。（二）非一般涉及該類資訊之人所知者。（三）因其秘密性而具有實際或潛在之經濟價值者。（四）營業秘密所有人已採取合理之保密措施者（如：公司將機密文件設密碼、或與員工簽訂保密契約）這四項要件，才算是營業秘密。 營業秘密本為一種或多種資訊，而資訊之自由流通，雖為民主社會之基石，惟對資訊之所有人之權益，仍應予以適當保護，以求調和。保障營業秘密才能達到提昇投資與研發意願之效果，也才能提供適當的環境，鼓勵在特定交易關係中的資訊得以有效流通，建立產業倫理並導正業者之競爭秩序。對於不法取得及使用該「營業秘密」之行為（如：竊取公司的機密配方及生產技術），雖然依營業秘密法之規定僅有民事損害賠償責任，但仍可能觸犯刑法洩漏業務上工商秘密罪、竊盜罪、侵占罪、背信罪，或違反公平交易法之相關規定，而有刑事責任之問題。"),_c('br'),_c('br'),_vm._v(" 四、公司電腦中灌有盜版軟體，應負的著作權侵害責任有哪些？"),_c('br'),_vm._v(" 我國的著作權法將電腦程式（軟體）規定為一種「著作」而加以保障。未經授權重製及非法散布都屬犯罪行為，觸犯者須受刑事制裁並負擔民事責任。如果你或你的公司非法複製了某一電腦程式，你個人(或與公司一同)將會同時受到追訴。 根據新修正著作權法規定，意圖營利而盜版他人著作者，可處五年以下有期徒刑、拘役或併科二十萬元到二百萬元罰金。如果是非意圖營利者，在超過法定門檻（五份或三萬元）時，可處三年以下有期徒刑、拘役，或科或併科七十五萬元以下罰金，如果盜版的媒介物是光碟，除了處相同自由刑，罰金還提高到五十萬元以上五百萬元以下，並屬於非告訴乃論。至於常業犯更可處一年以上七年以下有期徒刑，罰金則提高為八十萬到八百萬元，也是非告訴乃論。 縱使公司電腦中灌的盜版軟體是公司以外的人所灌入，公司如果明知為盜版軟體仍於營業中使用，也仍被視為侵害著作權，須負擔民事賠償責任，也可能必須受到刑事制裁。您及您的公司必須確定自己或公司內每一台電腦上所使用的每一套軟體均須有著作權人之授權。授權合約會告訴您哪些使用方式是被許可的。 使用盜版軟體，除了得負上述的法律風險外，也必須面對其它商務上的風險，包括電腦病毒、損毀之磁碟、以及軟體的瑕疵等，所以是相當得不償失的事情。因此無論是個人或公司電腦，使用者都應該去購買正版的電腦軟體，並依軟體所附之授權合約所規定之內容使用，方係正道。"),_c('br'),_c('br'),_vm._v(" 五、教授上課提供大家學術著作的影印，是否也涉及著作權問題？"),_c('br'),_vm._v(" 教授因為教學的需要而使用到他人著作的情形，可說是很平常的事。因授課或教學需要，影印他人之著作，編成講義，提供給學生作為免費教材，這樣是否違反了著作權法呢？其實，影印別人的文章，在著作權法上，是屬於把別人著作的內容予以重複製作，構成「重製」的行為。依著作權法的規定，著作人對自己的著作，享有重製的權利，這是著作財產權裡最重要的一個權利，稱為重製權。由於著作人享有重製權，所以任何人要重製別人的著作，都要經過作者的同意。不過學校裡的老師因為授課教學的需要，而僅重製他人著作少部分內容的行為，依著作權法第四十六條第一項「依法設立之各級學校及其擔任教學之人，為學校授課需要，在合理範圍內，得重製他人已公開發表之著作。」之規定，而屬於「合理使用」他人著作之行為，即毋需事先取得著作權人之同意或授權；反之，雖然學校教師，即使係為授課教學的目的，但將他人的著作作大量的重製，嚴重損害到著作人的權利，甚至發生替代市場的效果，就不能認為是屬於「合理使用」。 雖然在合理範圍內的重製行為，並無違反著作權之問題，不過需注意以下幾點："),_c('br'),_c('br'),_vm._v(" 1. 重製的著作如果依該著作的種類、用途以及其重製物之數量、方法對於著作權人的利益有妨害時，則不可進行重製行為。例如僅是利用一本書的少數幾頁，固然是合理使用，但若是印了該本書的最重要部分，使得學生因此可以不必再購買原書，或是影印測驗卷給學生作測驗，那就對著作人之利益有所妨害，不是合理使用。二、必須在合理的使用範圍內。所謂合理的使用是指基於特定目的（如個人非營利的使用、學術教育、新聞報導或公益活動）或特殊身分的、機關或團體（如立法、行政機關、學校教師、廣播電視台）對於他人著作，於合理範圍內得加以利用，而不構成著作權之侵害。於教師教學之情形，就必須是依法設立的各級學校的老師基於學校教學之目的。 因此，如果老師或教授是單純基於學術教育的目的而進行重製，且在「合理使用」的範圍內，利用他人的著作，那就不構成對於著作權的侵害行為，但如果逾越法律允許之「合理使用」範圍的話，就有侵害他人著作權之問題，所以，還是必須注意。"),_c('br'),_c('br'),_vm._v(" 六、我常收到電子郵件裡有好文章與圖片，可以再轉寄出去嗎？"),_c('br'),_vm._v(" 其實利用電子郵件轉寄他人文章或圖片之著作，都會涉及著作權法，因為「轉寄」他人著作這一行為，在法律上的意義，也是屬於著作權法中規定的「重製」行為，因此電子郵件的轉寄行為，是可以用著作權法來加以規範的，並非無法可管。不過根據著作權法規定，如果我們轉寄的對象是特定的少數人，又是少量且屬於「非營利性使用」性質，通常可以被認為是「合理使用」的範圍，不會構成侵害著作權。但還是得按照這些著作之性質、利用結果對著作潛在市場與現在價值之影響，來加以綜合判斷。舉例來說，被轉寄之文章，如果係著作人所出版書籍中之一篇代表性文章；被轉寄圖片是某本漫畫中的最精彩部分，則網路上之大量轉寄行為，因具有大量傳播之特性，可能就會發生所謂的「市場替代」效應（以淺顯的白話來說，就是會造成讀者不去購買紙本之文章或圖片，而只從網路上下載或直接在網路上瀏覽），對著作權人造成極大的傷害（因為紙本之書籍或漫畫銷路會受影響，著作權人之收益就會減少），那就可能不是單純以使用是「非營利性」性質這一點，就說是「合理使用」了，所以對於他人已經以紙本、CD等方法而發表之著作，除非是著作權人明白表示同意，最好還是避免在網路以轉寄方法讓大家共享，一來尊重著作權人之權益，二來避免自己觸法。 如果在未經著作權人同意下，將網路上受著作權保護的文章、圖片、音樂等下載，而且將這些「著作」，做成紙本或錄製於CD中，流通於市面，並有營利的行為時，就屬於侵害他人的著作權之行為，可能會被處以刑責或罰金。"),_c('br'),_c('br'),_vm._v(" 七、學校為了教學需要，常會放映視聽著作，會有問題嗎？"),_c('br'),_vm._v(" 著作權法之制定，具有「促進國家文化發展」及「調和社會公益」的目的，但是如果過度保護著作權，反而會阻礙學術、文化的交流發展與知識的傳遞，所以世界各國的著作權法，都有訂定「合理使用」原則，作為一種平衡機制。 我國的著作權法中也有「合理使用」原則之規定，著作權法第四十四條到第六十三條，及第六十五條的規定都是與「合理使用」有關的規定。即在不致大幅影響著作權人經濟收益或著作人創作意願的前提下，允許社會大眾在合理範圍內，基於教育、研究、評論、報導、公益等目的，不需事先取得著作權人的同意或授權，就可以無償利用他人之著作，並不構成對著作財產權的侵害。學校裡的老師或教授因為教學上的需要，而重製他人視聽著作內少部分內容的行為，原則上是符合了著作權法第四十六條的「依法設立之各級學校及其擔任教學之人，為學校授課需要，在合理範圍內，得以重製他人已公開發表之著作。」之規定，無需事先取得著作權人之同意。 另外，雖說在課堂上公開放映視聽著作，涉及公開上映或公開播送之行為，但是如果其行為符合非以營利為目的、未對觀眾或聽眾直接或間接收取任何費用，未對表演人支付報酬等三要件，即可以依照著作權法第五十五條規定公開上映或公開播送他人已公開發表之著作。但仍應注意不可以超越第六十五條第二項所定的判斷標準，即"),_c('br'),_c('br'),_vm._v(" １. 利用之目的及性質，包括係為商業目的或非營利教育目的。"),_c('br'),_vm._v(" ２. 著作之性質。"),_c('br'),_vm._v(" ３. 所利用之質量及其在整個著作所占之比例。"),_c('br'),_vm._v(" ４. 利用結果對著作潛在巿場與現在價值之影響。"),_c('br'),_c('br'),_vm._v(" 因此，只要學校的老師或教授是基於非營利的教育目的，而且所利用的質與量以及利用原著作的比例甚低，再加上利用結果對於發行視聽著作公司的市場並不會有任何影響，或影響很小，就符合合理使用的規範。但是如果是播放整部電影或整個錄影帶，則可能會造成「市場替代」效應，所以一般來說，電影或錄影帶發行公司或代理這些電影或錄影帶之公司，通常會發行提供學校專用的電影或錄影帶等（就像套裝軟體也有教育版一樣），學校如有需要，最好是購買此種電影或錄影帶，較為妥當。"),_c('br'),_c('br'),_vm._v(" 八、我是小攤販，賣假名牌很好賺，是否會坐牢？"),_c('br'),_vm._v(" 一般的商品都有品牌，而知名品牌的商品不僅具有一定的品質保障，還可作為使用者具有某種社會地位的象徵，所以，知名品牌商品很容易獲得消費者的青睞和信任，銷路也比較好。也因此，有些不肖的商人，喜歡仿冒知名品牌的商標，或是直接仿冒名牌商品的樣式，用較低廉的價格販售，以謀取不法之巨額暴利，完全不顧這些知名品牌的身價，都是來自名牌公司耗費了巨額金錢和心力打廣告，長時間經營的成果，若有人任意搭便車，仿冒其商標商品，竊取他人辛苦經營的努力成果，當然是不道德的、不公平的。所以，為了保障正當商標權人之利益，和避免消費者因買到品質低劣的仿冒品，而受到欺矇誤購的損失，並維護工商企業之正常秩序，商標法第八十一條爰明定，未得商標權人或團體商標權人同意，有下列情形之一者，處三年以下有期徒刑、拘役或科或併科新台幣二十萬元以下罰金："),_c('br'),_c('br'),_vm._v(" 1. 於同一商品或服務，使用相同之註冊商標或團體商標者。"),_c('br'),_vm._v(" 2. 於類似之商品或服務，使用相同之註冊商標或團體商標，有致相關消費者混淆誤認之虞者。"),_c('br'),_vm._v(" 3. 於同一或類似之商品或服務，使用近似於其註冊商標或團體商標之商標，有致相關消費者混淆誤認之虞者。另於第八十二條規定，明知為前條商品而販賣、意圖販賣而陳列、輸出或輸入者，處一年以下有期徒刑、拘役或科或併科新台幣五萬元以下罰金。也就是說，販賣或製造仿冒品，是會受到法律制裁的犯罪行為。所以，販賣商品時，千萬要小心，務必注意商品來源並保留交易憑證，以預防日後被誤會為販售仿冒品，涉及侵害他人註冊商標時，才可以作為舉證免責之自保證據，對於來源不明之商品或低於一般真品價格甚多之商品，則千萬不要輕意購買轉售，以免招來官司，啷噹入獄，屆時後悔也來不及了哦。"),_c('br'),_c('br'),_vm._v(" 九、寫文章時引用別人資料，是否會侵害著作權？"),_c('br'),_vm._v(" 著作權法第五十二條規定，為報導、評論、教學、研究或其他正當目的之必要，在合理範圍內，得引用已公開發表之著作。 這裡所稱之「其他正當目的」，係指與報導、評論（例如：影評、著作權侵害鑑定…等）、教學、研究等性質相同或類似之正當目的，並非泛指任何目的。至於所稱的「引用」，是指利用他人之著作，供自己創作、研究之參證、註釋或評論等等而言，因此被利用之他人著作內容僅屬自己著作之附屬部分而已。所以如果沒有自己著作之情形，即不符合「引用」之要件。除此之外，引用他人著作或文章時，必需注意應該明示其出處，且除非有必要，否則最好在引用他人著作時，避免整篇或整段之引用，較不會引起是否涉及「重製」他人著作之爭議。 引用他人的文章時，註明作者、出處，除了是一種禮貌外，也是對著作人格權的尊重。至於引用多少內容會在「合理範圍」內，可以參考著作權法第六十五條第二項所定之四項標準，即"),_c('br'),_c('br'),_vm._v(" １. 利用之目的及性質，包括係為商業目的或非營利教育目的。"),_c('br'),_vm._v(" ２. 著作之性質。"),_c('br'),_vm._v(" ３. 所利用之質量及其在整個著作所占之比例。"),_c('br'),_vm._v(" ４. 利用結果對著作潛在巿場與現在價值之影響。"),_c('br'),_vm._v(" 因此，我們在寫文章或創作時，如有引用他人著作內容之需要時，一定要想想自己是否符合著作權法中「合理使用」的規定，有無以合理的方式註明出處，以免侵犯了別人的權利。"),_c('br'),_c('br'),_vm._v(" 十、在餐廳、商店或飛機裡播放音樂或CD，是否須取得授權呢？"),_c('br'),_vm._v(" 當我們在百貨公司、商店逛街購物，或在餐廳用餐時，常會聽到這些公共場所播放著優美的音樂，來營造氣氛，增加情趣，藉以招攬顧客，提高業績。這種利用別人著作的行為，會不會侵害著作權呢？ 依照著作權法的規定，詞曲等音樂著作受著作權法的保護，如果我們要在公開場所藉用視聽設備傳達音樂著作的內容，是屬於公開演出的行為，必須要取得音樂著作財產權人的同意，否則就侵害了公開演出權。除了音樂著作，錄音著作的著作財產權人及其中演唱或演奏的表演人，對於播放錄音著作的公開場所，也享有公開演出的報酬請求權。 因此無論是百貨公司、餐廳、唱片行等這些商店，利用錄放音機及音響設備，或接收廣播電視節目再以擴音設備，播放音樂給顧客欣賞，都是一種將他人音樂著作公開演出的行為，應該事先取得著作財產權人的同意，才屬於合法。對於錄音著作及表演，雖然不必事先獲得權利人的同意，也應該支付使用報酬。 大家一定要培養正確的著作權常識，建立使用者付費的觀念。尤其是營業行為會牽涉到公開演出音樂著作、錄音著作及表演的行業，最好事先向音樂著作著作財產權人徵求同意，或向著作財產權人或表演人組成的著作權仲介團體預繳使用報酬，避免發生著作權的糾紛。"),_c('br'),_c('br'),_vm._v(" 十一、著作權法對舞蹈著作之保護"),_c('br'),_vm._v(" 所謂舞蹈著作，是指依身體動作之肢體語言所表現之著作。 我國的著作權法保護具有原創性的舞蹈著作。這裡所要強調的是原創性，因此一般僅僅具有單純自娛性質或是機械化的運動競技動作，因為缺乏創作性，就不是著作權法所保護的舞蹈著作，例如指定特定技術的體操、滑冰、跳水、社交舞蹈等等，因為這些活動僅是一些一再重複出現之標準動作，屬於一般人都可以模仿、模擬或重複進行的動作，沒有創作性在其中。另外，我國著作權法並非以著作必須「固著」於有形媒體為必要，因此，舞蹈著作僅需以表演方式為之，就可以立刻受到著作權法的保護，而不需將該舞蹈著作以錄影或其他方式固著於有形之媒體。 不過，要強調的是，著作權法保護的是表達，至於風格本身是不受著作權法保護的，因此，演出他人所編的舞碼，固應經著作權人的同意或授權始可，至於演出同一族群相同風格而非他人所創作之舞蹈，則無著作權侵害問題。例如演出他人所編的某一原住民族傳統舞蹈「歡慶豐收舞」，固應經著作權人同意，但如演出具該原住民族傳統風格之舞蹈，但非他人所編的「歡慶豐收舞」，則僅風格相同為原住民族傳統舞蹈，並不會侵害他人著作權，祇是其究為同一著作或僅是風格相同，應由舞蹈專家或法院認定。"),_c('br'),_c('br'),_vm._v(" 十二、是不是一定要手工製作、非大量生產才是著作權法所保護的美術工藝品？"),_c('br'),_vm._v(" 根據我國著作權法規定，美術著作包括繪畫、版畫、漫畫、連環圖﹝卡通﹞、素描、法書﹝書法﹞、字型繪畫、雕塑、美術工藝品及其他之美術著作。 而著作權法也明訂，美術工藝品是包含於美術之領域內，所以，應用美術技巧以手工製作與實用物品結合而具有裝飾性價值，可表現思想感情之單一物品的創作，例如手工捏製之陶瓷作品、手工染織、竹編、草編等，都是受著作權法保護的美術工藝品。美術工藝品的特質為一品製作，亦即為單一之作品，如果是用模具製作或機械製造可大量生產者，則屬於工業產品，就不屬於著作權法第三條第一項第一款所認定之美術工藝品，因此不受著作權之保護。 大家可能會擔心，原先以手工製作出來的美術工藝品，如果之後用機械方式量產重製物，是否會喪失受保護的地位？其實，如果以手工製作一件美術工藝品之後，再以手工或機械方式將它大量生產，這時候第一件製品與後來所作成的產品，就屬於「原件」和「重製物」之間的關係，「重製物」是不受著作權保護的，但是，重製美術工藝品「原件」的行為，本來就是著作人可以行使之權利，著作權也不會因為重製權之行使而消失。換句話說，原先單一製作的美術工藝品，並不會因為後來的量產而使其改變性質，喪失受著作權法保護之地位。"),_c('br'),_c('br'),_vm._v(" 十三、同學人人都有MP3，難道人人都有罪？"),_c('br'),_vm._v(" MP3是一種音樂檔案格式，它以MPEG Audio 3的壓縮模式，可以將一首30~50MB WAV格式的歌曲壓縮至3~5MB的大小而已，在正常650MB容量光碟下可裝130~200條歌曲，約為十幾張專輯的份量，加諸檔案小，易於在網路上傳輸，因此在這幾年來成為網路中最廣為流傳的一種音樂檔案格式。不過MP3因為牽涉到著作權之問題，所以讓許多人質疑使用或錄製MP3到底合不合法。 關於在網路上下載MP3的行為，如果只是自己欣賞，在少量的情形下，可以被認為是符合所謂的「合理使用」原則，不會侵犯他人之著作權。但若是大量下載，造成「市場替代」效果，不再購買合法CD產品，或是將MP3轉寄給其他親朋好友或公眾，甚至轉錄成CD之後公開販售，勢必會影響或威脅到唱片公司的營業利潤，這樣不僅不符合「合理使用」原則，且因為沒有經過他人同意而重製著作財產人之著作，侵害了著作權人依著作權法專有之重製權或公開傳輸權，則唱片公司有權利經由法律途徑請求損害賠償。 根據著作權法規定，未經授權而在網路上大量下載或傳輸MP3檔案，是侵害著作權的行為，如果是意圖營利而做這些行為，須負擔民、刑事責任，縱使沒有營利意圖，也須負擔民事責任，而無營利意圖的侵害，如果侵害超過五份，或是超過市價三萬元的侵害，仍會有刑事責任，如果是為販售光碟而進行盜版，或販售盜版光碟，可處五年以下有期徒刑，或併科五十萬元至五百萬元罰金，並屬於非告訴乃論之罪。而常業犯將重處一年到七年的有期徒刑，並得併科八十萬元至八百萬元罰金，因此網友們可要相當謹慎。"),_c('br'),_c('br'),_vm._v(" 十四、音樂著作權是否只保護具有藝術價值的音樂呢？"),_c('br'),_vm._v(" 不論何種類型的著作，祇要符合「原創性」與「創作性」，不管有沒有藝術價值，都可以受到著作權法的保護，所謂原創性，指著作必須是著作人自己所完成者，而非自他人著作抄襲而來，所以，並不要求新穎性，而且即使「英雄所見略同」，都無礙於著作之成立。至於「創作性」，採所謂「美學不歧視原則」，只要具有最起碼之創作性，即為著作受著作權法保護，不須將著作之品質列入考量。因此，著作權法並不是只保護具有藝術價值的音樂，像是古典音樂、藝術音樂等等；也就是說，著作之所以受到法律之保護，並不取決於著作本身是否具有藝術價值，業餘創作者所完成之具「原創性」及「創作性」的音樂著作，即使沒有達到完美的最高境界，也不妨礙其受著作權法的保護。為什麼著作權的保護，不需要也不應該以著作的藝術價值為前提呢？這是因為有些創作的新奇性與前衛性，在一開始時也許會遭到大眾的排斥，但一段時間後，大眾也可能接受他們新的表達方式。另外一種例子是，大眾所喜歡的音樂或照片等等，可能會被教育程度比一般人高的人，主觀上認定不應該受到法律的保護，而遏殺創作發展之空間。"),_c('br'),_vm._v(" 所以，任何的音樂著作，不應因其無藝術及教育價值，或因為品味低俗而被藐視，不管其藝術價值的高低，只要符合「原創性」及「創作性」這個原則，就受到著作權法的保護。"),_c('br'),_c('br'),_vm._v(" 十五、未經電影著作人同意，而依照電影的主要劇情與架構，而改編成電視劇之行為，是否違反著作權法？"),_c('br'),_vm._v(" 著作權法明訂，有十一種著作都屬於著作權法保護的項目，包括語文著作、音樂著作、戲劇和舞蹈著作、美術著作、攝影著作、圖形著作、視聽著作、錄音著作、建築著作、電腦程式著作及表演。而所謂的視聽著作則包括電影、錄影、碟影、電腦螢幕上顯示之影像及其他藉機械或設備表現系列影像，不論有無附隨聲音而能附著於任何媒介物上之著作。 由此，我們可以相當清楚的知道電影是屬於視聽著作的一種，受到著作權法的保護。如果不是以原版照抄的方式將一部電影重製成電視劇，那就沒有侵害到視聽著作權人依照我國著作權法第二十二條所享有的重製權。 另外，將電影改編成電視劇的行為，是屬於著作權法上所稱的「改作」。而根據著作權法第二十八條的規定「著作人專有將其著作改作成衍生著作或編輯成編輯著作之權利。」因此擅自將電影改編成電視劇的行為就已經侵害了電影著作權人所享有的改作權。而法院就可以依照著作權法第九十二條之規定對侵害改作權的人加以處罰，如果是意圖營利而侵害者，處三年以下有期徒刑、拘役，或併科新臺幣七十五萬元以下罰金。 非意圖營利而侵害，其所侵害之著作超過五件，或權利人所受損害超過新臺幣三萬元者，處二年以下有期徒刑、拘役，或科或併科新臺幣五十萬元以下罰金。"),_c('br'),_c('br'),_vm._v(" 十六、製作與販售盜版光碟，要負責什麼法律責任？"),_c('br'),_vm._v(" 我們常常在夜市中看到一些專門出售盜版CD、VCD、DVD等光碟的攤位。這些盜版者運用科技工具，在短時間內大量盜拷他人熱賣中的光碟片，讓辛辛苦苦費盡心血錄製完成的正版光碟片的著作權人應該享有的著作財產權益在一夕之間付諸流水。這些不法之徒在盜拷以後，還要把這些盜拷光碟片銷售出去，以便從中獲利。 為了杜絕盜版行為，保護著作人的權利，九十二年七月九日修正公布之新著作權法第九十一條第三項規定，意圖營利以重製光碟方法侵害他人著作權者，處五年以下有期徒刑、拘役或併科五十萬元到五百萬元罰金，常業犯可處七年以下有期徒刑，罰金則提高為八十萬元到八百萬元，所得利益超過八百萬元者還可以在其獲利範圍再加重，同時也將製造及販售盜版光碟之犯罪行為改為公訴罪。而為了遏制意圖營利者散布或販賣盜錄、盜版光碟的行為，新著作權法第九十一條之一第三項也規定，可處三年以下有期徒刑、拘役，或併科一百五十萬元以下罰金。當然，除了上述刑事責任外，著作財產權人還是可以依法請求盜拷正版光碟的盜版者負損害賠償責任，最高可要求五百萬元賠償（著作權法第八十八條）。"),_c('br'),_c('br'),_vm._v(" 十七、什麼是公開傳輸權？"),_c('br'),_vm._v(" 公開傳輸權(Right of Public Transmission)就是著作人享有透過網路或其他通訊方法，將他的著作提供或傳送給公眾，讓大家可以隨時隨地到網路上去瀏覽、觀賞或聆聽著作內容的權利。換句話說，就是作者可以將他的著作，不管是文字、錄音、影片、圖畫等任何一種型態的作品，用電子傳送（electronically transmit）或放在網路上提供（make available online）給公眾，接收的人可以在任何自己想要的時間或地點，選擇自己想要接收的著作內容。"),_c('br'),_c('br'),_vm._v(" 十八、為什麼要賦予著作人公開傳輸權？"),_c('br'),_c('br'),_vm._v(" (一) 由於數位科技、電子網路及其他通訊科技的興起，任何著作都可以輕易地以數位（digital）形式存在或呈現，再藉由網路快速地傳送給許多人，對著作權人產生相當不利的影響，傳統的著作權法所賦予著作人的權利，無法充分地保護著作人的權益，世界智慧財產權組織(WIPO)因而在1996年通過了著作權條約(WIPO Copyright Treaty，WCT)及表演與錄音物條約(WIPO Performances and Phonograms Treaty，WPPT)兩項國際公約，針對數位化網路環境，明定應賦予著作人公開傳輸權。"),_c('br'),_vm._v(" (二) 為與國際接軌，促進資訊傳播與電子商務之蓬勃發展，提升著作人在數位化網路環境中之保護，所以修正我國著作權法，賦予著作權人公開傳輸權，才能確實維護知識經濟及未來數位內容產業的正常發展，維持我國的競爭力。"),_c('br'),_c('br'),_vm._v(" 十九、對著作人賦予公開傳輸權之後，除了合理使用情形外，一般人在網路上有哪些應該注意的問題？"),_c('br'),_c('br'),_vm._v(" (一) 在網路上將文章、圖畫、音樂或影片等著作上載，下載，轉貼，傳送，儲存，都是屬於重製的行為，如果未經同意的話，將會侵害到著作財產權人的重製權。"),_c('br'),_vm._v(" (二) 著作人在網路上的權利，除了重製權之外，另外還享有公開傳輸權，所以重製別人的著作，放在網站上提供給大家瀏覽，觀賞或聆聽，除了要取得重製的授權外，還必須取得公開傳輸的授權。"),_c('br'),_vm._v(" (三) 凡是未經著作權人同意，把別人的著作放在網路上讓更多的人瀏覽、觀賞或聆聽，不但會造成侵害重製權的問題，還會侵害到著作財產權人的公開傳輸權。所以透過網路交換軟體將儲存在電腦中別人的著作檔案，主動提供網友下載，以及喜歡把各種資訊貼上網讓大家共享的人，要特別注意了，不要因為一時的疏忽，造成違反著作權法的困擾。"),_c('br'),_vm._v(" (四) 各個網站或BBS站的版主，對於網友貼上網的文章、圖畫、音樂或影片，如果不確定是作者同意在網路上流通的，最好刪除，免得無端發生侵害公開傳輸權的糾紛。"),_c('br'),_c('br'),_vm._v(" 二十、什麼是公開演出的使用報酬請求權？"),_c('br'),_vm._v(" 公開演出的使用報酬請求權，指當利用人在公開的場所，播放CD、錄音帶或唱片的時候，那些被播放的CD、錄音帶或唱片的錄音著作人或表演人，針對這種公開演出的行為，可以要求播放的行為人，給付使用報酬。"),_c('br'),_c('br'),_vm._v(" 廿一、為什麼要賦予錄音著作人或表演人公開演出的使用報酬請求權？"),_c('br'),_c('br'),_vm._v(" (一) 自有著作權保護制度以來，重製權、散布權和演出權就是三項最基本的權利，而我國著作權法從民國十七年立法開始到今天，對錄音著作就是給予一般著作的保護，不像德、日、韓等大陸法系的國家採取著作鄰接權的法制。"),_c('br'),_vm._v(" (二) 雖然如此，但是我國對於錄音著作的保護，並未賦予演出權，相較於其他享有公開演出權的音樂著作、戲劇、舞蹈著作，以及享有公開上映權的視聽著作來說，對錄音著作的保護程度，確實是略遜一籌的。這種情形意味著，在公開的場合放映影片或播放CD、錄音帶或唱片，要徵得影片、音樂著作人的同意，可是卻不需要徵得錄音著作人的同意，因為影片有公開上映權，音樂有公開演出權，而錄音著作卻沒有相對的權利。也就是說在公開場合，透過CD、錄音帶或唱片，聆聽到音樂，對於作曲的人要付費，但是對把曲子製作成有聲音的CD、錄音帶或唱片的錄音著作人及表演人，卻不需要支付任何對價，此種情形實有欠公平。"),_c('br'),_vm._v(" (三) 世界智慧財產權組織(WIPO)1996年通過的表演與錄音物條約(WIPO Performances and Phonograms Treaty，WPPT)，明定錄音物製作人及表演人對其錄音物之公開傳達應享有「適當之報酬請求權（equitable remuneration）」。為符合國際保護標準，爰參照法國、瑞士、瑞典、德國：：：等許多國家的立法例，給予使用報酬請求權，讓錄音著作人及表演人可以向公開演出的行為人收取使用報酬。"),_c('br'),_c('br'),_vm._v(" 廿二、著作權法對錄音著作公開演出的使用報酬請求權，有哪些相關的規定？"),_c('br'),_c('br'),_vm._v(" (一) 錄音著作公開演出的使用報酬請求權，不屬於專有的排他性利用權，而只是一種民法上的請求權。錄音著作人或表演人要求公開演出人支付使用報酬時，未依照其請求支付的話，只成立民法上的債權債務關係，不會發生著作權法規定的損害賠償民事責任，也不會發生違反著作權法的刑事責任的問題。"),_c('br'),_vm._v(" (二) 錄音著作裡面不含表演人的表演，則由錄音著作人自己向公開播放CD、錄音帶的行為人請求支付使用報酬。如果錄音著作裡面灌錄了表演人的歌唱或演奏等表演時，由錄音著作之著作人及表演人共同向公開演出的利用人請求支付使用報酬。"),_c('br'),_vm._v(" (三) 公開演出的利用人已經支付錄音著作人或表演人其中任何一方使用報酬的話，對另外一方就不必再支付報酬。換句話說，利用人只需要給一次錢，這筆錢由錄音著作人和表演人自行分配，收到使用報酬的一方（錄音著作人或表演人），必須要將所收的錢，分配給沒收使用報酬的另一方（表演人或錄音著作人）。"),_c('br'),_vm._v(" (四) 要注意的是，所有公開播放CD、錄音帶或唱片的場所，例如百貨公司、大賣場、餐廳、飛機等營業場所，將會因為公開演出CD、錄音帶或唱片這些錄音著作，必然增加一些使用報酬的成本，今後宜列入營業成本上的考量。 ")])])],1)],1)],1)],1)])]),_c('div',{staticClass:"frame"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s4",attrs:{"id":"s4","name":"s4"}},[_vm._v("詐騙集團手法")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-111a",modifiers:{"accordion-111a":true}}],attrs:{"block":"","variant":""}},[_vm._v("詐騙集團的手法有哪些?")])],1),_c('b-collapse',{attrs:{"id":"accordion-111a","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 歹徒電話假綁架、真勒贖"),_c('br'),_vm._v(" 高雄市某企業家第二代今天步出家門不久，家中就接獲歹徒勒贖電話，宣稱自己是黑道份子，已綁架Ｘ姓青年，要家屬交付新台幣三十萬元贖款。警方接獲報案，認為案件事有蹊蹺，調查後拆穿是歹徒詐騙新手法，及時制止家屬交付贖款。"),_c('br'),_c('br'),_vm._v(" 擄人勒贖-詐騙電話內容逼真"),_c('br'),_vm._v(" 詐騙手法翻新，最近有詐騙集團打電話給家長聲稱：你的孩子因為積欠黑道份子一筆錢，被人押走！而為了取信於家長，騙徒還不斷播放遭人毒打的哀嚎聲，讓家長急得像熱鍋上的螞蟻，然後依照騙徒的指示匯出款項，因此損失好幾萬元，其中不乏民意代表。"),_c('br'),_c('br'),_vm._v(" 電話遙控詐騙"),_c('br'),_vm._v(" 詐騙手法新花招，歹徒瞄準學生家長，還要求不准掛上電話，立刻匯款，否則就撕票，一名家長就因此被騙了30 萬。"),_c('br'),_c('br'),_vm._v(" 如果你們發現手機業者發出\" 因系統維護請配合關機一小時 \" 等內容的簡訊"),_c('br'),_vm._v(" 千萬不要上當!!!"),_c('br'),_vm._v(" 這又是詐騙的招數"),_c('br'),_vm._v(" 而且通常他們會在下班時間才發訊"),_c('br'),_vm._v(" 你知道為什麼嗎?"),_c('br'),_vm._v(" 如果你真的信以為真關機了"),_c('br'),_vm._v(" 那你家人可能就會接到，你被綁架的勒贖電話"),_c('br'),_vm._v(" 再加上銀行/郵局等機構，無人幫忙再一層把關"),_c('br'),_vm._v(" 家屬緊張之餘很容易就將錢給匯出去了"),_c('br'),_vm._v(" 而你們家便憑白損失了一筆為數不少的錢"),_c('br'),_c('br'),_vm._v(" 假恐嚇取財"),_c('br'),_vm._v(" 詐騙電話"),_c('br'),_vm._v(" 謊稱你的子女在地下錢莊借錢，還不出來被留置。還錢放人。厲害的詐騙集團還會事先打電話給你的子女，側錄電話之後，播放聲音以取信當事人。混雜著哭泣的聲音，跟錄下來的真實聲音，引起當事人的驚惶以模糊判斷力。"),_c('br'),_vm._v(" 要求使用者關閉行動電話"),_c('br'),_vm._v(" 假冒電信總局服務品質測試小組辦理品質查測之名，要求用戶關機一小時，並詢問住家電話。這大概是假恐嚇的前置步驟吧。"),_c('br'),_c('br'),_vm._v(" 詐騙集團養人頭掏空銀行"),_c('br'),_vm._v(" 刑事局偵七隊破獲一起養人頭超貸掏空金融案，這個犯罪集團不但蒐集人頭資料，還提供場所供人頭食宿，再利用他們從事銀行超貸、掏空銀行，估計從 88 年開始，詐騙銀行金額超過上億元。"),_c('br'),_c('br'),_vm._v(" 假刷卡消費簡訊，操作提款機詐財"),_c('br'),_vm._v(" 許多銀行在民眾刷卡消費時會提供簡訊服務，防止信用卡被盜刷，現在詐騙集團卻利用銀行名義，發出這種消費簡訊，告訴民眾剛剛完成一筆消費，讓收到簡訊的民眾誤以為自己遭盜刷，回撥簡訊中的服務電話，按指示到提款機辦理退款，卻已經誤入詐騙陷阱。"),_c('br'),_c('br'),_vm._v(" 假中獎真詐財 假公司假摸彩電話，詐騙民眾轉帳"),_c('br'),_vm._v(" 詐財集團假借成立公司舉辦摸彩名義，要被害人轉帳稅款，詐財集團為了取信民眾，還特別印了精美的廣告傳單、VIP 卡，害民眾受騙上當。"),_c('br'),_c('br'),_vm._v(" 退費詐騙手法，誘導民眾操作ATM提款機轉帳"),_c('br'),_vm._v(" 詐騙集團冒用國稅局、勞保局、中華電信公司名義，發手機簡訊或打電話給被害人，佯稱要核退給被害人一筆稅款、勞保費及電話費，要被害人利用提款機轉帳方式辦理「退稅(費)」手續，騙得被害人依指示輸入「密碼」，結果被害人錢沒到手，戶頭內的存款卻被轉匯到詐騙集團的人頭帳戶。"),_c('br'),_c('br'),_vm._v(" 婦女被刮刮樂詐財集團騙了20 多次、損失一千多萬"),_c('br'),_vm._v(" 一名婦女被同一個集團詐騙二十多次，總計被騙金額達一千六百多萬元，因此這名被害人向台中地院提出民事賠償訴訟，最後法官根據她的請求，判決這個詐騙集團的九名被告，必須如數賠償，而這名婦女也願意提出五百萬元擔保金，查封被告的財產，希望追回被騙的錢。"),_c('br'),_c('br'),_vm._v(" 手機中獎簡訊詐騙手法"),_c('br'),_vm._v(" 是為刮刮樂詐騙的翻版，詐騙集團將中獎傳單、信件換成手機簡訊廣為寄發，被害人貪念作祟，就註定要被騙失財。"),_c('br'),_c('br'),_vm._v(" 以送餐服務為由，向老人家騙取資料證件影本"),_c('br'),_vm._v(" 詐騙手法層出不窮，甚至還利用花蓮地區特有的為老人送餐服務，向民眾騙取個人相關資料及證件，近日因民眾懷疑不曾領取送餐服務，向社會局查詢才發現上當受騙。"),_c('br'),_c('br'),_vm._v(" 猜猜我是誰? 裝熟人騙錢"),_c('br'),_vm._v(" 5 通電話有一粗心者被騙， 騙子謊稱他是你的某某朋友，先跟你哈拉一下，消除你的戒心之後再開口跟你借錢！！騙子會說很急需要你立刻去匯款，而且過兩天就會還給你，如果你說你要問問看，騙子會留一支電話給你，要你打那支電話，謊稱自己的手機已經講到沒電了！收到此類的電話請小心求證！！"),_c('br'),_c('br'),_vm._v(" 雇用辣妹推銷假股條"),_c('br'),_vm._v(" 龍騰光電公司，涉嫌雇用辣妹推銷假股條詐騙吸金"),_c('br'),_c('br'),_vm._v(" 役男家屬提防詐騙集團"),_c('br'),_vm._v(" 「役男出事？憲調組通知，家屬不察被騙得逞」情事，役男家屬，如遇類案，應立即聯繫役男原單位、縣市兵役局查證，或向各地憲兵隊請求協處，以免吃虧上當。"),_c('br'),_c('br'),_vm._v(" 掛號沒領(郵局包裹被退)，假冒國稅局騙民眾操作提款機"),_c('br'),_vm._v(" 詐騙集團冒用郵局名義，佯稱寄發雙掛號郵件遭退回，要求收件人打電話到同樣也是冒名的國稅局查詢退費事宜，再騙取民眾持提款卡到櫃員機將款項匯出。"),_c('br'),_c('br'),_vm._v(" 歹徒偽造法院「關防」詐騙"),_c('br'),_vm._v(" 詐騙集團把歪腦筋動到法院身上，假冒法院關印，寄出公文，說是如果不匯款繳交積欠的上萬元電話費，就要查封動產。"),_c('br'),_c('br'),_vm._v(" 補稅！假的，通知書逼真，劃撥單有鬼"),_c('br'),_vm._v(" 假冒法務部各地行政執行處名義，寄發補稅執行通知書及劃撥單的詐騙手法，最近已成為南北詐騙集團的新伎倆，家住屏東的Ｘ姓女子收到台北行政執行處的補稅通知書，要她依劃撥單所示帳戶繳納稅款。"),_c('br'),_c('br'),_vm._v(" 假帳單"),_c('br'),_vm._v(" 騙子搜刮公寓門口的信箱，找出「信用卡帳單」並且仿製，改掉帳單上面的「ATM 轉帳帳號」等資訊。"),_c('br'),_c('br'),_vm._v(" 破解方式很簡單，大部分的信用卡銀行的繳款轉帳帳號都有固定的規則，多半是直接就是身分證字號或卡號，只要記下自己銀行的規則即可判別。事實上繳款轉帳帳號多半為固定不變，因此也可利用普通銀行的「約定轉帳」功能。另外就是注意信箱請設計成從外側只能投遞，不易拿取。並且投遞帳單時也請確定信封完全落入箱中。"),_c('br'),_c('br'),_vm._v(" 綜合式犯罪集團"),_c('br'),_vm._v(" 不法集團利用假色情假恐嚇假退稅等手法，向民眾詐騙，甚至還勾結桃竹地區知名大賣場的主管，將被害人約出來見面之後，予以強押盜刷信用卡購買菸酒。"),_c('br'),_c('br'),_vm._v(" 電話"),_c('br'),_vm._v(" 此類電話完全為使用者付費服務電話，過去多為 0204 開頭，但近年來也有 09xx 開頭的看起來很像手機號碼。此類騙術只要你打過去，騙子就有第一分鐘的話費入帳。騙子甚至可以一律回應「打錯了」，再播過去則又有一分鐘帳單收入。"),_c('br'),_c('br'),_vm._v(" 網路仙境美景，移花接木詐騙"),_c('br'),_vm._v(" 最近許多民眾在網路上看到幾張四處轉寄的風景照片，有瀑布、有翠綠的潭水，美景有如人間仙境，照片中說這裡是南投名間鄉的桃花源渡假農場，不過遊客要小心，這疑似是詐騙集團移花接木的手法，照片中的美景是在克羅埃西亞，並不是南投。"),_c('br'),_c('br'),_vm._v(" 支票存款"),_c('br'),_vm._v(" 這招很高明，因為很多人不知道支票存款是什麼?!"),_c('br'),_c('br'),_vm._v(" 當你被告知你的存摺內已經多出一筆鉅款，先別高興是橫財找上你，或許，這是噩夢的開始！無所不用其極的詐騙集團，最近又推陳出新一套詐財手法，讓不少人冤枉破財。"),_c('br'),_vm._v(" 這種詐財手法與歹徒獲知你的存摺帳號有關。"),_c('br'),_c('br'),_vm._v(" 當你的銀行或郵局存簿帳號外洩時，歹徒會把一筆可觀的錢，匯到你的戶頭裡去，當然，這個時候，他是沒辦法因此提領你戶頭裡面的錢，或知道你戶頭裡有多少錢的。"),_c('br'),_c('br'),_vm._v(" 不過，這個時候，你就會接到歹徒的電話，莫名奇妙的告訴你：中獎了！並會跟你要存摺帳號，先把五十萬獎金存到你的戶頭裡面去。"),_c('br'),_c('br'),_vm._v(" 當你半信半疑的刷卡查看存款餘額時，果然發現戶頭裡面已經多出五十萬！當你被這筆橫財沖昏了頭之際，對方接著會再打電話給你確認後，要求你繳交十五％ 的所得稅，你根本考慮都不用考慮，便把十五％的獎金所得稅匯了出去。"),_c('br'),_c('br'),_vm._v(" 高興還沒過，銀行或郵局不久就會通知你，先前的五十萬「支票存款」跳票啦，於是，你就被騙了。所以，當別人匯錢給你時，千萬要搞清楚，是「現金存款」還是「支票存款」？別只看到存簿上多了一筆數字就高興過頭！ ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-222a",modifiers:{"accordion-222a":true}}],attrs:{"block":"","variant":""}},[_vm._v("要如何預防詐騙?")])],1),_c('b-collapse',{attrs:{"id":"accordion-222a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 詐騙集團詐術花招百出，只要是利用ATM辦理金融交易，請遵行「三不」原則"),_c('br'),_c('br'),_c('ul',[_c('li',[_vm._v("不要依照別人指示操作提款機。")]),_c('li',[_vm._v("不要在電話中向別人告知您的銀行帳號、身份證號碼等個人資料，避免成為「人頭戶」。")]),_c('li',[_vm._v("不要聽信歹徒所提供的電話查證，應向一０四或一０五查詢正確的電話號碼，再以電話查證。")])]),_vm._v(" 民眾遇有可疑處，請撥打１６５，將有專人為您服務。"),_c('br'),_c('br'),_vm._v(" 為了進一步給詐騙集團致命一擊，警政署正積極規劃擴充「 165 反詐騙諮詢專線」 功能，以有效整合「警政」、「電信」及「金融」鐵三角，透過資訊的快速流通，從科技面徹底防範詐騙案件發生。"),_c('br'),_c('br'),_vm._v(" 根據刑事局資訊室指出，「 165 反詐騙諮詢專線 」規劃中的擴充功能，包括具備詐騙簡訊關鍵字彙整功能，整合各家行動電話業者提供的訊息特碼服務，提供民眾在接獲詐騙簡訊時，可以轉寄至一特定的訊息特碼（如 165），以逕行報案舉發。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-333a",modifiers:{"accordion-333a":true}}],attrs:{"block":"","variant":""}},[_vm._v("網路詐騙新花招 警方提醒網友注意")])],1),_c('b-collapse',{attrs:{"id":"accordion-333a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("記者魏明德／台北報導")]),_c('p',[_vm._v(" 虛擬的電腦網路目前已成為歹徒犯案的新天地，各種詐騙花招更令人防不勝防；由於近來網路上又出現不少的最新詐騙花招，刑事局偵查科二日特別呼籲提醒網友應特別小心，以避免受騙上當。"),_c('br'),_c('br'),_vm._v(" 刑事局偵查科指出，警方近來發現，網路詐騙新花招最常見的包括「移花接木」、「冒牌銀行網路詐欺」兩種。"),_c('br'),_c('br'),_vm._v(" 何謂「移花接木」？網路拍賣詐欺。警方表示，歹徒均是先上網向網路上的拍賣商家，下標購買商品，在取得賣家銀行匯款帳號後，再於網站上刊登販賣同型商品廣告，要求買方把錢匯入先前賣家的帳號內，等買方把錢匯入後，歹徒即向賣家要求交貨，並親自向賣家取貨，造成真正的買家反而拿不到貨。"),_c('br'),_c('br'),_vm._v(" 至於「冒牌銀行網路詐欺」就是一種新興網路詐騙手法，又稱『 網路釣魚 』，這是歹徒拷貝或仿冒網路銀行網站的網頁，然後假冒銀行之名，讓使用者誤上冒牌的網路銀行的網路，且無意間洩漏自己銀行帳號及密碼等重要資料，歹徒再進行盜領情事。"),_c('br'),_c('br'),_vm._v(" 網路釣魚，多半是利用偽造電子郵件與網站作為「誘餌」，輕則讓使用者不自覺洩漏個人資料，成為垃圾郵件業者的名單；嚴重一點，電腦可能會被植入木馬程式，破壞系統或讓重要資訊遭竊。而最危險的情況是：誘騙使用者的銀行帳號密碼、信用卡號與身分證字號等機密資料，釣魚者再伺機利用電話語音轉帳功能，將被害人戶頭內存款轉帳全部領走。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-444a",modifiers:{"accordion-444a":true}}],attrs:{"block":"","variant":""}},[_vm._v("網路釣魚-網頁詐騙陷阱")])],1),_c('b-collapse',{attrs:{"id":"accordion-444a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 小心「網路釣魚」陷阱"),_c('br'),_vm._v(" 刑事局偵九隊分析「網路釣魚」為犯罪手法的網路詐欺案件，其犯罪手法分析如下："),_c('br'),_c('br'),_vm._v(" 1. 以假名字購買易付卡及信用卡卡號。"),_c('br'),_vm._v(" 2. 申請網站--以假的工作室及銀行名義，向網路代理註冊服務公司申購並註冊國內二家知名銀行網站相似的網址。"),_c('br'),_vm._v(" 3. 複製網站--複製與銀行官方網站一模一樣的網站。"),_c('br'),_vm._v(" 4. 釣魚--等待不知情客戶上網鍵入網路銀行帳號、密碼。"),_c('br'),_vm._v(" 5. 盜取帳號密碼--將騙取之機密資訊側錄至電腦中。"),_c('br'),_vm._v(" 6. 上網轉帳--伺機至真正的網路銀行，輸入被害人的存款密碼後，盜轉存款至人頭帳戶。"),_c('br'),_vm._v(" 7. 至自動提款機提款。"),_c('br'),_c('br'),_vm._v(" 本案嫌犯並非資訊相關科系畢業，學歷也不高，靠自己在家研讀專業駭客書籍並上網向國內、外駭客討教，習得駭客入侵知識，更意外發現國內民間之代理註冊網址之網路公司，對網址申請的審核浮濫，竟成功詐取被害人存款。"),_c('br'),_c('br'),_vm._v(" 過去幾年內，網路釣魚（又稱為phishing）已經在美國造成嚴重災情。根據美國網路釣魚防範小組（APWG）的統計，釣魚網站平均的壽命長達6.1天，也就是說，從開始釣魚到被檢舉關站之間，這些網站通常可以存活將近一周，足以騙取相當多的使用者個人資料，而目前存活時間最長的釣魚網站，則有31天，網路使用者不可不多加提防。"),_c('br'),_c('br'),_vm._v(" 歹徒利用垃圾郵件的管道發送仿效知名網站的電子郵件，引誘無知的使用者進入偽裝的知名網站，藉此騙取使用者帳號、密碼或姓名、地址、電話及信用卡資料，然後再利用這些資料獲取不當利益。由於這種網路犯罪手法相當新穎，網路犯罪專家估計，第一次收到「釣魚信件」的使用者有3％會被騙。"),_c('br'),_c('br'),_vm._v(" 網路釣魚通常會先寄發垃圾郵件，寄件人通常偽裝成知名銀行、Yahoo、金融聯合徵信中心等知名公司，標題則多半為「系統更新，請檢查帳號」、「保護帳號，請變更密碼」、「帳號被關閉，請上網重新啟動」等容易吸引使用者注意的字眼，而電子郵件中幾乎都會有該公司的商標，藉以取信於受害者，然後再提供一個偽裝過的連結，誘騙使用者登入假網站輸入個人資料。"),_c('br'),_c('br'),_vm._v(" 由於製作釣魚網站、發送釣魚垃圾郵件都相當容易，不需要複雜的駭客技術，只要會製作簡單的網頁並加入表單，就連國中生都能夠順利上手，所以這樣的詐騙案件也愈來愈多。"),_c('br'),_c('br'),_vm._v(" 電子商務交易的便利與網路安全技術的不斷提昇，讓許多人以為網路交易應比與臨櫃辦理一樣安全，但網路服務的申請及查核卻出現許多漏洞，使歹徒有機可乘，為了您的權益與交易安全，提醒您以下的預防之道："),_c('br'),_c('br'),_c('ul',{staticStyle:{"margin-bottom":"0px"}},[_c('li',[_vm._v("許多釣魚網站為了避免日後偵查，都是利用網路跳板的方式侵入其他電腦，所以只要仔細觀察網址，就能夠輕易辨別真偽。")]),_c('li',[_vm._v("經常核對銀行帳戶存款，特別是在網路銀行辦理相關存、提款後，應立即以金融卡至自動提款機核對交易內容，或以電話方式直接向銀行承辦人確認。")]),_c('li',[_vm._v("若因被騙，與銀行發生追討存款糾紛時，可電1950消費者服務專線，請求消保官出面，代為請求追討。")])])])])],1)],1)],1)],1)])]),_c('div',{staticClass:"frame"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s5",attrs:{"id":"s5","name":"s5"}},[_vm._v("性騷擾防治及處理")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-a1",modifiers:{"accordion-a1":true}}],attrs:{"block":"","variant":""}},[_vm._v("性騷擾的迷思破解")])],1),_c('b-collapse',{attrs:{"id":"accordion-a1","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 許多研究指出，女性對人身安全的恐懼，源自於性騷擾的迷思。性騷擾的迷思在父權社會下，不斷複製、傳遞，為騷擾者的不當行為找到了藉口，使女性在遭受性騷擾後，還得為自己的受害負責。因此，以下針對常聽到的性騷擾迷思提出反迷思策略，以達『見招拆招』的解構效果。"),_c('br'),_c('br'),_vm._v(" ● 迷思：性騷擾加害人是因為被騷擾者有意無意的引誘，一時之間無法控制性慾，才會對女性性騷擾。"),_c('br'),_vm._v(" ◎ 破解：男性也許無法控制性慾的發生，但絕對可以控制個人的行為，性騷擾是有意識的選擇與決定，而非單純的生物反應。"),_c('br'),_c('br'),_vm._v(" ● 迷思：性騷擾事件多發生在陌生人間。"),_c('br'),_vm._v(" ◎ 破解：性騷擾事件同樣可能發生在熟識者之間，包括同事(學)、長輩對晚輩、上司對下屬間。"),_c('br'),_c('br'),_vm._v(" ● 迷思：從事某些工作的女性，如檳榔西施、酒店小姐，較易遭到性騷擾。"),_c('br'),_vm._v(" ◎ 破解：任何女性，不論年齡、長相、穿著或職業等等，都有可能成為性騷擾的受害人。"),_c('br'),_c('br'),_vm._v(" ● 迷思：會遭到性騷擾的女性是因為她們自己衣著不端莊、行為不檢點，所以她們自己也要負一點責任。"),_c('br'),_vm._v(" ◎ 破解：女性的外表和穿著，與性騷擾之間並無絕對的關係。"),_c('br'),_c('br'),_vm._v(" ● 迷思：性騷擾的後果並沒有像性侵害那麼嚴重，女性不必小題大作，大驚小怪！"),_c('br'),_vm._v(" ◎ 破解：被性騷擾的女性在生理、情緒和人際關係上都會受到影響，甚至影響到女性的工作權，造成極大的壓力和人身安全的恐懼，因此女性有權大聲向騷擾者說「不」！"),_c('br'),_c('br'),_vm._v(" ● 迷思：「我是看得起你才碰你！」"),_c('br'),_vm._v(" ◎ 破解：這句話暴露了騷擾者的性別歧視。事實上我們並不需要透過騷擾者的“青睞”來肯定自我，而一個人的價值更不只有身體五官而已。"),_c('br'),_c('br'),_vm._v(" ● 迷思：性騷擾事件只是個別男女間的衝突。"),_c('br'),_vm._v(" ◎ 破解：性騷擾事件反映的是父權社會下，男女不平等的權力結構。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-a2",modifiers:{"accordion-a2":true}}],attrs:{"block":"","variant":""}},[_vm._v("校園性騷擾與防暴")])],1),_c('b-collapse',{attrs:{"id":"accordion-a2","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 校園性騷擾係指：「一種在性與性別認同上的權力濫用，而導致妨礙或傷害學生，完整的教育福祉、氣氛或機會的行為。」"),_c('br'),_c('br'),_vm._v(" ● 校園性騷擾至少有以下幾種樣態："),_c('br'),_c('br'),_vm._v(" 1、威脅性交換之性騷擾"),_c('br'),_vm._v(" 學校行政人員或老師等上級人員，以開除、留級、重修、不及格等不利於學生的威脅，以要求、交換學生滿足其性索求的騷擾。"),_c('br'),_c('br'),_vm._v(" 2、敵意學習環境之性騷擾"),_c('br'),_vm._v(" 此種性騷擾係指學校之行政人員或老師等上級人員 及同事，蓄意營造或製造一個令當事人感到敵對、受恐嚇，或被侵犯的學習環境，而不利於當事人的騷擾。如老師上課時亂講黃色笑話、展示令學生感到受冒犯的黃色書刊圖片等。"),_c('br'),_c('br'),_vm._v(" 3、利益交換之性騷擾"),_c('br'),_vm._v(" 此種性騷擾係指學校之行政人員或老師等上級人員，對提供性服務的特定學生給予特殊待遇，如獎學金、變更分數等級、加分，或其他待遇，以致影響應得到該項獎勵學生之權益。"),_c('br'),_c('br'),_vm._v(" ● 面對各種校園性騷擾事件，老師應提供學生相關的宣導與教育，學校也應重視校園性騷擾問題，還給學生與老師安全的學習與教育環境："),_c('br'),_c('br'),_vm._v(" 1、需清楚告訴學生何謂性騷擾，以維護老師本身和學生的權益，其中要特別告知「騷擾」與「恭維」的區別，「恭維」和「性騷擾」在行為本質、當事人的感受和影響上都是有差別的，因為「恭維」的本質是：雙向互動的、是一種稱讚、是平等的、善意的碰觸等；但「性騷擾」的本質卻是：片面單方向的、貶抑的、權力不均衡的、惡意的碰觸等。"),_c('br'),_c('br'),_vm._v(" 2、學校必須有明確的反性騷擾政策，並對性騷擾之定義、法律效果，與處理性騷擾之程序、申訴管道均有清晰的書面資料發送所有教職員與學生研讀熟知。"),_c('br'),_c('br'),_vm._v(" 3、學校必須有有效之管道，能將騷擾的相關資料適當地散發傳播，而其資料內容則必須包括：學生之權益、性騷擾之定義，及如何投訴性騷擾等。"),_c('br'),_c('br'),_vm._v(" 4、學校必須建立一套監督機制，以有效評估各個性騷擾申訴系統中，各環節運作的有效性與適當性。"),_c('br'),_c('br'),_vm._v(" 5、學校必須對監督管理階層、教職員與學生，提供有關反性騷擾的課程訓練，這些課程不但應具有預防作用，更有矯正組織以往騷擾防治措施之缺陷的功能。"),_c('br'),_c('br'),_vm._v(" 6、學校應編制處理性騷擾的專業單位與人員，以確保性騷擾案件能獲得公平、適當之處理。且為避免受害者對案件難於啟齒，該專業人員應以女性為佳。"),_c('br'),_c('br'),_vm._v(" 7、專家建議，學校機關最好能同時具有正式與非正式的反性騷擾申訴管道。"),_c('br'),_c('br'),_vm._v(" 8、學校需有內部刊物，宣導有關防範性騷擾的資訊，以做為教職員工教育訓練之材料。"),_c('br'),_c('br'),_vm._v(" 9、學校需在社區裡宣導反性騷擾的觀念，以提升居民對性騷擾的認識。"),_c('br'),_c('br'),_vm._v(" 10、學校需建立良好之通報系統，令教職員與學生知悉性騷擾者，以發揮懲罰性騷擾者，並預防他人再次被該性騷擾者騷擾。"),_c('br'),_c('br'),_vm._v(" ● 學生們也應對於性騷擾事件有清楚的認識，提高敏感度，以保護自身安全。"),_c('br'),_vm._v(" 以下有一些學生應知道的基本防範知識，可要好好熟讀喔！"),_c('br'),_c('br'),_vm._v(" 1、了解區辨潛在騷擾者之警訊指標，並與其保持適度距離（學者曾指出五個重要指標如下）："),_c('br'),_c('br'),_c('ul',[_c('li',[_vm._v("歧視女性而認為女性本該順服、依賴者。")]),_c('li',[_vm._v("喜用汙衊性言語評論女性者。")]),_c('li',[_vm._v("濫用藥物或酗酒者。")]),_c('li',[_vm._v("過度壓抑自己情緒與感受者。")]),_c('li',[_vm._v("低挫折容忍力與處理壓力有困難者。")])]),_vm._v(" 2、清晰的溝通"),_c('br'),_vm._v(" 由於男女社會化過程很大的差異，因此對於某些事物之看法有很大的不同，甚至對於性騷擾的態度亦然，是以當事人對於潛在的加害人，在溝通上必須十分清楚地表達自己真正的想法，以減少因誤解而產生的性騷擾。"),_c('br'),_c('br'),_vm._v(" 3、對性騷擾堅決地說「不」"),_c('br'),_vm._v(" 某些潛在的加害人具有性侵害迷思，如「說\"不\"就是\"好\"的意思」，或是「女性有被強暴的慾望」，因此專家建議，如果對方強行施予不受歡迎的行為，當事人應該立刻表達出來，以適時制止。"),_c('br'),_c('br'),_vm._v(" 4、必要時應採取行動"),_c('br'),_vm._v(" 對於性騷擾者的行為，隱忍並不能解決問題，甚而會姑息養奸，使得情況變本加厲。因之，當事人應在衡量輕重後，於適當時機採取積極行動，而由於受害者常不只一個，是以其可以聯合其他受到相同騷擾的受害者一起行動。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-a3",modifiers:{"accordion-a3":true}}],attrs:{"block":"","variant":""}},[_vm._v("強暴的迷思破解與事實")])],1),_c('b-collapse',{attrs:{"id":"accordion-a3","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" ● 迷思："),_c('br'),_vm._v(" 兩相情願，何來強暴或騷擾，對多數女性而言，當她們說「不」的時候，其實就是願意，甚至還樂在其中呢？"),_c('br'),_c('br'),_vm._v(" ◎ 事實："),_c('br'),_vm._v(" 在下列三種情況下，較易產生上述的迷思："),_c('br'),_c('br'),_c('ul',[_c('li',{staticClass:"number"},[_vm._v("彼此認識，甚至有親密關係。")]),_c('li',{staticClass:"number"},[_vm._v("在強暴發生前，已經有身體接觸的動作。")]),_c('li',{staticClass:"number"},[_vm._v("被害人的職業是妓女、舞女、娛樂事業服務人。")]),_c('li',{staticClass:"number"},[_vm._v("事實上，任何人皆是獨立的個體，都有「性」自主權，不因為彼此是朋友、親戚、配偶、家人等的親密關係，就漠視被害人對自己身體的控制權，或拒絕與別人性交的權利；其次，之前親密關係的表示也不代表之後強暴行為的合理化，有某些男女朋友，之前可能會同意對方親臉、吻頰，但這並不表示對方後來可以不顧女方的的意志霸王硬上弓。再者，對於某些以娛樂或性服務為職業的個人，也不因為他(她)的職業，就表示他(她)喪失做為獨立個體的尊嚴，而渺視他(她)也想有拒絕與人發生性行為的權利。")])]),_vm._v(" ● 迷思："),_c('br'),_vm._v(" 一個不願意被強暴的女人，男人無論如何都是不會得逞的；再說，假如在過程中，如果沒有留下反抗的痕跡，如：紅腫、瘀傷、紫青，多半也是自願的呢！"),_c('br'),_c('br'),_vm._v(" ◎ 事實："),_c('br'),_vm._v(" 在強暴過程中，被害人有兩種情況，一種是無意識(如：被下藥、灌醉等昏迷狀況)，一種是有意識，也就是被害人神智清醒，但被害人往往受制於名譽威脅，如不從就告訴你父母親、工作伙伴….，生命威脅(如不從，就殺死你)，職權威脅(如不從，就將你解雇)，而任憑擺佈。其次，被害人也可能因驚嚇過度或危機處理能力薄弱，而腦筋一片空白不知如何處理應變，終至成為被害者。在上述狀況中，被害人均可能沒有反抗或激烈的反抗行動。"),_c('br'),_c('br'),_vm._v(" ● 迷思："),_c('br'),_vm._v(" 婦女藉著暴露的穿著及不雅的舉止引誘男性，導致強暴或性騷擾，這能怪誰？如果這種女生遭遇強暴事件，大概也是罪有應得吧！"),_c('br'),_c('br'),_vm._v(" ◎ 事實："),_c('br'),_vm._v(" 在傳統的刻板印象，女性的穿著、出入場所與時間均有極嚴格的限制，但是隨這社會的轉型，男女性別角色應隨著時代的調整而有所改變。女性應該擁有穿衣服、出入時間、場所的自由選擇權，任何人不能因為她不幸遇害就責難或質疑她身而為人的自由選擇權。"),_c('br'),_c('br'),_vm._v(" ● 迷思："),_c('br'),_vm._v(" 這種女性多半是有強烈的性慾，或是在不正經的家庭中長大，否則好好的女生怎麼會遇這種事。