c('li',{staticClass:"letter-s"},[_vm._v("有挑逗性的性行為")]),_c('li',{staticClass:"letter-s"},[_vm._v("不由自主的手淫")]),_c('li',{staticClass:"letter-s"},[_vm._v("性虐待兄弟姐妹、朋友或比他更小的孩子")]),_c('li',{staticClass:"letter-s"},[_vm._v("濫交")]),_c('li',{staticClass:"letter-s"},[_vm._v("懷孕")]),_c('li',{staticClass:"letter-s"},[_vm._v("逃家")]),_c('li',{staticClass:"letter-s"},[_vm._v("自殺")])]),_vm._v(" 2、受侵害兒童最重要的徵兆"),_c('br'),_c('ul',[_c('li',[_vm._v("直腸或生殖器官疼痛")]),_c('li',[_vm._v("直腸或生殖器官出血")]),_c('li',[_vm._v("性器官感染疾病")])]),_vm._v(" PS：以上徵兆也可能顯示了其他\"非\"性虐待上的問題，並非性虐待所獨有的徵兆。"),_c('br'),_c('br'),_vm._v(" ● 對未滿十六歲之男女為性交或猥褻之行為"),_c('br'),_vm._v(" 刑法認定未滿十六歲的人對於性不具有自主能力，因此只要與十六歲以下的人發生性關係，不管當事人的意願如何，都構成犯罪行為。倘若性行為的發生違反了當事人的意願，那當然構成刑法第二百二十一條的強制性交罪；若當事人未滿十四歲，根據刑法第二百二十二條，必須加重刑罰。"),_c('br'),_vm._v(" 如果是在兩情相悅的情況下呢？刑法並不認定那是兩情相悅。"),_c('br'),_vm._v(" 根據刑法第二百二十七條，對未滿十四歲者為性交，處三年以上十年以下有期徒刑。"),_c('br'),_vm._v(" 對十四歲以上十六歲以下之人為性交，處七年以下有期徒刑。"),_c('br'),_vm._v(" 上述刑罰男女皆適用，但是未滿十八歲者犯之屬告訴乃論，而且可以減輕或免除其刑；因此常會這種情形出現：父母發現小孩偷嚐禁果後，雙方家長互告對方小孩強暴，後又因達成和解而撤回告訴。"),_c('br'),_c('br'),_vm._v(" ● 兒童被長輩性侵害"),_c('br'),_vm._v(" 與未滿十六歲以下的人發生性行為，不管當事人同意與否，皆構成犯罪。違反意願當然構成刑法第二百二十一條的強制性交罪，若當事人未滿十四歲，還要加重刑罰。倘若受侵害的兒童因害怕而說是自願的，那麼根據刑法第二百二十七條，亦處三年以上十年以下有期徒刑。另外，就受性侵害的兒童而言，一旦提出告訴就可能必須上法院接受法官問話，這種壓力可能很大。因此，性侵害犯罪防治法第十五條對智障被害人、十六歲以下被害人有作一些特別保護規定，如受害人可以在法庭外接受訊問與詰問等。 ")])])],1)],1)],1)],1)])]),_c('div',{staticClass:"frame"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s6",attrs:{"id":"s6","name":"s6"}},[_vm._v("徵信案例")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-b1",modifiers:{"accordion-b1":true}}],attrs:{"block":"","variant":""}},[_vm._v("為什麼女強人也會遭受家暴?")])],1),_c('b-collapse',{attrs:{"id":"accordion-b1","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 家暴的對象是沒有特定的，無論是男生或者女生，任何人都可能是家暴的受害者!"),_c('br'),_c('br'),_vm._v(" 女強人有可能遭到家暴的原因，例如：拒行房事，不付生活費，整天啐啐唸，太強悍或者挑惕男性等.......太多原因了。所以女強人同樣也可能會遭受家暴，且 I Q 不等於 E Q，強壯不代表強者，重要的是懂得善用法律，並且預防家暴狀況，保護自己免於成為家暴的受害者。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-b2",modifiers:{"accordion-b2":true}}],attrs:{"block":"","variant":""}},[_vm._v("家暴專線-113可以給男生打嗎?")])],1),_c('b-collapse',{attrs:{"id":"accordion-b2","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 可以的，家暴專線是給家暴受害人甚至想要出手的加害人做心理諮詢及尋求協助的專線。不論年齡與性別。除了113之外，內政部還另外提供了男性的關懷專線：0800-013-999"),_c('br'),_c('br'),_vm._v(" ◆ 對‧象 ◆"),_c('br'),_vm._v(" 1. 男性家庭暴力被害人"),_c('br'),_vm._v(" 2. 男性家庭暴力加害人"),_c('br'),_c('br'),_vm._v(" ◆ 使‧用‧時‧機 ◆"),_c('br'),_c('br'),_vm._v(" 1. 當你遭受到家庭暴力時。"),_c('br'),_vm._v(" 2. 當你想打自己家人前，先打這通電話冷靜下來。"),_c('br'),_vm._v(" 3. 服務時間：每週二到週六下午兩點到晚上十點。"),_c('br'),_c('br'),_vm._v(" ◆ 服‧務‧內‧容 ◆"),_c('br'),_c('br'),_vm._v(" 1. 夫妻關係諮詢"),_c('br'),_vm._v(" 2. 親子教養諮詢"),_c('br'),_vm._v(" 3. 家暴法律說明"),_c('br'),_c('br'),_vm._v(" 高雄市94年家庭暴力事件6769案中，被害人數為6532人，其中女性5262人，佔80.56％；而相對人（施暴者）人數為6419人，男性人數5472人，佔85.25％。"),_c('br'),_vm._v(" 男人真命苦／家暴受害人10%是男性 老男人最容易被虐 2004／07／04"),_c('br'),_c('br')]),_c('h4',[_vm._v("記者 張勵德／專題報導")]),_c('p',[_vm._v(" 別以為家暴的受害人都是女性，事實上，被虐的男人也很不少！根據內政部家庭暴力及性侵害防治委員會統計，113家暴保護專線的來電求助者中，有10%的家暴被害人為男性，而且案例有逐漸增加的情形，顯示在追求男女平權的現代，家暴中的受害人已經不是女性或孩童的專利，男性加害人的刻板印象逐漸改變。"),_c('br'),_c('br'),_vm._v(" 內政部家庭暴力及性侵害防治委員會教育輔導組長張秀鴛表示，從來電的男性求助者案例分析，男性受暴的類型與女性大不相同，其中肢體虐待較少，大部分以精神虐待為主，包括長時間的語言諷刺、嘮叨碎碎唸、以及另一半強烈的行為控制等。精神虐待比起肉體上受傷，往往更讓男人受不了！"),_c('br'),_c('br'),_vm._v(" 另一個值得關注的現象是，台北市家庭暴力暨性侵害防治中心主任童富泉在男性受暴的案例中分析發現，老年受暴佔了多數！童富泉指出，根據該中心的統計，雖然男性受害人的比率只佔家暴中的3%到5%，但其中超過60歲以上的受害人就佔了8成！其中更不乏高社經地位與中產階級的家庭。"),_c('br'),_c('br'),_vm._v(" 童富泉解釋，男人進入老年階段後衰退較快，許多受暴老男人往往是因為生病、體能退化而導致反制能力降低，而這段年紀中的女性，在家事主掌權已經不再處於弱勢，再加上該年齡層中的許多婚姻情形是男女年紀差距較大，例如老榮民娶年紀比自己小的大陸新娘等，因此容易出現「少妻欺老夫」的情形！"),_c('br'),_c('br'),_vm._v(" 張秀鴛說，過去受到傳統文化的刻板印象影響，許多男人會認為受家暴是很丟臉的事情，往往不太願意到處聲張，更不可能會打電話求助！在加上社政資源與媒體注意力多放在婦幼受暴的案例，對男性的關懷比較少，男人有苦只敢自己吞，因此，相信男性受暴的比例絕對比現在所知的更高！"),_c('br'),_c('br'),_vm._v(" 童富泉也認同這點，強調在追求兩性平權的現代，還是免不了受到傳統父權文化的影響，對男性求助上確實多了一層障礙。他認為，有許多男人是苦悶多於受虐，他們需要的可能只是一個宣洩情緒的管道，能給他們情緒上的支持與解決方法諮詢，因此，男人應該大方的向外求助，不論是親友或是社政單位，幫自己找個情緒的出口，可以避免更多的婚姻危機發生。 ")]),_c('br'),_c('h4',[_vm._v("【中時電子報 / 修淑芬報導】 2003年")]),_c('p',[_vm._v(" 調查採訪台灣男人受到家庭暴力的問題，逐漸浮出檯面上。根據內政部92年家暴數據統計顯示，去年遭受家暴的男性受害者即達4896人，佔受暴人數總比率的13.31%。而比較內政部今年6月成立的男性關懷專線的數據分析，短短93天內打電話來求助的男性就達1053人，平均一天有12通。顯見，家庭暴力本無性別之分，也不再僅限制於夫妻之間的暴力問題，男人也會是家暴的受害者。"),_c('br'),_vm._v(" 家暴個案層出不窮，隨著男性受暴個案增加，男性受暴呈現出冰山一角的隱憂，已讓內政部家暴委員會擬推動修法，設置男性受暴資源中心，增列「家庭安全教育講習」，推動家暴強制逮捕、加害人治療等配套方案。"),_c('br'),_c('br'),_vm._v(" 根據各地方家暴防治中心的線上諮詢，發現近年來主動打電話來申訴「我被家暴了」的男人確實有逐漸增加的趨勢。男性受暴者多以老人、小孩居多，加害者有男、有女，多半是家族親戚成員，型態以虐待、動粗、精神、言語家暴為主，甚至包含殺人舉動，反而屬於來自妻子的婚姻暴力的比率相當低。"),_c('br'),_c('br'),_vm._v(" 比較91年男性受害者的年齡層，可略窺一二。35到50歲人數最多達958人，其次是50歲到65歲達774人，65歲以上者也達738人，12到18歲達441人，6到12歲615人，0到6歲428人。"),_c('br'),_c('br'),_vm._v(" 新竹家暴中心督導郭玲妃表示，老人暴力的加害人多是兒子為多，尤其近日這類案例有上升的跡象。日前就有1位40多歲的兒子，本身罹患有躁鬱症，因長期失業在家無所事事，他年邁的父親忍不住多嘮叨幾句，罵他「好吃懶做」，結果兒子精神病發作、動手打了老父親好幾拳。"),_c('br'),_c('br'),_vm._v(" 在婚姻暴力個案中，社工員發現，老榮民是最主要的婚暴受害者。郭玲妃指出，老榮民孤身到台灣，年老了總想找個伴來照顧自己，很多人都娶大陸新娘或外籍配偶。但這類老少配的婚姻型態，妻子年紀輕、比較有力氣、能力比較強，容易成為家暴的施暴者。"),_c('br'),_c('br'),_vm._v(" 曾有1位80多歲的老榮民娶了大陸配偶後，享受不到清福反被逼迫、虐待，要求老榮民給錢養大陸的家庭，若不給的話，就精神虐待、不準他睡覺、也不做飯，逼得老榮民向警方報案申請保護令。郭玲妃表示，由於台灣國籍法對大陸配偶有嚴格限制，為了往後順利取得身分證、居留權，大陸配偶多半「很怕警察」，所以大陸配偶會為了在台灣順利居留，收斂起家暴的氣焰，而老榮民在看到妻子「對他好」之後，也多半心軟下來，撤除保護令。"),_c('br'),_c('br'),_vm._v(" 社工員分析，台灣男性遇到家暴，在心態上，是很丟臉的一件事情，願意求助者少之又少，因此黑數絕對相當高。不過，社工接案經驗表示，男人有時候並不認為「被打了一下、流一點血」就是「施暴」，且通常雙方都是「受害人」也是「加害人」，要分清楚誰家暴誰，著實是個問題。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-b3",modifiers:{"accordion-b3":true}}],attrs:{"block":"","variant":""}},[_vm._v("一樣家暴 不同結局")])],1),_c('b-collapse',{attrs:{"id":"accordion-b3","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("浴火")]),_c('br'),_c('br'),_vm._v(" 「我不知道，法官為什麼給我一張無用的保護令，它只是讓警察消遣我，卻保護不了我和孩子。我真想申請國家賠償！」許雪卿望著女兒被燒傷的容顏，紅著眼眶泣訴。"),_c('br'),_c('br'),_vm._v(" 相識十年，許雪卿婚後才發現丈夫酗酒。先生蘇俸諆高職畢業當守衛，工作不穩定，酒醉盛怒，就在家肆虐，從摔電話、電扇，到用大榔頭砸毀電視。他把自己的不如意，用拳頭發洩，揮向妻子，更常威脅要帶孩子一起去死。女兒秋香從小學一年級就學會，當父親帶兄妹兩人去看電影時，得偷偷留紙條給媽媽，免得她擔心。"),_c('br'),_c('br'),_vm._v(" 忍受家暴十多年，連兒子也被父親打傷。許雪卿決定報警，警察告訴她可聲請保護令。2003年2月，國小畢業的她第一次上法庭。她告訴法官，丈夫是不定時炸彈，希望他戒酒。法官說，戒酒要自己出錢，他若不去也沒辦法。她提出丈夫遷出的要求，法官又以蘇俸諆出去，會製造社會問題為由，只給她一張限制先生騷擾及毆打的保護令。"),_c('br'),_c('br'),_vm._v(" 4月2日，保護令到手。薄薄的一張紙，很快被丈夫燒了，她只好申請補發。有天丈夫報復，把晚餐全倒進水槽，她找來警察，出示保護令。警察不耐煩地說，倒個菜也要報警？保護令又沒要丈夫離家，我們怎麼處理？警察的奚落，讓她想跳樓輕生。"),_c('br'),_c('br'),_vm._v(" 變本加厲的丈夫，開始把瓦斯桶從廚房搬到客廳，威脅母子三人。連女兒都覺得，「這個保護令是假的」。"),_c('br'),_c('br'),_vm._v(" 4月28日許雪卿再提告訴，日子就在膽顫心驚中度過。"),_c('br'),_c('br'),_vm._v(" 10月22日中午，蘇俸諆叫來三桶瓦斯，放進小孩房間，深鎖大門。許雪卿不得其門而入，警察趕到，蘇俸諆從門縫遞出「惡法亦法，六法全輸」的紙條，不顧一切引爆瓦斯。"),_c('br'),_c('br'),_vm._v(" 轟然爆炸聲中，先生當場死亡，保護令再度燒成灰燼。愛跳Hip-Hop 的女兒秋香，頭部和一半背部燒到見骨，十指全被燒融。在大火中被父親一腳踹倒在地的兒子泊昇，傷癒後動不動撞牆，夜半驚夢，夢裡父親還再砍他、殺他。"),_c('br'),_c('br'),_vm._v(" 只想爭取不被毆打的權利，卻換來家破人亡的慘劇。現代婦女基金會將協助許雪卿向監察院陳情，調查法官是否失職或申請國賠。她語帶哽咽地說，「我真的希望，不會再有人發生這樣的悲劇。」"),_c('br'),_c('br'),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("新生")]),_c('br'),_c('br'),_vm._v(" 「做錯事的又不是我，被打的人可是受害者耶。台灣的觀念怎麼這麼落伍！」經商多年的黃愛芳眨著深邃的眼睛，談起她家暴的往事，很坦然。"),_c('br'),_c('br'),_vm._v(" 本以為是天作之合。三十四歲的愛芳和前夫，同年同月同日，在同家醫院誕生；同姓黃，籍貫都是廣東梅縣，還是高中同學。這麼多巧合，讓移民澳洲的愛芳，最後接受先生的千里追求。"),_c('br'),_c('br'),_vm._v(" 身為富家女的愛芳做了客家媳婦，開始發現夫家不同的一面。童養媳的婆婆常被公公打，每次都認命地在傷口貼上撒隆巴斯，不敢聲張。"),_c('br'),_c('br'),_vm._v(" 先生似乎心頭住著一個魔鬼，隨時會竄出。有次為了不讓太太出門，竟然跑進廚房拿菜刀威脅說：「你如果走出大門，我就砍我自己。」"),_c('br'),_c('br'),_vm._v(" 自殘的老公，後來開始打老婆。有次揍了她後，先生竟然用皮帶懸樑自盡，她還得忍著痛，把失控的先生救下來。愛芳最難受的是小孩受到的傷害。牙牙學語的寶寶，因為看多了爸爸打媽媽，很小就會說「爸爸掐掐」，每次餵奶都會死命掐著媽媽的脖子。"),_c('br'),_c('br'),_vm._v(" 一開始她只想逃回澳洲。可是父親不贊成她離婚，姊姊建議她報警。第一次鼓起勇氣打到家暴中心，她不敢報自己和先生的名字。第二次才被轉介到龍山婦女中心，由小她七歲的社工輔導。"),_c('br'),_c('br'),_vm._v(" 2001年10月這天，是她生命中最驚恐的日子。爭吵後，前夫勒住她的脖子，婆婆企圖阻止，卻又不准愛芳逃出大門。公公不久回到家，見到一片混亂，暴怒地拿菜刀，要三人同歸於盡。隔日凌晨，愛芳一早抱了孩子逃出去，趕緊打電話找到社工，赴醫院驗傷、報案，馬上申請緊急保護令。"),_c('br'),_c('br'),_vm._v(" 愛芳頭也不回地離開了家，開始打一連串的官司。昔日枕邊人如今站在對立陣線，她很不安，走在路上東張西望，怕被前夫攻擊。到處找免費的法律諮詢，上法庭要由社工相陪，才能放心。"),_c('br'),_c('br'),_vm._v(" 去年8月她終於離婚，有如犯人解下手腳的鐐銬，心下陡然輕鬆。以前參加受害婦女聚會，她總會和大家哭成一團，現在她不哭了，只感到重獲新生的喜悅。"),_c('br'),_c('br'),_vm._v(" 看著身旁蹦蹦跳跳的兒子，及親如家人的社工，走出家暴的黃愛芳心懷感激地說，「本來還覺得家暴中心怎麼派一個菜鳥社工給我，沒想到她比家人更能讓我倚賴，我很感謝陪我走過這段路的所有人。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-b4",modifiers:{"accordion-b4":true}}],attrs:{"block":"","variant":""}},[_vm._v("在報紙刊登斷絕父子關係，有用嗎? 法律上有效嗎?")])],1),_c('b-collapse',{attrs:{"id":"accordion-b4","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 法律上沒效，除非有受到嚴重長期的家暴、虐待等，可以考慮走法律途徑來強行褫奪親權。"),_c('br'),_vm._v(" 否則登報絕對沒用。"),_c('br'),_c('br'),_vm._v(" 但就父母親來說，對不肖子女 (原則上已成年者)，登報斷絕父子關係~在法律上有效!"),_c('br'),_vm._v(" 就是子女無法繼承遺產！ ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-b5",modifiers:{"accordion-b5":true}}],attrs:{"block":"","variant":""}},[_vm._v("如果把正版音樂MV自行轉卡拉ok帶, 拿去販售算犯法嗎?")])],1),_c('b-collapse',{attrs:{"id":"accordion-b5","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 不管你的正版MV是VCD、DVD，你只有\"所有權\"，換句話說你可以把你持有的正版物轉賣；但權利人（唱片公司）並沒有授予你重製權，所以不管有沒有轉換成卡拉ok，只要經過拷貝行為再販賣，都算侵犯著作權，都算犯法。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-b6",modifiers:{"accordion-b6":true}}],attrs:{"block":"","variant":""}},[_vm._v("員工為公司撰寫程式，著作財產權歸公司? 所以公司能自由拷貝?")])],1),_c('b-collapse',{attrs:{"id":"accordion-b6","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 本人為程式設計師，過去為公司寫了一套系統 (個人獨力完成)"),_c('br'),_vm._v(" 完成後公司主管擅自拿去發表，該主管並向我要操作手冊，以巡迴各點做教育訓練，結果公司裡沒幾個人知道是我做的系統......."),_c('br'),_vm._v(" 後來該主管並擅自將我的系統COPY給公司的關係企業使用(未經我同意)，只改了個名稱，這樣算是侵權行為嗎？"),_c('br'),_c('br'),_vm._v(" 著作權是屬於公司嗎？難道我只能放任主管COPY到爽？"),_c('br'),_c('br'),_vm._v(" 著作權法"),_c('br'),_c('br'),_vm._v(" 第 11 條 受雇人於職務上完成之著作，以該受雇人為著作人。但契約約定以雇用人為著作人者，從其約定。"),_c('br'),_vm._v(" 依前項規定，以受雇人為著作人者，其著作財產權歸雇用人享有。但契約約定其著作財產權歸受雇人享有者，從其約定。"),_c('br'),_vm._v(" 前二項所稱受雇人，包括公務員。"),_c('br'),_c('br'),_vm._v(" 第 12 條 出資聘請他人完成之著作，除前條情形外，以該受聘人為著作人。但契約約定以出資人為著作人者，從其約定。"),_c('br'),_vm._v(" 依前項規定，以受聘人為著作人者，其著作財產權依契約約定，歸受聘人或出資人享有。未約定著作財產權之歸屬者，其著作財產權歸受聘人享有。"),_c('br'),_vm._v(" 依前項規定著作財產權歸受聘人享有者，出資人得利用該著作。"),_c('br'),_c('br'),_vm._v(" 『著作權法』，其分為\"著作產財權\"與\"著作人格權\"。"),_c('br'),_vm._v(" 首先，著作權於著作完成即享有之，因此是不需要申請的，無論是所謂著作人格權及著作財產權，前者為該著作姓名表示及公開發表等權利，代表的是完成著作的那個\"人\"，後者則為使用(copy)該著作\"物\"的權利。"),_c('br'),_c('br'),_vm._v(" 1. 雙方為僱傭關係："),_c('br'),_vm._v(" 僱傭關係按民法解釋，謂受僱人於一定或不定之期限內為僱用人服勞務，僱用人給付報酬之約定，因此按月領薪者即屬之(民法§482參照)，故於雙方契約未特別約定著作財產權歸屬的情況下(即在職務上所完成的)，著作財產權乃屬於僱用人的，也就是在此情況下受雇人未經僱用人同意不得使用該著作。"),_c('br'),_c('br'),_vm._v(" 2. 雙方為承攬關係："),_c('br'),_vm._v(" 承攬關係按民法解釋，謂當事人約定，為受聘人完成一定之工作，出資方俟工作完成，給付報酬之約定(民法§490參照)，故於雙方契約未特別約定著作財產權歸屬的情況下，著作財產權仍是屬於受聘人的，即日後受聘人無需經由出資人同意即可自由使用該著作，但是出資人仍可以其名義在合理的範圍內使用該著作。"),_c('br'),_c('br'),_vm._v(" 3. 由上述可知，如您之前寫該系統期間係受僱於公司(領固定月俸)，一般應屬於僱傭關係，即無論是否與雇主約定誰為著作人，公司皆享有著作財產權，即可任意利用之，但若無約定雇主為著作人，您能具有姓名表示權，即以自己為名義在其他地方發表。 ")])])],1)],1)],1)],1)])]),_c('div',{staticClass:"frame"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s7",attrs:{"id":"s7","name":"s7"}},[_vm._v("法律問題")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-c1",modifiers:{"accordion-c1":true}}],attrs:{"block":"","variant":""}},[_vm._v("公證結婚注意事項")])],1),_c('b-collapse',{attrs:{"id":"accordion-c1","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 辦理公證結婚，在辦公時間內，應隨到隨辦。"),_c('br'),_vm._v(" 結婚日期，由結婚人自行擇定，如其結婚日期在星期日或國定休假日時，應於前一天辦理聲請手續，並繳納公證費。"),_c('br'),_c('br'),_vm._v(" 公證人收受當事人公證結婚聲請書時，應注意審查是否合法，並得就到場人為必要之詢問"),_c('br'),_vm._v(" 公證人於公證結婚儀式舉行時，應切實查核到場之結婚人、法定代理人及證人是否本人。"),_c('br'),_c('br'),_vm._v(" 結婚人有下列情形者，不得為其辦理公證結婚。"),_c('br'),_c('ul',[_c('li',[_vm._v("男未滿十八歲，女未滿十六歲者。")]),_c('li',[_vm._v("女子自前婚姻關係消滅後未逾六個月之期間，但已分娩者不在此限。")]),_c('li',[_vm._v("違反民法第九八三條所定親屬結婚之限制者。")]),_c('li',[_vm._v("監護人與受監護人於監護關係存續中結婚而未經受監護人之父母同意者。")]),_c('li',[_vm._v("重婚者。")]),_c('li',[_vm._v("因姦經判決離婚或受刑之宣告而與相姦人結婚者。")])]),_vm._v(" 結婚人如有原配偶死亡（包括宣告死亡）或離婚情形者，應命其提出國民身分證查核，必要時得命提出戶籍謄本予以核對。"),_c('br'),_c('br'),_vm._v(" 結婚人如男已滿十八歲，女已滿十六歲而未滿二十歲者，其法定代理人（即生父母、養父母或監護人）應親自到場，行使同意權，其不能到場者，應出附有印鑑證明書之同意書或經其他公務機關簽註之同意書。"),_c('br'),_c('br'),_vm._v(" 未成年人結婚，其法定代理人到場行使同意權時，應於公證聲請書有關欄內，記明法定代理人姓名、性別、年齡、籍貫、職業、住所、身分證字號，表示同意，並由其於公證結婚證書上簽名蓋章。"),_c('br'),_c('br'),_vm._v(" 現役軍人聲請辦理公證結婚，應繳驗所屬長官核准其結婚之證明文件。在核准結婚之日期前，不得舉行公證結婚儀式。"),_c('br'),_c('br'),_vm._v(" 現役軍人結婚時，應於公證結婚證書有關欄內，註明請求人為現役軍人，並記載核准結婚證明文件之日期及字號。"),_c('br'),_c('br'),_vm._v(" 現役軍人所繳驗之核准結婚證明文件，由公證處附卷備查。如必須繳回者，應具備繕本或複印本，經佐理員核對相符，註明「與原件相符」字樣，並蓋章於該繕本或複印本上，送請公證人核准後發還之。"),_c('br'),_c('br'),_vm._v(" 公證結婚典禮時，男女雙方及其法定代理人、證人等必須各自攜帶國民身分證（軍人身分補給證）及私章親自到場，並應當場在公證結婚書上簽名、蓋章。公證人簽名，亦必須當場為之。"),_c('br'),_c('br'),_vm._v(" 結婚人如為外國人或其一方為外國人時，應繳驗各該當事人所屬大使館、領事館發給之婚姻狀況宣誓書或經其簽證得為婚姻之證明文件。如為外國軍人時，應繳驗各該當事人所屬大使館、領事館或服務機關、部隊主管，發給之軍人結婚批准書。"),_c('br'),_c('br'),_vm._v(" 華僑聲請公證結婚時，應提出僑居地大使館、領事館或華僑團體等，相當機關簽證之證明文件結婚人，如為無國籍人或為與我國無外交關係之外國人時，應繳驗足以證明其婚姻狀況之適當證明文件。"),_c('br'),_c('br'),_vm._v(" 結婚公證書應以打字、毛筆或黑墨水填寫。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-c2",modifiers:{"accordion-c2":true}}],attrs:{"block":"","variant":""}},[_vm._v("有公開儀式結婚但不登記，算無效婚姻?")])],1),_c('b-collapse',{attrs:{"id":"accordion-c2","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 依據民法親屬篇 第九八二條 (96.05.23修定如下)"),_c('br'),_vm._v(" 結婚，應有公開儀式及二人以上之證人。經依戶籍法為結婚之登記者，推定其已結婚。"),_c('br'),_vm._v(" （舊法條，一年後失效）← 舊法，儀式婚"),_c('br'),_c('br'),_vm._v(" 結婚應以書面為之，有二人以上證人之簽名，並應由雙方當事人向戶政機關為結婚之登記。"),_c('br'),_vm._v(" （新法條，自公布後一年施行） ← 新法，登記婚"),_c('br'),_c('br'),_vm._v(" 即在民國97年5月之後，若只有公開儀式而沒有到戶政機關作結婚登記者，其婚姻視同無效。"),_c('br'),_vm._v(" 而在此之前有公開儀式而無登記結婚者，於法律上仍屬有效婚姻。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-c3",modifiers:{"accordion-c3":true}}],attrs:{"block":"","variant":""}},[_vm._v("電視台.網路.光纖....等線路，在你家外牆釘東西...")])],1),_c('b-collapse',{attrs:{"id":"accordion-c3","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 我想請問下列幾個問題"),_c('br'),_c('ul',[_c('li',{staticClass:"number"},[_vm._v("電視台或網路公司對此住戶，未通知將敲打外牆或釘東西在外牆上，是否屬於違法行為...")]),_c('li',{staticClass:"number"},[_vm._v("住戶是否有權利提起告訴")]),_c('li',{staticClass:"number"},[_vm._v("假使住戶告知其公司，不願意接受他們的如此行為，諾屢勸不聽，是否會加重其刑")]),_c('li',{staticClass:"number"},[_vm._v("如果造成房屋外牆剝落，或是其他損失，是否可要求賠償?")])]),_vm._v(" 請附上詳細法律條文"),_c('br'),_c('br'),_vm._v(" 一. 房屋外牆仍為屋主所有,任何人不得侵犯,若為人侵犯時,可依民法侵權行為來主張,附民法184, 188條條文供參考"),_c('br'),_c('br'),_vm._v(" 第 184 條"),_c('br'),_vm._v(" 因故意或過失，不法侵害他人之權利者，負損害賠償責任。故意以背於善良風俗之方法，加損害於他人者亦同。"),_c('br'),_vm._v(" 違反保護他人之法律，致生損害於他人者，負賠償責任。但能證明其行為無過失者，不在此限。"),_c('br'),_c('br'),_vm._v(" 第 188 條"),_c('br'),_vm._v(" 受僱人因執行職務，不法侵害他人之權利者，由僱用人與行為人連帶負損害賠償責任。但選任受僱人及監督其職務之執行，已盡相當之注意或縱加以相當之注意而仍不免發生損害者，僱用人不負賠償責任。"),_c('br'),_vm._v(" 如被害人依前項但書之規定，不能受損害賠償時，法院因其聲請，得斟酌僱用人與被害人之經濟狀況，令僱用人為全部或一部之損害賠償。"),_c('br'),_vm._v(" 僱用人賠償損害時，對於為侵權行為之受僱人，有求償權。"),_c('br'),_c('br'),_vm._v(" 二. 另有關侵權行為的意義, 客觀要件, 主觀要件, 效力的規定如下:"),_c('br'),_vm._v(" (1) 意義 - 因故意或過失不法侵害他人之權利者,負損害賠償之責任"),_c('br'),_vm._v(" (2) 客觀要件"),_c('br'),_c('ul',[_c('li',{staticClass:"letter-s"},[_vm._v("需有加害行為")]),_c('li',{staticClass:"letter-s"},[_vm._v("行為需不法")]),_c('li',{staticClass:"letter-s"},[_vm._v("需侵害權利或利益")]),_c('li',{staticClass:"letter-s"},[_vm._v("致生損害")]),_c('li',{staticClass:"letter-s"},[_vm._v("損害與結果間有因果關係")])]),_vm._v(" (3) 主觀要件"),_c('br'),_c('ul',[_c('li',{staticClass:"letter-s"},[_vm._v("需有責任能力")]),_c('li',{staticClass:"letter-s"},[_vm._v("需為故意或過失")])]),_vm._v(" (4) 效力"),_c('br'),_c('ul',{staticStyle:{"margin-bottom":"0px"}},[_c('li',{staticClass:"letter-s"},[_vm._v("被害日起10年內應主張")]),_c('li',{staticClass:"letter-s"},[_vm._v("發現侵害後2年間不行使消滅")]),_c('li',{staticClass:"letter-s"},[_vm._v("但為不當得利,於15年內依不當得利請求反還")]),_c('li',{staticClass:"letter-s"},[_vm._v("加害人因侵權行為取得債權者,雖因時效消滅仍得拒絕履行")])])])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-c4",modifiers:{"accordion-c4":true}}],attrs:{"block":"","variant":""}},[_vm._v("離婚了，協議書可更改嗎 ? 對方如不肯，可上法院請法官重新判嗎?")])],1),_c('b-collapse',{attrs:{"id":"accordion-c4","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 離婚了，協議書可更改嗎? 對方如不肯，可上法院請法官重新判嗎?"),_c('br'),_vm._v(" 還沒登記前是可以改的~ 如果登記了離了婚就不行嚕! ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-c5",modifiers:{"accordion-c5":true}}],attrs:{"block":"","variant":""}},[_vm._v("請問如果在自己的BLOG (部落格)上罵人，犯法嗎?")])],1),_c('b-collapse',{attrs:{"id":"accordion-c5","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 如果在自己的blog上指名道姓寫一些言論來罵某個人，算是犯法的行為嗎?"),_c('br'),_c('br'),_vm._v(" 若您所罵的為屬實，且未加一些不文雅的話，那您並不犯罪"),_c('br'),_c('br'),_vm._v(" 但若您所罵的並非屬實，又多罵了一堆不文雅的話，那對方可以向您提出告訴，可以告您誹謗罪 (為告訴乃論的刑事案件)，刑法對於誹謗行為的處罰規定"),_c('br'),_c('br'),_vm._v(" 刑法 第三百十條：「意圖散布於眾，而指摘或傳述足以毀損他人名譽之事者，為誹謗罪，處一年以下有期徒刑、拘役或五百元以下罰金。散布文字、圖畫犯前項之罪者，處二年以下有期徒刑、拘役或一千元以下罰金。對於所誹謗之事，能證明其為真實者，不罰。但涉於私德而與公共利益無關者，不在此限」"),_c('br'),_c('br'),_vm._v(" 這樣，您明白了嗎 ? 總之，無論在生活之中或是網路世界，言行都務必小心唷! ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-c6",modifiers:{"accordion-c6":true}}],attrs:{"block":"","variant":""}},[_vm._v("請問嫖客有法定妻子時，會被判刑嗎?")])],1),_c('b-collapse',{attrs:{"id":"accordion-c6","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 依據中華民國現行法律，除違反妨害性自主罪、兒少法、兒少性交易防治條例外，不處罰嫖客。"),_c('br'),_c('br'),_vm._v(" 但民法上嫖客的配偶可向法院請求離婚。(參考民法第1052條)"),_c('br'),_vm._v(" 或者告嫖客與第三者通姦罪。"),_c('br'),_c('br'),_vm._v(" 若是與未滿18歲的人性交，嫖客還是會因違反兒少性交易防治條例，而被罰喔... ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-c7",modifiers:{"accordion-c7":true}}],attrs:{"block":"","variant":""}},[_vm._v("在大陸嫖妓，嫖客有罪嗎?")])],1),_c('b-collapse',{attrs:{"id":"accordion-c7","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 在台灣嫖客是無罪的"),_c('br'),_vm._v(" 那在大陸嫖客有罪嗎?"),_c('br'),_c('br'),_vm._v(" 不是「犯罪」，但有行政處罰。"),_c('br'),_c('br'),_vm._v(" 中華人民共和國治安管理處罰法 第六十六條"),_c('br'),_vm._v(" 賣淫、嫖娼的，處十日以上十五日以下拘留，可以並處五千元以下罰款；情節較輕的，處五日以下拘留或者五百元以下罰款。"),_c('br'),_c('br'),_vm._v(" 在公共場所拉客招嫖的，處五日以下拘留或者五百元以下罰款。"),_c('br'),_c('br'),_vm._v(" 這個法律，性質與作用上類似於我國的「社會秩序維護法」。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-c8",modifiers:{"accordion-c8":true}}],attrs:{"block":"","variant":""}},[_vm._v("劈腿戀不道德嗎?")])],1),_c('b-collapse',{attrs:{"id":"accordion-c8","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("本文轉載 自由時報3月19日自由廣場")]),_c('p',[_vm._v(" 卡維波 中央大學哲學研究所教授 (以下言論，不代表本網立場)"),_c('br'),_vm._v(" 有一陣子在討論女性情慾自主時，報紙曾經談過女性腳踩兩條船的經驗和祕訣，當時就把這類人稱為「劈腿族」。近來由於緋聞情殺的男主角都是腳踏多條船，故而引起人們對於「劈腿戀」這種性愛模式的批評或甚至制裁。"),_c('br'),_c('br'),_vm._v(" 但是劈腿戀真的是造成情殺或犯罪的原因嗎？"),_c('br'),_c('br'),_vm._v(" 首先，在這些案例中為感情殺人的並不是劈腿族本身，因此並不能就此斷言劈腿族，是心智不健全的。畢竟，劈腿戀並不會使人特別傾向偏差犯罪。其次，在我們的社會中，劈腿族不在少數，和劈腿族有性愛關係者必然是成千上萬，如果劈腿戀會導致他人偏差犯罪，那麼台灣社會應該早就是殺戮戰場了。由此可見，劈腿戀也並不必然導致他人犯罪殺人。那些殺人者自身的性格與行事風格恐怕才是重要原因。再者，我門是否可以說：清大洪曉慧之所以殺人是因為她自己的性愛模式是講求忠誠排他的，易言之，我們是否可以把情殺歸罪於這種傳統的忠誠性愛模式呢？"),_c('br'),_c('br'),_vm._v(" 或有人說，劈腿戀至少是促成情殺的間接原因之一。但是一件事情的間接原因何其多！清大的洪曉慧之所以有能力殺人，或許間接是因為她過去上過婦女防身課程、軍訓課程、化學課程等等，為什麼我們不去怪罪這些課程呢？為什麼一般人總是急急忙忙要把矛頭指向劈腿戀呢？"),_c('br'),_c('br'),_vm._v(" 其實劈腿戀就像同性戀、雙性戀、變性反串、性工作這些被污名化的性實踐一樣，很容易被人挑出來當作禍首。而劈腿戀之所以會被污名化，主要的是因為它違背了主流的性愛模式，因而被認為是不倫的、不道德的。"),_c('br'),_c('br'),_vm._v(" 主流的愛情模式常被學者稱為「浪漫愛」，講求排他、一對一、忠誠、持久、承諾責任和身心合一。這個愛情模式原來並不是人類社會的主流，一直到現代才透過無數愛情小說歌曲戲劇等建構為主流；而由這種浪漫愛所發展出來的性道德和婚姻觀，則有助於使現代一夫一妻制的父權家庭保持長期穩定。這個主流愛情模式取得霸權地位後，不但對個人的性愛實踐形成一種強制性，而且還壓迫其他愛情模式，將不同的性愛傾向污名化。為了反擊這種「愛壓迫」，爭取各種愛情模式的平等，因而出現了「愛解放」的呼聲，就是讓愛情不侷限於「排他忠誠持久」這種唯一的模式，而讓其他模式的愛情有多元的生存空間。"),_c('br'),_c('br'),_vm._v(" 劈腿狀況人人有之?"),_c('br'),_c('br'),_vm._v(" 劈腿戀這種愛情模式平常見不得人，但卻是一直存在的，而且幾乎人人都有（過）這種劈腿戀經驗，只是程度深淺和時間長短不同而已。即使那些並非遊戲人間、專以結婚為目的之男女，也常同時和多人交往以便選擇。而有了固定伴侶的人，對於新追求者也未必嚴詞峻拒，而可能保持某種友誼以便有個退路。換句話說，主流的愛情模式雖然有霸權統治，但是眾人卻在私下以不露馬腳（劈腿）為原則，進行各種愛情模式的實踐與實驗，只要不把事情鬧大，就不會被「自動退學」。"),_c('br'),_c('br'),_vm._v(" 有趣的是，明明人人都是或都有劈腿戀，但是一碰到因為社會事件而產生道德淨化的空氣時，就個個群起譴責劈腿戀來掩飾自己，把自己想像成從來就是正義道德的化身，決不同於那些「真正的」劈腿戀。"),_c('br'),_c('br'),_vm._v(" 但是究竟劈腿戀道不道德？或說劈腿戀涉及說謊欺騙，因此不道德。但是，不是所有劈腿戀都涉及說謊欺騙，清大的三角關係就一直是三方共知的。其次，說謊欺騙有時是審慎的善意謊言，未必就是不道德。例如，某女雖然有了新男友而且準備和舊男友分手，但是舊男友有暴力傾向，此時必須維持劈腿戀狀態以尋找妥善分手之道，否則實話實說將帶來災禍。（或問，為什麼不等前一個戀情結束後，再開始新的？但是有時當事人必須有新的戀情才能使她有勇氣和決心和支持來結束舊的戀情）。又例如，很多周旋在不睦婆媳之間的男人，必須靠謊言欺騙才能使這兩個愛他的女人相安無事。這些說謊欺騙的例子都顯示，不能一概而論劈腿戀必然不道德。"),_c('br'),_c('br'),_vm._v(" 還有人認為劈腿戀會傷害腳下踩的那幾條「船」，所以不道德。但是這種情感傷害並不一定能構成不道德的理由，因為有人即使不是劈腿族，但是覺得雙方不合，因而求去，這樣也會造成對方失戀的痛苦，而一般人並不因此認為斬斷情絲者不道德，反而還認為長痛不如短痛、揮劍傷人心才是道德的。"),_c('br'),_c('br'),_vm._v(" 這樣看來，劈腿戀是否道德要看具體情況來作倫理分析，不能說整個劈腿戀性愛模式都是不道德的，就像我們不能因為百分之99.9的強姦都是異性戀而說異性戀是不道德的一樣。劈腿戀就和異性戀一樣，是一個性愛範疇或模式，不必然道德或不道德，道德不道德還要看個別情況下的實踐。"),_c('br'),_c('br'),_vm._v(" 總之，如果我們不假設浪漫愛是唯一正當的愛情形態，如果我們同意後現代人也可以有非婚姻取向、非排他、非持久、無承諾、性與愛可分離的多樣愛情形態，那麼劈腿戀也不過就是一種性愛傾向或生活方式的選擇而已，無關乎道德。"),_c('br'),_c('br'),_vm._v(" 但是劈腿戀合不合乎愛情的遊戲規則？愛情遊戲的規則到底是什麼，這是一個文化共識的問題。這個遊戲規則不可能訂的很死，讓每個進入遊戲的人都不能變心，都不能劈腿，因為男男女女都可能在各種因緣際會裡，有不同程度的劈腿或是變心。故而，今天我們進入愛情遊戲，是否就應該假定對方（不論男或女）可能會同時交往、偶而偷吃，或者假定愛情不可能是一生一世，永不變心，即使結婚也會有劈腿戀或者離婚？"),_c('br'),_c('br'),_vm._v(" 我認為基本上現代人應該有上述的心理準備，戀愛就是要冒這些風險，承諾可能會有變、有假。玩戀愛遊戲不能算計資產負債表，志願付出時別期望同樣的回報。如果對方變心後，去潑硫酸，去騷擾，去公佈裸照，或想法讓對方失業、痛苦，那麼你又何曾真的愛過對方？還不是一種佔有和交易的心態，不甘損失的報復？"),_c('br'),_c('br'),_vm._v(" 所以，戀愛時，不要委屈自己，不要作不甘願的犧牲，否則自己的軟弱又能怪誰呢？！愛情能給人的就是戀愛時的快樂，想要從愛情得到更多，那就要冒著可能失去的心理準備，因為那已經超過愛情本身所能負擔。 ")])])],1)],1)],1)],1)])])])])])])])}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"hero-box"},[_c('div',{staticClass:"hero"})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"inner"},[_c('h1',[_c('span',{staticClass:"cont"},[_vm._v("Legal Advice")]),_vm._v(" 法律資訊免費諮詢 ")])])}]

