import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Service from '../views/Service.vue'
import Serve1 from '../views/Serve1.vue'
import Serve2 from '../views/Serve2.vue'
import Serve3 from '../views/Serve3.vue'
import Serve4 from '../views/Serve4.vue'
