<template>
<div class="BackTop">
    <div id="connectUsImg" class="img_box" v-show="showIcon" @click="backTop()">
        <img src="../assets/images/chevron-bar-up.svg" />
    </div>
</div>
</template>

<script>
export default {
    name: "connectUsImg",
    data() {
        return {
        showIcon: false
        };
    },
    mounted() {
        window.addEventListener('scroll', this.scrollBackTop);
    },
    methods: {
        backTop () {
            const that = this
            let timer = setInterval(() => {
                let ispeed = Math.floor(-that.scrollTop / 5);
                    document.documentElement.scrollTop = document.body.scrollTop = that.scrollTop + ispeed;
                    if (that.scrollTop === 0) {
                    clearInterval(timer);
                }
            }, 16)
        },
        scrollBackTop () {
            const that = this;
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
            that.scrollTop = scrollTop;
            if (that.scrollTop > 60) {
            that.showIcon = true;
            } else {
            that.showIcon = false;
            }
        }
    }, 
    destroyed () {
    window.removeEventListener('scroll', this.scrollBackTop)
    }
};
</script>

<style scoped>
.img_box img{
    width:40px;
    height:40px;
    background:url('../assets/images/chevron-bar-up.svg');
    background-size:100% 100%;
    position:fixed;
    z-index:999;
    right:2%;
    bottom:2%;
    border-radius: 10%;
    background: aqua;
}
.img_box{
    background-color: transparent;
}
.img_box:hover img{
    cursor: pointer;
    background-color: #AAAAFF;
}

</style>