                            一百二十人組志願律師團<br /><br />
                            三鹿奶粉志願律師團的成員二十八日也證實，幾個省份的官員都向律師團成員或其上司施壓，要他們不要接案。發起人之一的北京律師李方平說，近日已有約二十四名律師打電話來表明希望退出這項活動。「其中一些人表示，他們或事務所被告知，若繼續插手此事，將面臨嚴重後果。」<br /><br />
                            儘管如此，目前仍有約一百二十位律師不為所動，願意提供民眾免費法律諮詢服務。志願律師團北京分團的律師周世鋒說，「我猜想他們（中國政府）認為這個議題對律師及法院而言都太過敏感，當牽涉的利益過於龐大時，他們會想出辦法讓律師退場，而不必直接下令。」<br /><br />
                            事實上，據香港大公報日前報導，河北省政府在三鹿毒奶事件曝光前，即曾與律師界人士開會，要求律師不要受理毒奶粉事件受害者訴訟，導致河北省律師迴避和拒絕受害者索賠的委託。<br /><br />
                            中國全國律師協會憲法與人權專業委員會主任吳革說，中國憲法規定，公民依法享有辯護權，他們有獲得法律幫助的權利，河北禁止律師為三鹿奶粉受害者訴訟的行為，不合乎憲法精神，明顯違憲。
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                </div>
              </div>
            </div>
            <div class="frame">
              <div
                data-bs-spy="scroll"
                data-bs-target="#list-example"
                data-bs-offset="0"
                class="scrollspy-example"
                tabindex="0"
              >
                <h2 id="s2" name="s2" ref="s2">包二奶相關新聞</h2>
                <div class="accordion" role="tablist">
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-11 variant=""
                        >台商大陸送禮&nbsp;奶粉紅過美酒</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-11"
                      visible
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h4>更新日期:2008/09/20 04:35</h4>
                          <p>
                            大陸台商目前送禮拉關係，進口奶粉取代金門高粱酒、洋酒，成為最搶手的禮品。也有台商託人買進口奶粉，而且台灣進口的才要，目的只為了博取二奶的歡心。<br /><br />
                            楊姓船商指出，在大陸打關係、走後門，總要帶上伴手禮才行，以往洋酒和金門高粱酒都算是「好看」的禮物，但最近因為大陸毒奶粉鬧得沸沸揚揚，有些台商會貼心送上台灣進口奶粉，竟然還相當受歡迎。<br /><br />
                            他表示，一位經常往來東莞、漳州和上海的陳姓台商，在毒奶粉事件爆發之後，打聽到一名負責土地開發的官員，家中小孩只喝特定廠牌進口奶粉，在登門拜訪時除了陳年好酒之外，還要秘書帶了一箱該廠牌奶粉，結果博得官員太太的好感，事情愈辦愈順利。<br /><br />
                            金門跑單幫林姓民眾也透露，台商包二奶比比皆是，以往二奶不太講究奶粉的廠牌，但現在可是用心計較，不但自己和小孩都要喝進口奶粉，而且最好是台灣進口的才要，讓一些拈花惹草的台商也得費心託人帶到對岸。<br /><br />
                            另外，嫁到金門的大陸新娘，最近日子也過得特別忙碌，不少人忙著自家的事，還要關心大陸娘家，國際電話越打越多，越講越久，只要娘家有小孩要喝奶粉，就想託人帶奶粉過去，連金門老公也跟著操心。
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-22 variant=""
                        >台商包二奶&nbsp;直航怕抓姦?!</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-22"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            台商在中國包二奶時有所聞，而且衍生出來的順口溜還真的非常多。<br /><br />
                            接下來，兩岸包機直航，大家可能認為，台商會擔心家人突擊檢查，開始收斂。<br /><br />
                            不過經常往返中國的資深導遊就說，台商不怕元配查勤，倒是煩惱會不會有二奶團，搭直航班機，來台找老公。台商在中國包二奶，早就不是新聞，而且中國還有很多有趣的順口溜，反諷這種二奶現象，甚至連二奶該怎麼包?
                            哪些類型的女人最好?都有教戰守則。<br /><br />
                            不過，兩岸似乎馬上要直航，以後大老婆想抓姦，一班飛機3、4個小時就到，許多徵信業更是看準時機，狂推「抓二奶」業務，還製作網站介紹。<br /><br />
                            包二奶的台商總該收斂了吧?
                            看起來，似乎大老婆都還沒抓姦在床，二奶團就準備反攻，怎麼可能這麼囂張?<br /><br />
                            原來，因為在中國沒有「通姦罪」，就算大老婆掌握證據，頂多也只能訴請離婚，老公沒有任何刑責。<br /><br />
                            就有人建議，與其費心來回大陸抓姦，不如把老公的經濟大權掌握在手中，讓他想包二奶也沒錢包，可能更有效一點。
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-33 variant=""
                        >直航大陸抓姦只要半天&nbsp;台商二奶了斷</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-33"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            包機直航縮短兩岸往返距離，台商和台商太太可以經常對飛相聚，台商元配也可以隨時到對岸宣示主權，抓姦時間縮短成半天，台商圈盛傳，二奶危機浮現，不少台商忙著把二奶處理掉。<br /><br />
                            週末台商社交圈，先生圍在吧台坐一排，聊男人話題還得幫忙帶小孩，因為太太們喝下午茶，聊的是兒童不宜的辛辣話題「包二奶」。
                            台商太太：「抓我姦，還是我抓他姦？我有朋友有在抓姦的，我每天都在想這個，他會不會在外面亂搞，真的啊
                            ~」<br /><br />
                            台商太太：「妳老公在外面亂搞啊？」台商太太：「沒有啦，開玩笑。」
                            這群在上海的台媽家庭圓滿，直航後，婆婆可以坐飛機享受天倫之樂；但是一部分在台灣苦守寒窯的台商太太，卻得利用包機直航，快速抓二奶。<br /><br />
                            這樣到大陸抓姦到底有多快，一首打油詩這麼說「朝發台北彩雲間，百里深滬一日回，機場二奶啼不住，情夫被抓回台灣。」<br /><br />
                            TVBS記者彭馨儀：「詩裡說的是台商元配上午從台灣出發，中午就到大陸，抓偷吃只需要半天，面對元配一窩蜂登陸宣示主權，到底二奶有沒有像外界說的，開始『剉咧等』？」
                            台商太太：「基本上我不認為二奶會收斂，她們會隱藏的更好吧。」記者：「妳覺得直航後，她們會更誇張嗎？」台商太太：「不會啊，1個小時和6個小時，都是抓姦啊。真的要的話不差那一點時間。」
                            挺著肚子，台媽談二奶愈講愈激動，等不及直航，聽多了包二奶的花招，寧可放棄台灣一切跟在先生旁。<br /><br />
                            記著問台商太太：「妳幾月來的?」台商太太們：「我8月來的，我也8月來的。」台商太太：「我們台媽一些有家庭都是一樣的，都放棄台灣的工作，然後為了先生來這邊。」<br /><br />
                            還有一部分的台媽就靠直航馬上宣戰，希望容易多來大陸，能對台商先生縱情歡場，有一些警惕。
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-44 variant=""
                        >老公包二奶&nbsp;妻養小白臉&nbsp;隔海賽抓姦</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-44"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            桃園朱姓台商在大陸包二奶，妻子跨海抓姦失敗，氣得在台養小白臉報復，夫妻開始隔海比賽抓姦；結果丈夫先拍到太太偷人的激情光碟，告妻子妨害家庭，桃園地檢署昨天開庭，妻子當庭表示要反告丈夫包二奶。<br /><br />
                            朱姓台商（卅七歲）三年前赴上海浦東地區工作，原本三個月回台一次，近二年來只有過年回台，妻子江姓婦人懷疑先生出軌，她多方打聽後，確認丈夫在大陸包二奶。<br /><br />
                            去年六月十一日，江姓婦人首度赴上海抓姦，因人生地不熟，向丈夫同事打探消息時，反被對方洩密，遭丈夫痛罵一頓後回台。<br /><br />
                            江姓婦人痛恨丈夫包二奶，去年九月起在國內「包養」比她小五歲的曾姓保險員，她與小情人以乾姐弟相稱。不過她的紅杏出牆被人向她丈夫打小報告，夫妻二人從此展開諜對諜的隔海抓姦比賽。<br /><br />
                            據檢方調查，江姓婦人去年九月和十一月二度赴大陸抓姦，準備抓丈夫偷情後訴請離婚分產，其中一次報請公安配合，但她丈夫和上海浦東地區的公安關係良好，公安先洩密給朱姓台商，再故意拖延時間掩護，等江婦趕到住處時，丈夫和二奶早已離去。另一次則是丈夫搬家，她向公司秘書打聽住址，丈夫早一步得知消息，抓姦再度失敗。<br /><br />
                            朱姓台商去年十月得知戴綠帽後，隔海委託徵信社跟監，還在家中裝了針孔攝影機；今年一月十七日，拍到妻子帶小男友返家親熱的激情光碟，隨後拿著性愛光碟向桃園地檢署控告妻子和曾姓保險業務員妨害家庭。<br /><br />
                            檢察官昨天開庭時，江姓婦人和曾姓保險員面對性愛光碟無言以對，江姓婦人承認為了報復丈夫包二奶才交男友，她也當庭控告丈夫包二奶涉及通姦，檢察官要求她另提出告訴。她向檢察官表示會持續找人抓姦，找到丈夫偷腥的證據為止。
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-55 variant=""
                        >爸爸變成大陸台幹&nbsp;很少回來又包二奶</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-55"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            爸爸長時間在大陸工作 <br />
                            成了名符其實的台幹和空中飛人<br />
                            媽媽待在台灣當家庭主婦<br />
                            我發現爸爸似乎有外遇 <br />
                            一直以來我媽跟我都有從別人那聽到我爸爸有外遇 <br />
                            而對象就是大陸的員工 <br />
                            我們並沒有確切的證據去證明 <br />
                            但常常發生了事情 爸爸都偏袒她 <br />
                            媽媽都會跟我抱怨說我爸怎麼樣 <br />
                            又有人會跟我媽說你老公包二奶...跟那個女的怎樣怎樣<br />
                            我爸媽為了那個女人爆發多次的爭吵 <br />
                            她卻依然還在我爸的公司工作<br />
                            爸爸很疼我 對家人也很關心 <br />
                            但我知道這個第三者就像是一顆不定時炸彈 隨時會爆炸<br />
                            媽媽都一直睜隻眼閉隻眼<br />
                            但我知道她很不高興但又不會想要跟我爸離婚 <br />
                            所以每當她不高興時都會跟我說<br />
                            我好想哭我好難過雖然這已經存在好幾年 <br />
                            但我沒任何能力去解決 有試著暗示爸爸 但還是一樣
                            <br />
                            每次媽媽難過時 只能安慰她 <br />
                            但也無能為力去改變什麼 <br />
                            我又不能跟爸爸撕破臉 因為我是他們之間的溝通者
                            我該怎麼辦呢<br />
                            為什麼會有那個賤女人的存在
                            為什麼我就必須要去承受這些呢? 為什麼是我?<br />
                            為什麼把我家搞成這樣?
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                </div>
              </div>
            </div>
            <div class="frame">
              <div
                data-bs-spy="scroll"
                data-bs-target="#list-example"
                data-bs-offset="0"
                class="scrollspy-example"
                tabindex="0"
              >
                <h2 id="s3" name="s3" ref="s3">大陸徵信包二奶法規</h2>
                <div class="accordion" role="tablist">
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-1a variant=""
                        >大陸二奶生的小孩&nbsp;在台可列報受扶養親屬</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-1a"
                      visible
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            依台商在大陸包二奶，如果生了小孩，只要取得海基會的身份驗証，大陸小孩，可以列報受扶養親屬，享有免稅額抵減，不過，台灣是「一夫一妻制」，二奶不算配偶，不能列入受扶養親屬。<br /><br />
                            一旦大陸小孩列入受扶養親屬名單，在台灣的元配又參與報稅，納稅人在大陸幹了什麼好事，都將紙包不住火！<br /><br />
                            部分納稅人報稅，面臨受扶養親屬在大陸的疑問。<br /><br />
                            台北市國稅局中北稽徵所股長謝玉月說，老兵逐漸凋零，單身年收入不到十二萬三、有配偶的年收入不到二十四萬六，加上全年薪水不到七萬八的，這些都不用再報稅，比較少面臨元配在大陸的困擾；但老兵的小孩報稅，只能列報老兵爸爸和自己的親生母親，老兵爸爸在大陸的元配，因為沒有共同生活，不能列報受扶養親屬；如果是納稅人自己在大陸包二奶，雖然有扶養事實，但是，二奶不算合法的配偶，不能列報受扶養親屬，不過，二奶如果生了小孩，小孩雖然在大陸，只要小孩在大陸的居民身份証父親欄寫的是納稅人本人，就可以列報受扶養親屬。<br /><br />
                            「他要檢附大陸的居民身份証影本，以及，因為我們現在申報是九十五年所得稅，還要有九十五年的親屬關係証明，才能申報扶養。目前大陸的所有証件，都要經過海基會驗証，在台灣才有法律效力」不過，大陸二奶生的小孩雖然可以列報受扶養親屬，但教育部還不承認大陸的大學學歷，目前還不能申報教育學費特別扣除額。<br /><br />
                            國稅局官員說，通常只有已經被發現包二奶生小孩的情況才會列入，如果台灣元配還不曉得，多半不會列入，以免東窗事發
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-2a variant=""
                        >大陸二奶生了私生女，經台商生父認領後，是否具有繼承權？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-2a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            依據民法第1065條
                            非婚生子女經生父認領者，視為婚生子女。其經生父撫育者，視為認領。<br /><br />
                            非婚生子女與其生母之關係，視為婚生子女，無須認領。<br /><br />
                            認領之後，那個小女孩就是台商父親的直系血親卑親屬，她的法律地位與台商在台元配子女是一樣的，對台商父親，她也有遺產繼承權<br /><br />
                            若台商元配收養這個小女孩為養女，小女孩才可以有元配的遺產繼承權
                          </p>
                        </b-card-text>
