                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-3a variant=""
                        >繼承人中如有大陸繼承人，繼承遺產有否限制？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-3a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            如有大陸繼承人應依台灣地區與大陸地區人民關係條例第六十六、六十七條規定辦理，大陸繼承人應於繼承開始起三年內以書面向被繼承人住所地之法院為繼承之表示；如超過三年則視為拋棄繼承權，大陸地區人民不得繼承不動產，又繼承之遺產，每人不得超過新台幣二百萬元。
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-4a variant=""
                        >先生大陸「包二奶」生子，老婆該怎麼辦？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-4a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            丈夫在台灣與元配，育有兩名子女，事業及生活本均相當順利融洽。但是，近年來適逢全球經濟不景氣，先生為求圖謀重振事業，乃經友人之帶領下前往大陸重新設廠，並將整個機器及設備遷移至深圳，也正式加入成為台商一列，每個月僅返台與家人相聚數日，長久下來不但造成家庭成員間相處疏離，且元配也漸聽聞先生在大陸與女子同居的傳聞。<br /><br />
                            前些日子，先生終於向妻子表示在大陸與一女子已生有一子，想帶回台灣入籍，妻子雖傷心又氣惱，但先生似乎心意已決，並表示下次回台時，就要將該名小孩帶回並入籍，面對此種處境，元配只能黯然神傷。<br /><br />
                            問：面對老公在大陸包二奶又生子，有何權利？元配得拒絕該名小孩入籍嗎？<br /><br />
                            答：一、隨著兩岸經貿及民間往來日益密切，兩岸間因經貿及人民間往來所衍生之問題即日益增加，我國為因應兩岸未來更緊密之往來關係，乃於八十一年公布「台灣地區與大陸地區人民關係條例」，以作為未來處理兩岸人民相關權義之依據。<br /><br />
                            二、有關先生在大陸「包二奶」之情形，在現實社會上確實有數不完之個案，惟大多數在台灣的妻子面對在大陸先生「包二奶」之聽聞，總因國情及距離上之隔閡等因素而無法証實，縱擬委請徵信社查訪，蒐集到相關物証，但日後是否可直接作為訴訟上之証據，亦值得探究。因而，在台灣的太太面對類似之情況時，確實處於相當為難而無助之處境。<br /><br />
                            三、一般所稱「包二奶」乃係指在台灣仍有婚姻關係之男士，在大陸與大陸女子同居，並提供該名大陸女子一切生活及經濟上之需而言。至於，此種「包二奶」之行為，在我國法律上即會構「通姦」之要件，而夫妻一方對於他方之「通姦」行為，依我國法律隱可以依刑法第二三九條之規定提出通姦之告訴外，並可依民法第一○五二條第一項第二款之規定請求法院判決離婚所生之損害賠償及贍養費。<br /><br />
                            但依現行兩岸分治、我國司法管轄權尚不及大陸之情勢而言，縱在台灣的妻子確實掌握先生在大陸「包二奶」之証據，要對先生及該名大陸女子提出刑事通姦之告訴恐怕有難度，但能對先生提起民事離婚及損害賠償之訴訟。<br /><br />
                            四、本案元配知曉先生「與人通姦」為由提出離婚訴訟，恐怕日後丈夫在出庭時會否認與大陸女子同居並生一子，因而，若現在即擬提起離婚之訴，似應先讓先生坦承與大陸女子同居之內容加以錄音，並另蒐集先生在大陸與該名大陸女子相處之相片、或將兩人電話中親密的對話加以錄音，或請曾在大陸看過兩人同居之朋友或鄰居出庭作証，如此，該離婚訴訟應較容易成立。或者更容易的，元配可以等待丈夫將該名小孩帶回台灣並入戶籍後，以該名小孩作為先生與該名大陸女子通姦之証據，再提起離婚訴訟，如此不但可以省去蒐集証據對麻煩，且該離婚訴訟應較容易成立。<br /><br />
                            五、至於，先生若堅持要將該名小孩帶回台灣認祖歸宗，依現行民法規定，認領該名小孩，僅需丈夫單獨辦理即可；至於，元配並無拒絕之權利，而該名小孩經認領後，即視同為婚生子女，亦即以後該名小孩仍可對先生名下之財產加以繼承。至於，該名小孩雖經孫強認領，惟在法律上元配與該名小孩並無任何法律上之關係，亦即日後該名小孩對於元配妻子名下之財產亦無繼承之權利。
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-5a variant=""
                        >台商在大陸包二奶的法律責任&nbsp;包二奶是否構成大陸刑事重婚罪?</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-5a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <p>
                                由於兩岸經貿交流頻繁，台灣地區人民赴大陸投資或工作者日趨增多，渠等長期身處大陸，離鄉背井，因不耐寂寞與誘惑，抑或為尋求生理與心理慰藉，導致「包二奶」的現象日盛，除造成嚴重的家庭問題，甚至還逐漸衍生難解的後遺症。<br><br>
                                <span style="font-weight:bold;">包二奶不再無法可管</span><br><br>
                                所謂「包二奶」並非專有的法律名詞，一般是指有配偶的男性通過提供金錢等手段，較為長期地與一女性同居保持性關係的行動；也就是說，「包二奶」的特性，係男方已有「配偶」，並與另一女子「同居」。早期港商普遍在大陸內地金屋藏嬌，兩岸往來日趨熱絡之後，台商也積極加入「包二奶」的行列。<br><br>
                                大陸在二○○一年通過修改《婚姻法》的決定，「包二奶」的行為不再是無法可管。台商在大陸常見的行為，態樣除了「包二奶」外，還包括重婚、婚外情與通姦等，《婚姻法》的修正，雖非針對台商而來，惟透過立法措施的規範與制裁，勢仍將對台商產生一定程度之警示效果。綜觀大陸相關法律的內容，與台商較切身有關的部分，主要包括：<br><br>
                                <span style="font-weight:bold;">重婚者必須承擔刑事與民事責任</span><br><br>
                                大陸在《刑法》第二百五十八條設有關於「重婚罪」之規定，凡是有配偶而與他人結婚或者明知他人有配偶而與之結婚的，都構成重婚罪，可判處二年以下有期徒刑或者拘役。特別需要注意的是，依《婚姻法》第四十五條規定，對重婚者將依法追究刑事責任，「受害人可以依照刑事訴訟法的有關規定，向人民法院自訴；公安機關應當依法偵查，人民檢察院應當依法提起公訴」；另依《婚姻登記管理條例》第二十條規定，有配偶的當事人重婚，其配偶未提出控告者，婚姻登記管理機關應當向檢察機關檢舉。<br><br>
                                綜上所述，可以瞭解重婚在大陸地區屬公訴罪，除配偶可向人民法院提起告訴外，其他有權機關亦可主動依法處理，不再是「民不舉、官不究」的非告訴乃論。台商在大陸若有重婚享「齊人之福」的情事，在台之元配除可隔海促請大陸公安機關及檢察機關透過公權力協助偵辦，相關機關亦可主動出擊，台商切不可再心存僥倖。<br><br>
                                除此之外，重婚者還必須承擔民事責任，因為大陸《婚姻法》不僅規定重婚之後婚姻「自始無效」，當事人之間不具有夫妻的權利和義務，且同法第十二條規定，後婚姻同居期間所得的財產，交由當事人協定處理，若協定不成時，則由人民法院根據「照顧無過錯方的原則」判決，但其財產處理，不得侵害合法前婚姻當事人的財產權益。<br><br>
                                另當事人因重婚關係所生之子女，適用該法有關父母子女的規定，即與婚生子女享有同等之權利。故已婚台商應注意，在大陸之重婚行為若經法院認定成立，不僅後婚姻無效，且將不得再與重婚對象同居；重婚之原因若可歸責於台商隱匿在台已婚之事實，則依據「過失賠償制度」，其在大陸之置產，部份可能被迫歸重婚對象所有，若生有子女，亦須負擔撫育責任。其後果與代價，值得台商警惕。<br><br>
                                <span style="font-weight:bold;">跨海通姦行為仍受台灣法院管轄</span><br><br>
                                大陸《刑法》中對一般的通姦行為並沒有加以論罪科刑，僅就涉軍人眷屬部份作出規定；《婚姻法》修正後雖明確禁止「婚外同居」，但仍將通姦、婚外情等破壞家庭婚姻關係之行為劃在「禁止」之外，未作出相應規定或制裁，將違常之通姦行為列為「道德領域」之規範範疇。大陸法律既無通姦罪之罪名與處罰，則台商在大陸之「通姦行為」是否即無法可管？<br><br>
                                台灣的大法官會議在去年年底做出釋字第五五四號解釋，針對各界爭議已久的「通姦是否應除罪化」問題作了說明，大法官認為，性行為的自由，必須受到婚姻與家庭制度的制約，亦即認為刑法的通姦罪仍然有存在的必要。依據台灣《刑法》第二百三十九條規定，有配偶而與人通姦者，處一年以下有期徒刑。則前述台商在彼岸之通姦行為，縱行為地在大陸地區，且此行為在大陸未構成犯罪；惟依《兩岸人民關係條例》第七十五條之規定，在大陸地區之犯罪行為仍得依法論處，即台灣地區法院對台商在大陸地區之通姦案件，具有管轄權並得加以處罰。<br><br>
                                <span style="font-weight:bold;">台灣修改民事訴訟法保障在台元配的權益</span><br><br>
                                立法院三讀通過了「民事訴訟法」部份條文修正案，未來夫妻在提起離婚訴訟的同時，即可聲請假扣押對方財產，避免對方惡意遺棄，及透過脫產等方式迴避應負的贍養費、扶養費及家庭生活費用。同時，新法也大幅放寬聲請假扣押的擔保金額，規定在基於家庭生活費用、扶養費、贍養費、夫妻剩餘財產差額分配等原因請求假扣押時，法院僅得要求請求金額十分之一的擔保，以保障夫或妻之中經濟狀況較差者。<br><br>
                                這樣的立法，被戲稱為是「反包二奶」條款，因為部份台商在大陸另結新歡，不僅對台灣的元配不聞不問，甚至還依據大陸法律「分居三年者得提出離婚聲請」的規定，惡意遺棄元配。在過去，在台配偶除必須等判決確定才能扣押財產，導致丈夫往往在訴訟過程就完成脫產；且由於法院對聲請假扣押的條件過嚴，至少都會要求元配提出聲請金額三分之一以上的擔保，導致以持家為業的元配無力負擔。透過本次的修法，在某種程度上當可解決婚姻弱勢一方長期受困於對方脫產而一籌莫展的困境。<br><br>
                                <span style="font-weight:bold;">台商應注意兩岸法律之變動</span><br><br>
                                根據「中國婦聯」調查統計，台商在大陸之「二奶」及非婚生子女較多的城市，集中在上海、北京、重慶、武漢、深圳、廣州、廈門、福州、溫州等台商密集的城市，甚至發現台灣遊客在大陸「包二奶」的現象，而台灣地區人民在彼岸之非婚生子女初步估計至二○○○年底已超過五萬人，顯見問題之嚴重性，值得兩岸政府正視。<br><br>
                                「包二奶」不僅可能涉及刑事與民事責任，也會危害既有婚姻基礎，為避免無端遭惹麻煩，台商在大陸除應潔身自愛，並須謹慎注意兩岸法律規定之變動，以免事後抱憾。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                </div>
              </div>
            </div>
          </div>
