                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-44 variant=""
                        >《尋人，歡喜收場》遊民失聯10多年&nbsp;警助他回家</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-44"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h4>〔記者楊金城／新營報導〕</h4>
                          <p>
                            50歲遊民曾順鐘上月21日晚間倒臥新營街頭，新營中山派出所警員賴芳榮將他送醫，期間賴員根據曾順鐘說出模糊不清的名字，終於查出他的身分，昨天找到他10多年未見的家人。<br><br>
                            曾順鐘的二哥曾清森和妻子趕至派出所，再三感激賴芳榮救了弟弟一命，賴芳榮說，他只是盡職務找人。<br><br>
                            曾清森說，老家住東山鄉科里村，有3兄弟，他和大哥很早出外謀生，么弟曾順鐘單身未娶，曾是油漆師傅，不知為何失業，流浪在外，他和弟弟至少12年未曾聯絡，找不到他人。<br><br>
                            上月21日晚間8點多，曾順鐘被人發現倒臥在新營中山路圓環旁，頭部流血，警員賴芳榮據報前往處理，協同消防隊將人送往署立新營醫院救治，再轉送嘉義就醫，再與護士保持聯繫，待其清醒追問身分。<br><br>
                            結果曾順鐘在昏昏沈沈間說了模糊不清的3個名字，賴芳榮一一調查，最後找到有戶籍資料的曾順鐘，再追查到其大哥和二哥的下落，輾轉獲得確認。65歲的曾清森說，他沒工作，也無法照顧么弟；警方再通報縣府社會處給予可能的協助。
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-55 variant=""
                        >尋人啟示</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-55"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <p>
                            這則尋人啟事在社會上引起軒然大波，人們議論的焦點是：這個黑人會站出來嗎？顯然他面臨著兩難選擇，如果站出來，他將面臨名譽掃地、家庭破裂的危險；如果保持沈默，他將再一次犯下不可饒恕的罪行。<br><br>
                            啟事一經刊出，安德列醫生的信箱和電話都被打爆了，人們紛紛詢問這個女人是誰，他們很想見見她，希望能給她提供幫助。但孩子的母親瑪爾達拒絕了人們的關心，她不願意透露自己的姓名，更不願意讓別人知道莫妮卡就是那個強姦犯的女兒。<br><br>
                            當地的監獄積極地幫助瑪爾達。他們為醫院提供了一份 1992年後的罪犯名單，由於該市的黑人很少，所以10年來該市的黑人罪犯也很少。他們對瑪爾達說：「儘管有些人當年並不是因為強姦而被判刑，但也有可能曾經做過這樣的事情。」這些人有的已經出獄，有的還在獄中，孩子的父母瑪爾達和比特斯與這些人一一取得聯繫，許多當年的罪犯都表現出足夠的真誠和關注，紛紛提供了線索。但遺憾的是，他們都不是當年強姦她的那個黑人。<br><br>
                            久瑪爾達的故事在監獄中傳開，不少罪犯被她的母愛所感動，不論是黑皮膚還是白皮膚，他們都自願申報接受了骨髓匹配檢查，希望能為莫妮卡捐獻骨髓，但他們中間也沒有出現合適的骨髓。<br><br>
                            瑪爾達和比特斯焦急地等待著那個黑人的出現，然而兩個月過去了，這個人沒有出現。他們忐忑不安地想，也許那個黑人已經不在人世了？也許他已經遠走他鄉，早已不在義大利？也許他不願意破壞自己的生活，不想站出來？但無論如何，只要莫妮卡活一天，他們就不願放棄尋找那個黑人的希望。<br><br>
                            當這則特殊的尋人啟事出現在那不勒斯市的報紙上後，一個30歲的酒店老闆的心中起了巨大波瀾。他是個黑人，叫阿奇裏。1992年5月17日，在他的生命中經歷過這樣一個噩夢般的雨夜，他就是那個故事的肇事者。沒人能想到如今腰纏萬貫的阿奇裏曾經是個被人呼來喝去的洗碗工。由於父母早逝，沒有讀多少書的他很早就工作了。<br><br>
                            聰明能幹的他希望用自己的勤勞換取金錢以及別人的尊重，但不幸的是他的老闆是個種族歧視者，不論他如何努力，總是對他非打即罵。 1992年5 月 17日，那天是阿奇裏的20歲生日， 他打算早點下班慶祝一下生日，哪知忙亂中打碎了一個盤子，老闆居然按住他的頭逼他把盤子碎片吞掉。<br><br>
                            阿奇裏憤恨地給了老闆一拳，沖出餐館。怒氣未消的他決心報復白人，雨夜的路上幾乎沒有行人，在停車場他遇到瑪爾達，出於對種族歧視的報復，他無情地強姦了那個無辜的女人。<br><br>
                            事後，阿奇裏惶恐不安。當晚他用過生日的錢買了一張開往那不勒斯市的火車票，逃離這座城市。在那不勒斯，他交了好運。阿奇裏順利地在一個美國人開的餐館找到工作，那對美國夫婦很欣賞勤勞肯幹的他，還把女兒麗娜嫁給了他，最後甚至還把整個餐館委託他經營。<br><br>
                            幾年來，精明的他不但把餐館發展成了一個生意興隆的大酒店，還有了三個可愛的孩子。在員工和家人眼裏，阿奇裏是個好老闆、好丈夫、好父親。然而他內心一直沒有忘記自己犯下的罪惡，他祈禱上帝保佑那個被他強姦的女人，希望她能平安無事。但他從沒把心底的秘密告訴過任何人。<br><br>
                            那天早晨阿奇裏反復將那條新聞看了好幾遍，他直覺上判斷自己正是那個被尋找的強姦犯。他萬萬沒有想到，那個可憐的女人竟然懷孕了，並撫養了本不屬於她的孩子。這天，阿奇裏幾次想撥通安德列醫生的電話，但每次電話號碼還沒撥完，他就掛斷了電話。阿奇裏在內心掙扎著，如果自己站出來承認這一切，人們將知道他最醜陋的一面，他的孩子將不再愛他，他會失去幸福的家庭和美麗的妻子，也會失去社會對他的尊重。這一切是他辛苦奮鬥多年換來的啊！<br><br>
                            那天晚上吃飯的時候，全家人和往常一樣議論著報紙上的有關瑪爾達的新聞。妻子麗娜說：「我非常敬佩瑪爾達。如果換了我，是沒有勇氣將一個因強姦生下的女兒養大的。我更佩服瑪爾達的丈夫，他真是個值得尊重的男人，竟然能夠接受一個這樣的孩子。」阿奇裏默默地聽著妻子的談論，突然問道：「那你怎麼看待那個強姦犯？」「我絕對不能寬恕他，當年他就已經做錯了，現在關鍵時刻他又縮著頭。他實在是太卑鄙，太自私了，太膽怯了！他是個膽小鬼！」妻子義憤填膺地說。阿奇裏怔怔地聽著，不敢把真相告訴妻子。<br><br>
                            一夜未眠的阿奇裏覺得自己仿佛在地獄裏煎熬，眼前總是交替地出現那個罪惡的雨夜，和那個女人的影子。他仿佛能聽到那個女人的呼喚聲和哭泣聲。他不斷地問自己：「我到底是個好人，還是個壞人？」然而聽著身旁妻子均勻的呼吸，他就失去了站出來的勇氣。第二天他神情憔悴不堪。妻子很快察覺出了他的反常，關心地問他發生了什麼事，他藉口身體不舒服逃避過去！早晨上班的時候，員工們親切地向他問好：「早上好，總經理先生！」他臉色蒼白地一一回禮，心底滿是尷尬和羞愧。阿奇裏覺得自己要崩潰了！<br><br>
                            幾天後，阿奇裏無法沈默了，忍不住在公共電話亭裏給安德列醫生打了個匿名電話。他極力讓自己的聲音顯得平靜：「我很想知道那個不幸女孩的病情。」安德烈醫生告訴他，女孩病情很嚴重。最後安德列醫生傷懷地說：「還不知道她能不能等到親生父親出現的那一天。」這話深深觸動了阿奇裏，一種父愛在靈魂深處蘇醒了，那女孩畢竟也是自己的骨肉啊！他決定站出來拯救莫妮卡，他已經錯過一次，不能繼續錯下去了。<br><br>
                            那天晚上他鼓起勇氣，把一切都告訴了妻子。最後他說：「我很有可能就是莫妮卡的父親！我必須去拯救她！」麗娜震驚、憤怒、傷心，聽完這一切她氣憤地說：「你這個騙子！」當晚她帶著三個孩子，開車跑到父母的家裏。當她把阿奇裏的一切告訴父母時，這對老夫婦在盛怒後，很快就平靜了下來。畢竟是歷經人生滄桑的老人，他們告訴女兒：「是的，我們應該對阿奇裏過去的行為憤怒。但是你有沒有想過，他能夠挺身而出，需要多麼大的勇氣，這證明他的良心並未泯滅。你是希望要一個曾經犯過錯誤，但現在能改正的丈夫？還是要一個永遠把邪惡埋在內心的丈夫呢？」麗娜沈默了。第二天一大早麗娜回到阿奇裏身邊時，看著眼睛佈滿血絲的阿奇裏，麗娜堅定地說：「阿奇裏，你去找安德列醫生吧！我陪你一起去！」<br><br>
                            2003年 2月 3日，阿奇裏夫婦與安德列醫生取得聯繫，2月8日，阿奇裏夫婦趕到伊莉莎白醫院，醫院為阿奇裏做了DNA檢測，結果證明阿奇裏的確就是莫妮卡的生父。當瑪爾達得知那個強姦她的黑人終於勇敢地站出來時，她熱淚橫流。她對阿奇裏整整仇恨了 10年，但這一刻她充滿了感動。<br><br>
                            一切都在極為嚴密的情況下進行。為了保護阿奇裏夫婦和瑪爾達夫婦的隱私，醫院沒有對媒體說出他們的真實姓名和詳細身份，只是告訴記者莫妮卡的生父已經找到了。這個消息振奮了所有關心這件事的市民們，他們紛紛打電話、寫信給安德烈醫生，拜託他轉達他們對這個黑人的寬恕和尊敬。他們說：「也許他曾經是個罪犯，但現在他是個英雄！」<br><br>
                            2月 10日，瑪爾達夫婦要求和阿奇裏見面。 阿奇裏一開始沒有勇氣見他們，但在瑪爾達再三懇求下，他最終同意了。 2月 18日，在醫院的秘密安排下， 瑪爾達在醫院會客室裏見到了阿奇裏。他的頭髮顯然剛剛理過，看到瑪爾達時，他的腳步顯得沉重難移，臉色蒼白。<br><br>
                            瑪爾達和丈夫走上前去，緊緊握住他的手，頓時三個人失聲痛哭，三個人的淚水流到了一起。良久，阿奇裏聲音哽咽地說：「對不起，請原諒我！這句話我在心底說了10年，今天終於有機會親口對您說。」瑪爾達說：「謝謝你能夠站出來。願上帝保佑，你的骨髓能拯救我的女兒！」<br><br>
                            2月19日，醫生為阿奇裏做了骨髓匹配實驗，幸運的是他的骨髓完全適合莫妮卡！<br><br>
                            醫生激動地說：「這真是奇蹟！」<br><br>
                            2003年 2月 22日，人們期盼已久的時刻終於到了。 阿奇裏的骨髓輸入了莫妮卡的身體，很快，莫妮卡就度過了危險期。一個星期後，莫妮卡健康地出院了。<br><br>
                            瑪爾達夫婦完全原諒了阿奇裏，盛情邀請他和安德列醫生到家裏做客。但那一天阿奇裏卻沒有來，他托安德列醫生帶來了一封信。<br><br>
                            在信中他愧疚萬分地說：「我不能再去打擾你們平靜的生活了。我只希望莫妮卡和你們幸福地生活在一起，如果你們有什麼困難，請告訴我，我會幫助你們！同時，我也非常感激莫妮卡，從某種意義上說，是她給了我一個贖罪的機會，是她讓我擁有了一個快樂的後半生！這是她送給我的禮物！」
                          </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
