tion Item4",on:{"click":function($event){return _vm.selectItem('Item4')}}},[_vm._v("婚前新聞")])])]),_c('div',{staticClass:"col-lg-8 col-md-8 col-sm-12"},[_c('div',{staticClass:"frame mt-4"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s1",attrs:{"id":"s1","name":"s1"}},[_vm._v("婚前須知")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-1",modifiers:{"accordion-1":true}}],attrs:{"block":"","variant":""}},[_vm._v("何謂婚前徵信?")])],1),_c('b-collapse',{attrs:{"id":"accordion-1","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 現代不婚族愈來愈多，隨著離婚的比率不斷升高，國人對於結婚的開始產生疑問，害怕結婚之後面臨的就是離婚。這就更加顯示婚前徵信的重要性；"),_c('br'),_c('br'),_vm._v(" 其實就是因為婚前沒有徹底認識彼此，以為自己看見的對方就是對方的全部，忽略了對方有可能刻意或無意的隱瞞，最後無法接受才會導致離婚。"),_c('br'),_c('br'),_vm._v(" 而婚前徵信就是幫助您認識對方，比照您所認識的他是否就是全部的他，就可以知道對方對您是否全然坦承；您就能夠衡量是否可以走入婚姻，而不必時時刻刻擔心離婚的問題。 "),_c('br'),_c('br'),_vm._v(" 當然婚前徵信並不能夠完全保證婚姻的幸福、完全保證不會離婚，但是婚前徵信確實可以降低離婚的比率及不幸事件的發生。許多人隱瞞自己的暴力行為，結婚後動輒拳腳相向，如果能在婚前便發現，便能夠免去後續的家暴事件。"),_c('br'),_c('br'),_vm._v(" 人或多或少都會隱藏自己，有人會隱藏自己的一些小缺點當然是無可厚非。但是很多時候偏偏隱瞞的部分卻是對方無法容忍的部分。本來以為雙方如同天造地設般的契合，結了婚之後卻發現根本不是這樣，後悔沒有婚前徵信，最後以離婚收場。雖然離婚對於現代來講，已不是無法接受的事情；"),_c('br'),_c('br'),_vm._v(" 但離婚畢竟是負面的事情，如果可以，當然每個人都希望能有幸福的婚姻、毋須擔心離婚的問題；於是做好婚前徵信，如同是做婚前健康檢查一樣，讓您知道兩人是否適合！"),_c('br'),_c('br'),_vm._v(" 環球女人「婚前徵信」有如選舉前對候選人的徵信，也是抓住幸福的機會，它可以準確地為您倆的愛情開出診斷証明，讓您全盤了解將來的另一半。"),_c('br'),_c('br'),_vm._v(" 打造幸福的婚姻殿堂、需要堅固的地基，從相識到相戀，是否能無庸置疑地說瞭解另一半呢？是否總覺得猜不透對方的心思呢？「善意的謊言」～由誰界定？兩人關係的惡化，不在爭吵本身而是不願互相分享內心的秘密。環球女人「婚前徵信」的任務不是引發事端，而是發現問題、解決問題，不論您有何需求，只要是想擁有幸福的朋友，我們的專業團隊就會來到您的身邊～如果說婚前健檢是為了優生學而做準備，「婚前徵信」就是為了創造更美滿的家庭生活做準備。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-2",modifiers:{"accordion-2":true}}],attrs:{"block":"","variant":""}},[_vm._v("婚前徵信服務項目")])],1),_c('b-collapse',{attrs:{"id":"accordion-2","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('ul',[_c('li',[_vm._v(" 婚前徵信對方之個人信用、財務現況、理財約定、銀行往來之紀錄。 ")]),_c('li',[_vm._v(" 婚前徵信對方之品德操守、交友狀況、有無不良惡習、不良素行、家庭成員及相處情況、家庭背景投資事業有無誇大不實、家族有無先天性疾病或隔代遺傳之精神疾病。 ")]),_c('li',[_vm._v(" 婚前徵信對方之學歷文憑是否屬實、現在工作狀況。 ")]),_c('li',[_vm._v(" 婚前徵信對方之感情交往狀況、與前男 ( 女 ) 友藕斷絲連以及劈腿之惡行、現在是否婚姻持續中、過去有幾次婚姻? 是否有子女? ")]),_c('li',[_vm._v("婚前徵信對方之其他特別指定調查事項。")])]),_c('p',[_vm._v(" 婚前徵信調查您所要調查的對象，無論男女情人或者是準新人，為您提供相關法律諮詢或心理輔導等多項服務，並根除不肖份子以婚姻方式之名行詐騙之實，造成社會治安問題及人性道德之淪喪，亦可避免自身無端成為他人婚姻之第三者。我們提供您最溫柔的建議以及百分之百的忠誠服務！保護您的個人權益，更是我們永不退讓的堅持 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-3",modifiers:{"accordion-3":true}}],attrs:{"block":"","variant":""}},[_vm._v("人的一生中，往往需要做出許多重要的決定")])],1),_c('b-collapse',{attrs:{"id":"accordion-3","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 人的一生中，往往需要做出許多重要的決定，不論事情的大小；婚姻使得兩個相愛的人做出決定，但我們無法真確的得知所嫁或所娶的是良人還是非良人？往往我們都會尋求一種模式，交往過一陣子在愛情的喜悅與甜蜜中，自以為他 ( 她 ) 是我的真命天子、 Mr.right ？但真的了解以及認識我們的另一半嗎？?"),_c('br'),_c('br'),_vm._v(" 所以為何現今社會的婚姻所維繫的時間越來越短？人們當初眼中的佳偶，卻又往往以怨偶收場，不能老是拿那一套個性不合，因不了解而結合又因了解而分離吧！我們常聽到未雨綢繆，這叫事先規劃的風險，評估管理，投資理財會想到、但為何在人生婚姻上不做些評估及管理呢？屆時再來個吵鬧分離人才兩失！"),_c('br'),_c('br'),_vm._v(" 一個婚姻是兩個人的結合，我們自以為像童話故事般的王子與公主，從此過快樂無憂的日子，孰不知這才是故事的開始。但這個故事是要喜劇收場還是歹戲拖棚往往看雙方有無用心，干擾一段婚姻能否美滿，在於雙方有無真確了解？"),_c('br'),_c('br'),_vm._v(" 因為任何一件小事甚巨，比如雙方的家庭背景 ( 如前科、已婚、通緝 ) 財務狀況 ( 如信用、資產 ) 有無遺傳病，在感情尚未真正投入埋下往後的不定時炸彈前，做好婚前的徵信讓用心經營的人種下良緣得到善果，祝福全天下真心相愛的人，永浴愛河！ ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-4",modifiers:{"accordion-4":true}}],attrs:{"block":"","variant":""}},[_vm._v("婚前徵信的重要性更甚健康檢查！")])],1),_c('b-collapse',{attrs:{"id":"accordion-4","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 人的一生中，不管大小事，都不停的在做選擇賭注。"),_c('br'),_c('br'),_vm._v(" 對男人或女人而言，最大的賭注就是婚姻！押對的人，一生幸福快樂；押錯的人，情路坎坷波折不斷；甚至遭到暴力相向以至於家破人亡。"),_c('br'),_c('br'),_vm._v(" 這其中當然有環境或經濟上的因素，難以克服；但也有一部份是因為婚前對於交往對象的了解不夠，而造成終身的遺憾。"),_c('br'),_c('br'),_vm._v(" 往對象，也許是對方的甜言蜜語而沉溺在幸福浪漫的假象裡，而不敢去面對現實。然而婚前徵信就如同婚前健康檢查一樣重要！"),_c('br'),_c('br'),_vm._v(" 透過健康檢查可以幫您發現對方是否有家族遺傳病史或其他傳染疾病，在個人可接受的範圍內，或許您會願意接受陪伴您的另外一半一路走下來，哪怕是身體有缺陷的人，因為他 ( 她 ) 是坦白的,眼睛看的見的。"),_c('br'),_c('br'),_vm._v(" 婚前徵信的重要性更甚於健康檢查！"),_c('br'),_c('br'),_vm._v(" 對當事人百依百順，百般奉承的表現出一副忠誠老實模樣，一但結婚後慢慢地露出真面目侵占當事人的一切或家產，若配偶不從將以百般虐待，甚至於暴力相向，逼迫 ( 他 ) 她就範以及離婚>收取贍養費收場。"),_c('br'),_c('br'),_vm._v(" 也有人交往的對象是走私、販毒者前科累累而不知，等到感情全部投入後，才發現真相後悔已來不及了。"),_c('br'),_c('br'),_vm._v(" 這麼重要的終身幸福，您能不重視嗎？ "),_c('br'),_c('br'),_vm._v(" 現在只要花了 3 萬 ~10 萬元，本公司即可幫您做完整的婚前調查，為您的幸福把關。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-5",modifiers:{"accordion-5":true}}],attrs:{"block":"","variant":""}},[_vm._v("婚前了解結婚就是兩個人帶了不同的期待走進禮堂")])],1),_c('b-collapse',{attrs:{"id":"accordion-5","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 有人說：「結婚就是兩個人帶了不同的期待走進禮堂」所以婚後有人吵鬧有人白頭偕老。"),_c('br'),_c('br'),_vm._v(" 台灣的結離婚對 比，，從去年的每十對有一對到近年的的每十對就有七對，數字的飆漲不禁令人咋舌，但相對也讓我們警覺出婚前徵信的重要性。"),_c('br'),_c('br'),_vm._v(" 愛情的迷思有時讓人無法理性思考，但透過業者的協助，精準的搜尋與客觀的分析，希望能讓您在婚姻的路途上走的更踏實、甜蜜；而且若一但有不利於你的危機查出，你才能理性的去解決(還要不要繼續這段關係要不要懸崖勒馬) 而重新設定您的標準。"),_c('br'),_c('br'),_vm._v(" 離婚率不斷爬升，失婚男女越來越多，當一對對的佳偶變怨偶，當婚後的您必須面對喜歡酗酒、外遇 ( 偷情 ) 或者還要忍受家庭暴力、沉重負債，小孩的監護權及雙方家長的問題，林林種種的痛苦之後，何不讓我們防範於未然，人人都需要有危機意識，有時靠自己的眼光是有限的，求助於外力並不丟臉，而是建立信心的第一步，話說知己知彼，讓我們運用智慧來換取雙贏的美滿婚姻。真正的原因就是這麼簡單，但人們總是被誤導，生活中許多解決事情的辦法都很容易，但要找出這個辦法卻很不容易。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-6",modifiers:{"accordion-6":true}}],attrs:{"block":"","variant":""}},[_vm._v("婚前須知婚姻是一生的事業")])],1),_c('b-collapse',{attrs:{"id":"accordion-6","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("資料來源(轉載)http://www.loveworks.com.tw/ugC_Menu2_Detail.asp")]),_c('p',[_vm._v(" 婚姻是一生的事業，家庭經營是永續的。許多在工作生涯非常傑出成功而婚姻卻相當失敗的男男女女，最後紛紛體認到一個他（她）們抱憾的事實：任何的成功不能彌補婚姻的失敗。 "),_c('br'),_c('br'),_vm._v(" 你的人生是黑白還是彩色的，是豐富還是貧瘠的，是多采多姿還是索然無味的，這都跟每個人的婚姻經營有密切的關係。尤其當人類的壽命因飲食、運動以及醫藥科技的進步而更加延長，平均壽命從上世紀的30-40歲至今日的70-80歲，甚至未來三十年內更有可能延長至100歲以上。 但是，相對的，追溯過去到現在，適婚年齡仍然保持較少的差距，差不多維持在20-30多歲之間這段青春期。從結婚到最後往生的這一段人生旅程相對延長很多。如果很幸運的，婚姻穩定美滿，彼此恩愛一生，那麼婚後雙方共度的幸福時光將長達全部人生的1/2到2/3。因此，婚姻幸福與否，對每一個人的一生有相當重大的關係。 "),_c('br'),_c('br'),_vm._v(" 從過去到現在，對接觸到周遭的許多同學、親戚或朋友的觀察，可以大致得到一個很有趣的指標，那就是，認為婚姻是好的或會更好的，大部份他們確實可以經營美滿的婚姻生活。 我發現，不管婚前、新婚或婚後，對於婚姻抱持比較樂觀態度的人，凡事均呈現積極正面的思考。 至於那些對於婚姻抱持比較悲觀色彩的人，很容易在還沒有付出足夠的努力之前，就認命或放棄，相對的就沒有那麼幸福，甚至於離婚，似乎以這個不幸的結局來證實悲觀的看法。無論是好或壞、積極或消極、樂觀或悲觀，都有如自我實現的預言。 "),_c('br'),_c('br'),_vm._v(" 在今天資訊開放的社會裡，色情、暴力、誹聞、八掛等負面報導充斥在各種報紙、雜誌、電視、網路等媒體。這樣的環境很容易帶給人一種錯覺，對某些人甚至可能是錯誤的示範。 根據內政部的統計，2001年台灣有將近五萬七千對夫妻離婚，平均每天約150對夫妻成怨偶。換句話說，平均每不到10分鐘就有一對又一對的夫妻莎約娜拉。若同樣以2001年結婚與離婚對數觀察，則平均每三對新人進入結婚禮堂，就有一對舊偶仳離，驚人吧！ "),_c('br'),_c('br'),_vm._v(" 但是，你（妳）可不要被那些數字嚇倒，因為，仍有三分之二絕對多數的夫妻白首偕老呢！而你（妳）與另一半很可能是屬於這多數的一群，而且必然是幸福美滿的一對，如果你（妳）們真誠承諾，真心相愛，互許終身，並以積極正面的態度努力經營婚姻的話。相信我，你（妳）一定能做得到！ ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-7",modifiers:{"accordion-7":true}}],attrs:{"block":"","variant":""}},[_vm._v("有婚前性行為怎辦?")])],1),_c('b-collapse',{attrs:{"id":"accordion-7","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 案例："),_c('br'),_vm._v(" 芝文與男友嘉和已經交往一年多，雖然尚未談及婚嫁，不過兩人已經有了默契，反正再一兩年，等嘉和事業略有基礎後，兩個人就要一起走向地毯的那一端。他們的交往，大家都認為是十分相配的一對，父母親也不曾為他們擔心過，可是最近情況似乎有些異樣。"),_c('br'),_c('br'),_vm._v(" 向來開朗的芝文，不知何故，情緒變得比較浮躁不穩。雖然在家裡她也是有說有笑，但是敏感的媽媽察覺得出來，芝文的喜怒哀樂已經不像已往那麼率真、自然，而是帶有一些誇張與虛假，反正給人的感覺就是有點不對。以前她總是熱心教會的服事，現在卻不怎麼熱衷，比較起來，她也不像以往時常帶著大家禱告。"),_c('br'),_c('br'),_vm._v(" 芝文的情緒也不穩定，定睛注視她時，她的眼光卻經常逃避，芝文的父母親看在眼裡，心裡難免納悶著。更讓父母親傷心的是，芝文漸漸有了晚歸的記錄，以前她從來沒有這種情形，儘管芝文每次都說明了理由，芝文的父母親也不願去追問，畢竟孩子大了。但是為什麼一向乖巧懂事的女兒，最近卻變成這個樣子呢？"),_c('br'),_c('br'),_vm._v(" 分析"),_c('br'),_vm._v(" 芝文的母親向輔導人員談到這個問題後，經由妥善的安排，輔導人員與芝文有了機會做更詳盡的約談。除去了芝文的不安、疑懼與羞愧後，芝文終於說出了她內心的癥結──幾個月前與男友有了婚前性行為。她目前的行為現象，也正顯示了她對婚前性行為的不安與掙扎。"),_c('br'),_c('br'),_vm._v(" 一般而言，有了婚前性行為後，可能會有如下的行為現象："),_c('br'),_c('br'),_c('ul',{staticStyle:{"padding-left":"15px"}},[_c('li',{staticClass:"number"},[_vm._v(" 自暴自棄，對自己的行為不再矜持"),_c('br'),_vm._v(" 這是有了婚前性行為的女孩最常見的反應。自暴自棄不見得就是墮落，而是與男友在交往的過程中，因為自己已經不是處女，所以對自己的行為就不再矜持自重，反而變得無所謂，任憑男友與她的婚前性行為不斷地發展下去。"),_c('br'),_vm._v(" 憑心而論，婚前性行為對女孩最大的打擊不是生理上處女膜破裂的問題，而是心理上的失防。有了這種親密行為後，女孩的自我觀念完全貶值，更可怕的是有了這種貶值的觀念後的行為反應。女方若是有了婚前性行為不再設防，任憑男友予取予求，或是，此後把交往的重心轉移到對性的吸引，而不再是兩個人的溝通了解與成長。甚至認為反正已經有了婚前性行為，自己也不是好女孩，沒有什麼好保守的……等這些錯誤的觀念與行為反應，才是婚前性行為帶來的最可怕後果。")]),_c('li',{staticClass:"number"},[_vm._v(" 疑神疑鬼，擔心男友會變心"),_c('br'),_vm._v(" 事實上，國內很多男性仍然認為與女孩發生性關係，是得到她的芳心最好方法。因此無所不用其極，有的用暴力，有的用拐騙、引誘的方法，來使女友就範。有些卑鄙的男性更以與女友的性關係為要脅，不容女友離他而去，否則他便公佈「真相」，使女友無地自容，更無法與別人交往。在這種情況下，女孩從頭到尾都是輸家。"),_c('br'),_vm._v(" 從輔導經驗裡，我們常常發現，若是女孩不與男友發生性關係，則男友的態度一定都是低姿態，他會較尊重女友、較有耐性，而且深怕女友不愛他。但是萬一有了性關係後，則較緊張的就是女孩了；她只好百依百順，甚至疑神疑鬼，擔心男友離她而去。")]),_c('li',{staticClass:"number"},[_vm._v(" 自怨自艾，情緒起伏不定"),_c('br'),_vm._v(" 有了婚前性行為後，有些女孩子會悔不當初；有的更充滿罪惡感，致使無法一如往昔充滿信心，生活的步調完全混亂。在這種過程中，她有時可以克服，有時也會迷失，因此造成情緒的起伏變動，間接又影響了她與男友之間的關係。"),_c('br'),_vm._v(" 發生了婚前性行為固然是一件不幸的事情，但是，若處理得當，必可以將婚前性行為所造成的傷害力減到最低的限度。有關這方面的處理，我們分兩類來談，一類是男友仍然愛妳，另一類是男友已有若即若離，甚至於離棄妳的打算。 ")])]),_c('ul',{staticStyle:{"padding-left":"15px"}},[_c('li',{staticClass:"letter"},[_vm._v(" 男友仍然愛妳時 "),_c('br'),_c('br'),_vm._v(" 首先我們必須在觀念上澄清，婚前性行為對戀愛中的情侶最大的打擊，並不是處女或非處女的問題，也不是不遵守道德規範、怕父母親友不諒解的問題。最主要的是，婚前性行為會使情侶們失去機會學習相互尊重、忍耐、克制內心的衝動、心靈上的分享與溝通……等。（請參閱上篇：「婚前性行為好嗎？」）所以，有了性行為後，對當事者而言，最重要的是他們的心理狀況，而不是心理現象。當事者千萬不要以為反正沒有懷孕所以沒有什麼大不了的，此時，他必須更去注意自己及對方的心路歷程。 ")]),_c('br'),_c('li',{staticClass:"letter"},[_vm._v(" 如果可能，讓男友知道妳內心的徬徨與懊悔，並讓他知道性關係帶給妳的是壓力，痛苦多於歡樂與被愛。這樣做主要的目的是，使兩個人依然能夠注意到彼此感覺分享的重要性，也讓男女知道愛的成就感不是來自自私的性征服，而是來自愛惜、珍重對方，而得到成熟的喜悅。 ")]),_c('br'),_c('li',{staticClass:"letter"},[_vm._v(" 從此以後不再衝動得把性當作 兩人相愛時唯一的活動。因為當事者（尤其是女孩）會害怕失去對方，因此在男友提出性的要求時，儘管自己明明不喜歡，卻也不敢拒絕。長此以往，性便成了兩人約會的主題；而往往男方會在此時認為女孩也只不過如此，不僅失去了對女友的興趣，也減低了對女友的尊重。所以技巧地拒絕發生性關係，才是保住情感的一種方法。 ")]),_c('br'),_c('li',{staticClass:"letter"},[_vm._v(" 設法提高兩人約會的品質 "),_c('br'),_c('br'),_vm._v(" 要知道婚前性行為會引至兩人心靈溝通的障礙，因此，有了性關係後，除了要設法避免重蹈覆轍外，當務之急，便是如何使兩人的約會在不發生性關係的前提下更具有吸引力。比如，談話時傾聽對方的心意，適度的給予回饋與讚賞；散步或郊遊時，讓他知道妳有與他相聚的喜悅；看電影或約會時，讓他了解心靈的契合，遠比發生性關係的境界更高。 ")]),_c('br'),_c('li',{staticClass:"letter"},[_vm._v(" 避免置身容易發生性關係的場所與際遇"),_c('br'),_c('br'),_vm._v(" 一般而言，男人比較「生理化」，容易衝動。發生了婚前性行為時，男人的反應也會有「重複」的現象；所以每次約會時，會忘了戀愛是「談心」，而要求「再來一次」性的關係。女性在此種狀況當中，要技巧地避免到容易引起男友衝動的地方去，例如陰暗的樹叢、男友的寢室、或旅館、荒郊野地等。 ")]),_c('br'),_c('li',{staticClass:"letter"},[_vm._v(" 設法與男友到輔導中心來接受協談"),_c('br'),_c('br'),_vm._v(" 男性大都會排斥這種協談，因為他不認為這是個問題，所以女性請男友到協談機構去時，千萬不要讓他有「我們做錯事了，所以去接受治療」的感覺；反之，大可以以如何準備結婚，如何使愛情更鞏固為藉口，約男友到協談機構。當然，最好事先與協談人員協調一下，免得到時候協談人員手忙腳亂。 ")])]),_c('ul',{staticStyle:{"padding-left":"15px"}},[_c('li',{staticClass:"letter-s"},[_vm._v(" 當男友不愛妳時"),_c('br'),_c('br'),_vm._v(" 婚前性行為最麻煩的是可能促使男友離妳而去。（尤其在女孩沒有「落紅」時，最容易發生，真是賠了夫人又折兵，女孩子這時真是到黃河也洗不清。他也更會因為妳已與他發生關係，所以就斷定妳「一向不貞」。）此時女性的處置方法如下： ")]),_c('br'),_c('li',{staticClass:"letter-s"},[_vm._v(" 把婚前性行為當作是戀愛的「病」，而不是絕症。"),_c('br'),_c('br'),_vm._v(" 如果男友與妳有性行為，而他又移情別戀，當然令人痛苦，但最危險的是妳如何應付這種痛苦。或許有的人是死纏男友不放，有的自己了斷生命……，這些反應不但不成熟，而且幼稚，於事無補。聰明的方法是在心態上承認自己的錯誤、接納被始亂終棄的事實；告訴自己只要以後不要再犯錯，一定可以站起來。 ")]),_c('br'),_c('li',{staticClass:"letter-s"},[_vm._v(" 當機立斷，趕快離開男友"),_c('br'),_c('br'),_vm._v(" 男友若不珍惜妳，移情別戀的話，表示他不值得妳迷戀下去，尤其要注意的是，如果你愈迷戀他，就愈沒有身份與自尊。離開他固然會很痛苦，但是長痛不如短痛，否則事情鬧大的結果可能就是遺恨終身。同時在男友面前千萬不要苦苦乞憐，應該要表現出自己的自尊與風度。讓他知道妳已不屑於他，他不值得妳再愛，裝也要裝出來，否則妳將輸得更慘。 ")]),_c('br'),_c('li',{staticClass:"letter-s"},[_vm._v(" 千萬不要報復。報復的結果，兩個人都是輸家。若是妳心裡忿怒不平，一定要報復的話，最好的報復方法是努力使自己過得更好一點。妳表現得愈好，對方愈後悔。"),_c('br'),_c('br')]),_c('br'),_c('li',{staticClass:"letter-s"},[_vm._v(" 遠離男友，設法不要看到他。沒有辦法不想他時，也要設法不再接近他，否則難免會情不自禁。 ")]),_c('br'),_c('li',{staticClass:"letter-s"},[_vm._v(" 趕快接受心理輔導"),_c('br'),_c('br'),_vm._v(" 此時的輔導應包括心理與生理的重建。心理重建主要著重於你的「自我形象的接納」與「信心的建立」，妳依然是個好女孩，未來還充滿希望──如果妳能理智地處理妳的問題的話。生理的重建包括處女膜整型，但此項應與協談人員好好討論後再進行較妥。 ")]),_c('br'),_c('li',{staticClass:"letter-s"},[_vm._v(" 日後交異性朋友時，千萬不要再有婚前性行為的情況出現，而且千萬不要向他坦白招供妳過去的經歷。"),_c('br'),_c('br'),_vm._v(" 不與他發生性為可以建立他對妳的信心，否則他會以為妳太隨便，妳表現得愈堅持，對妳愈有利。"),_c('br'),_c('br'),_vm._v(" 若男友問及以前的經歷時，千萬不要承認過去的事實，妳可以技巧地答覆他的問話。例如：「難道你不相信我的人格嗎？」、「你把我看成什麼樣的女孩？」故意把以前的情形輕輕帶過。注意，我們不是要妳去「騙」他，而是要妳技巧地以行為去愛他，很少有男人能接受太太不貞的事實，儘管他口頭如何保證一定不介意。因此技巧地答覆對方是為上策，必要時，可以與協談人員討論。 ")]),_c('br'),_c('li',{staticClass:"letter-s"},[_vm._v(" 若對方以公佈你們兩個人的性關係為要脅時，則儘速與協談人員聯絡，共同處理問題。 ")])])])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-8",modifiers:{"accordion-8":true}}],attrs:{"block":"","variant":""}},[_vm._v("婚前應知婚姻詐騙層出不窮")])],1),_c('b-collapse',{attrs:{"id":"accordion-8","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("資料來源http://www.makoto007.com.tw/case4.htm")]),_c('p',[_vm._v(" 未婚男女若抱著「麻雀變鳳凰」或「癩蛤蟆想吃天鵝肉」的偏頗思維，小心婚姻詐騙殺手上門，有不少如此案例，呼籲︰選擇另一半除了量力而為外，更該務實優先。"),_c('br'),_c('br'),_vm._v(" 最近受理數起被婚友社「愛情詐騙」投訴案例，當事人被騙少則三、四萬元，有人因為「同情」女方還被騙兩百多萬元，社工人員分析，被害人如果無法理性面對情事，受騙的故事仍會「重演」。"),_c('br'),_c('br'),_vm._v(" 今年四十一歲的于姓離婚婦人表示，前夫外型瀟灑，因前夫外遇而離婚後，她內心不服輸的期望找一個更帥的老公，半年前到一家婚友社找對象，對方拿出三十份多金帥男照片與資料，于婦立即刷三萬八千元，開始安排外型中上的男士，後來男方表明要「吃軟飯」，有的則吹噓有多棟房子，結果都是吹牛一族。"),_c('br'),_c('br'),_vm._v(" 今年五十二歲，國中畢業，在一家紙廠擔任電機工作的潘姓男子，離婚時撫養三個兒女，如今兒女長大，也各自成家，他認為該為自己找下半輩子的幸福，因而要我們為他找一位年在四十歲以下，未婚貌美的女老師或護士為侶。"),_c('br'),_c('br'),_vm._v(" 潘姓男子說，他願意付雙倍報名費娶老師或護士，因為老師比較有氣質，而護士可以照顧身體，徐主任告訴他，未婚老師和護士都是大學程度，不太可能與離婚而國中程度的他交往，潘某卻氣呼呼的說︰只要給錢，婚姻介紹所會安排，雖然提醒他不要被婚友社「人頭詐騙」，潘姓男子卻相信金錢萬能，我們擔心他會是下一個受害者。"),_c('br'),_c('br'),_vm._v(" 一名年已半百的徐姓男子，由於抱持「幼齒補眼睛」，因而一直希望找三十歲以下女子為妻，一家婚友社一連介紹五位妙齡女子，先後買了三個納骨塔、兩張度假村持分，花了兩百多萬元，當報上刊出某婚友社詐騙三億多元，徐姓男子還為那些騙他的少女叫屈，認為她們也是被婚友社所害。更讓我們訝異的事︰徐姓男子還死心的表示︰如果能認識年紀輕的妹妹，花點錢也值得。"),_c('br'),_c('br'),_vm._v(" 傳統婚姻「門當戶對」或許重視社經地位，如今講究自由戀愛或開放的婚姻，但現今的婚姻仍重視兩人價值觀、興趣、年齡、學、經歷、外在等客觀條件的「門當戶對」，如果妄想客觀條件不對等的婚姻，付出的代價將更大。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-9",modifiers:{"accordion-9":true}}],attrs:{"block":"","variant":""}},[_vm._v("婚前教育 逾八成贊同")])],1),_c('b-collapse',{attrs:{"id":"accordion-9","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("資料來源http://www.epochtimes.com/b5/1/5/9/n86452.htm")]),_c('p',[_vm._v(" 教育部草擬的「家庭教育法」月前已在立法院教育委員會審查通過，鑑於目前的台灣離婚率高居亞洲之冠，離婚所衍生的諸多社會問題，也嚴重威脅社會的倫理與安寧。 根據一項問卷調查發現，八成以上受訪者贊成政府提供婚前家庭教育課程，也有超過九成認為課程對建立家庭幸福有幫助。 由國立台灣師範大學家庭教育中心主辦的「婚前教育策略國際研討會」，昨日進行各國婚前教育情況的廣泛研討，許多學者都提出深刻的探討。"),_c('br'),_c('br'),_vm._v(" 會中，由台灣師大家政教育系林副教授所做的這項問卷調查，針對各縣市家庭教育中心工作人員，取得約一千五百份問卷，抽樣代表性相當夠，調查發現贊成婚前必須接受家庭教育課程的受訪者有九成五會鼓勵其他親友參加這類教育課程，年齡越大者越認為婚前教育有其必要性。"),_c('br'),_c('br'),_vm._v(" 根據最新統計，台灣人每結婚三點五對就有一對離婚，離婚率高居亞洲第一，因此政府將研訂家庭教育法，鼓勵即將結婚者接受婚前教育。而新加坡的問題則是年輕人流行遲婚，生育率降低為百分之一點五，社會呈老化現象，希望來台取經。"),_c('br'),_c('br'),_vm._v(" 据《中央社》報導, 我國家庭教育法在已完成一讀，法案草案鼓勵各縣市政府提供適婚男女至少四小時的婚前教育課程，以提升適應婚姻能力，減少婚姻危機，教育部委託台灣師大家庭教育中心與家政教育系承辦婚前教育策略國際研討會，今、明兩天在國家圖書館國際會議廳舉行。"),_c('br'),_c('br'),_vm._v(" 附注：台灣最新的離婚率會議討論我國、亞澳與歐美國家婚前教育推動概況，也將討論立法規定四小時婚前教育的可行性與必要性，邀請的學者專家除本土學者外，也都是全球各地的海外華人學者。新加坡社會發展署署長黃鑠嵋致詞強調，這次研討會的吸引力就在屬於東方環境的言論，面對的論點與新加坡關心的問題類似，她希望獲得有創意的構思帶回新加坡。"),_c('br'),_c('br'),_vm._v(" 台灣師大家政教育系主任黃迺毓發表論文提到台灣地區的婚姻現況，包括人類壽命延長、初婚年齡提高、適婚年齡者漸趨晚婚、離婚率與再婚率升高造成單親與繼親家庭漸多等現象，據以強調婚前教育確有其需求。"),_c('br'),_c('br'),_vm._v(" 前述現象的數據指出，國人初婚年齡從一九七一年到一九九九年，男性從二十六點八歲提高到二十九點一歲，女性從二十二歲提高到二十六點六歲，二十八年間，男性初婚年齡約晚三歲，女性約晚五歲；離婚率則從六十年代的百分之零點七五提高到百分之四。"),_c('br'),_c('br'),_vm._v(" 去年公元兩千年有十八萬一千六百四十二對結婚，年增加百分之四點九，五萬二千六百七十對離婚，年增加百分之七點五，粗結婚率千分之八點一九，年增千分之零點三二，粗離婚率千分之二點三七，年增千分之零點一四。"),_c('br'),_c('br'),_vm._v(" 去年結婚對數中，與外國人結婚者二萬一千三百三十九人，年增百分之四十五點四六，其中，男性一萬九千零六十二人，年增約五成，女性二千二百七十七人，年增一成六六；離婚對數中，與外國人離婚者一千六百一十人，年增約九成，其中，男性一千一百五十九人，年增約九成二，女性四百五十一人，年增八成四；數據顯示離婚率明顯上升。 ")])])],1)],1)],1)],1)])]),_c('div',{staticClass:"frame"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s2",attrs:{"id":"s2","name":"s2"}},[_vm._v("感人的婚姻故事")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-11",modifiers:{"accordion-11":true}}],attrs:{"block":"","variant":""}},[_vm._v("永遠的祝福-聾人婚姻的故事")])],1),_c('b-collapse',{attrs:{"id":"accordion-11","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 當顧玉山夫妻倆抱著他們七個月大的新生兒，出現在眼前的時候，我的感覺就像「一粒沙綻放一朵花綻放一個世界」那樣的喜悅和受感動。"),_c('br'),_c('br'),_vm._v(" 尤其難忘孩子那雙黑晶石的眼睛，那雙無邪的黑眼睛啊，展示了童真世界裏的一 切純淨和坦然。我真的很希望孩子趕快長大，因為，他實在應該早點知道，他有一位多麼令人引以為傲的傑出父親和一位多麼可愛不凡的母親。"),_c('br'),_c('br'),_vm._v(" 推算起來，認識顧玉山已將近三年了。第一次見面，是在他廈門街的家中。顧玉山當時是「聾劇團」的團長，為了籌備第一次公演，聾劇團的導演汪其楣和近二十位聾友們，每星期五的晚間七時半，在顧玉山家裏排戲。"),_c('br'),_c('br'),_vm._v(" 多數聾友給我的第一印象是沉鬱而矜持的，這位「顧團長」恰好相反。開朗豁達是他的特徵，特別是說話（比手語）的時候，眼睛、鼻子、嘴巴，甚至眉毛，幾乎可以溢出笑聲來。"),_c('br'),_c('br'),_vm._v(" 顧玉山不但可親還很大方，他有一招學崔苔菁嬌嗔扭擺的絕活，簡直唯妙唯俏，嚴肅的排戲暫停，眾聾友最愛吵著他表演崔苔菁的「愛人就在你身邊」，他很少拒絕。"),_c('br'),_c('br'),_vm._v(" 在靠近大門口的角落，擱著一具電唱機，戴上助聽器的顧玉山，可以利用殘餘聽 力來感覺音樂的節奏，唱片聲一響，顧玉山便跳將起來了，隨著他有趣的舞姿，小屋子裏的人全東倒西歪的笑得直不起腰來。如果換了平常，這間小屋子不知會爆滿著怎樣一種笑鬧喊聲，但是，和聾人們在一起，你最好用兩眼來感覺千百種情緒，在這個時候，「耳朵」是不怎麼管用的。不論如何，一種快樂兼些微傷感的情緒仍是很快的感染了我。如果顧玉山會說話，他可以登臺成另一個恬妞了。"),_c('br'),_c('br'),_vm._v(" 六十七年五月五、六、七三天，聾劇團首次在台北市金華街月涵堂公演。其中最叫座的一個節目是「人體攝影機」。「人體攝影機」的靈感來自美國聾人劇團創辦人布萊格先生到臺灣訪問時所做的一次示範表演──無聲動作劇（Sign-mime），也就是運用電影拍攝的技巧，如長鏡頭、中距離、大特寫等剪接方式，把故事串演出來，一 人自飾主角甚至數個配角。"),_c('br'),_c('br'),_vm._v(" 布萊格的示範表演引起了聾劇團團員的共鳴和認同。本來，聾人們就很擅於用自己的身體敘述故事，可以一下子演好幾個人，一下子又變回來成為敘述者，他們自小就習慣了這種戲劇性的說故事手法。有了布萊格的表演啟示，靈感更是源源不絕了，所以，聾劇團的第一次公演項目中，即安排了「人體攝影機」的表演，如石迪翔的「捧球賽」、黃榮昌的「人猿泰山」、褚錫雄的「弄蛇人」、吳學孟的「拾玉鐲」…… 等等。"),_c('br'),_c('br'),_vm._v(" 表演特別精彩的，除了石迪翔的「捧球賽」，就數顧玉山「希特勒閱兵」了。只顧玉山一人又扮希特勒；又扮陸、海、空軍分列式；又扮蹩了腳的步兵；又扮故障錨的戰車；又做飛機失事；舉手投足間，很快就把全場觀眾帶領到一個閱兵的場面中，嚴肅中不失輕鬆，輕鬆中不失深度，就如朱西寧先生所稱的：「不光是做表的真實，變化得神速，還在深得這『扮』的意思和意境。」"),_c('br'),_c('br'),_vm._v(" 此後，我又對顧玉山有了較多的瞭解和關心。我知道他是彰化縣人，兩歲時因注射金黴素失聰，家有一弟三妹，大妹是重聽，其他是聽人。小學、初中畢業於台中啟聰學校，之後就讀台南啟聰，由於成績優異，又繼續進中國文化學院美術系攻讀，在文化學院時，還曾參加外系體操比賽，獲徒手操第一名。"),_c('br'),_c('br'),_vm._v(" 大學畢業後，顧玉山任職一家塑膠製品有限公司的吹氣玩具設計師兼攝影師。 聾劇團公演之後，我仍不時從汪其楣那裏知道一些與聾友有關的近況，如某某人正在戀愛，某某人即將畢業了，或某某人出國之類的好消息。六十八年一月，我收到一張喜帖，好，這回輪到顧玉山要結婚啦！ 我參加過很多聽人的婚禮，但聾人結婚的婚禮，這還是第一次。 顧玉山傳奇的故事，我還沒出生勒...:) （本文選自伊甸基金會圓與緣業書） ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-22",modifiers:{"accordion-22":true}}],attrs:{"block":"","variant":""}},[_vm._v("愛情和現實")])],1),_c('b-collapse',{attrs:{"id":"accordion-22","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("http://life.iiietc.ncu.edu.tw/xms/content/show.php?id=11023")]),_c('p',[_vm._v(" 韓國有一個著名飲料的廣告，上映後造成很大的轟動。"),_c('br'),_c('br'),_vm._v(" 廣告中女主角說：「對愛情最大的誤解，是認為它是萬能的，現實的愛是搖擺不定的，即使只是有沒有工作那樣小小區別。」"),_c('br'),_c('br'),_vm._v(" 廣告中的男主角說：「對愛情最大的誤解，是認為我們必須面對現實，因為當時間流逝，一切現實問題都將成為微不足道的回憶。」而我想，愛情在某些程度來說的確是現實的，當我們在選擇對象的時候，長得高不高、美不美、端不端正、順不順眼、健不健康、都是第一眼的條件；再來，個性溫不溫和、有沒有不良嗜好、從事什麼職業、有無正當的工作、1個月多少薪水更是現實的考量。"),_c('br'),_c('br'),_vm._v(" 喜歡、欣賞或者愛，在每一個人心中都有一把尺存在。我記得我以前的朋友曾對我說：「還好你家離我家很近。」"),_c('br'),_c('br'),_vm._v(" 「離家很近？那如果很遠會怎樣嗎？」"),_c('br'),_c('br'),_vm._v(" 「很遠，我就不會選擇你啦！」"),_c('br'),_c('br'),_vm._v(" 「為什麼？」我很驚訝我家位置竟然也是被對方選擇的條件之一。 「因為每次來回很花油錢的耶！如果油錢太多，可不好。」"),_c('br'),_c('br'),_vm._v(" 其實，我們在選擇朋友時，已有許多的標準，如果當愛情再繼續走入婚姻，涉及的層面又更多了。我曾聽過一個真實的故事：一位朋友和她未婚妻訂婚後，一起去國外旅行。未婚妻非常渴望看當地的著名歌劇，沒想到卻因整修沒有看成。"),_c('br'),_c('br'),_vm._v(" 在欣賞不到的情況下，未婚妻竟因此鬱鬱寡歡、陷入無盡的沮喪情緒中，久久不能回復。那位朋友對此感到非常訝異，覺得這簡直不像他從前所認識的女孩，他必須要重新看待這段關係。"),_c('br'),_c('br'),_vm._v(" 於是，當他們旅行回來後，朋友再三考慮的結果，竟是解除了婚約。"),_c('br'),_c('br'),_vm._v(" 人生有無數的抉擇，我想如果能找到一個能一起吃苦的而不是一起享樂的；能一起承擔的而不是一起做夢的；能對你真正負責而不是無奈應付的那個人，那麼請你，真的、真的要好 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-33",modifiers:{"accordion-33":true}}],attrs:{"block":"","variant":""}},[_vm._v("讓我成為妳的聲音")])],1),_c('b-collapse',{attrs:{"id":"accordion-33","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("http://h611.mysinablog.com/index.php?op=ViewArticle&articleId=1051359")]),_c('p',[_vm._v(" 打從一開始........女孩的家人便反對她和男孩交往。"),_c('br'),_c('br'),_vm._v(" 說什麼家世的問題，說什麼女孩和男孩在一起後，會吃苦一輩子....而女孩也因家裡的壓力，而時常和男孩吵架。女孩是愛男孩的；女孩常問男孩....”你有多愛我？"),_c('br'),_c('br'),_vm._v(" 男孩因為不善言語，常惹女孩生氣。再加上父母的話，心情就更不好...."),_c('br'),_c('br'),_vm._v(" 男孩，便成為了她的出氣筒了。而男孩，只是默默的任由女孩，發著脾氣......"),_c('br'),_c('br'),_vm._v(" 後來，男孩大學畢業了。打算要出國進修；出去前....男孩向女孩求婚了:"),_c('br'),_c('br'),_vm._v(" 我，不會說好聽的話；但是我只知道，我愛你。如果你願意，我會照顧你一輩子。至於你家人，我會努力讓他們認同的。嫁給我，好不好？ 女孩答應了男孩。"),_c('br'),_c('br'),_vm._v(" 而女孩的家人，再男孩的努力下，也承認了他們。於是，在男孩出國前，他們訂婚了。"),_c('br'),_c('br'),_vm._v(" 後來，女孩留在國內。步入了社會；而男孩則在異鄉，繼續學業....他們平時靠著電話及信件，維繫著感情。"),_c('br'),_c('br'),_vm._v(" 日子雖然難熬，但從未說過放棄。一日，女孩如往常的出門上班；在走往公車站的路上，一台失控的車子，向她衝來。 當女孩清醒過來後，女孩看到了父母，才知道自己傷得不清，幸好命沒丟。"),_c('br'),_c('br'),_vm._v(" 看著父母這麼難過，女孩想要安慰他們。但卻發現........自己一個字也說不出來，"),_c('br'),_c('br'),_vm._v(" 女孩極力的想要發出聲音，但卻只能發無聲的喘息。女孩啞了........醫生說因為傷及腦部，"),_c('br'),_c('br'),_vm._v(" 所以女孩後輩子都不能再說出一字一語了。聽著父母的安慰，嘴卻不能再出聲。 女孩塴潰了........"),_c('br'),_c('br'),_vm._v(" 成天除了無聲的哭泣， 還是哭泣........"),_c('br'),_c('br'),_vm._v(" 後來，女孩出院了。回到了家中，一切還是像從前一樣。唯獨電話鈴聲，成為了女孩最大的夢饜。"),_c('br'),_c('br'),_vm._v(" 那一聲一聲的鈴聲，不斷不斷的刺激著女孩。女孩的痛苦....卻不能告訴男孩。"),_c('br'),_c('br'),_vm._v(" 女孩不想拖累男孩，於是寫了封信.......告訴男孩不想再等了，他們之間結束了，並把戒指給退了回去。"),_c('br'),_c('br'),_vm._v(" 面對著男孩一封封的回信及打來的電話，女孩除了流淚還是流淚........"),_c('br'),_c('br'),_vm._v(" 女孩的父親看著女孩如此的痛苦，便決定搬家。希望女孩能忘掉這一切，能快樂些........"),_c('br'),_c('br'),_vm._v(" 換了一個環境，女孩慢慢的試著學習，用手語去溝通，重新開始........"),_c('br'),_c('br'),_vm._v(" 也告訴自己，忘了男孩....... 一天，女孩的好友， 告訴女孩，男孩回來了。"),_c('br'),_c('br'),_vm._v(" 並四處尋找著女孩，女孩拜託好友，別告訴男孩，她的事，告訴男孩，忘了她。"),_c('br'),_c('br'),_vm._v(" 之後........就沒有再聽到男孩的消息了。過了一年多，女孩的好友，告訴女孩，"),_c('br'),_c('br'),_vm._v(" 男孩要結婚了並託她帶張喜帖給女孩。女孩傷心的打開，卻看見了自己的名字在上面。 正想問好友時，男孩出現在女孩的面前。用著生硬的手語，告訴女孩........ "),_c('br'),_c('br'),_vm._v(" [我花了一年多的時間，逼自己學手語；為了就是要告訴你，我沒有忘記我們的約定， 給我個機會讓我成為你的聲音。 ”我．愛．你 ”] "),_c('br'),_c('br'),_vm._v(" 看著男孩緩慢的比著，及當初女孩所退回去的戒指"),_c('br'),_c('br'),_vm._v(" 女孩終於笑了......... ")])])],1)],1)],1)],1)])]),_c('div',{staticClass:"frame"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s3",attrs:{"id":"s3","name":"s3"}},[_vm._v("婚前徵信相關")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-1a",modifiers:{"accordion-1a":true}}],attrs:{"block":"","variant":""}},[_vm._v("婚前協議書的效力?如何訴請離婚")])],1),_c('b-collapse',{attrs:{"id":"accordion-1a","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("資料來源 http://www.makoto007.com.tw/case4.htm")]),_c('p',[_vm._v(" 先出軌的那個人，賠一千萬元！"),_c('br'),_c('br'),_vm._v(" 西方人是自由戀愛，合則來，不合則去，我們則是從一而終。"),_c('br'),_c('br'),_vm._v(" 當然這是以前的東方人，現在大家還不是想離就離，自然婚前契約就出現了... "),_c('br'),_c('br'),_vm._v(" 案例一：小王是個科技新貴，名下有數個不動產，銀行存款更超過千萬。最近他遇到了理想對象，開始有了結婚的念頭，卻又怕結婚後若兩人個性不合，一旦離婚，會導致自己的財產大失血，因而想在結婚前先簽訂協議書，約定離婚後財產分配、贍養費給付等問題。"),_c('br'),_c('br'),_vm._v(" 案例二：小娟在廣告公司裡任職，工作十分忙碌，面對男友的求婚，小娟心裡十分徬徨，害怕結婚之後家庭事業兩頭燒，會忙得完全沒有自己的時間，甚至得放棄事業，因此想在婚前先與男友簽訂協議書，約定結婚後家務如何分工、家庭生活費支出如何分配等問題。"),_c('br'),_c('br'),_vm._v(" 受到美國好萊塢明星流行簽訂婚前協議書的影響，加上現代人保障自我權益之意識日漸升高，許多男女在婚前也開始興起簽婚前協議書的念頭。但須注意的是，因為我國法律認為，婚姻是以夫妻雙方經營永久共同生活為目的，因此若是以「離婚」為前提所簽訂之婚前協議書，是違反公共秩序與善良風俗的，所以這種婚前協議書在法律上無效。"),_c('br'),_c('br'),_vm._v(" 簡而言之，這種類似「預先訂立的離婚協議書」是無效的，所以在案例一的情況，小王在婚前協議書中約定約定離婚後財產分配、贍養費給付等問題，這種約定內容，即使雙方都同意且簽訂了，在法律上也不生效力。"),_c('br'),_c('br'),_vm._v(" 但是在不違反公共秩序與善良風俗的情況下，婚前協議書還是可以具有法律效力的。例如我國民法就明文規定，對於夫妻之冠姓、住所、家庭生活費用之分擔、夫妻財產制、自由處分金、子女姓氏、對子女權利義務之行使與負擔等，都可以在由雙方在協議書中約定，且具有法律效力。另外針對家務分工的問題，雖然我國民法並未明文規定，但因不違反公序良俗，在法律上仍是有效。此外，若約定外遇的一方應給予另一方金錢上的賠償，只要協議內容不涉及「離婚」，這樣的約定也可以具有法律效力。"),_c('br'),_c('br'),_vm._v(" 因此在案例二的情況中，小娟可以在婚前協議書中約定婚後雙方應如何分擔家務，以及每個月雙方應各負擔多少比例的家庭生活費支出，避免其婚後會因家務繁重而須放棄工作，或家庭生活費分擔失衡的問題。"),_c('br'),_c('br'),_vm._v(" 但要提醒各位，婚前協議書的立意是在避免雙方的婚姻生活不致因材、米、油、鹽、醬、醋、茶等日常瑣事引發爭吵，破壞婚姻和諧，倘若雙方都太過保護自我權益，婚前協議反而導致雙方爭吵繼而分手，那就失去了婚前協議的美意了，畢竟婚姻生活還是需要夫妻雙方互相尊重、體諒、包容，才能永久經營下去，不是嗎？ ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-2a",modifiers:{"accordion-2a":true}}],attrs:{"block":"","variant":""}},[_vm._v("婚前心理健康檢查")])],1),_c('b-collapse',{attrs:{"id":"accordion-2a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("文由健康雜誌提供，作者／陳俊欽醫師")]),_c('p',[_vm._v(" 隨著婚前健康檢查的觀念日漸普及，婚前「心理健康」檢查也逐漸受到人們重視。您也許不明白：什麼叫做婚前心理健康檢查？心理健康檢查跟一般的健康檢查有什麼不同？"),_c('br'),_c('br'),_vm._v(" 婚前心理健康檢查的意思其實很簡單，就是在兩個人決定終生廝守的時候，雙方都要為「自已的心情」與「未來的互動」做好準備。有別於「因為我愛你，所以我想跟你在一起。」的傳統說法，婚前心理健康檢查的真意是「因為我想跟你在一起，所以，我要確保我的心理沒有問題，也確保你的心理沒有問題，還要確保雙方的相處都沒有問題。"),_c('br'),_c('br'),_vm._v(" 這樣，我們兩個人才能擁有終身的幸福。」這是一種真正負責任的態度。換句話說，如果男女雙方有任何的問題，都必須在結婚以前找出來，一來表示負責，二來作為雙方可以努力解決的對象，同時也避免干擾或侵害對方的權利。"),_c('br'),_c('br'),_vm._v(" 現在的婚前健康檢查已經可以實現部份的概念，但是，幾乎都著重在生理問題的層次。所以，安排的內容通常為抽血、心電圖、X光、超音波等等。比較仔細一點的，還加上了腦波、胃鏡等等。"),_c('br'),_c('br'),_vm._v(" 上述的檢查洋洋灑灑，但全部都只涵蓋了生理的層面。但是心理呢？那就被忽略了。事實上，要結婚的雙方，來自於兩個不同的家庭，有著不同的文化與價值觀，有著截然不同的生活模式，差異可能很小，但也可能很大。今天他們要融合在一起，在心理上所要進行的調適，可是遠遠大於生理上的調適。試想：婚前與婚後，胃腸不好的人還是一樣胃腸不好，但是脾氣不好的人可就不能繼續保持脾氣不好了，不然幾乎都可以預見日後的吵吵鬧鬧。進一步分析，前者是生理問題，通常是自限性的，除非是傳染病，否則疾病不會透過相處而傳染；後者是心理問題，而心理問題幾乎都是會彼此影響的，過度敏感、善妒懷疑、過度依賴等等，都是婚姻的潛在殺手。從結婚那刻，兩個人的行為模式、想法、生活居住模式或習慣等等，都得或多或少的融合在一起。沒有人知道：自己是否承擔的起這樣的「文化價值」衝突。"),_c('br'),_c('br'),_vm._v(" 所以，倘若婚前不曾安排過婚前心理健康檢查的話，那就等於在賭運氣了。也難怪：有人會說婚姻是愛情的墳墓──因為都被打死了，當然要找地方埋！"),_c('br'),_c('br'),_vm._v(" 按照道理來講：結婚雙方有幾種方式來彼此融合。第一種就是相處日久，都未曾發生破壞性的爭吵，最後彼此容忍；第二種最為常見，就是小孩的出生，成了家裡的最後談資，而兩個人則在無法互融下，孤獨的終老。"),_c('br'),_c('br'),_vm._v(" 婚前心理健康檢查的目的"),_c('br'),_c('br'),_vm._v(" 所以，如何能夠在讓感情在惡化以前便及早發現彼此的心理障礙、人格缺陷、精神狀，那就有賴婚前心理健康檢查。"),_c('br'),_c('br'),_vm._v(" 婚前心理健康檢查的標的非常的清楚，那就是針對於要結婚的雙方，他的「人格特質」、「精神狀態」、「心理狀態」、「認知狀況」分別探索。需要檢查的地方包括：焦慮度、認知結構、處事待人、憂鬱程度、雙方家庭心理動力。再接下來，才是精神病的檢查。"),_c('br'),_c('br'),_vm._v(" 認知結構是一個人的思想方式，以及他所相信的事物。待人處世呈現著它的社交能力。雙方家庭對於這場婚姻可能有不同的看法，這些看法都會在各自的家庭中激盪，形成一種特殊的家庭心理動力。當這些都沒事時，還要注意精神病的可能性。理由是：精神病患者可以暫時一切正常，讓人無法判斷，如果婚前心理健康檢查不仔細，說不定會娶個精神分裂症老婆回來也不知道，因為很多時候，在結婚前，剛好是他家人安排到最好、最穩定的時候，目的當然是要讓你見面時有好感。萬一結了婚就來不急了。"),_c('br'),_c('br'),_vm._v(" 雖然說，心理健康檢查的目的在於檢查上述三項，但在實務上我們的檢查方式是先從有沒有精神疾病檢查開始，其次才是檢查他的心理有沒有什麼障礙，最後就是檢查他的人格部份有沒有什麼障礙。婚前心理健康檢查會查遍所有的事件，諸如：價值觀，做事、處事、態度、情緒、人格特質與EQ等等。婚前心理健康檢查只能做做到：「釐清所有潛在的精神病」、「盡量發掘將來可能的心理衝突」、「勾勒出雙方的人格特質」。至於當事人的選擇，我們就不能勉強了。"),_c('br'),_c('br'),_vm._v(" 婚前心理健康檢查的好處"),_c('br'),_c('br'),_vm._v(" 婚前心理健康檢查的好處，其實很多，有一部份在前面已經談過了，現在再仔細的說明一次──第一個就是做為當事雙方的參考，這一點沒有絕對力，沒有人說他心理有問題就不能結婚，但是我們要把事情呈現開來，免得一個婦女今天看到一個男的很喜歡，結了婚之後才知道他是超級大男人主義。那如果他能夠在早一點的時候就知道了，那是不是可以讓她稍為早點做選擇的？假如她依然選擇了，那沒關係，她至少在她後續的過程中當個小女人，因為那是她所選擇的。"),_c('br'),_c('br'),_vm._v(" 第二就是：預測接下來的爭執跟衝突會在哪裡，甚至我們可以進行家族性的評估，我們甚至可以評估到，結婚的雙方以及她的家裡的妯娌兄弟父母姐妹之間，有沒有可能會有衝突存在，這個都是我們要做的事情。第三就是：它提供了一個線索，讓你們可以及早做判斷，譬如說，發現他有重大而且不能痊癒的疾病、或是有遺傳性的疾病時，那你還要不要？當然，你可以要，但就得為自己的選擇負責。"),_c('br'),_c('br'),_vm._v(" 婚前心理健康檢查的壞處"),_c('br'),_c('br'),_vm._v(" 你要重大的決心，真正實踐，你才能享有上述的好處。但是婚前心理健康檢查也是有壞處的──不用太訝異，所有的檢查都有好處也有壞處，只是呢，很少有醫生會告訴你壞處的，不過沒關係，我們將依序類推——第一個就是破壞所有的矇矓的美感。破壞期待、破壞美夢、破壞價值。因為經過這個分析之後，所謂愛情建立起來的雙方關係，就要轉變成為務實取向的結婚關係了，婚前心理健康檢查雖然可以指出未來可能的衝突點，但是也犧牲掉了當下的朦朧美感。"),_c('br'),_c('br'),_vm._v(" 第二個就是提供一個自證預言的機會。婚前心理健康檢查雖然是在找尋未來的可能衝突，但是他還是避免不了對各案有些暗示。婚前心理健康檢查所找出來的是「可能」，不是「將會」。但是必然有一些人，會將兩者混在一起，從此認定對方就是這個樣子，反而忽視掉了對方的真正態度。"),_c('br'),_c('br'),_vm._v(" 婚前心理健康檢查的項目"),_c('br'),_c('br'),_vm._v(" 婚前心理健康檢查的項目部份，在台灣還不是很盛行，會的人也不多，所以項目的安排，也依照各自的安排方式，我們沒有辦法一一詳述，我們只能就自己的方式來說明。"),_c('br'),_c('br'),_vm._v(" 第一個要注意的對象，無庸置疑就是精神病，如果另一半曾經出現妄想、幻聽、怪異言語、怪異行為、退化思維等症狀的時候，我們就要立刻提出警訊：「他有精神疾病的可能性」。這些症狀可能很明顯，但是多數時候是很隱微的，很多門外漢看了兩、三遍，都看不出個所以然。"),_c('br'),_c('br'),_vm._v(" 第二個部份就是要檢查有沒有心理上面的障礙──特別是重大創傷經驗。重大創傷經驗通常出現於大災難之後，但是不限於災難事件，不少人在面對職場的鉤心鬥角或家庭衝突時，也會出現重大創傷經驗。成年以後的重大創傷經驗比較容易鑑別，而青春期以前的，就需要靠治療者的功力了。除了重大創傷經驗以外，心理狀態也是很重要的，包括：認知功能、歸因方式、自我要求、未來預期等。通常，敏感的認知能力，加上內向歸因（例如：有錯都是我的錯），再合併上高度的自我要求以及悲觀預期，是最容易誘發情緒不穩的。"),_c('br'),_c('br'),_vm._v(" 最後一個就是人格特質的檢查。人格是一種橫貫人類一生的反應模式，它會與各種環境發生牽連。不同的人格，面對同一的環境，會有截然不同的認知與反應。只要能勾勒出一個人的人格，就可以預測未來他可能會發生障礙的地方（諸如：婆媳問題、管教問題等）。人格的特徵就是永恆不變，一旦當它形成之後，要改變很困難了，所以，人格具有最強的一致性，舉例來說：一個人二十歲的人格，到了五六十歲還是可能相同，藉此，我們可以推論個案未來的發展。"),_c('br'),_c('br'),_vm._v(" 婚前心理健康檢查的進行方式，其實非常的簡單，而且與其他的健康檢查比起來，輕鬆容易的多了。唯一的付出，只有時間而已。婚前心理健康檢查建議先由二個一起來，然後呢，男的來一次，女的來一次，然後呢，再來一起來，那就是四次解決，這個過程當中，不用你去做任何的檢驗說怎麼抽血啦，照Ｘ光啦，照核子共振啦，什麼都不用，我們只要談話，透過言語以及行動，就能看出足夠的資訊。"),_c('br'),_c('br'),_vm._v(" 結語"),_c('br'),_c('br'),_vm._v(" 婚前心理健康檢查之後，要到多久才能拿到報告？一樣的，這必然受到診所作業流程的影響。以我們的經驗，時間約為所有會談結束後的一個禮拜左右。在詮釋報告的時候，本身也以會談的形式進行會比較好。"),_c('br'),_c('br'),_vm._v(" 至於怎麼面對並處理婚前心理健康檢查的報告？其實，不用太過嚴肅，它只是一份報告，它會仔細的寫出了你內心的事情，而且評估了風險，評估了你們在什麼狀況底下最容易吵架，評估了你們不良的互動，其實你只要把報告看過一遍，好好的去想一想，那就可以了。最重要的還是雙方的共同檢討與計畫。"),_c('br'),_c('br'),_vm._v(" 最後我們只提到一點點就是婚前心理健康檢查的現狀，老實說，婚前心理健康檢查的主題一直都被忽視，而且很少人重視它，必須等到鬧了家庭革命了，甚至離婚了才會發現，我們對這些感到相當的痛心，因為有些人的人個缺陷太明顯，早晚會破壞婚姻關係，我們雖然知道，但他們不願意接受這個事實。這時候，這個婚姻可以說是處在風雨飄搖之中。"),_c('br'),_c('br'),_vm._v(" 為了避免浪費生命，也為了向自己與另一半負責，即將結婚的伴侶，理論上都應該接受婚前心理健康檢查的。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-3a",modifiers:{"accordion-3a":true}}],attrs:{"block":"","variant":""}},[_vm._v("婚前須知重婚罪須負何種法律責任? ")])],1),_c('b-collapse',{attrs:{"id":"accordion-3a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 所謂重婚，指於合法的婚姻關係存續中，夫妻之一方重為婚姻或同時與二人以上結婚。其法律效果如下："),_c('br'),_c('br'),_vm._v(" （一）構成刑法第二百三十七條之重婚罪，可處五年以下有期徒刑。其相婚者亦同。本罪為非告訴乃論之罪，其追訴權為十年（刑法第八十條第一項第二款）。又本罪為即成犯，一經舉行結婚儀式，犯罪行為即已終了，其結婚後之婚姻存續狀態，乃狀態之繼續，不能認為犯罪行為之存續（註四）。"),_c('br'),_c('br'),_vm._v(" （二）民國七十四年六月四日前之重婚，依修正前舊民法第九百九十二條之規定，利害關係人（包括結婚當事人、當事人之配偶、當事人之親屬及家長均屬之----註五）得向法院撤銷重婚之結婚，在未向法院撤銷婚姻前，重婚之婚姻關係仍然存在，前後婚姻之當事人均取得法律上配偶的地位（註六），此時即發生「齊人之福」的婚姻配偶身分關係。"),_c('br'),_c('br'),_vm._v(" （三）民國七十四年六月五日後之重婚，因民法第九百八十八條第二款修正為結婚違反第九百八十五條之規定（即重婚及同時婚）者，無效。故有配偶者再為結婚，或一人同與二人以上結婚者，均屬無效之結婚，無待於撤銷，即當然無效，欲享合法的齊人之福者，已無可能。 ")]),_c('br'),_c('ul',[_c('li',[_vm._v(" 離婚未辦離婚登記，再與他人結婚，是否觸犯重婚罪？"),_c('br'),_vm._v(" 我國民法對於結婚採當事人放任主義的儀式婚，只須有公開儀式及二人以上證人即可，並不以登記為要件，但對於兩願離婚卻採國家監督的登記婚主義，除應以書面為之，有二人以上證人簽名外，尚須向戶政機關為離婚之登記，才能發生離婚的法律效果，兩者立法政策步調並不一致。 ")]),_c('br'),_c('li',[_vm._v(" 故離婚雖已簽訂離婚協議書（書面），且有二人以上證人之簽名，如未向戶政機關為離婚登記，仍未能發生離婚的法律效果，亦即雙方當事人仍屬合法的配偶關係，如再與他人結婚，仍可構成刑法第二百三十七條重婚罪。 ")]),_c('br'),_c('li',[_vm._v(" 因重婚而同居，是否另成立通姦罪？又在外國犯重婚罪，而回國同居，是否成立犯罪？"),_c('br'),_vm._v(" （一）因重婚而同居，乃重婚的當然結果，依實務見解，只能論以刑法第二百三十七條的重婚罪，而不另論刑法第二百三十九條的通姦罪（證七）。"),_c('br'),_c('br'),_vm._v(" （二）又在外國犯重婚罪，縱令兩人回國同居，依刑法第五條、第七條之規定，無刑法之適用，故不能以刑法處罰之（註八）。 ")]),_c('br'),_c('li',[_vm._v(" 如僅有事實上的同居關係，能否構成重婚罪？如元配同意他方重婚，是否構成重婚罪？"),_c('br'),_vm._v(" （一）所謂重婚，係指有配偶之人再與他人結婚而言，是須有婚姻的形式，亦即須有公開儀及二人以上之證人，故雙方僅事實上同居，並未正式結婚，如一方另與他人結婚，仍不能構成重婚罪（註九）。又如有正式結婚，而另與他人同居，並未再行公開儀式，亦不能構成重婚罪。"),_c('br'),_c('br'),_vm._v(" （二）又有配偶之人在正式之婚姻關係存續中，重行再與配偶以外之人結婚者，即應構成重婚罪，縱元配同意他方重婚，仍可構成重婚罪，惟既經元配事前同意，依民法第一千零五十三條規定，元配不得再以重婚為理由請求判決離婚。 ")])])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-4a",modifiers:{"accordion-4a":true}}],attrs:{"block":"","variant":""}},[_vm._v("有關婚前財產分割協議書")])],1),_c('b-collapse',{attrs:{"id":"accordion-4a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("http://www.fl-home.com/articles/827.htm")]),_c('p',[_vm._v(" 根據美國法律，夫妻雙方無論是婚前或婚後所擁有的財產屬共同財產(其中包括不動產，銀行存款，有債證券等)，除非雙方在婚前簽有協議申明雙方在結婚前所擁有的財產屬個人所有，婚後仍維持不變；否則即使一方在婚前一無所有，離婚後仍有權平分另一方的財產。由於離婚爭產的官司經常發生，故不少人在結婚前請律師起草一份婚前協議書，以保護自己辛苦賺取的婚前財產免被有心人利用婚姻來瓜分。"),_c('br'),_c('br'),_vm._v(" John是一位頗為成功的地產商人，在第一次婚姻失敗後碰上一名心儀的女子。由於John在第一次離婚時付出重大的經濟代價，以致後來John後來對婚姻有恐懼感，John恐怕她利用結婚來達到分產的目的。由於女友多次示意結婚，John最後決定請律師起草一份婚前財產分割協議書，申明雙方在婚前的個人財產，在婚後仍然維持不變。"),_c('br'),_c('br'),_vm._v(" 女友當時似乎並不在意該份協議書，而且照簽不說。一年後，該女子向John提出離婚，並向法院提出爭產訴訟。在法庭上，該女子的代表律師聲稱當時在簽字時，該女子並不了解協議書的內容。最後法院認為該女子的英文程度不佳，在沒有律師的協助下，無法了解協議書的真實內容而判其勝訴。"),_c('br'),_c('br'),_vm._v(" 根據經驗法律人士的意見：雙方當事人在簽署這類法律文件時，雙方都要有代表律師在場，以免其中一方以當時簽字時並不了解協議的內容而將其推翻。另外，雙方在結婚後都要將其個人財產分開，特別是房屋是屬協議書上一方個人財產，而婚後雙方都有支付有關房屋的費用，這樣一旦離婚時，婚前協議書有關房屋的個人財產申明將會失效。"),_c('br'),_c('br'),_vm._v(" 當然，絕大部分人都視婚姻為神聖，男女雙方在結婚前簽下婚前財產分割協議書，可能會在另一方的心裡投下陰影。況且，簽協議容易，但雙方在婚後要保持個人財產分割似乎並不容易，特別是房屋，其中一方要獨自支付所有的開支才能保持財產獨立。因此，婚後要完全保持雙方原來的財產獨立，對雙方來說都是一個挑戰。 由於婚前財產分割協議書牽涉到萬一離婚時的財產分配，是非常重要的法律文件，如雙方有必要在婚前簽署該協議書，應向擅長處理財產及熟悉婚姻法律的律師諮詢。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-5a",modifiers:{"accordion-5a":true}}],attrs:{"block":"","variant":""}},[_vm._v("《結婚前，結婚後》／愛恨衝突")])],1),_c('b-collapse',{attrs:{"id":"accordion-5a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("文／任兆璋 http://www.ettoday.com/2002/04/29/157-1296293.htm")]),_c('p',[_vm._v(" 夫婦、父母子女、兄弟之間，以及一切其他人際關係互動，都潛藏著衝突的種子。衝突本身無所謂對錯，人們的態度和處理方法才會賦予衝突正面或反面的意義。以建設性的態度面對衝突，是婚姻關係中絕不可或缺的。"),_c('br'),_c('br'),_vm._v(" 一般人都覺得衝突不好，大家也不喜歡跟別人衝突，甚至有些文化認為衝突是不道德的，所以大家拚命壓抑，不讓自己跟別人發生衝突，結果人跟人之間就有很多誤會、憤怒、仇恨。這都是壓抑、無法發洩衝突的情緒造成的。"),_c('br'),_c('br'),_vm._v(" 其實中國人有很多古諺是容許衝突的。譬如：「不打不相識」，就是容許我們可以有衝突。可是有時候在婚姻早期，大家不喜歡破壞氣氛，或是怕吵完了以後跑出來很多負面感受。許多人都不敢碰這個問題，怕碰了也許沒辦法維持正常生活，然後自己就垮掉了。"),_c('br'),_c('br'),_vm._v(" 很多人把愛、恨看成兩極對立。事實上它們並不是對立的，而是相對的，愛、恨通常連得很近。如果一對情侶相識很久以後能夠吵上一架，很好！其實你們已經有了安全感，情緒可以進到更深的階段了。"),_c('br'),_c('br'),_vm._v(" 在日常生活中，你有時敢跟大哥吵架，就是不敢去碰小弟弟，唯恐他懷恨在心，一輩子都不跟你講話。"),_c('br'),_c('br'),_vm._v(" 在我們修女會團體生活裡，這件事就很清楚，有些人我會跟她吵上三天三夜，明天關係還是很好；有些人我就不敢碰，連三句重話都不敢說，唯恐碰了以後，我們的關係從此就完了。這主要是因為如果我敢去吵的時候，內心深處已經體會到就算跟對方敵對，還是可以讓自己真正的感受自由跑出來。"),_c('br'),_c('br'),_vm._v(" 愛與恨其實是：有了愛，恨就可以自由跑出來了。反之，沒有愛時，恨也不敢出來。一位新來的修女，如果我不認識她，就不敢自由表達內心的情緒，主要是我不清楚會出現什麼互動，害怕我們的關係從此完了。"),_c('br'),_c('br'),_vm._v(" 進行個別婚前輔導時，我會問：「交往多久？有沒有吵過架？」"),_c('br'),_c('br'),_vm._v(" 他回答：「交往三個月，沒有吵過架。」"),_c('br'),_c('br'),_vm._v(" 我說：「沒資格結婚耶！沒吵過架，你怎麼知道吵完了以後會和好？吵過架又和好了，這個才是。」"),_c('br'),_c('br'),_vm._v(" 在人生歷程中，有很多機會你會碰到我，我會碰到你，互碰時可以讓情緒很自在地跑出來，當然這時候要有建設性。"),_c('br'),_c('br'),_vm._v(" 我曾跟某位修女吵架，她說：「你沒出息。」"),_c('br'),_c('br'),_vm._v(" 我說：「拜託！不要這樣講。我知道剛才那個行為讓你不舒服，不要針對我這個人好不好？」"),_c('br'),_c('br'),_vm._v(" 吵架也要有建設性，不是針對人，而是講出別人的某些行為帶給我什麼心情，同時這也碰觸到你背景中的經驗，可以把握機會深入探索。"),_c('br'),_c('br'),_vm._v(" 如果我們關係很好，我自然可以讓背景裡的經驗自由跑出來，所以真正建設性的吵架，會很清楚跟對方說：「也許是碰到了我過去不愉快的經驗吧！可是剛才你那個行為讓我很不舒服耶！」"),_c('br'),_c('br'),_vm._v(" 我很清楚，不愉快的感覺的確是碰到了自己背景裡的經驗，不過因為我們的關係很好，所以要講講我的感受。"),_c('br'),_c('br'),_vm._v(" 溝通是我碰到了某項經驗，我要化解自己的感受，而不是改變別人。非建設性的吵架經常是這樣說的：「我已經跟你講了五遍，你還是不改！」這不是建設性的吵架。吵架之所以能溝通，重點是在於衝突是講我的心情、是化解我的心情，以後我們的關係才不會梗在那裡，而不是我講了，你就要改，這不是建設性的溝通。"),_c('br'),_c('br'),_vm._v(" 夫婦吵架常常要求對方改變，把自己跟父母的關係轉移過來，與配偶吵得一團糟。把配偶看成是過去的父母，事實上就是停留在過去。有時候夫妻吵架也是一種投射，一個人說：「你看不起我！」其實是看不起自己，因為矛頭指向自己，不容易說出口，於是改口說：「你看不起我！」比較容易，這就是一種投射作用，什麼都是對方的錯。"),_c('br'),_c('br'),_vm._v(" 我們的心情投射在過去的關係裡，過去父母如何對待我們，現在夫妻一吵，過去的心情跑出來了，然後就用跟父母衝突的方式對待配偶。"),_c('br'),_c('br'),_vm._v(" 事實上，衝突是任何人與他人來往時都會發生的狀況，衝突談不上好壞、對錯，我們可以有衝突，可是必須以建設性的方式跟人衝突。我們慢慢學習以「我的訊息」溝通，就是告訴對方：你的行為帶給我什麼樣的心情。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-6a",modifiers:{"accordion-6a":true}}],attrs:{"block":"","variant":""}},[_vm._v("結婚前，結婚後／個性取向決定你對配偶的期望")])],1),_c('b-collapse',{attrs:{"id":"accordion-6a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("文／任兆璋 http://www.ettoday.com/2002/04/29/157-1296296.htm")]),_c('p',[_vm._v(" 心理學家佛洛姆（Erich Fromm）分析人有四種基本「個性取向」，不同的個性取向決定個人對婚姻的需要，也決定了對配偶的期望。這四種取向的共同點是：為獲取個人內心的安全感，我們會傾向袪除焦慮、沮喪、無聊、空虛、沉悶及罪惡感。"),_c('br'),_c('br'),_vm._v(" 在成長過程裡沒有通過某一階段時，人會用一些方法來得到滿足。在理想情況下，每個人進入婚姻前都需要通過以前的階段。當人沒有通過而必須跟配偶相處時，就會出現某種人格取向。有些人為此付出很大的代價，甚至犧牲美滿的婚姻關係。"),_c('br'),_c('br'),_vm._v(" 一、接納取向"),_c('br'),_c('br'),_vm._v(" 具有接納取向的人，相當於口慾期的人格特質。這種人小時候沒有得到足夠的關愛，母親常常要他等待，現在他就不要孩子等了。他小時候沒被愛夠，所以希望孩子依賴他，基本特性就是「你們都可以來依賴我」。進入婚姻後，配偶可以依賴他，這種傾向的人希望由他人處得到所需的滿足，因此對配偶往往有各種不切實際的要求。"),_c('br'),_c('br'),_vm._v(" 人有兩面性，「我沒得到，我也不給人」或「我沒得到，我就要拚命給人」，就看一個人的取向放在哪面。有的人是沒有得到，就習慣去依賴，這輩子就要依賴配偶了。有的人則覺得自己沒得到，在別人身上看到我的翻版，我很不捨得，就拚命去給。一個沒有得到的人不一定完全依賴別人，也不一定完全給別人。而一面倒的情況就是，我沒得到的就不給、不收，或者我沒得到的就拚命收、拚命給。"),_c('br'),_c('br'),_vm._v(" 我們特別要幫助這種人做一個中庸的人。沒錯，你是沒得到，可是有時候你可以去依賴，有時候就要去給予。只有拚命依賴或拚命給一面倒的時候，才會出問題。"),_c('br'),_c('br'),_vm._v(" 一對青年男女為什麼容易進入婚姻裡？因為我沒有得到，但碰到了一個拚命要給的人，我們倆就相愛了。表面上是相愛了，似乎早年沒有得到的被滿足了。談戀愛時，你關心這位小姐，因為想把她帶回去，等到結婚以後你就不給她了，或是一面倒向她要了，那這個婚姻就要出事情。"),_c('br'),_c('br'),_vm._v(" 二、剝削取向"),_c('br'),_c('br'),_vm._v(" 具有剝削取向的人，屬於肛門期的人格特質。以「在人之上」、「超越他人」而獲得自身的安全感。這種人需要不斷控制他人的力量，不能與他人平起平坐，對任何人際關係──包括婚姻──都構成威脅。這種人的論調是：我總是對的，我是領袖，你差太遠了。這樣的人在戀愛過程裡時常出問題，因為他在成長階段中沒有通過「懷疑」這個部分，總是要站在人家上面才不會懷疑。他必須做領袖，必須比人高、比人行，才不會懷疑自己有問題。"),_c('br'),_c('br'),_vm._v(" 這種人常常是第二個階段幼兒期沒有通過，他在婚姻中常常要配偶聽他的話，連婆婆或岳母都要聽他的。因為自我懷疑，為了證明自己是有能力的人，所以才需要站在別人上面。"),_c('br'),_c('br'),_vm._v(" 一個穩定的人與他人的關係是平等的，就是你有時候領導我，我有時候領導你，大家互惠平等。所以，有這種人格取向的女孩常常要獨身，因為她要壓倒別人，男生若不要被她壓倒，就很難談成戀愛。"),_c('br'),_c('br'),_vm._v(" 做輔導的人就是要去觀察、提醒，看到她可能需要什麼。她應該去陪伴自己裡面受傷的、小小的我？常常懷疑自己的能力不夠，所以需要信心？這種人在幼兒期說「不！」卻常被拒絕，或是問「為什麼？」也被拒絕，所以很懷疑自己的能力。"),_c('br'),_c('br'),_vm._v(" 三、佔有取向"),_c('br'),_c('br'),_vm._v(" 具有這種個性的人，對外界的一切毫無信心，他們的安全感奠基於必須儲存很多東西，覺得任何耗損都是威脅。在這種人的想法裡，愛是佔有，對於自己愛的人往往不願給他們自由與獨立。同時，這種人也很怕被套牢，沒有安全感，總是要霸佔別人。我回家，你就要在家。一個具有強烈佔有性的人，需要愛的保證，不論配偶給多少保證，他永遠無法相信自己會被人愛，主要的癥結在於他童年沒有感覺到被愛。"),_c('br'),_c('br'),_vm._v(" 四、市場取向"),_c('br'),_c('br'),_vm._v(" 這種人視自己在市場中的交易價值而決定自身價值，而不認為自己是獨特、有尊嚴的人。我常常強調，我能做的事，沒有別人能完全取代。你能演講自己的東西，可是我講的東西就是跟別人不同，也就是說要有自己的尊嚴、自己的價值感。依賴市場的評價，他就沒有這份尊嚴，而會依賴：「啊！我演講有好多人聽，可見我講得多麼好！」"),_c('br'),_c('br'),_vm._v(" 不是的！有時候只有一位來賓來聽也是講得很好，就是我能做的，我都做了，這就是很好，而不是我做的事有多大價值。我這個人上天只給我十個元寶，十個都擺出來，那就夠了。假如別人有一百個元寶，那是別人的能量。"),_c('br'),_c('br'),_vm._v(" 當一個人以他人的評價為重，而不重視自我評價時，一旦他期許於市場的價值未能得到，婚姻關係必定受到打擊。他缺乏內在的自我肯定和自我實現，不覺得雖然我做的只是小小一份，但這是我所能給的全部，這就是美，而且很美！每個人都需要這樣的自我肯定，因為結婚以後，先生會碰到更漂亮、更能幹的女人，太太也會碰到更英俊、更瀟灑的男士，我們現在就要欣賞現有的，而且現有的就是最好的。"),_c('br'),_c('br'),_vm._v(" 首先要能欣賞目前有限的我，然後內在才能夠穩定，否則這個有限的我都覺得自己好糟糕！別人都比我行！那麼，我永遠不得安寧，這是十分重要的。每個人在基本的人格傾向上需要有一個根基，如果沒有，婚姻是一件很痛苦的事。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-7a",modifiers:{"accordion-7a":true}}],attrs:{"block":"","variant":""}},[_vm._v("婚前了解為何近親不能通婚?")])],1),_c('b-collapse',{attrs:{"id":"accordion-7a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("http://www.epochtimes.com/b5/5/8/7/n1010427.htm")]),_c('p',[_vm._v(" 目前已知近親通婚最大的危機，就是「隱性遺傳疾病」的問題；台安醫院產前優生遺傳診斷學科主任周輝政表示，簡單地說，近親結婚的男女雙方，由於源於同一血緣，兩人很有可能具有同一種類的不良遺傳因子，這些潛藏在基因中的不良因子，表面上看不出來，卻很可能因為兩人通婚而「湊在一起」，生下發病的下一代。"),_c('br'),_c('br'),_vm._v(" 周輝政說，沒有親戚關係的兩個人結婚，隱性遺傳因子湊在一起的機率很低，但近親通婚的話，一些家族中的遺傳疾病因子，湊在一起的機率就會高出許多。"),_c('br'),_c('br'),_vm._v(" 以優生學的角度來說，近親結婚違反了自然演化所需要的基因複雜性與可選擇性，過去有種說法，一旦一個物種剩下不到五十個個體後，最後勢必絕種。"),_c('br'),_c('br'),_vm._v(" 雖然過去普遍認為有血緣關係通婚，很容易生出有缺陷的孩子或是智力等受影響﹔不過，台大醫院基因醫學部主治醫師蘇怡寧說，近年有越來越多的人認為，除非像是「三等親」以內，如此親近的血緣間通婚，否則並不會增太多遺傳缺陷風險。"),_c('br'),_c('br'),_vm._v(" 蘇怡寧說，有些婆婆甚至連有點遠親關係的媳婦都不太能接受，但是除了一些特殊隱性疾病，可能在家族間流傳外，現在學界漸漸認為，家族間的婚姻，未必會提高畸形或智障等機率。他表示，雖然這樣的觀點目前仍有許多爭議，但相信以後大家會越來越能夠認同這些看法。"),_c('br'),_c('br'),_vm._v(" 殷鑑不遠 達爾文娶表姊 兒女夭折、罹病"),_c('br'),_c('br'),_vm._v(" 〔記者羅碧、黃以敬╱台北報導〕「近親通婚」在現今可能引發法律與醫學爭議，但歷史上，卻有不少近親通婚故事，英國史上統治時間最長的維多利亞女王，就與表哥結婚，古埃及更是公開准許近親婚配，知名的「埃及豔后」克麗奧珮特拉的第一任丈夫，就是自己的親弟弟；甚至知名的生物學家達爾文，也是與表姊艾瑪結婚，兩人所生的十個孩子卻有不幸夭折與罹病問題。"),_c('br'),_c('br'),_vm._v(" 無論在西洋或東方的神話中，許多有關人類的起源，其實就是緣起於兄妹或姊弟通婚，例如聖經中以色列祖先雅各就是娶舅舅的兩個女兒。"),_c('br'),_c('br'),_vm._v(" 台灣原住民神話中也可見到，例如有研究指出，泰雅族神話盛傳，太古之時只有姊弟二人，姊姊很憂慮如何繁殖人類，因此塗黑臉和無法認出自己的弟弟交合，從此人類可以傳宗接代下去，泰雅族也開始有了紋面風俗。"),_c('br'),_c('br'),_vm._v(" 在中國歷史中，商朝嚴格禁止近親通婚，周朝也不許同姓通婚，春秋戰國時代也有「男女同姓，其生不蕃」的不准通婚說法；但兩晉、南北朝後由於戰亂頻繁、人口流動，因此一直到隋唐，貴族門閥允許近親或同姓通婚，北朝甚至曾有父親娶女兒的野史傳聞。"),_c('br'),_c('br'),_vm._v(" 台大歷史系教授黃富三指出，在西方，古埃及為了維持階級制度，允許血親通婚，埃及豔后就是嫁給親弟弟。近親通婚制度甚至因為埃及艷后嫁給安東尼，還傳到歐洲，也由於限於同等通婚，王室間彼此通婚多年，幾乎都有親戚關係；最著名的近親結婚悲劇，大概是英國史上統治時間最長的維多利亞女王因和她的表哥（舅舅的二子 )阿爾伯特結婚，結果生下九個孩子中，有三男罹患血友病，五個女孩也是血友病帶因者，只有一個兒子未發病。 著名的生物學家達爾文也是與他的表姊艾瑪結婚後，生育十次中，有三個孩子夭折，三個孩子不能生育，所剩的四個子女也存在不同其他疾病。 ")])])],1)],1)],1)],1)])]),_c('div',{staticClass:"frame"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s4",attrs:{"id":"s4","name":"s4"}},[_vm._v("婚前新聞")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-11a",modifiers:{"accordion-11a":true}}],attrs:{"block":"","variant":""}},[_vm._v("公益超人？婚前協議？散財9成對象未定"),_c('br'),_vm._v("郭台銘樂捐 時間、方式也不確定")])],1),_c('b-collapse',{attrs:{"id":"accordion-11a","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("http://news.yam.com/tlt/entertain/200807/20080726784873.html")]),_c('p',[_vm._v(" 在結婚前夕，鴻海董事長郭台銘宣稱要捐出九成財產做公益，並且得到新娘子曾馨瑩的簽署同意，是否是一種兩人簽署「婚前協議」的行為？"),_c('br'),_c('br'),_vm._v(" 據了解郭台銘的人士指出，郭台銘有這麼大的財富，未來應該不是留給少數人的，最好是拿出來做公益，他的這個精神與原則，已經獲得準夫人的同意與簽署，可以說是類似「婚前協議」的約定。"),_c('br'),_c('br'),_vm._v(" 目前這個九成財產的捐贈，對象沒有確定，時間也沒有確定，這不可能是一次就到位的。他並舉例，捐贈台大防癌中心150億，也還是有很多細節在研究、討論當中。"),_c('br'),_c('br'),_vm._v(" 褒揚郭台銘"),_c('br'),_c('br'),_vm._v(" 捐了再說"),_c('br'),_c('br'),_vm._v(" 有人找他救流浪狗，有人要他捐圖書館，台大的防癌研究中心也是他出的錢，如今又公開宣稱將捐出個人九成財產做公益，郭台銘似乎成了台灣的公益超人。"),_c('br'),_c('br'),_vm._v(" 不過現在坊間在討論的話題是，郭董說要捐財產做公益，並沒有表明捐助方式及對象，「未來」又是何時？是退休前？退休後？還是生前？生後？"),_c('br'),_c('br'),_vm._v(" 富比世雜誌在今年6月曾估算郭台銘個人身價，以他名下的鴻海持股達71萬張，群創也有將近18.8萬張，總市值就加上其他個人持股，以及透過投資公司持有的股權，換算起來達60億美元，也就是新台幣1,800億元，捐出九成，相當於新台幣1,620億元。然而有意思的是，郭台銘的財產幾乎都是股票，若是捐出九成，鴻海董事會勢必改選。"),_c('br'),_c('br'),_vm._v(" 其次，若是捐給自家基金會，擺明就是節稅，但這一句連捐贈細節都沒弄清楚的豪語，就為郭台銘買到免費的宣傳。"),_c('br'),_c('br'),_vm._v(" 還有，民法規定，配偶可繼承一半的遺產，因此若郭董遵守協議，捐出九成，以目前郭董身價計算，曾馨瑩尚有90億元可以繼承。但是目前郭董的身價1,800億元全是婚前所得，民法規定，婚前財產為個人所有，曾馨瑩並不能主張請求權，至於能分到多少贍養費，就要看郭董的心意了。"),_c('br'),_c('br'),_vm._v(" 郭台銘的豪語滿天飛，媒體應該檢視的是他過去的實質捐贈到底有多少？要褒揚善舉，等真的捐贈出來也不遲。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-22a",modifiers:{"accordion-22a":true}}],attrs:{"block":"","variant":""}},[_vm._v("曾馨瑩婚前焦慮 最怕被問「為何喜歡郭董」")])],1),_c('b-collapse',{attrs:{"id":"accordion-22a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("http://www.nownews.com/2008/07/22/91-2308113.htm")]),_c('p',[_vm._v(" 即將與台灣首富共結連理的「狗狗」曾馨瑩得了婚前急躁症，她的好友向媒體透露，狗狗最怕朋友問她有關郭台銘的事情，尤其是「為什麼會喜歡郭董？」讓拒絕與「拜金女」劃上等號的她十分困擾。 "),_c('br'),_c('br'),_vm._v(" 據自由時報報導，曾馨瑩會用MSN跟好友分享近況，但跟郭董相戀後，她開始出現焦慮情緒，面對大家詢問戀情進度感到無法招架，加上婚期在即，狗狗就更是無所適從。 "),_c('br'),_c('br'),_vm._v(" 相對梁朝偉與劉嘉玲的豪華世紀婚宴，郭董跟狗狗表明婚禮絕不鋪張，將走簡樸路線，外傳是因為郭台銘不願被媒體拿來跟劉嘉玲比較，但據了解，郭董仍會另外設宴招待好友，大肆慶祝第二春來臨。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-33a",modifiers:{"accordion-33a":true}}],attrs:{"block":"","variant":""}},[_vm._v("吳建豪婚前拒嘗禁果 辣妹勸酒催情 念阿門煞車")])],1),_c('b-collapse',{attrs:{"id":"accordion-33a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("http://tw.news.yahoo.com/article/url/d/a/080802/57/d9s.html")]),_c('p',[_vm._v(" F4的吳建豪（Van Ness）去年11月受洗為基督徒，今年3月向上帝承諾1年內不交女友，並拒絕婚前性行為，發誓到婚前都「停機」。他坦承期間曾有撒旦的誘惑出現，靠心裡猛念「阿門」拒美女於千里之外，現在都靠自己解決生理需求。"),_c('br'),_c('br'),_vm._v(" 受洗成基督徒 謹守誓約"),_c('br'),_c('br'),_vm._v(" Van Ness表示，去年11月經紀合約到期，加上即將邁入30歲大關，碰到瓶頸，回到美國遇見開導他的年輕牧師，於是受洗。今年3月，他正式向上帝承諾不再有婚前性行為，他現在天天帶著寫有「婚前不能發生性關係」戒律的卡片在身上，他說：「其實我沒正式與上帝簽這條約，但我隨身攜帶，提醒自己。」"),_c('br'),_c('br'),_vm._v(" 戒律卡隨身帶 時時提醒"),_c('br'),_c('br'),_vm._v(" 向上帝承諾後，他之前在美國朋友的生日派對的酒吧中，結識身材姣好的墨西哥籍性感調酒妹，兩人十分聊得來，對方還不斷請他酒喝，他內心動盪不已，但最後還是煞車，笑說：「我相信上帝會幫我安排一個身材好，對外個性單純，但關上門很火辣的女生。」"),_c('br'),_c('br'),_vm._v(" 新書寫情事 徐若瑄寫序"),_c('br'),_c('br'),_vm._v(" 他把成長歷程和情事都寫在新書《夢．遊Daydreamer》中，雖然請前女友徐若瑄寫序，但在書中完全避談他和徐若瑄的那段情，他表示，徐若瑄對他是很重要的人，但他這回不想讓媒體有做文章的機會，既然如此，何必請徐若瑄寫序？他說：「寫序的人大半都跟我傳過緋聞，不只她，還有蕭亞軒和大Ｓ呀！」 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-44a",modifiers:{"accordion-44a":true}}],attrs:{"block":"","variant":""}},[_vm._v("擔心離婚分家產 澳伴侶流行簽署婚前協議")])],1),_c('b-collapse',{attrs:{"id":"accordion-44a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("http://www.dwnews.com/big5/MainNews/Topics/cna_2008_08_17_17_43_05_381.html")]),_c('p',[_vm._v(" （中央社記者陳明隆雪梨十七日專電）擔心一旦鬧離婚分家產﹐導致荷包嚴重失血的富豪名人﹐選擇簽署婚前協議消息時有所聞﹐這股潮流也逐漸為澳洲人接受﹐只不過﹐這些簽署婚前協議的伴侶﹐不僅想保障自己的身家﹐還包括生活模式。"),_c('br'),_c('br'),_vm._v(" 「周日電訊報」引述澳洲負責處理婚姻關系的律師指出﹐近來簽署婚前協議已經不單單屬于富豪名人的專利﹐尤其高達三成三的離婚率﹐讓許多民眾更實際的看待婚姻關系而選擇簽署婚前協議。"),_c('br'),_c('br'),_vm._v(" 不過﹐許多選擇婚前協議的伴侶﹐除了單純的要求如何區分「家產」﹐同時要求的額外附加條件更是包羅萬象﹐包括﹕另一半婚後不能變胖多少﹑必須對婚姻保持忠誠﹑誰負責打掃廁所等。"),_c('br'),_c('br'),_vm._v(" 家庭律師文森表示﹐事務所最近處理的婚前協議的個案﹐比五年前多出許多﹐她指出﹐選擇婚前協議的夫妻﹐通常不是一方年紀較大想保障自己的資產﹐要不就是兩人都曾經經歷離婚低潮﹐想藉此保障自己生活。"),_c('br'),_c('br'),_vm._v(" 另一名家庭律師潔佛瑞絲也認為﹐由于愈來愈多的民眾比過去更懂得法律常識﹐並且對于永恆的婚姻關系不抱持過高期望﹐導致他們步入禮堂前﹐通常選擇更務實的態度面對婚姻。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-55a",modifiers:{"accordion-55a":true}}],attrs:{"block":"","variant":""}},[_vm._v("婚前救急 嘉玲素顏狂操 慢跑回春")])],1),_c('b-collapse',{attrs:{"id":"accordion-55a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 梁朝偉、劉嘉玲以「721不丹世紀大婚」結束20年愛情長跑，相愛20年，感情和容顏同樣得歷盡時間考驗！今天出刊的香港東方新地報導指出，42歲的劉嘉玲為迎接「歷史時刻」，婚前加緊鍛練，在她和梁朝偉準備飛往曼谷當天早上，週刊記者拍到她爭取時間素顏跑步，並大量使用各種美容聖品希望讓自己儘速回春。"),_c('br'),_c('br'),_vm._v(" 大寶法王擔任主婚人"),_c('br'),_c('br'),_vm._v(" 昨天澤東公布兩人的婚紗照，一對新人果然神采飛揚，東方新地指出，婚禮真正主婚人其實是劉嘉玲信奉的印度十七世大寶法王噶瑪巴。去年偉仔、嘉玲因郭台銘事件讓感情驟起風波，其後劉嘉玲曾隨王菲到印度覲見法王，獲法王開示後，心結得以解開，戀情終開花結果，婚期及婚禮地點，據知亦是法王所建議。"),_c('br'),_c('br'),_vm._v(" 東方新地指出，二人結婚用品極盡考究，「偉玲」所預備的100份回禮，是一個銀製藥盒，印有宗教符號及鑲有彩石，經大寶法王加持，內有兩顆「神丹」，聲稱有起死回生之效，無事則可保平安，週刊指出，姑且不論是否真有神效，「偉玲」因宗教而有所領悟終修成正果，兩人也希望借助佛法向好友們獻上祝福！"),_c('br'),_c('br'),_vm._v(" 劉嘉玲跑步動作生硬"),_c('br'),_c('br'),_vm._v(" 東方新地報導，上周三（16日）由香港啟程飛曼谷當日，臨飛前，嘉玲爭取時間，一大早9點就在山頂豪宅附近跑步半小時。不施脂粉的她，身穿運動服，簡單束起頭髮，與平日的雍容華貴判若兩人，從她跑步動作有些生硬看來，習慣夜生活的劉嘉玲，應該是最近才開始晨跑，明顯是婚前臨急狂操。"),_c('br'),_c('br'),_vm._v(" 偉仔穿不丹皇叔禮服"),_c('br'),_c('br'),_vm._v(" 「偉玲」在不丹結婚成為當地一件盛事，不丹王室更特別打破慣例，提供皇宮的寢室給新人拍婚紗照，並由不丹皇叔夫婦（即將登基國王之叔）親自為這對新人著裝，據了解，偉仔的禮服正是當年皇叔結婚所穿的禮服，而嘉玲所穿的禮服則是王妃他們為年僅兩歲的女兒日後出嫁所準備的嫁衣。"),_c('br'),_c('br'),_vm._v(" 這次統籌婚禮的是準新娘劉嘉玲，她表示，其實去年與偉仔已選定今年的7月21日完婚，但決定在不丹舉行婚禮，是直至今年4月份才決定，她說：「我和偉仔討論過很多地方，例如法國南部、佛羅倫斯、巴黎、日本和東南亞等地，但都感覺「少了一點什麼」。」嘉玲指直至不丹這地方出現，他們終於心領神會，當下決定這是現階段最理想的婚禮地點，也是兩人展開另一階段人生的最佳起點。"),_c('br'),_c('br'),_vm._v(" 王菲抵曼谷造成混亂"),_c('br'),_c('br'),_vm._v(" 另外，香港星島日報昨日也報導，不丹移民局正四處搜尋赴不丹採訪的各地傳媒，據悉已有3位記者被驅逐出境，不丹移民局是拿著部份香港記者的名單在酒店及會場搜尋，香港記者與不丹移民局就像捉迷藏，捉到便會趕出境。另外，昨早十一時，王菲與李亞鵬及女兒竇靖童一家三口從曼谷坐私人飛機往不丹，一家三口出現曼谷機場時引起一陣混亂。 ")])])],1)],1)],1)],1)])])])])])])])}
