                                <br />
                                家庭中，丈夫的長篇大論通常是用來「說理」的，妻子的長篇大論則是用來「表達情緒」的；但女人常不愛聽男人動不動就說理，男人也不愛聽女人以抱怨表達情緒。兩性名著《男人從火星來，女人從金屋來》的作者葛瑞（John&nbsp;Guy）博士說：「女人在難過時，並不希望聽到一大串理由說明她為何不該難過；男人也並不希望聽到一大串理由，要他滿足她的要求。」喋喋不休，會變成「你關心，他煩心」，或讓自己覺得「狗咬呂洞賓，不識好人心」。<br />
                                <br />
                                與對方的利害關係有關，話說得越多，越會遭到反持。男人或女人的喋喋不休，無論為了什麼理由，常成為關係僵化的「快速冷卻劑」。<br />
                                <br />
                                多數女人難過時，她需要他專注地傾聽，而不是他斬草除根、迫不及待地解答，那會讓她覺得雪上加霜。女人以不太直接的語句提出要求時，也可能會激起男人的反感。在一般婚姻生活的對話上，有很多實例，比如：女人不直接說：「今晚我們出去吃飯嗎？」而會說：「我今天做了一大堆事，累都累死了，實在沒力氣做飯，你今晚總該可以帶我吃飯吧！」萬一她有事，沒法到保姆家階孩子，她不會簡短的問：「你去接孩子好嗎？」可能會以一大串牢騷開頭：「每一次都是我去接，你是孩子的爸，偶爾去接一次，沒問題吧？」就算男人樂意，聽到這樣的喋噪不休，也會變成心不甘情不願。<br />
                                <br />
                                「簡短乃極智之靈魂」——就兩性之間而言，要求、勸告、爭論時，簡短也是愛情的平安險。喋喋不休，只會使人的耳朵因惰性而遺失重點，而不該進去的話，則鑽進心裡煽風點火。<br />
                                <br />
                                平常愛喋喋不休的人講的話，一定不受重視，因為，別人一定認為你會再講一遍。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-5 variant=""
                        >太熟悉的愛情只剩習慣？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-5"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <h4>作家:筱筑</h4>
                            <p>
                                <span style="font-weight:bold;">不用溝通不用交談，這是彼此已太過了解對方，還是早已無話可說？</span><br><br>
                                遠遠的，看到了一對年約４０多歲的夫妻一道走了進來，一坐定之後彼此各自點了一道菜，然後男人開始整理身上證件、發票與許多小單據，女人則靜靜的翻看放在桌上的雜誌約五分鐘後，菜上桌了，僅是簡單的兩碗什錦湯麵。男人收起了正在整理的小東西開始專心吃麵，女人則一邊用餐眼睛仍盯著桌上的雜誌看。<br />
                                <br />
                                就這樣３０分鐘，彼此不發一言一語，然後一起走出了餐廳。<br />
                                <br />
                                整個過程我一點一滴的看在眼裡，坐在身旁的朋友體貼的問看什麼看的那麼專心，還不時幫我夾菜，怕我吃不夠或吃不好，望著桌上這麼豐富的佳餚與朋友的殷切關懷，沈醉在幸福中的我突然有一種失落感罩上心頭，有句廣告詞好像是這樣說的：<br />
                                <br />
                                當生活中只有習慣，我們的愛情還剩下什麼？<br />
                                <br />
                                多年以後，我的愛情與婚姻會不會也像那一對夫妻一樣？因為要吃飯所以一起坐了下來，只要吃飽了就可以，所以吃的簡簡單單，不用溝通不用交談，這是彼此已太熟悉還是早已無話   可說？當生活中不再有爭執，是一種幸福還是一種可悲走出餐廳，我問朋友：你會每頓飯都對我這麼體貼嗎？<br />
                                <br />
                                〞當然〞他笑笑的摸著我的頭，我又問：那你會一輩子都對我這麼好嗎？<br />
                                〞當然〞他深情的點點頭。<br />
                                <br />
                                即使現實生活中總是漫長而殘酷的，但人們還是那麼輕而易舉的相信愛情，相信婚姻，明知這樣的答案有一點浪漫與天真，我仍深信自己會是那個不一樣的幸福女子！
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-6 variant=""
                        >人沒變，變的是感覺</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-6"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <h4>作家:劉墉</h4>
                            <p>
                                放暑假，兒子回家住，成天窩在他樓上的臥室裡。<br><br>
                                「也不知道兒子在幹什麼？」有一天，我對太太說：「讓我上去瞧瞧。」「你千萬別去！」太太居然攔我：「免得生氣。」「為什麼？」「因為亂得跟狗窩似的。」「真的啊！」我伸伸舌頭：「不曉得他在宿舍裡，是不是也這麼亂。」<br><br>
                                「也亂！」「也亂？他不是有女朋友嗎？」我說：「女朋友去，會不幫他收拾？」 太太一笑：「我早問過了，你知道兒子怎麼答嗎？他說女朋友的房間比他的還亂。」   「哎呀！年輕人的房間沒有不亂的。」<br><br>
                                一個朋友聽我抱怨，拍拍我：「放心！等他結婚，自然就好了，不好也得好了，否則就真不好了。」 「這是什麼意思？」我不懂。   「很簡單，當他進入社會，常有朋友到家來。有人來，能不收拾嗎？就算沒人來，人去上班，在外頭忙，情緒一定亂，回到家只希望清清爽爽的，如果再看到一團亂，兩個人非翻不可。」   他這話說得一點沒錯，我有次去個親戚家，兩口子正鬧彆扭，丈夫把我拉到裡面，打開臥室門，大聲問：「你看看！家！有這樣的嗎？」   他太太在外面聽到了，立刻拉著嗓子喊：「我從年輕就這樣，你怎麼不嫌我啊？搞到今天，看我什麼地方都不順眼。」<br><br>
                                丈夫一下子被堵住了，停了幾秒，吼回去：「此一時也，彼一時也！妳不是大小姐了，妳是太太！」 「那你就去找太太，」太太又吼了回來：「我現在還是大小姐！」   我有個老朋友，最近離婚了，理由很抽象──太太嫌他太「愛現」。   只是，我這個朋友，跟我相交半世紀了，我從沒見他不愛現的時候。<br><br>
                                小學，他為了現給女生看，能背整本「小學生字典」；中學他去教堂「現」，聖經上用紅藍綠筆畫得比牧師還多；大學，他跟我學畫，能整夜不睡覺地趕工，只為了要讓我驚訝他能畫得又快又好。進入社會，他演電視劇，不演戲的時候，走在街上還是現，逗得人人都盯著他看。<br><br>
                                他跟他前妻也是在街上認識的。「他是個甘草人物，團體裡有了他，就有生氣了。」記得二十年前，他女朋友在婚前對我說：「他的才華，沒話說。」他的才華是沒話說，所以能把那傑出的女朋友，由馬路上吸引到臥室裡，成為他的妻。只是，曾幾何時，他的愛現竟成為他的致命傷。會不會也是「此一時也，彼一時也」。<br><br>
                                因為成了丈夫，就應該老成持重，不再活潑愛現呢？   太太不收拾，丈夫可能會抱怨，妙的是，老婆如果太愛收拾，丈夫也可能怨。有個朋友最近總對我訴苦：「現在啊！我沒覺得有老婆，只覺得有個菲傭，每天吃完晚飯，她能用兩個鐘頭在廚房裡擦擦洗洗，擦洗完了，也累垮了，洗個澡就自己去睡了。結果你知道嗎？下班回家，跟她說句話的時間都沒有。連放假，都不閒著，東掃掃、西擦擦，搞不好，哪天把我都當垃圾掃出去了。」<br><br>
                                我怔了一下，笑問：「我只記得十幾年前，常聽你讚美你女朋友多愛乾淨，說到她家，發現她家裡一塵不染，什麼東西都安排得井井有條。還說你一向鼻子敏感，可是到她家全好了。我問你，你是不是說過？」   他點頭。「於是你娶了她，而且到現在十幾年，鼻子都不敏感了，對不對？」我再問。他又點頭。<br><br>
                                「那時候，你覺得有潔癖是她的優點，為什麼現在又覺得是缺點了呢？」我問：「是她改了，還是你變了？」   人總是在變，   此一時也彼一時？每個人都總在變，即使身體沒變，感覺也可能變。<br><br>
                                好比在火車站，別的車子移動了，你卻覺得是自己的車在動。當自己的車移動時，又錯以為另一輛車在動。   一切都沒變，變的可能是感覺！人多難捉摸啊！   一個吸引你的妖嬈女子，在成為你老婆之後，那妖嬈就可能被你譏為淫賤。<br><br>
                                一個被妳讚賞為急公好義的人，成為妳的丈夫之後，就可能被妳評為好管閒事。一個豪放大方的朋友，成為你的職員之後，就可能被你責備「公私不分」。一對相戀的情侶，結婚之後，一切都沒變，卻可能「相愛容易，相處難」，使婚前一切的優點，都變為對方眼中的缺點。為什麼不想想，妳當初為何看上他？<br><br>
                                為什麼不想想，如果他真不灑脫了、不再整齊了、不再逗趣了、不再豪情了…他還是不是妳原來心中的那個他？當你們彼此說對方變了的時候，會不會兩個人都沒變，變了的只是那種感覺、那點情懷、那分默契？
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-7 variant=""
                        >生命中註定的另一瓣貝殼</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-7"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <h4>作家:Davie</h4>
                            <p>
                                <span style="font-weight:bold;">在感情上，當你想征服對方的時候，實際上已經在一定程度上被對方征服了。</span><br><br>
                                首先是對方對你的吸引，然後才是你征服對方的欲望。<br />
                                戀人之間，宜小別，不宜長別；朋友之間，宜長別，不宜小別。<br />
                                愛情是自私的，因為自私就容易產生嫉妒，不論是男人還是女人，有一點嫉妒心是可以理解的；但是若嫉妒心太強，不是使自己變得可憐，便是使自己變得可笑。<br />
                                男人為痛苦流淚說不上剛強，男人若為幸福流淚，卻顯得俠骨柔腸。<br />
                                過早戀愛或過遲戀愛都不太好。<br />
                                太早戀愛，因為不懂得愛，往往糟蹋了愛；太遲戀愛，則會影響愛的純潔度。<br />
                                異性之間，戀人比朋友容易反目，這是戀愛關係不如朋友關係的地方；朋友沒有戀人親近，這是朋友關係不如戀人關係的地方。<br />
                                如果愛上，就不要輕易放過機會。<br />
                                莽撞，可能使你後悔一陣子；怯懦，卻可能使你一輩子後悔。<br />
                                沒有經歷過愛情的人生是不完整的，沒有經歷過痛苦的愛情是不深刻的。<br />
                                愛情使人生豐富，痛苦使愛情升華。<br />
                                有一句俗話說：男人追女人隔座山，女人追男人隔層紗。<br />
                                盡管如此，實際生活中男人往往能追到他喜歡的女人，女人卻常常得不到她喜歡的男人。<br />
                                原因是﹕男人不怕翻山越嶺，女人卻怕傷了手指頭。<br />
                                初戀，不一定能夠成功，卻是幸福的；婚姻，已是一種成功，卻不一定幸福。<br />
                                難忘初戀，珍重婚姻。<br><br>
                                好男人都是別人的男朋友，如果妳有這種症狀，永遠覺得別人的總是比較好，而自己缺乏探險的勇氣，那麼，出社會時妳也很容易會發現，好男人總是別人的老公。<br />
                                所謂：親愛的，別人的.... <br />
                                你在尋找什麼樣的人？這個問題很簡單。<br />
                                你在尋找一個你喜歡的人，而人的喜歡是很直覺的，如果你想跟這個人來往，跟他來往一段時間後，在心情上覺得很舒適，那麼他就是一個你喜歡的人。<br />
                                每個人喜歡的人都不一樣。問你自己，不要問別人。<br />
                                不要像對媒婆開條件一樣，條件是機械性的，是非人性的。<br />
                                沒有找不到好男人好女人的人，但求人之前請先求己，何謂一見鐘情：妳遇到了一個生命中素昧平生的人，忽然間，他的眼睛那麼清澈；忽然間，有一股熱流在你的心臟中昇起；只那麼一瞬間，世界好像不一樣了！<br />
                                是的，這個人世界存在這麼一個人，和你毫無血緣關係，但是偏偏注定讓你歡喜讓你憂。<br />
                                很多人有一見鐘情的感覺，但並不是每個人都有機會和一見鍾情的人談戀愛。<br />
                                因為一個巴掌拍不響，要兩相情願才行。<br />
                                過去人戀愛，想開口還怕門不當戶不對。顧慮會不會失敗，會不會傷及自尊，顧慮這個那個，其因而錯失姻緣的不知凡幾。<br />
                                不知有多少人最後只好靠相親結婚？<br />
                                你願意抱憾以終？不，當然來自己來得好。<br />
                                如果失敗，那也是屬於你的失敗，雖敗尤榮。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-8 variant=""
                        >獻給在愛情中迷惘的人</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-8"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <h4>作家:Angel</h4>
                            <p>
                                不知是從何時開始的……<br><br>
                                <span style="font-weight:bold;">原來，感情也是會欺善怕惡的?</span><br><br>
                                也許，知道他已經離不開我，知道他真的愛我，所以我開始折磨他。習慣是可怕的事，就像我越來越任性的恣意放縱我的喜惡，他也越來越能夠忍讓，一切看起來都是如此的match~<br />
                                <br />
                                我幾乎要忘記，當初，我也是深深愛戀過他啊！?<br />
                                我們就這樣，一直的習慣下去，不知不覺，很多夏天過去了；我的生活中習慣有他，他也習慣有我，就像呼吸一般自然。這，是不是「愛」？誰也不敢說是，也不敢說不是……<br />
                                <br />
                                昨天，我們按照習慣一起窩在沙發上看影碟，正好結束，轉到週末習於看的節目，多年來的習慣，終於有了點變化。是一個悲傷的愛情故事，一個失去愛人的男子，訴說著以往……癌症帶走了他深愛的妻子，他說，從不知道，原來，好好的一個人，燒得只剩下一堆骨頭……看著看著，竟痛哭失聲，眼淚有一發不可收拾的慘烈，他可能早就預知我的淚水，抱著哄著……在他懷裡，我恣意的哭著。<br />
                                <br />
                                電視上的男子，失去了所愛的人，唱出的歌聲，不成調。我，有著他，卻常常為著一些小事計較著。忽然，我感到自己竟是如此踐踏著，忽略著自己的幸福。<br />
                                <br />
                                窗外下著雨，我們撐著傘，去還影碟，順便照顧我疲累的肚子。走在雨中，牽著我的是他溫暖的，我習慣已久的手。雖然我的眼中仍然有淚，我想，在看到別人失去的，不完全的；才能了解，自己所擁有的，是多麼彌足珍貴。我們所不在意的，也許是人家眼中的—天堂。<br />
                                <br />
                                我從不知道自己的幸福，長得什麼樣子，也曾經將玻璃誤認為永恆的鑽石，當玻璃摔碎割手時，才知道，玻璃的不堅固與自己的愚蠢。還好，我的幸福仍未遠離，所有的疑慮，在平凡卻甜蜜的生活中，一攬無疑。<br />
                                <br />
                                我想，我再也不會眩惑於廉價玻璃所發出人造的光。因為，我擁有鑽石，只是我一直未曾珍惜。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                    <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-9 variant=""
                        >憂傷是復原的第一步</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-9"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <h4>作家:吳幸宜</h4>
                            <p>
                                你所有的希望和夢想，寄託於和所愛的人分享，而今，你卻失去了他。這是人生中最最痛苦的經驗。<br><br>
                                憂傷是從失戀中痊癒的一個正常且必經的過程。可以確定的是，這個過程不會永無止境。但是，讓這過程保持前進是相當重要的事。<br><br>
                                憂傷，代表所有因個人失戀而來的情感經驗。要結束你的憂傷，首要知道就是告訴自己：「我已經受傷。我有個複雜的感覺，把感覺表達出來是一件重要的事。」<br><br>
                                憂傷是循環式的過程。當你的情緒已經沉到谷底，就是進入最遭的狀況。不過，也是向上攀升的轉折點。<br><br>
                                在憂傷階段的晚期，寬恕是你要去做的重要抉擇，也是你揮別責難的最後一個步驟。這意味你不在需要抓著某一個人，要他為你的情況與感情負責任。寬恕可以讓己從責難別人的情緒中解放出來，讓你過得更好。<br><br>
                                如果你能看見自己復元的道路，你一定會選擇它。看見處於每個憂傷階段中的自我，是你以更快速度走出憂傷的第一步。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                    </b-card>
                    <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-10 variant=""
                        >冒險讓你更有勇氣</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-10"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <h4>作家:吳幸宜</h4>
                            <p>
                                小蔓婚後為了全心照顧小孩，把工作辭了。現在小孩已就讀小學，她開始有較多自己的時間，只是始終擔心自己已經離開職場十幾年了，恐怕不好找工作。<br><br>
                                另一方面，她也想去上課充實自己，可是自己沒有收入，先生不知道同不同意？而且小蔓又認為自己年紀大了，恐怕也學不來，所以還在猶豫當中。有時，她也會想打電話給以前的好友，只是內心馬上有個聲音告訴她：「別人都很忙，哪有空跟你閒扯！」<br><br>
                                先生看到小蔓不開朗，常建議她去爬山或運動，她的回答往往是「好啊！可是......」於是很多事就不了了之。<br><br>
                                一天，小蔓在電視看到一名癌症患者即使在生命的最後一刻仍能夠盡情去生活時，她感動的掉下眼淚，她發現，原來自己太過保護自己，總是想像可能的威脅或傷害來避免真正的冒險或行動。終於她知道了真正讓她裹足不前的，不是事實本身的可怕，而是自己不自覺的否定性想法。<br><br>
                                如果你也一樣習慣性地潑自己冷水、害怕改變，想要找回生命該有的熱情與勇氣，可嘗試以下的方式：<br><br>
                                <ul style="margin-bottom:0px;">
                                    <li>每天做點小改變，如走不一樣的路回家。</li>
                                    <li>觀察內心的想法，每當對事情有一些可怕的想像時，提醒自己停止嚇自己。</li>
                                    <li>觀察並記錄自己自我否定的想法，過一段時間再檢視，或找好友討論。</li>
                                    <li>重新定義成敗，如只要有學到東西就算成功。</li>
                                    <li>嘗試與自己個性不同的行為，如堅強的學習去撒嬌。</li>
                                    <li>少一點盤算與準備，信任自己的自發性與衝動，去做些想做又還沒做的事。</li>
                                    <li>跟家人練習說「我想對你說，但又不太敢說的是......」。</li>
                                </ul>
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                    </b-card>
