                    <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-button block v-b-toggle.accordion-22 variant=""
                        >玩得起愛情遊戲嗎？</b-button
                        >
                    </b-card-header>
                    <b-collapse
                        id="accordion-22"
                        accordion="my-accordion"
                        role="tabpanel"
                    >
                        <b-card-body>
                        <b-card-text>
                            <p>
                                <span style="font-weight:bold;">假如你突然賺到一筆小外快，而它對你來說非常重要，那麼你會把錢放在房間裡的什麼地方呢？</span><br><br>
                                <ul>
                                    <li class="letter-s">雪櫃裡面</li>
                                    <li class="letter-s">書房抽屜裡</li>
                                    <li class="letter-s">牆上掛著的畫後面</li>
                                    <li class="letter-s">書架上的書與書之間</li>
                                </ul>
                                <span style="font-weight:bold;">a.雪櫃裡面</span><br><br>
                                顯然你很清楚地知道自己身在哪裡，在做什麼，玩歸玩，正經事還正經事，絕對不會混為一談。你遇上強烈吸引你的對象時，會乾脆地去玩玩。但你心裡非常清楚不可以玩火，所以你是那種就算試新鮮的東西以後會立刻回頭的人。<br><br>
                                <span style="font-weight:bold;">b.書房抽屜裡</span><br><br>
                                你是個很率性、跟著感覺走的人，喜歡新奇、有趣的事物，遇上了和你平常不一樣的生活版本模式時，很容易就會投身去玩一玩、開開眼界。你遇上浪漫豔遇時，很快地就能融入氣氛、ＨＩＧＨ在激情裡。而當激情一過感覺沒了時，你冷下來的速度也和投入的時間一樣快，立刻轉頭讓自己回歸到原始的正常生活中。<br><br>
                                <span style="font-weight:bold;">c.牆上掛著的畫後面</span><br><br>
                                你是個感情豐富的人，對你這樣一個重感情的人來說，一旦開始了，就會慢慢地陷進去，分不清這段戀情究竟是真是假，甚至癡情到無可自拔的地步。你對浪漫、刺激沒有抵抗力，即使你已有了交往甚久的男、女朋友，但在遇上具吸引力的異性時，你會拋開一切。<br><br>
                                <span style="font-weight:bold;">d.書架上的書與書之間</span><br><br>
                                你是一個相當理性的人。在別人眼中看起來是個堅持自己原則、潔身自愛的人。所以，即使有豔遇降臨在你身上，你亦絕對不會讓自己跳下去玩一場沒有保障的愛情遊戲。不過，這樣的你雖然喊著「拒絕一夜情」，但你心底真正的潛在欲望可是比別人都來得強烈。
                            </p>
                        </b-card-text>
                        </b-card-body>
                    </b-collapse>
                    </b-card>
                    <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-button block v-b-toggle.accordion-33 variant=""
                        >你會發生外遇嗎？</b-button
                        >
                    </b-card-header>
                    <b-collapse
                        id="accordion-33"
                        accordion="my-accordion"
                        role="tabpanel"
                    >
                        <b-card-body>
                        <b-card-text>
                            <p>
                                你在海上發生了意外，被海浪沖到一個荒島上去，醒來後看到旁邊也有個人，你希望是誰？<br><br>
                                <ul> 
                                    <li class="letter-s">有生活能力的啞巴</li>
                                    <li class="letter-s">好吃懶做，但是會講笑話給你聽的人</li>
                                    <li class="letter-s">喋喋不休，但是會服侍你的人</li>
                                </ul>
                                <span style="font-weight:bold;">a.有生活能力的啞巴</span><br><br>
                                你的觀念很傳統，行為也很保守，會遵守社會道德的規範，對於男女之間的界線劃分的很清楚，不倫或外遇的事情，絕對不會發生在你的身上，同時，你對於另一半的要求也是很嚴格的，簡直就像是虔誠的教徒，無論如何都要嚴守記律一樣，不容許出一點點的差錯。<br><br>
                                <span style="font-weight:bold;">b.好吃懶做，但是會講笑話給你聽的人</span><br><br>
                                對於外遇這一檔事，你是既期待又怕受傷害，你不會主動去招惹別人，或是故意製造這樣的事件，但是如果因為環境或外力的因素，產生了一些曖昧不明的關係時，你往往會禁不起誘惑而掉入外遇的深淵，但是在事發之後，又會責怪自己不夠堅持。<br><br>
                                <span style="font-weight:bold;">c.喋喋不休，但是會服侍你的人</span><br><br>
                                你不願意給承諾，無法給對方安全感，外遇機率非常高，是危險份子，和你在一起就必需承受你朝三暮四的個性，不能忠實的面對另一半，可說是你的慣性，無論表現的多麼真誠，也都只是一時的，你總是不能控制自己的情慾，忍不住要到處拈花惹草。
                            </p>
                        </b-card-text>
                        </b-card-body>
                    </b-collapse>
                    </b-card>
                    </div>
                </div>
            </div>
            <div class="frame">
              <div
                data-bs-spy="scroll"
                data-bs-target="#list-example"
                data-bs-offset="0"
                class="scrollspy-example"
                tabindex="0"
