                <h2 id="s4" name="s4" ref="s4">輔導書籍</h2>
                <div class="accordion" role="tablist">
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-11a variant=""
                        >你在做什麼？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-11a"
                      visible
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <h4>
                                原文書名 ：What Are You Doing？－How People Are Helped Through
                                <br>
                                作者：威廉．葛拉瑟（William&nbsp;Glasser）<br>
                                譯者：譚智華、李瑞玲
                            </h4>
                            <br>
                            <img src="@/assets/images/MD1.jpg" class="img-fluid mb-2" alt="家庭暴力">
                            <p>
                                <span style="font-weight:bold;">書籍簡介</span><br><br>
                                成功改變自我、婚姻、親情的真實故事<br><br>
                                本書試圖填補現實治療的理論和實際之間的鴻溝，它包括現實治療案例、理論、步驟與習作。<br><br>
                                本書中所選的案例，均由現實治療師執筆，內容呈現各種你我可能會遭遇到的問題，例如：青少年叛逆、老人安養、子女教養、婚姻觸礁、情侶分手、學生教育和智障、精神病訓練治療……等等。<br><br>
                                透過這些現實治療案例，以及治療師所採用的步驟．人人都能學到如何審視自己在做什麼，並找出更滿意的生活方式。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-22a variant=""
                        >遠離危險關係</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-22a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <h4>
                                原文書名：Dangerous&nbsp;Relationship<br>
                                作者：諾莉．尼爾森<br>
                                譯者：楊芷菱
                            </h4>
                            <br>            
                            <p>
                                當瑪莉第一次與約翰邂遘，約翰只是替瑪莉檢起一封信並親吻她的手，約翰熱烈地追求瑪莉，瑪莉覺得彷彿置身於愛情小說的情節中，瑪莉卻也料想不到此是情感暴力的開始。<br><br>
                                鮑伯調任新部門，凱倫拿著熱咖啡到鮑伯的辦公室，交叉穿著短裙的迷人雙腿坐在桌邊，以極性感挑逗地對鮑伯說：「咖啡讓人發燙，希望你也一樣。」鮑伯陷入致命的吸引力而毫無招架之力，一步步陷入親密關係的網羅，成為凱倫操控轄制的受害者。<br><br>
                                泰莉初到公司上班，什麼都不懂，安看在眼裡，立刻主動協助，有如老鳥教菜鳥，泰莉甚感窩心，於是無論上下班都是好朋友。直到安提議住在一起，且未經泰莉的同意即主動提前為泰莉解除租約始，泰莉逐漸陷入安的暴力控制仍不自知。
                            </p>
                            <br>
                            <img src="@/assets/images/MD2.jpg" class="img-fluid mb-2" alt="家庭暴力">
                            <p>
                                <span style="font-weight:bold;">書籍簡介</span><br><br>
                                愛情的七大警訊<br><br>
                                熱戀中的男女常被愛情沖昏了頭，以至於識人不清，等到婚後、同住後才如夢初醒。有大吵大鬧者，有「相敬如兵」者，甚至形成施暴者－受虐者的關係。<br><br>
                                親密關係中的暴力行為已經成為社會上屢見不鮮的犯罪事實，坊間有許多幫助受虐者的單位團體及類似書籍，但這些幫助幾乎都著重在事後治療及應對。本書則是從全新的角度出發，是一本易讀的實戰手冊，讓讀者在暴力行為產生之前，預先查知警訊，及早做好安全準備，以免誤上賊船、遇人不淑、交友不慎。<br><br>
                                作者以多年的輔導經驗，歸納出暴力者七大警訊：一,激烈的開始、二,占有欲、三,轉變、四,責怪、五,言語侮辱、六,冷漠、七,過去和現在的暴力。<br><br>
                                本書簡潔清楚，以四個生動實例為主軸，像是一位知心的朋友，帶著讀者審視自己親密關係中的危險警訊。<br><br>
                                事實上，親密關係中的暴行所帶來的傷害非常巨大，不但迫害當事人，糟蹋了這份情感關係，而且間接傷害到身邊的親人及整個社會。本書的作者身為一位知名臨床心理學家，面對過無數的類似心痛故事，幫助這些受虐者修復傷口，正因為如此，作者深切感受到預防勝於治療的重要性，在本書中詳盡解釋危險關係的七大警訊，並提出因應方法與觀念，希望讀者能夠以此自省，避免不必要的傷害，邁向更為健康明亮的親密生活。
                            </p>
                            <br>
                            <p>
                                <span style="font-weight:bold;">作者簡介</span><br><br>
                                諾莉．尼爾森(Noelle&nbsp;Nelson,Ph.D.)<br />
                                全美知名的臨床心理學家，顧問及作家，著有「日常生活的奇蹟」，「贏家得勝」及「敢說就贏」。尼爾森博士致力以臨床治療，寫作和研習營幫助人們過更快樂更健康的人生。她曾接受過CNN，CNBC節目蒙特威廉秀，新聞觀點和家庭生活的訪問。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-33a variant=""
                        >結婚前，結婚後</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-33a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <h4>作者：任兆璋</h4>
                            <br>
                            <img src="@/assets/images/MD3.jpg" class="img-fluid mb-2" alt="家庭暴力">
                            <p>
                                <span style="font-weight:bold;">書籍簡介</span><br><br>
                                成長與改變<br><br>
                                結婚，是人生另一階段的開始。<br><br>
                                在現今離婚率高漲的情形下，許多人對步上紅毯躊躇不前；即使婚後，也發覺並不是「從此過著幸福快樂的日子」。<br><br>
                                本書是作者輔導婚姻多年的經驗累積，以幽默詼諧、不帶說教意味的方式讓讀者明瞭在結婚前後應當要釐清的問題與心態。<br><br>
                                《結婚前、結婚後》是台灣心理輔導先驅任兆璋修女繼《孩子的心，我懂》之後的最新力作。<br><br>
                                本書累積任修女四十多年婚姻諮商的智慧與精華，是婚前男女必讀的健心書、婚後夫妻的強心書，更是待婚兒女之父母的安心書。本書主題明確，案例鮮活，不僅沒有說教味，還帶有爽朗的幽默感，是本人人必讀的好書。
                            </p>
                            <br>
                            <p>
                                <span style="font-weight:bold;">作者簡介</span><br><br>
                                任兆璋<br>
                                江蘇上海人，為天主教社會服務修女會的修女。震旦大學畢業後赴美休習婚姻輔導碩士學位，回台後先後開辦天主教耕莘醫院社會服務部、台北教區美滿家庭服務中心、並創設及主持華明心理輔導中心家庭服務部，目前是懷仁全人發展中心專業督導，具四十餘年的社會工作及婚姻輔導經驗。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>                                                     
