                      <b-card-body>
                        <b-card-text>
                          <h5>Q：怎樣保證我不會反遭你們恐嚇?</h5>
                          <p>A：本公司是經由政府審核評估發放合法執照；正派經營！完全沒有兄弟背景；而且，客戶在與公司訂定契約的同時也可以由公司所聘請的律師團隊做見證，相當有公信力！再者，契約甚至可以拿到客戶管轄區的法院做公證，在法律上對客戶是有絕對性的保障！！更何況我們是受客戶的正式委託，公司是沒有理由去恐嚇客戶的必要。</p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-2c variant=""
                        >徵信資料會外流嗎？</b-button
                      >
