                    <b-collapse
                      id="accordion-2c"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h5>Q：徵信資料會外流嗎？</h5>
                          <p>A：本公司既然接受了客戶的委任；進而，雙方訂定契約後是與客戶站在同一條船上的；況且，資料的調閱本身就帶了些許的風險色彩。更何況，公司是有一定的制度才會有契約的形成；並非那種一人，公司基於種種理由於情、於理、於法公司都沒有把資料外流的理由；相對的，公司還得保護客戶亦是保護公司自身；而且，誠信！保密！本來就是公司成立之初的主旨本位。</p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
