                    <b-collapse
                      id="accordion-3c"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h5>Q：我會被報復嗎？</h5>
                          <p>A：公司既然接受了客戶的委任就有相對的義務和責任保護客戶的生命財產安全；不論是何人要加不利於客戶，本公司都會盡全力全面禁止所有不幸的發生。</p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-4c variant=""
                        >你們是合法的公司嗎？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-4c"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h5>Q：你們是合法的公司嗎？</h5>
                          <p>A：我們是經過內政部審核條件才通過並核發執照的公司；所以接受委任，是有發票收據可以給客戶的；並且公司除了全省各分駐處外，總公司亦在台北市南京東路五段48號八樓，隨時歡迎客戶至分駐處或總公司參觀、指教，以表明我們絕對是合法正派的公司，並非一般不肖業者或所謂的一人公司。</p>
                        </b-card-text>
                      </b-card-body>
