                    <b-collapse
                      id="accordion-5c"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h5>Q：若辦案過程中觸法，我有罪嗎？</h5>
                          <p>A：當公司業務經理人與客戶簽約的同時；通常會與客戶簽署一份刑民事責任歸屬附約；也就是說，案件處理過程中如有涉及刑民事法條觸犯的問題，則一切與客戶無關。債務追償的案子甚至可以做一份債務移轉契約的訂定。</p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
