                      <b-card-body>
                        <b-card-text>
                          <h5>Q：我沒有對方的資料要怎麼找人？</h5>
                          <p>A：首先委託人需知『冰凍三尺，並非一日之寒』所有的問題並非一夕造成，我們會先從外遇者的心態及與外遇對象的交往過程、狀況去研判；如何令外遇者與外遇對象的感情產生嫌隙，再教育客戶如何改變自身的個性及外遇者將夫妻雙方的感情提昇到以前初談戀愛的時空下；外遇者的心自然就會在合法的另一半身上。這樣的處理方向，才是治本非只治標而已；自然『外遇』這兩個字就不攻自破。</p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-9c variant=""
