                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-10c"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h5>Q：我的另一半是外國籍，找的到嗎？</h5>
                          <p>A：當然可以，只要你的另一半與你在台灣是有登記立案的合法夫妻就一定找得到或者是在國外結婚有登記立案的合法夫妻，亦同。</p>
