                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-11c variant=""
                        >如果我的案件你們無法處理，可以退費嗎？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-11c"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h5>Q：如果我的案件你們無法處理，可以退費嗎？</h5>
                          <p>A：假如案件是因為公司能力不足而導致無法處理，公司不但可以『全額退費』而且還會向客戶致上最深的歉意；但是，如果是因為客戶的因素或第三者其它不可抗拒的原因造成案件無法順利如期完成，則公司會參考案子的成本支出；再斟酌退費的成數金額。</p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-12c variant=""
                        >如果債權人談妥償還協議，但事後不履行那怎麼辦？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-12c"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h5>Q：如果債權人談妥償還協議，但事後不履行那怎麼辦？</h5>
                          <p>A：假如案件是因為公司能力不足而導致無法處理，公司不但可以『全額退費』而且還會向客戶致上最深的歉意；但是，如果是因為客戶的因素或第三者其它不可抗拒的原因造成案件無法順利如期完成，則公司會參考案子的成本支出；再斟酌退費的成數金額。</p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-13c variant=""
                        >債務人已潛逃出國了你們要怎麼找</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-13c"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h5>Q：債務人已潛逃出國了你們要怎麼找</h5>
                          <p>A：首先，我們會以全國所有出入境的機構去了解；債務人潛逃到哪一個國家？再向台灣的法院提出訴求，向該國的情治單位請求支援；並告知該債務人惡劣潛逃之情事，並請該國的法治人員將該名債務人遣送回國；處理清償債務事宜。</p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-14c variant=""
                        >你們都怎麼跟？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-14c"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h5>Q：你們都怎麼跟？</h5>
                          <p>A：跟蹤有單人、雙人、多人、徒步監視法及徒步與車輛併用監視法、車輛監視法；本公司會因案件需要評估因人、因時、因地制宜。並視現場狀況靈活運用器材拍攝存證。</p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-15c variant=""
                        >我如何能確立貴公司會確實辦案？有無憑據？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-15c"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h5>Q：我如何能確立貴公司會確實辦案？有無憑據？</h5>
                          <p>A：基本上案件主要可分為流動跟監、定點勘察及動態觀察，不論是何種現場人員皆會以隨身攜帶的袖珍型攝影器材拍攝存證，而資料證據皆有時間、日期及現場門牌、路標可供委託人憑判。</p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-16c variant=""
                        >你們跟蹤是否會跟丟？會不會被發現？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-16c"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h5>Q：你們跟蹤是否會跟丟？會不會被發現？</h5>
                          <p>A：本公司的現場調察人員是經過憲警職退幹員嚴格訓練，調教許久並需經由總公司考核和現場測試通過才能辦理正式案件，所以調查員首要主旨除了保密、確實外，還要記憶跟監對象的特徵以防止脫線。而現場人員也會以現場環境找出有利於自身的藏匿處避免曝光；目標移動時也會保持適當距離以免目標察覺。</p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-17c variant=""
                        >你們徵信的範圍有多寬？有無文件可供憑證？</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-17c"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h5>Q：你們徵信的範圍有多寬？有無文件可供憑證？</h5>
                          <p>A：一般個人徵信的範圍包括信用、財產、交友狀況及活動範圍及家庭、背景…。企業則有銀行往來、信用、現金流量、上下游廠商、公司資產、營業項目、股東名冊資本額…。所有調查結果，我們會以專業的書面報告，一般不動產、資產或可調閱的憑證我們皆會附加報告以供客戶評判。</p>
                        </b-card-text>
                      </b-card-body>
