                                如果他/她沒有能力去考慮別人的感受，則表示他/她可能會傷害別人。
                                <br><br>
                                <span style="font-weight:bold">Q8.他/她曾經打過你嗎？</span><br>
                                不管他/她事後如何表示道歉，一旦他/她打你一次，就已經打破男女關係的禁忌，這也使得下一次出手更容易些。
                                <br><br>
                                <span style="font-weight:bold">Q9.他/她曾經打他/她前任的女友嗎？</span><br>
                                這更能顯示他/她會打你的潛在因子。
                                <br><br>
                                <span style="font-weight:bold">Q10.他/她重視你的意見嗎？</span><br>
                                施虐者的低自尊往往使他/她想要控制他/她人的意志、行動，以增強他/她的自尊。因此往往他/她是非常自我中心，而忽視你的意見與自主選擇權。
                                <br><br>
                                <span style="font-weight:bold">Q11.他/她或他/她的父母是否有飲酒或吸毒的問題？</span><br>
                                <span style="font-weight:bold">Q12.他/她讓你積極的追求你自己的興趣嗎？</span><br>
                                施虐者的低自尊常會因為你追求個人理想而感到威脅，以致於不願意你追求自己的興趣、成長與發展。
                                <br><br>
                                <span style="font-weight:bold">Q13.當你要他/她停止對你的無禮行為，他/她是否真的停止？</span><br>
                                記錄一下他/她如何回應你的要求！如果改善有限，便需要認真考慮你們是否要繼續目前的關係！
                                <br><br>
                                <span style="font-weight:bold">Q14.他/她的需求是否來得太快太強烈？</span><br>
                                他/她要求立刻要擁有你而且想一天到晚都看到你。這已顯示他/她只是要滿足他/她 的幻想而非與真實的你建立關係。
                                <br><br>
                                <span style="font-weight:bold">Q15.他是否對男人、女人的角色非常傳統、堅持男人應如何，女人應該如何？</span><br>
                                注意看他是否認為女人應該留在家，照顧先生並且遵循先生的指示。
                                <br><br>
                                <span style="font-weight:bold">Q16.他/她是否用暴力來解決問題？</span><br>
                                習慣用暴力解決問題的人，往往無法用理性的方法來處理事情，因此在你決定與此人建立長久的關係前，最好了解他/她是否有意願改變自己用暴力解決問題的方式！
                                <br><br>
                                <span style="font-weight:bold">Q17.他/她是否看輕他/她自己？</span><br>
                                越是看低自己的人，越容易用暴力掩飾他/她的不安全感。
                                <br><br>
                                <span style="font-weight:bold">Q18.他/她的情緒呈兩極化？</span><br>
                                心情好時，他/她很仁慈；但其他/她時候都相當暴力而殘忍？
                                <br><br>
                                <span style="font-weight:bold">Q19.他/她是否來自一個暴力定庭？</span><br>
                                在暴力家庭中成長的孩子，往往也只學會了以暴力面對問題。
                                <br><br>
                                <span style="font-weight:bold">Q20.他/她是否對他/她人也相當無禮？</span><br>
                                他/她能夠公開地對他/她人無禮，則難保私底下，他/她不會對你粗暴。
                                <br><br>
                                <span style="font-weight:bold">Q21.在約會時，他/她是否對你很惡劣？</span><br>
                                如果在約會時他/她已對你粗暴，那麼施虐者決不會因為與你結婚而改變暴力行為。
                                <br><br>
                                <span style="font-weight:bold">Q22.你是否因害怕分手後遭到報復而不敢提出分手要求？</span><br>
                                若你已感受到這股恐懼威脅，馬上尋求協助。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-6 variant=""
                        >該如何預防家庭暴力?</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-6"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <ul style="margin-bottom:0px;">
                                <li>在家裡<br>
                                    <p>
                                        做您的孩子的模範及建立非暴力原則<br>
                                        孩子的行為，常是經由學習模仿大人而來。當大人在家裡可以用溝通協調的方式表達其不 同意見與解決問題時，孩子將可學習到更適當的意見表達與問題處理辦法。<br>
                                        如果您和您的配偶不能解決問題，您可以尋求協助，夫妻之間的問題，並非永遠都是可以自行解決的。尋求協助並不丟臉，它很可能是轉變的機會，如果需要，你可以先自行求助，藉由談話以解決衝突，清晰的溝通可以解決許多問題。把事情說出來有時候雖不容易，但其結果常是值得付出努力的。
                                    </p>
                                </li>
                                <br>
                                <li>在社區<br>
                                    <p>
                                        當暴力發生時，盡快報警，您的行動可以避免嚴重的傷害發生，並且鼓勵這個家庭去尋求協助。<br>
                                        支持家庭暴力預防方案預防家庭暴力是一項重大的工作，需要政府、民間團體及個人一起共同努力。<br>
                                        拒絕家庭暴力，不接受、不支持、不強化發生在您周遭的暴力。 
                                    </p>
                                </li>
                                <br>
                                <li>善用社會資源<br>
                                    <p> 
                                        內政部全國24小時保護您專線：113<br />
                                        台北市家庭暴力暨性侵害防治中心：0800-024-995<br />
                                        警察局：110<br />
                                        現代婦女基金會：02-23917133
                                    </p>
                                    <br>
                                    <p>記住：沒有人有權利虐待他人</p>
                                </li>
                            </ul>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-7 variant=""
                        >遇到家庭暴力情況發生，該如何處理?</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-7"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <p>
                                家暴受害者雖然可以申請保護令或其它單位協助，但暴力發生時，還是得先作好以下自保的防範措施：<br><br>
                                <ul style="margin-bottom:0px;">
                                    <li>受到身體的虐待，如被打、被刀械槍枝威脅、被捆綁或不實指控時，千萬要先保護自己的身體，不要逞強反抗。</li>
                                    <li>被施暴者辱罵、諷刺、言語威脅或不實指控時，不要企圖回嘴、反諷或辯解，以免火上加油。</li>
                                    <li>發現施暴者在喝酒、嗑藥或情緒焦躁時，即要小心應對。</li>
                                    <li>家暴發生時，要儘快設法向外求救，讓鄰居或親友能及時趕到。</li>
                                    <li>受虐後要報警、驗傷(甲種驗傷單)、做筆錄、保留證物(如-驗傷單、凶器)，以便向有關單位提出告訴。</li>
                                </ul>
                                <br>
                                <span style="font-weight:bold">家暴受害者該如何走出家暴陰影，重新迎向人生的光明面 ?</span><br>
                                長期在家庭暴力中生活的人，無論大人或小孩，身心一定都已遭受非常大的摧殘。身體的傷害也許有痊癒的一天，但心靈的痛，卻需要很長時間的治療。<br />
                                提醒正在承受家暴的人，試試下列的方法，協助自己走出家暴的陰影：<br><br>
                                <ul style="margin-bottom:0px">
                                    <li>利用家暴求助管道來申請協助及諮詢。</li>
                                    <li>向法院申請保護令。</li>
                                    <li>積極爭取就業、就學機會，尋求可以獨立自主的機會。</li>
                                    <li>嘗試擴展自己的生活圈、多交朋友，不要害怕說出自己的遭遇。</li>
                                    <li>多看書、聽演講或自己去當志工，不但可以發現人生的光明面，還可以增加自己面對事實的勇氣。</li>
                                    <li>不要感到羞恥、氣餒、被隔離，也不要因此退縮、易怒、衝動，而是應該積極面對，並且更努力學習、更充實自己的生活，才能逃離困境，迎向人生的光明面。</li>
                                </ul>
                            </p>
                            <br>
                            <h5 style="color:rgb(152, 75, 75)">家庭暴力求助管道</h5>
                            <ul>
                                <li>24小時家暴專線113或0800-024-995</li>
                                <li>台北市家暴防治中心02-27229543</li>
                                <li>台北縣婦幼中心02-29679665</li>
                                <li>桃園縣婦幼中心03-3336106</li>
                                <li>台中市家扶中心04-23162085</li>
                                <li>台南市家暴暨性侵害防治中心06-3901601</li>
                                <li>高雄市家暴防治中心07-7262085</li>
                                <li>外籍配偶保護諮詢0800-088-885</li>
                                <li>男性關懷0800-013-999</li>
                                <li>內政部家暴防治委員會02-89127331</li>
                            </ul>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-8 variant=""
                        >同居與同志&nbsp;適用家暴法</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-8"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <p>
                                立法院昨天三讀通過家庭暴力防治法修正案，未來包括沒有結婚的男女同居關係或同志，也可適用家暴法，擴大保護令保護對象。<br><br>
                                此外，為強化家暴事件通報機制與處理，修正案明定相關業務執行人員如接獲家暴訊息，應在二十四小時內報知當地主管機關立即處理。<br><br>
                                家庭暴力防治法自一九九八年六月二十四施行迄今已八年餘，為使相關單位與實務工作人員更有效率推展家暴防治工作，讓規範更符合社會實際需要，兩年來經立法院舉行三次聯席會議與四度朝野協商，婦女團體也籌組「台灣防暴聯盟」，在立委王昱婷、黃淑英、楊麗環等人推動下，昨天完成法案三讀。<br><br>
                                家暴法修法後將放寬對被害人訊問與詰問限制，法官得依聲請或依職權在法庭外訊問或詰問，或採取適當隔離措施。法院得與直轄市、縣市主管機關共同合作設立家暴被害人服務處所。<br><br>
                                為利適用並配合實務，修正案也明定民事保護令分為通常保護令、暫時保護令及緊急保護令三種，並增列通常保護令種類及加害人處遇計畫裁定前鑑定制度。<br><br>
                                條文也新增暫時監護權的強制執行條款，未來若法院判定被害人有暫時監護權，加害人不依保護令交付未成年子女時，被害人可聲請警察機關限期要求加害人交付，並可聲請法院強制執行，並暫免徵收執行費。<br><br>
                                新法也保障執行家暴保護的醫事人員、社工人員臨床心理人員，若有受到身體及精神上不法侵害者，得請求警察機關提供必要協助。<br><br>
                                此外，若當事人因實施家庭暴力、對被害人施以騷擾等非必要聯絡行為、未依規定遷出住所或依法裁定的特定場所、破壞加害人處遇計畫，觸犯違反保護令罪，依法可處三年以下有期徒刑、拘役或併科十萬元罰金。<br><br>
                                條文也增定加害人遠離被害人之住居所、工作場所等釋放條件及緩刑保護管束期間確實遵守條件，並強化家暴羈押條件，進而達到保護被害人人身安全目的。<br><br>
                                現代婦女基金會副執行長姚淑文表示，只要有「同居共財」關係，像雅痞族、不婚族、同志伴侶等，都可視為「家庭成員」，《家庭暴力防治法》納入這類非婚同居人、同性伴侶適用，符合當前社會型態與國際趨勢。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                </div>
              </div>
            </div>
            <div class="frame">
                <div
                    data-bs-spy="scroll"
                    data-bs-target="#list-example"
                    data-bs-offset="0"
                    class="scrollspy-example"
                    tabindex="0"
                >
                    <h2 id="s2" name="s2" ref="s2">家庭暴力法律</h2>
                    <div class="accordion" role="tablist">
                    <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-button block v-b-toggle.accordion-11 variant=""
                        >家庭暴力的施暴者有什麼法律刑責?</b-button
                        >
                    </b-card-header>
                    <b-collapse
                        id="accordion-11"
                        visible
                        accordion="my-accordion"
                        role="tabpanel"
                    >
                        <b-card-body>
                        <b-card-text>
                            <p>
                                如果家暴本身造成受暴者之身體傷害，那就是刑法之傷害罪，可判刑!<br><br>
                                刑法第二三章傷害罪如下<br><br>
                                第277條&nbsp;&nbsp;(普通傷害罪)<br />
                                傷害人之身體或健康者，處三年以下有期徒刑、拘役或一千元以下罰金。<br />
                                犯前項之罪因而致人於死者，處無期徒刑或七年以上有期徒刑；致重傷者，處三年以上十年以下有期徒刑。
                                院審理後法院同意兩人離婚，這名副教授的太太還可以平分丈夫600多萬元的財產。<br><br>
                                第278條&nbsp;&nbsp;(重傷罪)<br />
                                使人受重傷者，處五年以上十二年以下有期徒刑。犯前項之罪因而致人於死者，處無期徒刑或七年以上有期徒刑。第一項之未遂犯罰之。<br><br>
                                第279條&nbsp;&nbsp;(義憤傷害罪)<br>
                                當場激於義憤犯前二條之罪者，處二年以下有期徒刑、拘役或一千元以下罰金。但致人於死者，處五年以下有期徒刑。<br><br>
                                第280條&nbsp;&nbsp;(傷害直系血親尊親屬罪)<br>
                                對於直系血親尊親屬，犯第二百七十七條或第二百七十八條之罪者，加重其刑至二分之一。<br><br>
                                第282條&nbsp;&nbsp;(加工自傷罪)<br>
                                教唆或幫助他人使之自傷，或受其囑託或得其承諾而傷害之，成重傷者，處三年以下有期徒刑。因而致死者，處六月以上五年以下有期徒刑。<br />
                                <br />
                                第283條&nbsp;&nbsp;(聚眾鬥毆罪)<br>
                                聚眾鬥毆致人於死或重傷者，在場助勢而非出於正當防衛之人，處三年以下有期徒刑，下手實施傷害者，仍依傷害各條之規定處斷。<br />
                                <br />
                                第284條&nbsp;&nbsp;(過失傷害罪)<br>
                                因過失傷害人者，處六月以下有期徒刑、拘役或五百元以下罰金，致重傷者，處一年以下有期徒刑、拘役或五百元以下罰金。<br />
                                從事業務之人，因業務上之過失傷害人者，處一年以下有期徒刑、拘役或一千元以下罰金，致重傷者，處三年以下有期徒刑、拘役或二千元以下罰金。<br />
                                <br />
                                第285條&nbsp;&nbsp;(傳染花柳病、麻瘋罪)<br>
                                明知自己有花柳病或麻瘋，隱瞞而與他人為猥褻之行為或姦淫，致傳染於人者，處一年以下有期徒刑、拘役或五百元以下罰金。<br />
                                <br />
                                第286條&nbsp;&nbsp;(妨害幼童發育罪)<br>
                                對於未滿十六歲之男女，施以凌虐或以他法致妨害其身體之自然發育者，處五年以下有期徒刑、拘役或五百元以下罰金。<br />
                                意圖營利，而犯前項之罪者，處五年以上有期徒刑，得併科一千元以下罰金。<br />
                                <br />
                                第287條&nbsp;&nbsp;(告訴乃論)<br>
                                第二百七十七條第一項、第二百八十一條、第二百八十四條及第二百八十五條之罪，須告訴乃論。但公務員於執行職務時，犯第二百七十七條第一項之罪者，不在此限。
                            </p>
                        </b-card-text>
                        </b-card-body>
                    </b-collapse>
                    </b-card>
                    <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-button block v-b-toggle.accordion-22 variant=""
                        >兩願家庭暴力後夫或妻自己之債務，應由誰負責償還?</b-button
                        >
                    </b-card-header>
                    <b-collapse
                        id="accordion-22"
                        accordion="my-accordion"
                        role="tabpanel"
                    >
                        <b-card-body>
                        <b-card-text>
                            <p>
                                夫或妻在婚前欠的債及婚姻關係存續中所負的債務，都由夫或妻個人並就共同財產（任一方因管理自己名下之特有財產所生之債務或超越日常家務代理權之權限所負之債務，需由該方之特有財產支付）負清償之責，所以並不是夫債妻還或妻債夫還的觀念，換句話說雙方是獨立的、有行為能力的個體，個人之負債，另一方沒有償還之必要。<br><br>
                                夫或妻婚後任一方之債務，除非一方替另一方所開支票背書或做保人時，當債務人還不出錢時，背書或當保人之一方則有清償債務之責任，這是債務關係並非源於婚姻關係而來，不管是何種財產制度，需要替配偶負連帶責任的機率很小。
                            </p>
                        </b-card-text>
                        </b-card-body>
                    </b-collapse>
                    </b-card>
                    <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-button block v-b-toggle.accordion-33 variant=""
                        >什麼情況可以申請保護令?</b-button
                        >
                    </b-card-header>
                    <b-collapse
                        id="accordion-33"
                        accordion="my-accordion"
                        role="tabpanel"
                    >
                        <b-card-body>
                        <b-card-text>
                            <ul style="margin-bottom:0px; padding-left:18px;">
                                <li class="letter">當被害人遭受家庭暴力之急迫危險時，可申請緊急性暫時保護令。</li><br>
                                <li class="letter">當被害人遭受家庭暴力，如有未達急迫危險情況，但確有安全上之現實考量時，可向法院申請一般性暫時保護令。</li><br>
                                <li class="letter">當被害人遭受家庭暴力，有必要聲請保護令時，可向法院申請通常保護令。</li>
                            </ul>
                        </b-card-text>
                        </b-card-body>
                    </b-collapse>
                    </b-card>
                    <b-card no-body class="mb-1">
                        <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-button block v-b-toggle.accordion-44 variant=""
                            >誰可以申請保護令?</b-button
                        >
                        </b-card-header>
                        <b-collapse
                        id="accordion-44"
                        accordion="my-accordion"
                        role="tabpanel"
                        >
                        <b-card-body>
                            <b-card-text>
                                <p>
                                    被害人、檢察官、警察機關、家庭暴力暨性侵害防治中心均可申請保護令，被害人為未成年人、身心障礙者或因故難以委任代理人者，其法定代理人、三親等以內之血親或姻親，得為其向法院申請，但緊急性暫時保護令被害人並不能擔任申請人。
                                </p>
                            </b-card-text>
                        </b-card-body>
                        </b-collapse>
                    </b-card>
                    <b-card no-body class="mb-1">
                        <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-button block v-b-toggle.accordion-55 variant=""
                            >面對家庭暴力-自我保護篇</b-button
                        >
                        </b-card-header>
                        <b-collapse
                        id="accordion-55"
                        accordion="my-accordion"
                        role="tabpanel"
                        >
                        <b-card-body>
                            <b-card-text>
                                <p>
                                    <span style="font-weight:bold">遭暴力攻擊時，可以採取以下方式保護自己：</span><br>
                                    以兩手臂去抵擋。尤其施暴者攻擊頭部時，更應以手臂保護頭部，因頭皮較不易驗出傷痕，而手臂比頭部容易驗出傷痕，以手臂保護頭部時，可以讓攻擊點落在手臂上。<br />
                                    大聲喊叫以引起家人或鄰居注意，除了可以協助報警外，日後也可出庭作證。<br />
                                    現場錄音：如果有機會，可以將現場施暴過程錄音存證，將有助於法庭上使用。<br><br>
                                    <span style="font-weight:bold">暴力攻擊結束後，應採取以下方式以保護自己：</span><br>
                                    現場應拍照存證。尤其若施暴者有毀損物品或撕爛我們的衣服時，都應拍照，以供未來法庭上使用。受傷處應拍照，可與驗傷單比對作為證物。無論傷勢的大小，都應到醫院或診所接受醫師檢查，更應告知醫師，是遭受家庭暴力，並開立驗傷單（開立乙種驗傷單即可）。<br><br>
                                    立即告知親友，讓親友知悉事件過程，除可以舒解遭受暴力虐待後之情緒外，將來也可以出庭作證。到派出所要求備案，無論將來是否走上法庭，都要堅持備案，將人、事、時、地、物清楚地要求派出所記錄下來。如果員警拒絕備案時，妳應該把他的姓名及警章號碼記下，再投訴市警局督察室。<br><br>
                                    如果施暴者向妳認錯、道歉時，應立即要求施暴者書立悔過書，將施暴事件記錄下來，並由施暴者親筆簽名蓋章。如果妳要離家時，應到安全的地方，如警察局、親人或朋友家居住，也可打婦女保護專線113尋求暫時庇護。如果妳是已婚者，在離家後最好避免到男性友人家留宿，以免對方指稱妳通姦，影響到自己的權利。<br><br>
                                    孩子如果也有危險性或年齡小需要母親照顧時，可以將子女一起帶出，不要將子女單獨留下。離家後，一定要將戶籍遷到可以收到信件的地方，以避免受暴力離家後，反而遭施暴者提出不履行同居之訴訟，又因戶籍地仍與施暴者在一起無法收到法院信件，而未能出庭反駁，致遭敗訴。如果孩子有見到暴力過程時，應先將孩子的證詞錄音下來，以免日後孩子遭受壓力或害怕親人判罪而不願作證或做不實之證詞。如果偕同子女離家時，子女的學區可以透過社會局的協助辦理轉學，毌需擔心學籍的問題。<br><br>
                                    <span style="font-weight:bold">在生命可能遭受危害時刻，受虐婦女應帶那些必要東西離家？</span><br>
                                    當暴力的嚴重程度可能危害到受虐婦女生命安危時，離開施虐者，是必須要考量的。<br>
                                    離家時需隨身攜帶的物件有：<br>
                                    <ul style="margin-bottom:0px;">
                                        <li>身份證</li>
                                        <li>健保卡</li>
                                        <li>零錢</li>
                                        <li>換洗衣物</li>
                                        <li>聯絡電話</li>
                                        <li>提款卡或是信用卡</li>
                                    </ul>
                                    平時，可以將這些物件裝在可隨身攜帶的小袋子中放好，以備不時之需。
                                </p>
                            </b-card-text>
                        </b-card-body>
                        </b-collapse>
                    </b-card>
                    <b-card no-body class="mb-1">
                        <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-button block v-b-toggle.accordion-66 variant=""
                            >如何申請保護令?保護令的種類及內容是什麼?</b-button
                        >
                        </b-card-header>
                        <b-collapse
                        id="accordion-66"
                        accordion="my-accordion"
                        role="tabpanel"
                        >
                        <b-card-body>
                            <b-card-text>
                                <p>
                                    <span style="font-weight:bold">如何申請保護令?</span>被害人、檢察官、警察機關、家庭暴力暨性侵害防治中心均可申請保護令。被害人為未成年人、身心障礙者或因故難以委任代理人者，其法定代理人、三親等以內之血親或姻親，得為其向法院申請，但緊急性暫時保護令被害人並不能擔任申請人。<br>
                                    只有緊急暫時保護令由檢察官、警察機關、或直轄市、縣（市）主管機關向法院申請，最快在四小時內核發。一般暫時保護令、通常保護令則由被害人直接填寫聲請書狀向法院聲請。<br><br>
                                    <ul style="margin-bottom:0px; padding-left:18px;">
                                        <li class="number">聲請緊急性暫時保護令，由檢察官、警察機關或直轄市、縣（市）主管機關，以言詞、書面或電信傳真方式向法院聲請。</li>
                                        <li class="number">聲請一般性暫時保護令應填具「民事（一般性）暫時保護令聲請書狀」，向被害人之住居所地、相對人之住居所地或家庭暴力發生地之法院聲請。</li>
                                        <li class="number">聲請通常保護令應填具「民事通常護令聲請書狀」，向被害人之住居所地、相對人之住居所地或家庭暴力發生地之法院聲請。</li>
                                    </ul>
                                    <br>
                                    家庭暴力防治法中，將保護令區分為兩種：<br>
                                    <ul style="margin-bottom:0px; padding-left:18px;">
                                        <li class="number">通常保護令</li>
                                        <li class="number">暫時保護令</li>
                                    </ul>
                                    <br>
                                    <span style="font-weight:bold">保護令的種類及內容是什麼?</span>暫時保護令又分『緊急暫時保護令』與『一般暫時保護令』。
                                    <br>保護令內容：如下列１２項。<br>
                                    <ul style="margin-bottom:0px;">
                                        <li class="number">禁止傷害令</li>
                                        <li class="number">禁止騷擾令</li>
                                        <li class="number">命加害人遷出住居所及禁止處分不動產令</li>
                                        <li class="number">命加害人遠離一定住居所令</li>
                                        <li class="number">定汽、機車或其他生活、職業或教育上必需品之使用權並交付</li>
                                        <li class="number">對子女定暫時狀態之權利義務之行使負擔令及命交付子女</li>
                                        <li class="number">定相對人對未成年子女會面交往之方式，必要時並得禁止會面交往</li>
                                        <li class="number">命相對人給付被害人住居所之租金及子女之扶養費用</li>
                                        <li class="number">命相對人交付被害人或特定家庭成員之醫療、輔導、庇護所或財物損失等費用</li>
                                        <li class="number">命相對人完成處遇計劃，即戒癮治療、精神治療、心理輔導等</li>
                                        <li class="number">命相對人負擔相當之律師費用</li>
                                        <li class="number">命其他保護被害人及特定家庭成員之必要命令</li>
                                    </ul>
                                </p>
                            </b-card-text>
                        </b-card-body>
                        </b-collapse>
                    </b-card>
                    </div>
                </div>
            </div>
            <div class="frame">
              <div
                data-bs-spy="scroll"
                data-bs-target="#list-example"
                data-bs-offset="0"
                class="scrollspy-example"
                tabindex="0"
              >
                <h2 id="s4" name="s4" ref="s4">家庭暴力新聞</h2>
                <div class="accordion" role="tablist">
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-11a variant=""
                        >林曉培難忘家庭暴力陰影&nbsp;習慣沒有爸爸的日子</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-11a"
                      visible
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <p>
                                每逢父親節，林曉培總在無感中度過，她從國中二年級起就被從事美髮的父親家暴，國中畢業旅行時，她被空心水管打到脖子不能轉，哥哥被打到裹石膏，直到父親赴日採購美髮用具，繼母帶著她與兄弟們逃家，從此與父親失聯，她說：「跟我爸沒聯絡，也習慣沒他的日子了！」提起父親，林曉培難忘家暴陰影，國二時她每天放學回家就趕到父親的美髮院當髮妹，幫客人洗頭，晚上下班後身兼會計作帳。<br><br>
                                凌晨父親喝醉返家後，就會直接找她訓話，伴隨拳打腳踢，連續３年活在父親家暴陰影下，林曉培說：「有時不懂我爸為何要這樣對我，事後我才知道，我爸最愛我，只是他的表達比較另類。」<br><br>
                                16歲時，她與繼母、哥哥及弟弟趁父親到日本出差10天時，連夜逃家，繼母回娘家躲藏，原本打算帶弟弟及她逃往美國，後來媽媽反對，才讓她及哥哥續留台灣，直到17歲因為不愛讀書，被媽媽送往日本。青春期受父親家暴影響，林曉培坦承，雖沒有肢體暴力傾向，卻有很嚴重的精神暴力，男友交不了多久就分手。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-22a variant=""
                        >調查：男性若遭家庭暴力&nbsp;只有兩成願求助</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-22a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <h4>（中央社記者郝雪卿台中市二日電）</h4>
                            <p>
                                家暴受害者並不是女性的專利，為協助男性受暴者，政府已設立男性關心專線提供協助，台中市書香關懷協會今天公布一項調查結果顯示，高達八成五受訪者不知可向專線求助，若發生家暴，男性受訪者中也只有兩成願意求援，八成男性礙於面子不願打電話到專線求助。<br><br>
                                台中市政府社會處下午舉辦「婚姻幸福講座」，邀請即將結婚的新人、戀人或夫妻一起參加，透過兩性專家、律師及社工員的專題演講與綜合座談，讓參加者學習如何防止婚姻暴力、如何求援、如何藉由法律保障權益。<br><br>
                                活動一開場，一群活力爸爸及媽媽在勁歌熱舞中揭開序幕，並一起高舉「0800-013999」男性關懷專線的招牌，在父親節前夕，呼籲大家也要關心男性受暴問題，「以平等的心疼惜男人，讓男人當個幸福爸爸」，向家暴說再見。<br><br>
                                由於家暴受暴對象大多為女性，男性較少受到關注，雖然政府早已設立男性關懷專線，但知道的民眾並不多，根據台中市書香關懷協會調查顯示，在五百一十二份有效問卷中，有高達八成五的受訪者，不知道男性受暴人或加害人可以打「男性關懷專線」。<br><br>
                                書香關懷協會理事長黃瑞汝表示，其中一百七十四名男性受訪者，只有兩成男性受訪者表示會求援，仍有八成表示不會打電話到專線求助，不願求助的原因，包括沒面子、怕被別人笑、專線電話號碼不好記等。<br><br>
                                為了降低家暴案件的發生，活動中除帶領多對夫妻一同投遞「愛的承諾」給心愛的爸爸或先生，主動表達心中的愛意與感受外，主辦單位並將以宅急便方式，將「愛的承諾」送達遠方的爸爸或先生，讓他們擁有一個溫馨的父親節。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-33a variant=""
                        >「蝙蝠俠」貝爾&nbsp;帶老婆破家暴傳聞</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-33a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                            <p>
                                飾演蝙蝠俠的克里斯汀貝爾，本周驚傳涉嫌對母親和姊姊家暴，但氣勢如虹的《黑暗騎士》絲毫不受影響，繼續打破更多的票房紀錄。該片在短短５天內，成為美國影史上映後最快突破兩億美元（約台幣60億）票房的電影，打破之前由《神鬼奇航２：加勒比海盜》保持的８天記錄。<br><br>
                                克里斯汀貝爾在一度遭警局拘留又獲釋之後，則已繼續他的《黑暗騎士》巡迴宣傳行程，周三在巴塞隆納出席西班牙首映，不僅笑口常開，連他傳出不和的妻子也親密同行，似乎要打破各種有關「家務事」的傳言。不過貝爾整整花了30分鐘為影迷一一簽名，卻巧妙迴避了任何首映紅毯上的記者提問，刻意不讓私生活的紛擾搶了電影的焦點。
                            </p>
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-44a variant=""
                        >防約會暴力&nbsp;婦援會:家暴防治再教育有必要</b-button
                      >
                    </b-card-header>
                    <b-collapse
                      id="accordion-44a"
                      accordion="my-accordion"
                      role="tabpanel"
                    >
                      <b-card-body>
                        <b-card-text>
                          <h4>（中央社記者翁翠萍台北二十二日電）</h4>
                          <p>
                            婦女救援基金會今天表示，現代男女感情糾紛有明顯增加趨勢，家暴受害婦女有很高比例在婚前另一半即有暴力傾向，婚後仍遭受暴力相向，因此，有必要針對婚前約會暴力，實施家庭暴力防治再教育。<br><br>
                            婦援會舉例，最近修法通過的家庭暴力防治法新修法條中，已經明定將「同居關係」被害人也納入防治法的保護當中，因此，當有約會暴力發生時，也可以撥打113及110專線，得到立即的求助與諮商。<br><br>
                            婦援會也呼籲大家在面對危險情人、約會暴力時，要有一些正確觀念，包括面對感情保持理性不可動口或動手，摒棄對危險情人的刻板印象，情感自主權應操在自己手上，面對好友感情問題聆聽與陪伴是最好方式，諮商單位的功能與宣導也應加強，媒體還應多做正面教育宣導。<br><br>
                            婦援會持續針對約會暴力與危險情人教育宣導，發現十五歲到二十五歲的年輕世代情感糾紛，除了大家都瞭解的溝通不良、個性不合、劈腿或第三者介入感情、為錢發生爭執等是暴力發生的主要原因，在家庭暴力防治法宣導後，年輕人開始覺察除了肢體暴力與言語辱罵等以外，精神威脅與虐待也是暴力。<br><br>
                            最新婦團調查數據指男女對方提出分手造成的暴力情殺社會事件最多，婦援會稍早調查也發現，面對感情危機時，男生比較想溝通，女生較想分手，因此情殺事件通常為男性不滿分手而傷害對方，被害人多為女性；而社會觀念也多認為不要介入別人感情的事、那是別人自己的事。<br><br>
                            因此，婦援會針對危險情人與約會暴力，呼籲教育單位應加強性別教育，並讓年輕世代有親密關係暴力防治觀念及處理問題的能力，並具體提出前述建議，例如，在摒棄對危險情人的刻板印象方面，當交往對像不符合所謂危險情人特質時，還是必須注意對方行為所帶給你的恐懼心理，因為危機的種類和感覺是依照兩人交往狀態而定，不是絕對模式。<br><br>
                            情感自主權更應在自己手上，婦援會提醒，當親密關係出現暴力等危險因素時，單純冀望透過溝通來改變對方是不夠的。親密關係在初期或許無恙，但當個人感受和關係本身有改變時，就應信任自己做和平分手的打算。
                          </p>
