                                一群皮膚曬得黝黑的國中生，今天上午騎著腳踏車出現在台中市區，原來他們是來自宜蘭家扶中心的孩子，為了讓這群曾經受到家暴威脅的孩子，學習信任他人，建立自信心，宜蘭家扶中心今年第一次舉辦「與愛同行--少年鐵馬環島團」活動，宜蘭家扶中心主任林秀鳳說，希望藉由這次活動，能夠提升這群弱勢孩子們的自我價值。林秀鳳：「這群孩子來自弱勢家庭，有些是兒童保護的個案，有些是高風險的個案，辦這樣的活動就是要幫助孩子內心自我價值的建立，這群孩子在原來的家庭長期被忽略、冷漠，希望透過這樣的活動建立自信、與人互相信任，讓自己自我價值更提升。」<br><br>
