,{staticClass:"col-lg-8 col-md-8 col-sm-12"},[_c('div',{staticClass:"frame mt-4"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s1",attrs:{"id":"s1","name":"s1"}},[_vm._v("新女性愛情故事")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-1",modifiers:{"accordion-1":true}}],attrs:{"block":"","variant":""}},[_vm._v("最後的玫瑰")])],1),_c('b-collapse',{attrs:{"id":"accordion-1","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 真的好浪漫喔..... 好像愛一個人永遠都做的不夠...."),_c('br'),_vm._v(" 好男人加油... 好女人是值得你如此真心對待的 ....."),_c('br'),_vm._v(" Dear friends ~"),_c('br'),_vm._v(" 也許心中的『真愛』讓我們尚未遇到....."),_c('br'),_vm._v(" 但~~~~~~請記得要『珍愛』我們身旁"),_c('br'),_vm._v(" 用心『真愛』過我們的人喔!!!"),_c('br'),_c('br'),_vm._v(" 他最喜歡紅玫瑰，因為她的名字也叫做玫瑰(Rose)。"),_c('br'),_vm._v(" 每一年她的丈夫都會送她一束用漂亮絲帶紮起來的紅玫瑰。"),_c('br'),_vm._v(" 他過世那一年的情人節，紅玫瑰一如往年的送到家門口， 附上的小信封，也一如往年的寫著: \"做我的情人吧！\" "),_c('br'),_vm._v(" 每一年他都會送玫瑰給她，信封裡的小卡片則會寫著:\"我比去年今天更愛妳。對妳的愛只會逐年增長。\" 她知道這是她最後一次收到玫瑰了，他一定事先早早的就訂好了花。"),_c('br'),_c('br'),_vm._v(" 他都總是這樣 -- 什麼事情他都儘早安排妥當。"),_c('br'),_vm._v(" 可惜，那寵愛她的丈夫不知道，今年他們不會在一起過節了。"),_c('br'),_vm._v(" 她修剪好玫瑰枝，把花插在一個特別的漂亮花瓶中，把花瓶放在他那張帶著微笑的照片旁邊，然後，在他最喜歡的椅子上，一坐就是幾個鐘頭 -- 看著被花伴襯的照片。"),_c('br'),_vm._v(" 一年過去了，沒有伴的日子是十分難過的，寂寞，孤獨是她每天的節目。"),_c('br'),_c('br'),_vm._v(" 然後，就如往年的情人節，門鈴響了，玫瑰準時地出現在門口。"),_c('br'),_vm._v(" 她驚異地把花捧進屋裡。然後，她打了個電話給花店。"),_c('br'),_vm._v(" 花店老闆接起電話，她問他，是什麼人開的玩笑，激起她心中的隱痛？"),_c('br'),_c('br'),_vm._v(" 老闆說，\"我知道妳的丈夫去年就去世了，我也預計妳會打電話來問起那把花的事。"),_c('br'),_vm._v(" 妳收到的花，是早就預訂的了，妳丈夫事先已為妳設想好了。"),_c('br'),_vm._v(" 每一年，妳都會收到他預訂的玫瑰。"),_c('br'),_c('br'),_vm._v(" 喔！請妳看一下今年的小卡片，他去年留了這張卡片在我這裡，交待我，如果有一年我發現他不在了，就在花中附上那張卡片。\" 她向老闆道了謝，掛上電話，眼淚止不住流下面頰。"),_c('br'),_vm._v(" 她顫抖的手指慢慢觸到那張卡片，靜靜地讀著卡片中熟悉的字蹟: \"親愛的，今年我不在妳身旁了。"),_c('br'),_c('br'),_vm._v(" 希望這些日子，對妳來說不是太難適應。"),_c('br'),_vm._v(" 我知道，妳一定很寂寞，很傷心。"),_c('br'),_vm._v(" 要是換成妳先走，我也會有一樣地感受。"),_c('br'),_vm._v(" 我們所分享的愛， 使生活中每一件事都變得那麼美好。"),_c('br'),_vm._v(" 對妳的愛戀，早已超出文字能表達的。"),_c('br'),_vm._v(" 妳，是我完美的妻。"),_c('br'),_vm._v(" 妳是我的密友，我的愛侶，妳滿足我所有的渴求。"),_c('br'),_vm._v(" 雖然只分別了不久，請妳還是不要太傷心。"),_c('br'),_vm._v(" 就算妳掉淚時，我還是希望妳有喜樂的心。"),_c('br'),_vm._v(" 這就是我送妳玫瑰的原因。"),_c('br'),_vm._v(" 當妳接到這些花時，請妳回想我們一起分享的喜樂，"),_c('br'),_vm._v(" 想到我們如此蒙祝福。"),_c('br'),_vm._v(" 我一向鍾愛妳，這是永恆不變的。"),_c('br'),_vm._v(" 可是，親愛的，妳要勇敢的活下去。"),_c('br'),_vm._v(" 請試著找到新的幸福，快快樂樂的過日子。"),_c('br'),_vm._v(" 我知道，這不是容易的事，但請妳嚐試，好嗎？"),_c('br'),_vm._v(" 這些玫瑰還是會每年出現的，只有當送花人發現沒人應門時 --"),_c('br'),_vm._v(" -- 要是妳出門的話，他會再回來試五次--花店才會停止送花。"),_c('br'),_vm._v(" 我已經告訴他們，請把最後的那一束花送到山坡上，我們重逢之處。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-2",modifiers:{"accordion-2":true}}],attrs:{"block":"","variant":""}},[_vm._v("命運捉弄人")])],1),_c('b-collapse',{attrs:{"id":"accordion-2","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 她伸出左手，仔細端詳著載在無名指上的鑽戒，一行清淚不爭氣的滑落下來。「為什麼？他怎麼可以這樣對待我？」"),_c('br'),_c('br'),_vm._v(" 那天，她一如往日的在6:00下班前將當日的工作準時完成，起身將桌上喝剩的咖啡拿去洗手間倒掉，一個轉身不小心撞到剛來送公文的他，半杯咖啡就這麼濺在他的襯衫上，當然引起一陣慌亂的清理跟不斷的道歉，他們也就這樣認識了。"),_c('br'),_c('br'),_vm._v(" 開始，她下班的時間從6:00延至6:30到7:00，而他下班打卡時間也從6:00延至6:30到7:00，下班後就相約一起去吃晚餐，慢慢的對彼此就有更深的認識及瞭解。她欣賞他的成熟穩重、他喜歡她的冰雪聰明。三個月後，他們開始正式交往。"),_c('br'),_c('br'),_vm._v(" 她的朋友警告她說他之前花名在外，要她小心一點。可是她覺得現代人誰沒有過去、誰沒有談過好幾場的戀愛？為了愛他，她可以不計較他的過去，只要他是真心愛她就夠了。和她在一起後，他也真的沒再傳出什麼緋聞，只是有時候週末她要找他的時候，call他手機會直接進語音信箱找不到他的人，問他為什麼關機，他說有時候想一個人靜一靜不想接電話。"),_c('br'),_c('br'),_vm._v(" 她不想讓他覺得她是一個依賴性、佔有慾很強的女人，所以她尊重他的自由、給他想要的空間，在他有事無法陪她的時候一個人去逛逛誠品消磨時間。"),_c('br'),_c('br'),_vm._v(" 這樣的日子其實也沒有什麼不好，她反而認為這樣淡淡的穩定的交往才能夠細水長流，他尊重她的獨立也體貼她偶爾的任性，他們分享彼此的親蜜，也擁有各自的空間，她實在想不出如果放棄他，她還會不會找到一個比他更好的男人？"),_c('br'),_c('br'),_vm._v(" 她的死黨們開始羨慕她，說她命好，找到一個好男人，可以準備嫁了。她笑笑，心裏覺得還早咧，她才不想這麼早就走進婚姻的墳墓，只是有時候經過公司樓下的銀樓時，她開始會去注意擺在展示櫃裏的鑽戒，並想像著哪一天她披上白紗戴上鑽戒幸福的模樣。"),_c('br'),_c('br'),_vm._v(" 命運的戲劇性轉折總是一再的發生，上帝總是在通往幸福的道路上埋藏著地獄之火。某日，他約她週末晚上出來吃飯，說重要的事情要跟她商量，她問他說，有什麼重要的事情嗎？他說，見了面再談。"),_c('br'),_c('br'),_vm._v(" 掛上電話，她開始猜想他會跟她談什麼事情，他做事一向乾淨利落，很少像這樣支支吾吾的。她開始在上班時像掉了魂似的在座位上發呆，還打錯好幾份公文，同事們都笑她快當新娘子了，所以做事才會這麼心不在焉。"),_c('br'),_c('br'),_vm._v(" 好不容易等到週末晚上，她仔仔細細的打扮自己，還特地穿上他最喜歡的白色套裝、灑上她最喜愛的CD香水，帶著興奮愉快的心情去赴約。"),_c('br'),_c('br'),_vm._v(" 那天晚上餐廳的氣氛很好，柔和的燈光、輕快的音樂、精緻的食物。他們愉快的用餐，他從頭到尾都沒提到他找她商量什麼事情，只是跟她談一些報上的新聞、公司的八卦及這幾天他發生一些有趣的事情。她也很聰慧的不去問他倒底他找她要談什麼事情，只是靜靜微笑看著他說話的樣子，等他向她主動提起。"),_c('br'),_c('br'),_c('br'),_vm._v(" 「嗯，有件事情我想跟妳商量。」"),_c('br'),_vm._v(" 「什麼事？」"),_c('br'),_vm._v(" 「呃....我要結婚了。」"),_c('br'),_vm._v(" 「什麼？對不起，我有沒有聽錯？是我們要結婚了還是你要結婚了？」"),_c('br'),_vm._v(" 「是....我要結婚了。」"),_c('br'),_c('br'),_vm._v(" 他不安的低著頭轉動桌上的水杯，杯裏的水搖晃的令她心慌意亂。她腦袋「轟」的一聲變成一片空白，她不知所措的雙手交疊十隻手指頭彼此扭絞著。"),_c('br'),_c('br'),_vm._v(" 他說，在唸書時他曾經跟一個女生交往，後來發生兵變，因為那個女生嫌他沒有經濟基礎又無法常常陪她，所以就跟他分手，後來嫁給一個她父母替她相親的對象，他很愛她，為了她還差一點想不開做出傷害自己的事情。"),_c('br'),_c('br'),_vm._v(" 他退伍後失去她的消息，隻身上台北工作，然後在現在這個公司遇見了她。他本來以為可以忘掉過去的傷痛，全心全意的愛著現在的這個她，他本來都計劃好了，當他跟她結婚後要去歐洲維也納去聽歌劇去共度蜜月，他本來連喜餅要訂伊莎貝爾喜宴要訂在福華飯店都想好了，可是就在這個時候，過去的她出現在台北出現在他的面前。"),_c('br'),_c('br'),_vm._v(" 她向他訴苦說她的婚姻不幸福已經離婚了，她很後悔當出年輕不懂事拒絕了他的愛情，她問他是不是還是深愛著她，現在還願不願意娶她？"),_c('br'),_c('br'),_vm._v(" 他開始迷惘開始陷入前所未有的掙扎，他還愛著過去的她，也愛著現在的她，他原本以為在電視連續劇上才會出現的三角關係竟然會發生在他身上，一向做事不喜歡拖泥帶水的他突然間不知道該怎麼辦，他的思考模式開始當機，情緒開始牽著理智走，他的生活變得一團混亂、日子過的一片昏暗。"),_c('br'),_c('br'),_vm._v(" 現在的她雖然查覺到他的改變，但是仍然尊重他的隱私給予他空間跟自由，然而過去的她卻藉著這個機會黏著他依賴著他最後跟他上了床發生了關係。"),_c('br'),_c('br'),_vm._v(" 上個月，過去的她告訴他說她懷孕了。為了未出世的孩子、也為了不再繼續拖下去錯下去，他決定跟過去的她結婚。雖然他知道這個決定會傷害到現在的她，但是，他還是只能夠很抱歉辜負了現在的她對他的愛情。"),_c('br'),_c('br'),_vm._v(" 聽完他說的故事，她很訝異她竟然會如此平靜。她用她自己都很訝異平靜的語調跟他說，「待會能不能陪我去逛街，買個我一直很想要的東西？」"),_c('br'),_c('br'),_vm._v(" 我看著對面這個倔強的女子，很為她感到心疼難過。都過去了，愛裏的苦難折磨，一切都過去了。都自由了，分手之後大家都自由了。不管再怎麼不情不願不甘心不放手都已經喚不回早已逝去的變質了的愛情。"),_c('br'),_c('br'),_c('br'),_vm._v(" 「那現在呢？」我問她。"),_c('br'),_vm._v(" 「之後我就遞出辭呈，隨便找個藉口就去英國遊學，然後一待就是一個冬天。你知不知道，在英國一個人的冬天是多麼的冷？」"),_c('br'),_vm._v(" 「那他呢？」"),_c('br'),_vm._v(" 「他後來沒有跟她結婚，聽說那次是假性懷孕，他覺得她在欺騙他。後來他們之間不知道發生什麼問題，他沒跟我說我也就沒問。」"),_c('br'),_vm._v(" 「那妳跟他之間還有可能復合嗎？」"),_c('br'),_c('br'),_vm._v(" 她突然沉默，低頭玩弄著手上的戒指，許久，她抬起頭像是下了極大的勇氣似的，用堅定的語氣告訴我說：「不了，我怕了。我的死黨都說我很聰明，懂得利用他對我的虧欠拖他去為我買下這枚鑽戒，就算兩人已經分手了，至少還撈到一點實質的回饋。可是，她們不知道的是，我心底早就認定他是我今生的唯一，就算我們不結婚，我還是愛著他，比誰都愛他。就是因為太愛他了，所以我不忍心看他痛苦，當他提出分手時我尊重了他的選擇。至少，一個人能夠得到幸福總比兩個人都痛苦來的好；至少，他願意送我這枚戒指去紀念我們曾經美好的過去；至少，我跟他以後還會是好朋友，是不是？」"),_c('br'),_c('br'),_vm._v(" 如果相見是一種緣份，那麼分手是不是就是早就安排好的結局？"),_c('br'),_c('br'),_c('br'),_vm._v(" 店裏正播放林曉培以低沉的嗓音唱出「心動」這首歌曲，一向堅強的她再也忍受不住難過的伏在桌上哭了起來。"),_c('br'),_c('br'),_vm._v(" 她說：「雖然一直到現在我還是深愛著他。可是，我不想再和他繼續折磨彼此內心考驗雙方勇氣了。我已經學會了，愛一個人只要靜靜的在他身邊陪伴著他，一丁點都不要佔有，才能夠長長久久的擁有他。這樣就好了，真的，至少我跟他曾經彼此相愛過，就足夠了。」"),_c('br'),_c('br'),_vm._v(" 她眼底的淚光，像鑽石般閃耀著自信的光彩，比她手指上的鑽戒更教我尊敬。我在心底默默的祝福她，能夠平安，能夠順心，能夠有勇氣早日再度相信愛情，再次去尋找屬於她。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-3",modifiers:{"accordion-3":true}}],attrs:{"block":"","variant":""}},[_vm._v("我們結婚吧")])],1),_c('b-collapse',{attrs:{"id":"accordion-3","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 如果您現在有位非常喜愛的人！"),_c('br'),_vm._v(" 請永遠記得他（她）的好,珍惜每一刻; 讓所有美好的感覺都成真.........."),_c('br'),_c('br'),_vm._v(" 那一年，他突然出現，長得不怎麼起眼........."),_c('br'),_vm._v(" 在室友小娟的慫恿下，勉強和他交往，慢慢地，發現他的善解人意與溫柔體貼..."),_c('br'),_vm._v(" 日子一久，倆人關係密切，感情穩定，並獲得多方同學的祝福..."),_c('br'),_vm._v(" 大四畢業那一年，有一天，他對我說：「我已經申請到研究所獎學金，不過在美國，這一去不知多久，我們先訂婚好嗎？」"),_c('br'),_vm._v(" 可能是捨不得的情況下，我點頭了....."),_c('br'),_vm._v(" 因此，畢業典禮隔天，也就是我們訂婚的日子..."),_c('br'),_c('br'),_vm._v(" 訂婚後沒多久，在眾多親人的祝福及我的依依不捨下，他搭上了飛機，飛往另一個未知的國度，我則找到了一份不錯的工作，開始朝九晚五的日子"),_c('br'),_c('br'),_vm._v(" ........長途電話是我倆聯絡感情及消除相思之苦的方法........"),_c('br'),_vm._v(" 一天， 不幸的事情降臨在我身上........"),_c('br'),_vm._v(" 早晨，我依序梳妝打扮上班，途中，一輛疾駛的計程車為了閃躲路旁流浪狗，一個急轉彎........~~"),_c('br'),_c('br'),_vm._v(" 不知過了多久，醒來已在醫院，病床旁圍繞著的家人見我醒來，一臉高興的呼叫醫生........爸....怎麼了? 為什麼我叫不出來?醫生衝過來替我做了檢查，護士替我打了一針，就把家人叫了出去........."),_c('br'),_c('br'),_vm._v(" 等我再度醒來，所見到的是每個人一臉的哀傷，到底怎麼回事 ?"),_c('br'),_vm._v(" 我為什麼叫不出聲？"),_c('br'),_c('br'),_vm._v(" 父親忍著悲傷告訴我："),_c('br'),_vm._v(" 「小敏，醫生說妳神經受了點傷，暫時不能說話，過一陣子就好了！」"),_c('br'),_vm._v(" 「我不要！」我努力的敲打著病床，張大著嘴吶喊，但只是無言的抗議！"),_c('br'),_c('br'),_vm._v(" 回到家後，生活全變了，以前所盼望的電話鈴聲，現在變成恐懼的催魂聲我不再出門，也變得自暴自棄，父親想想，決定搬家...... 而他，在地球的另一端，所知道的只是我已解除婚約，所打的電話全沒回應，所寫的信也石沈大海........"),_c('br'),_c('br'),_vm._v(" 過了兩年，我慢慢地走出陰霾，開始新的生活，也學習用手語和人打交道。一天，小娟到家來替我過生日，並告訴我說：他回來了，現在在一家公司當工程師...... 我沉思了一會兒，沒說什麼！"),_c('br'),_c('br'),_vm._v(" 突然門鈴響了，家人被這急促的門鈴聲催著不知如何是好，只見父親拖著蹣跚的步伐門.......此時，屋內一陣平靜，他出現了，就站在我家門口，他深呼一口氣，不徐不緩 的走到我面前用他那熟練的手語對我說："),_c('br'),_c('br'),_vm._v(" 「對不起！我晚了一年才來看妳，這一年中，我拼命的學著手語，為的就是今天，不論妳變成怎樣，妳永遠是我最愛的人，除了妳，我不會再選其他人，我們結婚吧！」"),_c('br'),_c('br'),_vm._v(" 愛需要傻勁，但不能傷害別人"),_c('br'),_vm._v(" 愛需要耐力，但不是一再的騷擾愛人"),_c('br'),_vm._v(" 愛有許多的能力，能使人快樂，亦能使人痛苦。"),_c('br'),_vm._v(" 時常補給自己愛的知識，讓愛人與被愛都有福。"),_c('br'),_vm._v(" 你不能決定生命的長度，但是你可以控制它的寬度"),_c('br'),_vm._v(" 你不能左右天氣，但是你可以改變心情"),_c('br'),_vm._v(" 你不能改變容貌，但是你可以展現笑容"),_c('br'),_vm._v(" 你不能控制他人，但是你可以掌握自己"),_c('br'),_vm._v(" 你不能預知明天，但是你可以利用今天"),_c('br'),_vm._v(" 你不能樣樣勝利，但是你可以事事盡力 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-4",modifiers:{"accordion-4":true}}],attrs:{"block":"","variant":""}},[_vm._v("幸福便條紙")])],1),_c('b-collapse',{attrs:{"id":"accordion-4","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 我們用盡了所有的方法想要一個屬於我們的孩子，也許是命中注定，我們是不會有了。"),_c('br'),_vm._v(" 但是，親愛的，請你明瞭我的心情，我不會為了生孩子而跟你離婚，因為我把你看待得比我的生命還重要，你的喜怒哀樂往往可以決定我一天的心情。"),_c('br'),_vm._v(" 這些天我想了很久，如果你的不快樂來自於我，或許我的離開對你來說，正是一個全新的開始，可是我卻會因為你的離開而意志消沈，我無法想像往後沒有你的日子裡該如何度過。"),_c('br'),_vm._v(" 當我第一次看見你，我就知道就是你了，雖然你的脾氣差，但我卻甘之如飴，這是我欠你的，心甘情願並用一輩子的時間還給你。"),_c('br'),_vm._v(" 親愛的，如果你還愛我，如果你還願意與我相守，就請你別再用離婚這個理由來打擊我。我會用行動證明，我愛你的證據。"),_c('br'),_vm._v(" 我會在我們第一次約會的地方等你。永遠愛你的老公"),_c('br'),_c('br'),_vm._v(" 同樣的便條紙三張，滿滿地寫下他想說的話，我情緒激動地全身發抖，眼淚再也不能自止地流下來。還有一張折好的紙，我將之打開...."),_c('br'),_c('br'),_vm._v(" 天！這是一張診斷說明書，他竟然跑去結紮了！"),_c('br'),_vm._v(" 我放聲大哭，哭他的傻，好心疼他....趕快把眼淚擦乾，他還在等我，在我們第一次約會的地方...."),_c('br'),_c('br'),_vm._v(" 我還要把所有的便條紙帶去，告訴他我好愛他，我不能沒有他...."),_c('br'),_c('br'),_vm._v(" 隔天...."),_c('br'),_c('br'),_vm._v(" 「親愛的老公，希望昨天晚上沒把你累壞了。我先去上班了，早餐要記得吃喔！永遠愛你的老婆」 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-5",modifiers:{"accordion-5":true}}],attrs:{"block":"","variant":""}},[_vm._v("她是我的新娘")])],1),_c('b-collapse',{attrs:{"id":"accordion-5","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 在我18歲那年,有一個女孩就說願意嫁給我,可是我聽到的反應,卻是化成了笑意,因為她只是一個六歲的小女孩,所以我也不以為意的跟她說:[好! 好! 等妳將來長大! 若是還願意嫁給哥哥! 哥哥一定娶妳的! ] 她聽了之後好高興喔,開心的到處跟別人說她已經是我的新娘了,她好快樂好幸福喔!可是她卻不知道! 我當時只是在敷衍她罷了!"),_c('br'),_c('br'),_vm._v(" 可笑的是,後來鄰居看到我,都會故意取笑我說:[耶! 怎麼沒帶妳老婆出來啊! 然後就偷偷在竊笑! ] 唉!想不到我的一句玩笑話,她卻如此的認真,而且只要是她認識的人幾乎都通知了,我心裏在想,會不會弄假成真啊,但又覺得日子還那麼長,不要想太多好了,於是我到她最常去玩耍的公園找她!因為時間已經很晚了!而且她母親開玩笑的說,要我把我老婆帶回娘家吃晚飯,我聽了也只能笑笑的答應,因為,唉~~~她是我老婆啊~~~~~~~~!"),_c('br'),_c('br'),_vm._v(" 到了公園果然看到了我那六歲的老婆,看她天真活潑可愛的樣子,就令人想要去疼她,怎可能去傷害她呢! 所以我心裏就在想,陪她一起作作夢好了,反正我也沒差,而且現在有個六歲的老婆也不錯,仔細想想一定羨煞旁人吧!雖然要承受別人的取笑! 不久,她看到了獨自思索的我,高興的像一隻小蝴蝶飛奔了過來,用她那縑細的小手拉著我的手,走到公園裏的沙堆旁,介紹我們的家庭佈置,廚房、廁所、房間的位置,還有家庭成員,我和她還有我爸媽和我們的兩個小孩,瞧她那認真的模樣,彷彿好像是真的似的,至少對現在的她來說是真的,可是以後呢?算了!不要讓自己想太多,我就陪她走過這一段好了!"),_c('br'),_c('br'),_vm._v(" 突然,她回過頭來看著我,好像有什麼話要對我說,可是又說不出口,於是我輕問著她:[ 怎麼了? ]她才有點害羞的跟我說,她看電視上的情侶或是夫妻都會親嘴! 所以她也好想親親我! 我聽到後真的是嚇了一跳,覺得現在的小孩怎麼那麼早熟啊,還是因為電視的關係,所以有樣學樣! 可是看到她煩惱的樣子,我又不好意思拒絕,於是我跟她說:[妳七歲的時候,哥哥一定給妳親的,只要妳那時候還喜歡著哥哥! ]她聽到之後高興的點頭了,因為她跟我說她絕不會變心的!"),_c('br'),_c('br'),_vm._v(" 有時候,我反而覺得大人對自己的信心,卻不如一個小孩子還來的強,因為他們只要相信自己就好了,不會像我們一樣,顧慮那麼多的事物,到最後都不敢相信自己是否能做到!"),_c('br'),_c('br'),_vm._v(" 本來想就此帶她回家了,可是她說她好想在吃飯前先吃個冰棒,我想了一會兒,便叫她在公園裏等一下,自己便去附近的雜貨店買冰棒,因為我覺得只要一下子而已,可是想不到竟遇到了國中同學,可能是太久沒見面了,所以不自覺的聊了一陣子,聊到一半時想起了六歲的老婆還在等我!所以我趕緊跟他辭別,然後買了冰棒衝向公園去,可是到了公園時竟找不到她了,我想她可能獨自回家了吧,於是我也回家了!"),_c('br'),_c('br'),_vm._v(" 回家大概十分鐘後吧! 她母親打電話來問她女兒是不是在我這呢? 我聽到後訝異的反問她母親:[ 不是回家了嗎? ]於是我們驚慌的在公園附近找尋,都沒有發現她的蹤影,此時的我好責怪自己,因為我的心裏好害怕另一種不好的結果,於是我和她的家人挨家挨戶的問有沒有人看到她,可是每個人都說沒看到,直到問到小明的家裏,小明才說他看到一個大人把小馨硬抱進一輛汽車裏,然後匆忙的開走了,我和她家人聽到心都涼了,淚水竟不由自主的滴了下來,大家哭成了一團,沒有人知道要怎麼辦,如今只能等電話了,因為我們覺得可能是綁架,可是她家又不是很富有,為何挑她家下手呢?如今想那麼多也沒有用了,於是我們只能等待而已!"),_c('br'),_c('br'),_vm._v(" 時間一分一秒的流逝,案也報了,錢也準備好了,可是就是沒有任何的訊息,此時的我們想到了更不好的結果,就是KKK..已經被撕票了,其實這也是警方的推斷,我看到她家人傷心欲絕的樣子,我真的好想自殺來負起這個責任,因為那時的我已經快要崩潰了,因為都是我害的,如果那時候我帶著她去雜貨店,就不會發生這件事了,可是又想到,若她還活著,我若自殺死了,這不是另一件悲劇嗎?所以我決定繼續等待著,只要有一絲的希望,我都不會放棄的!"),_c('br'),_c('br'),_vm._v(" 不知不覺一年過去了,我和她家人從來沒有間斷過找尋著她,因為我們都認為她一定還活著! 一定還活著! 直到有一天晚上,她家人打電話來,激動且興奮的告訴我,警方已經在高雄的街上找到她了,明天就會送回台北來,聽到這個消息的我,真的好高興好高興,不知為何眼淚不由自主的掉了下來,可能是喜極而泣吧! 於是等待明天來臨的這一晚,我都睡不著覺,因為我真的太高興了!"),_c('br'),_c('br'),_vm._v(" 隔日,我和她家人一同到警察局去消案,順便辦一些手續才能帶她回家,到警察局後,警察把他們的調查結果告訴我們,我們才知道原來她是被人口販子賣到高雄去,這一兩天才自己逃了出來,還說,她全身上下都是傷痕和瘀血,可能是買她的家庭施虐的,叫我們不要去刺激她或是挑動她的情緒,我們聽了心裏都好難過,但又不能在她的面前表露,於是我們就靜靜的不說半句話,開車載她回真正的家!"),_c('br'),_c('br'),_vm._v(" 不久,便已到家了,因為我不知道要如何面對她,可能是害怕繼續面對她吧,所以我送她到門口之後,我轉身就想逃離了,就在我轉身要離去的時候,突然感覺有一隻小手緊 抓住我,我回過頭時,發現她眼神凝視著我,於是我蹲下去鼓起勇氣對她說:[ 怎麼了? ] 她遲疑了一下子,便緩緩的說:[ 你說過我七歲的時候要給我親的! ] 她一說完便在我還來不及防備之下,奪走了我的初吻,之後輕輕微笑的看著我,此刻的我 真的已壓抑不住我內心的情感了,忍不住的將她抱緊,控制不住自己,哽咽的哭了起來,覺得她這麼小卻那麼堅強那麼勇敢,突然間,我感覺她的身體也微微的顫抖了起來,原來,她也哭了,於是我們兩個人相擁著哭泣,因為我們分離一年了!"),_c('br'),_c('br'),_vm._v(" 十三年後,她已要嫁作人婦了,她還真沒良心,沒寄帖子給我就算了,只用電話通知我不要遲到了,都不知道我這十幾年來對她所付出的,竟只是換來如此罷了,可是又能如何呢! 因為這是她自己的選擇! 我也只能在心裏祝福她,希望她婚後會更 幸福更快樂,因為我知道 ,如果娶到她這種老婆,真的是會幸福一輩子的,可是如今卻只是我的奢望和夢想!"),_c('br'),_c('br'),_vm._v(" 不過,她結婚的那一天,我還是到了,因為在我家人和她家人的催促下,我帶著百感交集的心情到了結婚典禮的現場,不知為何,我一到了現場,大家卻安靜了下來,此刻的我竟有些怯意,突然間,左臂已被人勾住了,轉頭看了過去,原來是她啊! 頓時間,我竟因為她的美麗而愣住了,剎那間,我還以為是天使在我身邊呢! 只看見她帶著有些狡猾又可愛的神情說:[想逃啊!已經來不及喔! 你就乖乖的認命吧!]突然間,結婚進行曲的音樂放了起來,鞭炮聲音不斷,我就這麼被她勾著一起走到了台上,直到這一刻,我才知道這不是奢望也不是夢想,因為她是我的新娘! ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-6",modifiers:{"accordion-6":true}}],attrs:{"block":"","variant":""}},[_vm._v("最完美的結局")])],1),_c('b-collapse',{attrs:{"id":"accordion-6","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 我在想這樣的愛值得嗎??"),_c('br'),_c('br'),_vm._v(" 一個天氣凜冽的冬日，我走路回家之際，在街上絆到了一個某人掉在街上的皮夾。?"),_c('br'),_c('br'),_vm._v(" 我撿起皮夾，翻看內部，想找尋失主的資料，然後打電話通知他。但皮夾裡只有三塊錢和一封被折著，看起來好像已經放在皮夾裡很多年的信。信封已經磨破了，唯一看得清楚的是寄信人的地址。我把信打開，希望能找出一點兒線索。我找到了寄件日期，這一封信竟然是一九二四年寫的，這是一封將近六十年前寫的信。?"),_c('br'),_c('br'),_vm._v(" 信上的字跡很漂亮、很女性化。信紙是淡藍色的，左上角印有一朵小花。那是一封情人間的道別信，收信人名叫麥可，寄信人署名漢娜。漢娜說要和麥可分手，只因為母命難違。即使如此，漢娜信上說自己還是會永遠愛著麥可。?"),_c('br'),_c('br'),_vm._v(" 那是一封很美的信，但除了知道所有人的名字叫麥可之外，根本找不到其他訊息。如果我打電話到查號台詢問，應該可以找出信封上地址的電話號碼。?"),_c('br'),_c('br'),_vm._v(" 我打電話到查號台說：「是這樣的，我撿到了一個皮夾，我試著找出失主，但皮夾裡面只夾著一封信。請問你有沒有辦法依著信封上的地址查到電話號碼？」?"),_c('br'),_c('br'),_vm._v(" 查號台的服務人員說必須問一下主管才知道。那位主管猶豫了一下才說：?"),_c('br'),_c('br'),_vm._v(" 「是有一個電話號碼列在這個地址下面，但我不能把號碼給你。」?"),_c('br'),_c('br'),_vm._v(" 她說基於禮貌，她會打電話到這個號碼，然後轉述我講的話，讓接電話的人決定是否要和我通話。我等了幾分鐘，然後那名主管回到了線上。「有人願意跟你通話。」?"),_c('br'),_c('br'),_vm._v(" 我問跟我通話的女人認不認識誰叫漢娜。跟我說話的人嘆了口氣說：「這間房子前任的屋主有個女兒名叫漢娜。但他們三十年前就搬走了！」?"),_c('br'),_c('br'),_vm._v(" 「你知道那家人現在可能搬到哪兒去了嗎？」?"),_c('br'),_c('br'),_vm._v(" 「我記得幾年前，聽說漢娜要送母親到養老院去。」電話另一端的女人說。?"),_c('br'),_c('br'),_vm._v(" 「如果你和養老院聯繫的話，可能就會找到那個女兒了。」?"),_c('br'),_c('br'),_vm._v(" 女人給了我養老院的電話，我打了電話過去。接電話的女人告訴我此人已在數年前過世了，但養老院卻還有這個女兒住處的電話。?"),_c('br'),_c('br'),_vm._v(" 我謝過養老院的人，然後撥了她給我的號碼。接電話的女人告訴我，漢娜現在也住在養老院裡。?"),_c('br'),_c('br'),_vm._v(" 「這整件事真是荒謬，」我這樣想著，「為什麼我要費這麼大的勁去找一個皮夾的主人，而事實上皮夾裡也只有三塊錢和一封有六十年歷史的信。」?"),_c('br'),_c('br'),_vm._v(" 雖然如此，我還是打電話到那家養老院，因為據說漢娜就住在那兒。接電話的男子告訴我：「是的，漢娜是住在這兒沒錯。」?"),_c('br'),_c('br'),_vm._v(" 即使當時已經晚上十點鐘了，我還是問能不能立刻過去看她。男子吞吞吐吐的說：「如果你想的話，你可以碰碰運氣。她可能還在娛樂室裡看電視。」?"),_c('br'),_c('br'),_vm._v(" 我向他道謝後，立刻開車去養老院。夜間護士和警衛在門口和我打招呼。然後我們一起登上那座巨大建築物的三樓。在娛樂室裡，護士介紹漢娜給我認識。她是一個個性甜美、滿頭銀髮的老太太。臉上帶著笑容，眼睛明亮有神。?"),_c('br'),_c('br'),_vm._v(" 我告訴她我撿到了一個皮夾，然後把信拿給她。她看到左上角印著小花的淡藍色信紙時，深深的吸了口氣，然後說：「年輕人，這封信是我和麥可最後的聯繫。」?"),_c('br'),_c('br'),_vm._v(" 她將頭轉開了一會兒，深思後輕聲的說：「我非常愛他，我當時才十六歲，母親認為我太年輕了，因此禁止我們交往。他真是英俊，看起來就像演員史恩康那萊。」?"),_c('br'),_c('br'),_vm._v(" 她又說：「他全名叫麥可‧高斯汀。如果你見到了他的話，請告訴他我常想起他。」她遲疑了一下，幾乎咬著嘴唇又說：「告訴他我還愛著他。」她眼中含淚的笑著說：「我從沒結過婚，因為我覺得沒有人比得上麥可。」?"),_c('br'),_c('br'),_vm._v(" 我謝謝她與我談話並向她告辭。我搭電梯回到一樓。站在門邊的時候，警衛問我：「那個老太太有幫上你的忙嗎？」?"),_c('br'),_c('br'),_vm._v(" 我說她給了我一個線索。「至少我現在知道了失主的姓氏。不過我想我會先暫停一陣子。我花了幾乎一整天的時間找尋這個皮夾的主人。」?"),_c('br'),_c('br'),_vm._v(" 我拿出皮夾，那只是個有紅色襯裡的普通咖啡色皮夾。警衛看到皮夾後說：?"),_c('br'),_c('br'),_vm._v(" 「喂，等一下！那是高斯汀先生的皮夾，我一眼就認出來裡面的紅襯裡。他總是不停的丟掉這個皮夾。我至少在走道上幫他把皮夾撿回來過三次。」?"),_c('br'),_c('br'),_vm._v(" 我問：「誰是高斯汀先生？」這時我的手開始顫抖了起來。「他是住在八樓的一個老先生。那絕對是麥可‧高斯汀的皮夾。他一定是散步時不小心丟掉的。」?"),_c('br'),_c('br'),_vm._v(" 我向警衛道謝後，急速奔向護士站。我將警衛的話對護士至轉述，然後和她一同搭電梯上八樓。我不停的祈禱，希望高斯汀先生尚未就寢?"),_c('br'),_c('br'),_vm._v(" 到了八樓，護士說：「我想他還在娛樂室裡。他喜歡在晚上看書。他是個討人喜歡的老人家。」?"),_c('br'),_c('br'),_vm._v(" 我們走到唯一還有燈光的房間，房裡有個男人正在看書。護士走向他，問他是否掉了皮夾。?"),_c('br'),_c('br'),_vm._v(" 高斯汀先生驚訝的抬起頭，用手摸摸褲子後面的口袋，然後說：?"),_c('br'),_c('br'),_vm._v(" 「真的不見了。」?"),_c('br'),_c('br'),_vm._v(" 「這個好心的先生撿到了一個皮夾，懷疑可能是你掉的。」?"),_c('br'),_c('br'),_vm._v(" 我把皮夾拿給高斯汀先生。他看到皮夾時，臉上帶著微笑，明顯的鬆了一口氣，他說：「是，是我的皮夾！一定是今天下午掉的，我一定要犒賞你。」?"),_c('br'),_c('br'),_vm._v(" 「不，不用了。謝謝你！」我說：?"),_c('br'),_c('br'),_vm._v(" 「但我必須告訴你一件事。因為我想試著 找出失主，所以讀了信。」?"),_c('br'),_c('br'),_vm._v(" 他臉上的笑容忽然消失了：?"),_c('br'),_c('br'),_vm._v(" 「你讀過信了？」?"),_c('br'),_c('br'),_vm._v(" 「我不僅讀了信，還知道漢娜現在人在哪裡。」?"),_c('br'),_c('br'),_vm._v(" 他的臉色突然轉白。「漢娜？你真的知道她在哪裡？她好嗎？她還是像以前 一樣漂亮嗎？求你趕快告訴我。」他哀求道。?"),_c('br'),_c('br'),_vm._v(" 「她過得不錯，還像你剛認識她的時候一樣漂亮。」我輕聲道。?"),_c('br'),_c('br'),_vm._v(" 老先生帶著期盼的微笑問道：「你能不能告訴我她在哪兒？我想要明天打電話給她。」他握住自己的手說：「先生，你知道嗎？我是多麼的愛著這個女孩，收到這封信的時候，我的生命等於結束了。我一直沒結婚，我想我永遠都愛著她。」?"),_c('br'),_c('br'),_vm._v(" 我說：「麥可，跟我來。」?"),_c('br'),_vm._v(" 我們搭乘電梯下到三樓。通往娛樂室的走道一片黑暗，只有一兩盞小燈還亮著。漢娜獨自坐在娛樂室裡看電視。?"),_c('br'),_c('br'),_vm._v(" 護士走向漢娜。?"),_c('br'),_c('br'),_vm._v(" 「漢娜，」護士輕聲地喊，然後指指在我身旁的麥可，「你認識這個人嗎？」漢娜推推眼鏡，看了一陣子，一句話也沒說。?"),_c('br'),_c('br'),_vm._v(" 麥可幾乎自言自語的說：「漢娜，我是麥可。你還記得我嗎？」?"),_c('br'),_c('br'),_vm._v(" 漢娜喘著氣說：「麥可！我不相信！麥可！真的是你！我的麥可！」?"),_c('br'),_c('br'),_vm._v(" 麥可緩緩的走向漢娜，然後兩人相互擁抱。護士和我流著淚相偕離開。?"),_c('br'),_c('br'),_vm._v(" 我說：「看啊！看神是怎樣安排的！如果注定怎樣，便一定會怎樣。」?"),_c('br'),_c('br'),_vm._v(" 大概三周後，養老院的人打電話到我辦公室。?"),_c('br'),_vm._v(" 「你這個星期天有空到這兒出席一個婚禮嗎？麥可和漢娜要結婚了！」?"),_c('br'),_c('br'),_vm._v(" 那是個美麗的婚禮。養老院裡的每一個人都盛裝出席觀禮。漢娜穿著一件明亮的米黃色禮服，看起來十分美麗。麥可穿著深藍色的西服，挺拔的站著。他們請我當伴郎。?"),_c('br'),_c('br'),_vm._v(" 養老院給了他們自己的房間。如果你想看一對表現得像是青少年的七十六歲新娘與七十九歲新郎的話，一定要看看這對夫婦。?"),_c('br'),_c('br'),_vm._v(" 這是這份持續了將近六十年的愛情最完美的結果。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-7",modifiers:{"accordion-7":true}}],attrs:{"block":"","variant":""}},[_vm._v("1999網路愛情故事首獎")])],1),_c('b-collapse',{attrs:{"id":"accordion-7","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 一."),_c('br'),_vm._v(" 飛機平穩地飛在平流層上，大概已經飛了有十多個小時了吧，或者﹍﹍唉！不管了，反正很久了，整個人被困在這座位上，真不舒服，下次要求公司，飛長程的一定要給我頭等艙，嘿！他們不會因此開除我吧！"),_c('br'),_c('br'),_vm._v(" 飛機上，空服人員正推著小推車，細聲地問著乘客「TeaCoffee﹍」每次聽到這句話都使我想到一句廣告詞「Teacoffeeor me」想到這兒，自己不禁微笑了一下，此時一位空姐正好看著我，她看見我在笑，就也回給我一個親切的笑容。我想，如果她知道剛才我在想什麼的話，大概會皺起眉頭吧！不過也許還不至於馬上向我發脾氣吧！我想每個航空公司都是這樣告訴她們的吧！"),_c('br'),_c('br'),_vm._v(" 不久，繫上安全帶的燈號亮起，接著一陣不算嚴重的搖晃後，我們開始下降，不是墜機，只是要飛入機場了。從窗口可以看見廣大的機場，以及一輛輛黃色的拖運車。等飛機安全停下後，空姐們站在門口歡送旅客下飛機，我提起腳邊的公事包（也就是筆記型電腦），正要起身，胸口前的筆卻掉了。於是我低下頭去，到處找，由於一直找不到，以致於當大家都下飛機後，我一個人還在座位附近走來走去。"),_c('br'),_c('br'),_vm._v(" 「有什麼問題嗎？」我身後突然傳來一個女人的聲音。"),_c('br'),_c('br'),_vm._v(" 「筆掉了。」我一邊回頭一邊說，等我看到她，沒想到正是剛才對我微笑的空姐。"),_c('br'),_c('br'),_vm._v(" 「妳會說國語啊？」我說。"),_c('br'),_c('br'),_vm._v(" 「當然啦！」她微笑著說「我還是台北人呢！」"),_c('br'),_c('br'),_vm._v(" 「我本來以為妳是日本人。」我一邊回過頭去找筆一邊說著。"),_c('br'),_c('br'),_vm._v(" 「喔！是嗎？」她笑了一下「我幫你開燈比較好找。」她說。"),_c('br'),_c('br'),_vm._v(" 「謝謝！」我回應了一下，她掂了一下腳，手伸到我的頭上開燈，她身上飄來一股淡淡的香味。"),_c('br'),_c('br'),_vm._v(" 「妳們當空姐一直在飛機上走來走去，累不累啊！」我跪在地板上，看著地板說。"),_c('br'),_c('br'),_vm._v(" 「不會啊！一下子就習慣了！」她回答。從我眼睛的餘光中可以看到她穿的紅色高跟鞋緊緊靠著。"),_c('br'),_c('br'),_vm._v(" 「你剛才在笑什麼啊？就在我推著車子經過的時候啊！一般坐長途飛行的旅客不容易有笑容的。」她說。"),_c('br'),_c('br'),_vm._v(" 「我啊！」我思考了幾秒鐘，總不能說我在想那句廣告詞吧！於是我起身轉向她說：「我覺得妳很好看很可愛啊！」"),_c('br'),_c('br'),_vm._v(" 她聽了我的回答，手遮著嘴小聲地笑了笑，雙肩微微震動，在她左肩膀靠近脖子部分有一顆痣，好像一滴點在白紙上的墨那般。"),_c('br'),_c('br'),_vm._v(" 「嘿！等一下！」她好像看見了什麼似的，她微微向我靠近了一步，趨身向前擦過我的右手，從座椅上不曉得拿了個什麼東西回來，當她身體又擦過我的右手時，那香味又撲向我的鼻子。"),_c('br'),_c('br'),_vm._v(" 「你在找這枝筆吧！」她拿著一枝金色的筆說。"),_c('br'),_c('br'),_vm._v(" 「是啊！妳在哪找到的？」我好奇地問。"),_c('br'),_c('br'),_vm._v(" 「就在手把的縫隙間啊！」她一邊說一邊把筆放入我西裝外套胸前的口袋。"),_c('br'),_c('br'),_vm._v(" 「嘿！」她用一種懷疑的口氣說「你真的找不到筆嗎？」"),_c('br'),_c('br'),_vm._v(" 「是啊！」我被她問的莫名其妙，但過了一、兩秒我才領悟她的話。"),_c('br'),_c('br'),_vm._v(" 「喂！我可不是那種藉機和人搭訕的人喔！」我說。"),_c('br'),_c('br'),_vm._v(" 她沒再說話，只笑了笑。等我們走到飛機出口，她突然問我：「你來日本做什麼啊？」"),_c('br'),_c('br'),_vm._v(" 「出差！」我停下腳步看著她，然後拿了一張名片給她。"),_c('br'),_c('br'),_vm._v(" 「會待很久吧！住哪裡啊？」"),_c('br'),_c('br'),_vm._v(" 「一家大飯店喔！」我拿出筆記本給她看，上面有飯店名字以及房間號碼。"),_c('br'),_c('br'),_vm._v(" 「妳也要在日本待一會兒吧！有空可以找我，我們公司有在賣化妝品喔！也許可以給妳一些。」"),_c('br'),_c('br'),_vm._v(" 她看著筆記本又笑了出來。"),_c('br'),_c('br'),_vm._v(" 「幹嘛？」我問。"),_c('br'),_c('br'),_vm._v(" 「還說不是搭訕，哪有第一次見面就叫對方去飯店找你的啊！」她一面笑著說一面把筆記還給我。"),_c('br'),_c('br'),_vm._v(" 「不﹍﹍不是﹍﹍」我急忙回答「是妳先問我住哪裡啊！這﹍﹍」"),_c('br'),_c('br'),_vm._v(" 「嘿！你臉紅了耶！」她笑得更開心了。"),_c('br'),_c('br'),_vm._v(" 「哦﹍﹍我﹍﹍」我真是哭笑不得「我投降了，放過我吧！」"),_c('br'),_c('br'),_vm._v(" 「好啊！再見！」她微笑著說。"),_c('br'),_c('br'),_vm._v(" 雖然我叫她放過我，但我心中卻有一種感覺----我一定還會再遇見她。"),_c('br'),_c('br'),_vm._v(" 二."),_c('br'),_vm._v(" 回到飯店，又是累人的一天，幸好今天我不用陪他們去喝酒，否則明天一定又會頭痛的，我像在慶幸什麼似的在電梯內自言自語。五樓的電梯一打開，迎面看見一位年輕的飯店行政小姐，她一看見我馬上露出禮貌性的微笑，她按了九樓的鈕。我則是要到十樓的。她就站在我左前邊，離我兩步遠的位置，我回想了一下她的笑容，有點職業性的感覺，難道飯店的人有特別訓練笑容嗎？想到這裡，不禁覺得有趣，「嘿！」的笑了一聲。"),_c('br'),_c('br'),_vm._v(" 她轉頭看了我一下，我剛才的舉動似乎引起她的注意，但我仍然假裝若無其事，她也只看了一會兒就又把頭轉過去了，但似乎稍微皺了一下眉頭。我想，她該不會以為我剛才的笑聲很輕浮吧！假如真是這樣那我就太失敗了！我還以為那是種極瀟灑地笑法呢！"),_c('br'),_c('br'),_vm._v(" 「叮！」的一聲，九樓到了，她走出電梯後，又對我微微一笑，笑容隨著電梯的門也漸漸關了起來，看著她的笑容，心中突然有一個人的影子在心中閃過，是誰呢？不曉得，也想不太起來，套用村上的一句話「都到喉嚨裡了，就是說不出來！」"),_c('br'),_c('br'),_vm._v(" 我蠻喜歡村上的書，日本的合作公司知道這件事後，有一天當我們談完生意，突然他們的主管拿了兩本書給我，用生硬的國文說：「村上春樹的兩本書，台灣沒有。」我高興地收下了，心中有點感謝那位告訴他們我喜歡村上的人，而且下次我考慮說我喜歡美女！但回到飯店後，我又有點責怪那人的疏忽，他忘了告訴他們我不懂日文。"),_c('br'),_c('br'),_vm._v(" 「叮！」的一聲又響起，電梯門又開了，但我還是沒想起剛才在我心中閃過的影子到底是誰。走進房間，我像一般上班族討厭打卡制度那樣地討厭束縛在自己身上的西裝以及常使我脖子感到燥熱的領帶，我用最快的速度卸下它們，它們就像不被愛的孩子一樣丟在床上。拉開窗簾，窗外只見孤單地道路和不遠處的大樓，都在泛黃的路燈下，道路懶洋洋地送著一輛輛汽車通過，我打開房間的音響，緩緩傳來清脆的鋼琴聲，我從公事包裡拿出一瓶咖啡，拉了張椅子坐在窗邊靜靜地望著窗外，易開罐清亮的聲響，隨著我的手指扳動傳了出來。"),_c('br'),_c('br'),_vm._v(" 溫柔的音樂，醇醇的咖啡香，街上車燈划過的亮線，一點一滴，像傾入口中的咖啡，慢慢滲透在醞釀著，醞釀著什麼呢？腦中殘留的記憶又再一次浮起，耳畔中傳來的笑聲﹍﹍"),_c('br'),_c('br'),_vm._v(" 電梯中殘留的記憶被莫名的喚起﹍﹍"),_c('br'),_c('br'),_vm._v(" 音樂、咖啡、香味、亮線、記憶、笑聲、遙遠﹍﹍"),_c('br'),_c('br'),_vm._v(" 三."),_c('br'),_vm._v(" 慢慢地，琴聲漸逝，手上的咖啡也只剩最後一口，我站起來，伸了伸腰，但隨即我又躺在靠窗的床上，想想實在也有趣，當初公司以為三個被派來出差的人會住同一個旅館，於是訂了一間有兩張床的四人房，但沒想到日本分公司也「好心」的又在別處訂了一個四人房，於是我們三個被派來出差的人，經過我們私下的抽籤，只有我可以住進這家五星級的飯店，其他兩個人就住另一間飯店，反正也不影響我們來的目的，基本上公司就是希望我們三人別經過任何彼此的討論，個別提出一份對日本合作公司的報告。"),_c('br'),_c('br'),_vm._v(" 最後，我還是決定在自己不小心睡著之前，起身從衣櫃裡拿了套休閒服，然後進到浴室洗澡，洗完澡，圍著浴巾，我坐在床上，背靠著淡藍的壁紙，打開床頭燈，拿出我的筆記型電腦，正打算再檢視一下文件，電話卻在這時響起。"),_c('br'),_c('br'),_vm._v(" 「喂！」我還是不習慣用日本話說 摩西摩西"),_c('br'),_c('br'),_vm._v(" 「喂！」是個女生的聲音，她叫了我的名字。"),_c('br'),_c('br'),_vm._v(" 「嗯！是啊！我就是，妳是誰？」"),_c('br'),_c('br'),_vm._v(" 「我是在飛機上幫妳找筆的人。」"),_c('br'),_c('br'),_vm._v(" 「找筆﹍﹍喔！是妳啊！幹嘛！要化妝品嗎？」說完，我不禁笑了出來。"),_c('br'),_c('br'),_vm._v(" 「嘿！沒時間和你開玩笑啦！我可不可以到你那邊過夜？」"),_c('br'),_c('br'),_vm._v(" 「什麼？過﹍﹍過夜？」放在我腿上的電腦差點掉下去。"),_c('br'),_c('br'),_vm._v(" 「喂！可別想歪喔！總之，一言難盡啦！你先說我可不可以過去啦！」"),_c('br'),_c('br'),_vm._v(" 「這個﹍﹍到我這裡﹍﹍」"),_c('br'),_c('br'),_vm._v(" 「喂！行不行啊！我快沒零錢了！」"),_c('br'),_c('br'),_vm._v(" 「可以！」我不知哪來的勇氣說這樣的話。"),_c('br'),_c('br'),_vm._v(" 「哈！我終於得救了！謝啦！對了！你等一下到大門等我，我穿一件白色的大衣。」她才一說完就掛上了電話。"),_c('br'),_c('br'),_vm._v(" 我慢慢掛上電話，呆呆看了電話上的計時器，我們剛才兩分鐘的電話，使我房間裡多了一個女客人，也使我將要在攝氏十度的晚上站在飯店門口。"),_c('br'),_c('br'),_vm._v(" 換上休閒服，披著大衣，在脖子上繞上圍巾，由於大衣是黑色的，儘管我沒戴上飯店服務生的帽子，還是有人把我當作小弟，直接把行李交給我，最後我乾脆坐在大廳的沙發上，選了一個可以看見大門的位置坐下，就這樣子一直盯著大門看。"),_c('br'),_c('br'),_vm._v(" 十點，有一輛計程車停在門口，走下一位穿著淡藍套裝披著白色大衣的女子，她下了計程車後，就一直站在車旁，好像有點手足無措的樣子，我仔細地看了她一下，才像想到什麼似的跑了過去。"),_c('br'),_c('br'),_vm._v(" 我走出大門口，她正看著服務生拿下行李，她拉緊大衣，深深吐了一口氣，在這種氣溫下，化成一團白白的霧氣。她臉色顯的很蒼白，我走到她面前，她抬頭看到了我才露出了安心的笑容。"),_c('br'),_c('br'),_vm._v(" 我吩咐服務生提著她唯一的咖啡色行李，然後我帶著她走進飯店，但她似乎還是覺得很冷，微微地顫抖著，我把自己的圍巾拿下繞在她脖子上，她只向我微微一笑，並沒再多說什麼。她連微笑一下似乎都很費力。"),_c('br'),_c('br'),_vm._v(" 電梯緩緩地上升，我看著她臉上泛著寒冬中特有的紅，但她的眼神卻帶著疲勞和迷惘，她的視線不在電梯樓層的指示燈，而是停留在一種空洞的黑暗中。"),_c('br'),_c('br'),_vm._v(" 「先到房間裡好嗎？」這是她見到我後，唯一傳達出來的微弱訊息，也是她目前為止唯一的一句話。進到房間，她靜靜地坐在床邊，服務生把行李放在床邊就關上門出去了。她把視線轉向窗外的景色，兩隻手交叉地抱在胸前，即使房間中有暖氣，但她仍然連大衣都不脫，還是緊緊抱著，似乎很冷的樣子。"),_c('br'),_c('br'),_vm._v(" 「怎麼了？為什麼跑來這裡，真的想要化妝品嗎？」我故做輕鬆地問。"),_c('br'),_c('br'),_vm._v(" 她並沒有回答，過了一會兒，她才慢慢地像說著遙遠記憶似的說了一句。「有沒有綠茶，我好渴。」她這才回頭看我。"),_c('br'),_c('br'),_vm._v(" 「綠茶？喔！好啊！我下樓去買買看有沒有。」我走到她面前俯身看著她的臉說：「妳好像有點累喔！沒事吧！」"),_c('br'),_c('br'),_vm._v(" 她微微點了點頭，表示沒事。"),_c('br'),_c('br'),_vm._v(" 我到樓下買了兩瓶溫過的綠茶回來，然後在門外敲了十幾聲後她才打開門，只見她有氣無力地靠著門緣。"),_c('br'),_c('br'),_vm._v(" 「喂！妳沒事吧！」我才說完，她整個人就向我倒過來了。在我還來不及高興前先嚇了一跳，她的額頭出奇的燙，我放下手上的綠茶把她扶回床上，這時她似乎連張開眼睛都沒辦法了。我仔細地看著她的臉，才發現她臉上泛著微紅不是因為天冷，而是高燒的結果，她的嘴唇早就又乾又白了！我趕緊打了通電話到櫃臺請他們幫我叫了部計程車，然後把掉在床上的圍巾又一次繞在她脖子上，然後扶起她，她幾乎沒辦法走路，我把她的左手繞過我的脖子架著，我的右手則緊緊抓住她的右肩，讓她身體靠在我身上好扶著她走。"),_c('br'),_c('br'),_vm._v(" 坐上計程車，我催促著司機到最近的大醫院，到了醫院門口，她已經沒辦法走路了，整個人使不出力氣的樣子，我抱起她走進醫院，而司機則好心地幫我去醫院內叫醫生。醫生稍微診斷一下說是高燒過度，他們把她放在一張病床上，打了針退燒針，又替她打了葡萄糖點滴，我坐在床邊靜靜地看著她，手上抱著她的白色大衣和我的圍巾。"),_c('br'),_c('br'),_vm._v(" 四."),_c('br'),_vm._v(" 退燒針似乎有用，只見她原本皺著的眉頭，漸漸展開了，慢慢地呼吸也有規律，看她微動的嘴唇，她或許正在作夢吧！總之，生病能好好睡一覺是最好的恢復方式。點滴快打完時，她也醒了過來，喝了一些水，整個人看起來好多了，醫生又再替她量了體溫，然後讓她在床上休息一個小時後，就叫我去拿藥，可以回去休息了。"),_c('br'),_c('br'),_vm._v(" 回到飯店房間，她似乎還是很累，我讓她躺在床上，才幫她蓋上棉被，她就睡了，我坐在兩張床中間的地毯上，撿起出門時掉在地上的綠茶拉開瓶蓋喝著，我偶爾伸手摸摸她的額頭，好確定她沒有再發燒。我拿起床邊的筆記型電腦，一邊檢視著資料，一邊注