子，沒睡好吧！」"),_c('br'),_c('br'),_vm._v(" 被她這樣一說，我才又覺得眼皮重了起來，全身也顯的十分疲倦，昨天最後一次看手錶是凌晨四點，而現在是早上七點。"),_c('br'),_c('br'),_vm._v(" 「你先在去睡一下好了，我自己會處理我的事。」"),_c('br'),_c('br'),_vm._v(" 「嗯！那好吧！妳自己叫東西吃，如果又不舒服，可要告訴我喔！」說完，我已經倒在床上了。在躺到床上的同時，隱約聽到她說等一下，但我沒理會她，等我整個人倒在床上後，聞到枕頭上的香味，我才知道她剛才只是想告訴我她昨天睡在這張床上。半夢半醒之間，耳邊可以聽到她打電話叫飯的聲音，她說了一口標準的日語，應該是標準吧！至少我聽起來是如此。"),_c('br'),_c('br'),_vm._v(" 不久，房間傳來一陣香味，我想是飯送來了吧！她曾經輕聲叫我，問我要不要起來吃，我微微搖了搖頭回應他，我實在太累了。我做了一個夢後也就慢慢醒過來了，我在床上坐起身子。「隱約」可以看見她坐在靠窗的床上，背靠著淡藍的壁紙，因為我有深度近視，反正只要不戴眼鏡，看任何東西都只能「隱約」。"),_c('br'),_c('br'),_vm._v(" 「睡醒啦！」她問。"),_c('br'),_c('br'),_vm._v(" 「嗯！睡的很飽。」我一邊說一邊從小櫃子上拿起眼鏡戴上，這時眼前的景象才又清晰了起來，她拿著一本黃色書皮的書。"),_c('br'),_c('br'),_vm._v(" 咦？你在看書啊！」我問。"),_c('br'),_c('br'),_vm._v(" 「是啊！從那個書桌上拿的，這本書不錯喔！」她說。"),_c('br'),_c('br'),_vm._v(" 她說的是日本公司送我的書。「喔！那我兩本書都送妳好了！」"),_c('br'),_c('br'),_vm._v(" 「真的？」她似乎很高興。"),_c('br'),_c('br'),_vm._v(" 「是啊！反正我看不懂日文。」我站起來舒展一下身體。"),_c('br'),_c('br'),_vm._v(" 「不懂日文？那你昨天怎麼送我到醫院？」她吃驚地說。"),_c('br'),_c('br'),_vm._v(" 「這個啊！」我把視線從窗外轉了回來「醫院、醫生，是我僅知的日文中的其中兩個，其他一概不懂。」"),_c('br'),_c('br'),_vm._v(" 「那你怎麼到日本出差？」她問。"),_c('br'),_c('br'),_vm._v(" 「用英文啊！不然就請翻譯囉！何況我的搭擋也懂日文，而且我昨天也是用英文向醫生解釋妳的病情的。」"),_c('br'),_c('br'),_vm._v(" 「哦！」她恍然大悟似的點了點頭。"),_c('br'),_c('br'),_vm._v(" 「對了，這是妳的一些證件。」我從口袋裡拿出一個裝證件的皮夾給她。"),_c('br'),_c('br'),_vm._v(" 「咦？我放證件的皮夾怎麼在你那邊。」她拿著皮夾說。"),_c('br'),_c('br'),_vm._v(" 「他們需要用妳的證件存檔啊！妳又昏的不省人事，我摸遍妳全身才找到的。」我說。"),_c('br'),_c('br'),_vm._v(" 「你摸﹍﹍」她一臉驚訝地看我。"),_c('br'),_c('br'),_vm._v(" 看她那付擔心的樣子，我不禁笑了出來說道：「開玩笑的啦！我請護士幫妳拿出來的，何況這放在妳大衣口袋，我怎麼可能去摸到妳。」"),_c('br'),_c('br'),_vm._v(" 「呼！嚇我一跳！別開我這種玩笑。」她說。"),_c('br'),_c('br'),_vm._v(" 「妳想不想吃點東西啊！我覺得有點餓！」我看了看錶，已經十二點了。"),_c('br'),_c('br'),_vm._v(" 「好啊！我們出去飯店外吃啊！」"),_c('br'),_c('br'),_vm._v(" 「出去？妳的病剛好耶！這樣不太好吧！」"),_c('br'),_c('br'),_vm._v(" 「放心啦！我又不是三歲小孩，我會照顧自己的。」"),_c('br'),_c('br'),_vm._v(" 雖然我一直反對，但她十分堅持她的病好了，我說不過她，也就只好聽她的了。她拿了套衣服走進浴室裡換，我則直接在床邊換了上衣，我把自己和她的大衣從衣櫃裡拿出來放在床上，然後坐在椅子上等她。大概過了五分多鐘，她換好衣服出來， 穿了件合身的淡藍色長褲搭配一件白色毛衣，衣服舒服地貼在她身上，完全表現出她修長的身材。"),_c('br'),_c('br'),_vm._v(" 「好看嗎？」她站直在我面前微笑著說。"),_c('br'),_c('br'),_vm._v(" 「好看，當然好看，只是﹍﹍我們只是出去吃飯，可以穿輕鬆一點啊！」我說。"),_c('br'),_c('br'),_vm._v(" 「那可不行！」她一副認真的樣子，還伸出右手食指向我搖一搖說「我的原則是上街一定要漂漂亮亮的！」"),_c('br'),_c('br'),_vm._v(" 我微微一笑，把大衣拿給她，然後在我的堅持下，她不甘心地圍上圍巾。我們到了飯店外大概兩、三百公尺外的小麵店裡，兩個人各叫了碗麵吃，麵熱呼呼的，很適合這種天氣吃，但由於我剛睡醒，肚子雖然餓，但食慾還不好，她也因為生病剛好，也是吃不下太多。麵店前有一條小河，過了橋就是公園，我們走出麵店後，就到那公園散步，公園很大，有許多兒童的遊戲器材，鞦韆、沙坑、溜滑梯，雖然是中午剛過不久，但還是有許多小孩在那裡玩。"),_c('br'),_c('br'),_vm._v(" 我們找了張長椅坐下，現在雖然是冬天，但午後的風卻在太陽的關懷下，吹拂著溫柔的氣息，非但不冷而且令人覺得舒服。長椅後面的大樹，正開著淡黃的小花，感覺上有點像印度紫檀，但應該不是吧！印度紫檀是在夏天開花的吧！總之，那淡黃的小花，隨著溫柔風中的旋律，伴著香氣飄下，看那淡黃的花瓣悄悄地落在眼前，突然覺得自己像是在歐洲森林小道間漫步的旅人。"),_c('br'),_c('br'),_vm._v(" 就和她，循著屬於我們的林間小路漫步，和她﹍﹍？我到底再想什麼啊！我只認識她一天吶！"),_c('br'),_c('br'),_vm._v(" 六."),_c('br'),_vm._v(" 回到飯店房間，她脫下大衣，突然像小孩子似的「嘻！」的笑了一聲跑到窗邊去。"),_c('br'),_c('br'),_vm._v(" 「快！快！快來看！」她看著窗外，十分興奮地嚷著。"),_c('br'),_c('br'),_vm._v(" 「什麼啊？」我跟著過去看著窗外。GOD！ 有一對男女在兩幢大樓間的小巷中熱吻著，也許他們以為沒人看到吧！但不幸我們兩個就是看到了，他們「越吻越烈」，那男人的手開始上下游移，剛開始女生還抵抗著，但不久也就順著他了，慢慢地女生似乎快被攻破最後防線了。但突然，那女生掙脫了男生，然後左右看了一下，拉著男生急急忙忙地走出小巷。"),_c('br'),_c('br'),_vm._v(" 「唉！可惜！」我們兩人竟不約而同地看著對方說。"),_c('br'),_c('br'),_vm._v(" 就這樣我們看著對方楞了一會兒，接著就笑了出來，我們整整笑了有兩分鐘說不出話來。她的笑容中，可以看出女生應有的那絲害羞。"),_c('br'),_c('br'),_vm._v(" 「嘿！妳這女人怎麼可以說這種話！」我笑著說。這句話讓她的臉更紅了。"),_c('br'),_c('br'),_vm._v(" 「喂！你這男人怎麼那麼色啊！」她硬是回了一句。"),_c('br'),_c('br'),_vm._v(" 接下來的一段時間我們又沒有對話，只有不停地大笑，不知怎麼已經嚴重到光看對方的臉就想笑。"),_c('br'),_c('br'),_vm._v(" 「我﹍﹍我不行了！你別笑了，我肚子好痛！」她按著肚子說。然後整個人仰躺在床上。我則也好不容易止住了笑，靠在衣櫃上看著她。奇怪的事又發生了，她躺在床上兩眼直看著我，而靠在衣櫃上的我也正盯著她，就著樣四眼（不！我有眼鏡，也許該說是六眼。）相對了一陣子，房間裡突然安靜下來。過了一會兒，她突然又笑了出來說：「喂！你該不會看著我想剛才窗外的事吧！」"),_c('br'),_c('br'),_vm._v(" 「嘿！嘿！Girl！」我苦笑著說「本人也是受過中華文化薰陶，懂著禮義廉恥，才不會這樣呢！」"),_c('br'),_c('br'),_vm._v(" 「真的？」她揚起眉一付「就憑你」的樣子問我。"),_c('br'),_c('br'),_vm._v(" 「當然！」我不服輸地說「本人有三不！一.不把髒話用在女人身上；二.不打女生：三.不和不是女友的女生發生任何親密接觸！」"),_c('br'),_c('br'),_vm._v(" 「哇！二十世紀新好男人！」她笑著說。"),_c('br'),_c('br'),_vm._v(" 「不好意思！不好意思！」我驕傲地回答。"),_c('br'),_c('br'),_vm._v(" 「那你有沒有女朋友？」她坐起身子露出好奇的眼神。"),_c('br'),_c('br'),_vm._v(" 「這個﹍﹍這個就別說那麼多了！」"),_c('br'),_c('br'),_vm._v(" 「呵呵！說實話！我有種你被甩過的感覺喔！」"),_c('br'),_c('br'),_vm._v(" 「這個﹍﹍是啊。」我緩緩地說著，視線又移