挑逗你喔！」說完後仍若無其事地從行李箱中拿出衣服。"),_c('br'),_c('br'),_vm._v(" 『不會吧！我心裡想什麼她會知道？』"),_c('br'),_c('br'),_vm._v(" 「所以你想什麼我都知道哦！」她又說了一句。"),_c('br'),_c('br'),_vm._v(" 『天吶！好像真的知道，我豈不是什麼都不要想！』"),_c('br'),_c('br'),_vm._v(" 「是啊！」她站起來抱著衣服說「最好什麼都別想。」說完，竟然向我眨一下右眼呢！"),_c('br'),_c('br'),_vm._v(" 「GOD！」我說完後，整個人仰倒在床上。"),_c('br'),_c('br'),_vm._v(" 這女人太奇怪了。"),_c('br'),_c('br'),_vm._v(" 七."),_c('br'),_vm._v(" 晚上，我請她到飯店樓上的餐廳吃飯，我依然穿著西裝，她卻不知從哪裡拿了一件露背的晚禮服，整個人顯的十分高貴的樣子，站在她身旁，我竟有種自卑感。"),_c('br'),_c('br'),_vm._v(" 「嘿！你會感到自卑嗎？」當我們走進餐廳時，她這麼說。"),_c('br'),_c('br'),_vm._v(" 「怎麼會！」我馬上不認輸似的回了一句「起碼我是請妳吃飯的人。」看到菜單我一開始拿不定主意，因為有些菜單上的東西，我沒吃過也不曉得好不好，她看我在猶豫，就向我說她點的東西很好吃，乾脆和她吃同樣的就好了，最後我決定和她吃一樣的，可是到底名字叫什麼，我卻記不太清楚了，只知道是牛排。接著我們另外要了紅酒。我們坐在窗邊的位置，整個城市中只有少數的大樓和我們一樣高，往左邊看去，可以看到一條公路，就是從我窗中看到的那一個，車頭燈仍在公路上努力地製造人工光河。"),_c('br'),_c('br'),_vm._v(" 「哈！真奇妙！」她吃下一塊牛肉後說「昨天晚上我是個病的要昏倒的人，今天卻坐在這兒吃飯！」"),_c('br'),_c('br'),_vm._v(" 「嘿！是啊！」我微笑著說，然後喝了一口紅酒「說真的，妳很會穿衣服喔！很少有人可以穿出真的適合她的衣服。」"),_c('br'),_c('br'),_vm._v(" 「當然囉！」她高興地說，似乎很滿意我剛才所說的，「但也要有代價呢！」"),_c('br'),_c('br'),_vm._v(" 「代價？買衣服的錢嗎？」我停下手上切肉的動作問她。"),_c('br'),_c('br'),_vm._v(" 「這是其中一個，但只要懂得節制也就行了。」她開始像個經驗老道的人說著「但是還要懂得保持身材，否則衣服再好看也沒用！」說完，她在嘴裡放進一塊牛肉，象徵她的申論結束。"),_c('br'),_c('br'),_vm._v(" 「我有一件事不懂，為什麼你們女生特別愛打扮。」"),_c('br'),_c('br'),_vm._v(" 她舉起右手向我輕輕搖了一下，意示等她把嘴中的東西嚥下去，然後她喝了一口紅酒說：「你怎麼為那麼基本的問題啊！難道你從和你女友分手後就沒和其他女人接觸過啊！」"),_c('br'),_c('br'),_vm._v(" 我沒回答，只微微笑了一下。"),_c('br'),_c('br'),_vm._v(" 「喔！對不起啦！我說錯話了，讓我告訴你吧！」她放下手上的餐具「有些女生啊！她愛美是天生的，事實上有些男生也是天生愛美的喔！這就沒辦法了，簡直像宿命一樣跟著，改不了，也就是說在天生的方面，大家是平等的！但女生比較可悲的是，我們女生卻像被下了詛咒一般地必須愛美，這又不像那些天生的單純了喔！」"),_c('br'),_c('br'),_vm._v(" 聽到這兒，我看她似乎要發表一些有趣的言論了，於是我停下手邊的動作，兩手掌相握著等她繼續說，這種兩手掌相握的姿勢，幾乎成了我認真聽講的MARK。"),_c('br'),_c('br'),_vm._v(" 「男人啊！假如穿的隨便，譬如西裝上衣沒燙平，鞋子上沾著一點一點的泥巴，甚至不穿襪子上台領獎，都可以被人接受，甚至有人因此敬畏那些人呢！心中以為那種外表隨便的男人，總有過人之處」說到這兒，她露出一臉不屑的眼神，又喝了一口紅酒接著說「真是一廂情願的白癡想法！那種人連自己都管不了，如何管理其他的事！但如果換個角色，今天女生套裝上衣沒有平整的折痕，別人會說這女人真是邋遢，甚至會不明不白地被冠上「不賢慧」，「不是好老婆」的稱號，還可能因此被解雇呢！你知道嗎？許多大老闆的女秘書一點用都沒有，但為何被雇用，只因為那些女人漂亮，他們才有面子，天啊！男人這種生物！」"),_c('br'),_c('br'),_vm._v(" 她說的大老闆只用美女，這我一點都不想否認，至少到目前為止，我都是看到