我們到旅館後面的一座小山中的小徑走著，原本不打算出發了，因為天空開始飄下細雨，但旅館老本卻告訴我們說這種天氣去才好，他說這種細雨的天氣中，走在那小徑裡，會有意外的發現。她一聽說會有「意外發現」就催我和她一起去看看，實在被她吵的受不了了，我才無可奈何的和她去。"),_c('br'),_c('br'),_vm._v(" 走進山中，先是一段有階梯的上坡，走上去後有一間頗有歷史的廟，我想去看看供奉什麼，她卻一直叫我走慢些，因為我們只有一把傘，而拿傘的我走太快她就會淋到雨。後來，經過我這看不懂日語，和她那懂得日語的人討論結果，我們對這廟所供奉的東西結論是「不知道」！呵呵！多不負責的結論。"),_c('br'),_c('br'),_vm._v(" 我們又繞著廟旁一條小路走上去，沒有階梯，只有用竹子在陂面上勉強做出階梯的樣子，小徑的左右全是竹子，雨打在竹子上發出「咚！咚！」的聲音，和遠處斷斷續續的鳥鳴呼應著。走到一半，她突然滑了一下，我趕緊捉住她的左手撐住她，她看著我笑了一下，顯的很不好意思的樣子。"),_c('br'),_c('br'),_vm._v(" 「腿軟啊！」我笑著說。"),_c('br'),_c('br'),_vm._v(" 「是被你嚇到啦！」她說。"),_c('br'),_c('br'),_vm._v(" 接下來我的手並沒有離開她，依然緊緊握住她的手，也許該說是「牽」住她的手。她沒說什麼，只說「你別滑倒啊！我可捉不住你。」我們繼續在這雨中的小徑走著，一路上除了竹子，還是竹子，我們卻沒人說要回去，只在這小徑間不停走著，我突然想到那天下午公園我們坐在長椅上時，我曾經想過要和她走在只有我們兩個人的小徑，如今雖然不在歐洲的林間，但確實是在只有我和她在「竹林」間走著。想到這兒，我不禁臉上露出了笑容。但我趕緊把笑容收斂起來，怕被她看見。我轉過頭去看她，只見她低著頭臉上掛著笑意。不久，我們走出竹林，來到一座小平台，我們站在上面向城市的方向望去，有一道彩虹就在那裡。"),_c('br'),_c('br'),_vm._v(" 「這彩虹台灣看的到嗎？」她問。"),_c('br'),_c('br'),_vm._v(" 我沒回答她，只知道我們的手到回旅館為止一直牽著。"),_c('br'),_c('br'),_vm._v(" 十二."),_c('br'),_vm._v(" 早上，我醒過來，只見我床腳附近有一團白影，等我戴上眼鏡，才知道是她坐在那邊，她背對著我，似乎在看著窗外，我看看床邊櫃子上的手錶，才五點半呢！平常她不可能那麼早起的。我掀開棉被，在床上坐起身子看著她的背影，她發現我醒來了，回過頭看著我，那一瞬間，她的右手拂過眼角，我彷彿看到了一滴眼淚。但在她臉上，卻又是掛著愉快的笑容。"),_c('br'),_c('br'),_vm._v(" 我坐到她的身邊看著她說：「妳沒事吧！」"),_c('br'),_c('br'),_vm._v(" 她突然笑了出來說：「怎麼會有事，兩個禮拜來吃住全都花你的錢，高興都還來不及呢！」"),_c('br'),_c('br'),_vm._v(" 她說完，繼續維持著那故作堅強的笑容。她這一說我才想到我和她已經在一起過了兩個禮拜了。我微笑著說：「那咱們這白吃白喝的白小妹（她不姓白喔！），今天還會聯絡妳家人嗎？還是他們又不在家呢！」"),_c('br'),_c('br'),_vm._v(" 「嗯！可能吧！」她的神情轉為落寞了，這和她以前不同，以往我這樣問，她都會裝出一副認真的樣子說：「難說喔！真傷腦筋！」"),_c('br'),_c('br'),_vm._v(" 「喂！幹嘛一早臉臭臭的，打起精神來，我去洗臉刷牙囉！」"),_c('br'),_c('br'),_vm._v(" 說完，我拍了拍她的肩膀。走進浴室，我站在大鏡子前刷牙，她突然走了進來說要和我一起刷牙，我點點頭。她進來以後，拿出我買給她的牙膏、牙刷，和我一同站在鏡前刷牙。她突然含住牙刷，忍住笑似的指指鏡中我的臉，我轉過頭看著她，她拿下牙刷忍著笑說了一句：「你的泡泡好多，快掉下來了。」說完，她終於忍不住笑了出來，結果她的牙膏泡沫噴了我滿臉都是，而我不知怎樣，也跟著笑了出來，同樣地她的臉上也全是泡沫了。後來我們各拿了條毛巾替對方擦著臉上的泡沫，有一瞬間，我們幾乎同時停下手來，只盯著對方的臉看，不過，那真的是只有一瞬間的事。接著我們到樓下餐廳吃著早餐，今天的早餐，她特別的安靜，幾乎不說話，這時，我才發現自己是多麼習慣耳邊有她說著話。"),_c('br'),_c('br'),_vm._v(" 「快八點了，我要出去囉！」我對她說。"),_c('br'),_c('br'),_vm._v(" 「嗯！」她點點頭，在我要叫服務生結帳時，她卻突然叫住我。"),_c('br'),_c('br'),_vm._v(" 「可不可以﹍﹍今天把你的電腦借我。」她小聲地問著。"),_c('br'),_c('br'),_vm._v(" 我看了她一下，然後說「可以啊！反正今天要用的資料搭檔那邊有了，就借妳吧！」我把電腦推到她的左手邊。"),_c('br'),_c('br'),_vm._v(" 「謝謝！」她看著我說。"),_c('br'),_c('br'),_vm._v(" 「那妳要電腦幹什麼呢？」我問。"),_c('br'),_c('br'),_vm._v(" 「這是秘密哦！」說完，她臉上露出了開心的笑容。"),_c('br'),_c('br'),_vm._v(" 算了，管她幹什麼，她能這樣開心就好。下午五點，天空下起微微的細雨，我不想讓手中的資料濕了，於是了部計程車回飯店去。才走進大廳，她就跑了過來，抱住我的右臂，靠在我的身邊拉著我往電梯走。"),_c('br'),_c('br'),_vm._v(" 「快！快！」她露出開心的笑容催促著我。"),_c('br'),_c('br'),_vm._v(" 回到房間，她還幫我從衣櫃裡拿出一套西裝來。"),_c('br'),_c('br'),_vm._v(" 「幹嘛？」我站在書桌旁看著她問。"),_c('br'),_c('br'),_vm._v(" 她臉上依舊掛著笑容走到我面前說：「今天要慶祝一下啊！」"),_c('br'),_c('br'),_vm._v(" 「慶祝什麼？」我一臉茫然地問她。"),_c('br'),_c('br'),_vm._v(" 「你不知道啊！」她似乎有點吃驚，但隨即又露出笑容說：「沒關係，沒關係，等會兒你就知道，總之現在先去洗澡，一定要好好洗喔！還有頭髮也要洗，知道嗎？」她像在交代小孩子似的對我說。"),_c('br'),_c('br'),_vm._v(" 我點了點頭，她才滿意地走出房間。我該不會是要被她捉去賣了吧！不然幹嘛叫我洗乾淨。等我換好衣服，她又對我僅有的五條領帶選了半天，她選了一條藍色的說：「這才有男人味！」然後，我又在房間外等她，差不多過了四十分鐘後她走了出來，穿著一件紫色的晚禮服，脖子上戴著一條白金的項鍊有一個K金的墜子，而她的長髮以最舒服的姿勢披垂在她的肩上。她拉著我進電梯，然後說今天她要請客。我想，請客的錢還不是我出。我們到了樓上的餐廳，也就是我們第一次吃晚餐的餐廳，兩人熟練地點著菜，我們還是點了上次點的牛排，只是我一直記不住叫什麼。"),_c('br'),_c('br'),_vm._v(" 「妳今天真的很漂亮。」這是我今天在餐桌上對她說的第一句話，她看著我，臉上露出了開心的笑容。好美，真的好美。"),_c('br'),_c('br'),_vm._v(" 八點半，我們吃完了牛排，依舊喝著柳橙汁，我問她要不要叫甜點，她卻叫我等一下。不久，，有一個人走上鋼琴演奏的圓台，用麥克風說一些著我聽不懂日語，然後，幾乎全餐廳的人目光全轉到我們身上而且向我們熱烈的鼓掌。接著，有個服務生推了一個小推車過來，上面放著一個蛋糕，插著兩根紅色的蠟燭分別是「3」和「0」的形狀，然後服務生小心地把蛋糕放在桌上。"),_c('br'),_c('br'),_vm._v(" 「生日快樂！你三十歲囉！」她開心地說。"),_c('br'),_c('br'),_vm._v(" 餐廳的燈光也在這時暗了下來，整個餐廳中，蛋糕上的蠟燭成了最亮的兩點。鋼琴聲也在這時悠悠地傳了出來，四周的聲音也全靜了下來，只留著鋼琴的音符在跳躍，我仔細一聽，原來在彈著「生日快樂歌」。而她，就坐在我的對面，隨著琴聲，她柔柔地唱著生日快樂歌，透過燭光，我看著她充滿笑容的臉龐，在她溫柔無比的歌聲中，不知不覺，眼眶的水分變的太多，模糊了我的視線。仔細想想，自己也有十年沒有人替我慶祝生日，甚至說聲「生日快樂」的人也沒有。等琴聲漸逝，等她的歌聲停止，餐廳的光線才又慢慢亮了起來，我趕緊拭去就要掉落的眼淚。"),_c('br'),_c('br'),_vm._v(" 「幹嘛！」她微笑著說「感動的想哭啊！」"),_c('br'),_c('br'),_vm._v(" 「不是！」我也微笑著說「我打哈欠！」說完，兩人都笑了。"),_c('br'),_c('br'),_vm._v(" 她叫我趕緊許個願，然後我和她一起吹熄蠟燭。"),_c('br'),_c('br'),_vm._v(" 「嘿！你許什麼願啊！」她好奇的問。"),_c('br'),_c('br'),_vm._v(" 「不能說啊！說了就沒效了！」我說。"),_c('br'),_c('br'),_vm._v(" 她聽我這麼說，有點失望地說：「小氣鬼！」"),_c('br'),_c('br'),_vm._v(" 「喂！」我從西裝口袋裡拿出個小盒子「這個送妳！」我把盒子放到她面前。"),_c('br'),_c('br'),_vm._v(" 「這是什麼？不會裡面放戒指吧！我還不打算嫁喔！」她拿起盒子看著說，然後她慢慢打開盒子，裡面放了一支手錶，紫色的錶帶，不同層次的紫色渲染著錶面和錶框，錶面裡鑲著一顆小鑽石，整個錶面因此顯的耀眼。"),_c('br'),_c('br'),_vm._v(" 「哇！好漂亮！謝謝！」她高興地看著錶說「而且和我今天的衣服搭配呢！」她很快地把手錶戴在左手腕上，然後舉起左手向我搖一搖說：「漂亮嗎？」"),_c('br'),_c('br'),_vm._v(" 「漂亮，手錶因為妳所以變的更漂亮了。」她聽完，露出了害羞的笑容。今夜的日本特別美，因為有她的笑點綴著。"),_c('br'),_c('br'),_vm._v(" 「那我呢？漂亮嗎？」她問。"),_c('br'),_c('br'),_vm._v(" 「妳因為手錶的陪襯，更吸引人了！」我說。"),_c('br'),_c('br'),_vm._v(" 她呵呵地笑了出來，然後反覆地摸著手錶。我們把桌上的小蛋糕切成四塊，我和她各吃了兩塊，由於奶油很多，依據她的說法，她是「捨身陪君子」。君子？我以為我是他的良人呢！"),_c('br'),_c('br'),_vm._v(" 十三."),_c('br'),_vm._v(" 回到房間，我脫下西裝外套，然後對她說：「妳在這兒換衣服吧！我去浴室內換，妳換好了再叫我！」"),_c('br'),_c('br'),_vm._v(" 「不用了，」她說「你在窗邊的衣櫃換就好了，我在床邊換。」"),_c('br'),_c('br'),_vm._v(" 「妳不怕我偷看啊！」我開玩笑似的說。"),_c('br'),_c('br'),_vm._v(" 「我知道你不會的。」說完，她從衣櫃裡拿了套運動服走去她床邊。「我要換衣服囉！你轉過頭去吧！」"),_c('br'),_c('br'),_vm._v(" 我趕緊轉過頭去看著窗戶，但從窗戶玻璃的反射，可以看見她脫下衣服的背影，我趕緊把視線移回在我手上的運動服，然後以最快速度換好衣服。"),_c('br'),_c('br'),_vm._v(" 「我換好了！」她對我喊著，而早換好衣服的我，這才轉過頭去。她盤坐在她床上，背靠著枕頭，手上拿著一個用紅色包裝紙包著的盒子。「喂！坐到我旁邊來，我有禮物給你！」她高興地說著。我走過去坐在床邊，她卻叫我坐到床上，背靠著她身邊的另一個枕頭，她則緊緊地靠在我的右手邊。"),_c('br'),_c('br'),_vm._v(" 「打開看看。」她把盒子放到我的大腿上。我打開盒子，裡面放著我的筆記型電腦，我轉頭看著她，她卻指指電腦，要我打開電腦，電源一開，不久歡迎畫面又出現，但這次竟然有鋼琴聲。"),_c('br'),_c('br'),_vm._v(" 「鋼琴聲？」我轉頭問她，她伸出食指按住我的嘴唇，要我仔細的聽。那琴聲慢慢地傳遍了房間，琴聲中，我聽到了一股纏綿不絕的情意偷藏在音符間，但卻又有一種悲傷的休止符在裡面點綴著，琴聲大概過了兩、三分鐘後又開始重複。"),_c('br'),_c('br'),_vm._v(" 「如何？好聽嗎？我彈的喔！」她開心地對我說。"),_c('br'),_c('br'),_vm._v(" 「很不錯呢！」我說「不過，妳怎麼用的？哪來的設備？」"),_c('br'),_c('br'),_vm._v(" 「先別管啦！我明天再告訴你。」她說。「快！快！你看螢幕！」"),_c('br'),_c('br'),_vm._v(" 接著螢幕中央出現了張她的照片，照片中的背景是我們去過的公園，她側坐在我們上次坐的長椅上，背後還是有那棵開著黃花的樹，她穿著一件藍色的絨毛衣和一條淡藍的長褲，看來她真的很愛藍色，臉上露著開心的笑容，那種小孩特有的甜甜的笑。"),_c('br'),_c('br'),_vm._v(" 「還有喔！」她把右手按在我放在鍵盤的手上，就這樣按住我的手，移動著畫面上的箭頭，指著照片下的一行字。「思念? 是從回憶深處伴著微風而來的熟悉香味」"),_c('br'),_c('br'),_vm._v(" 然後點了兩下，開出了一個新的視窗，裡面是她的小檔案，寫了她的興趣、學歷、喜歡的東西、崇拜的偶像﹍﹍事實上這些她平常就向我說到我都可以背下來了。不過最令我吃驚的是，她還寫下她的三圍。"),_c('br'),_c('br'),_vm._v(" 「33B」我用不可思議的眼光看著她說。"),_c('br'),_c('br'),_vm._v(" 「早跟你說過我的比32B大了！」她若無其事地說著。"),_c('br'),_c('br'),_vm._v(" 「那妳的姓名和生日怎麼都沒寫？」我問。"),_c('br'),_c('br'),_vm._v(" 「那個不重要啦！我改天再告訴你！」她又用這句避開話題。"),_c('br'),_c('br'),_vm._v(" 接著，她又點了一下螢幕，又開出了一個視窗，裡面有二十多張她的照片，她興奮地指著一張張的照片叫我看，而二十多張照片中竟然有十多套不同的衣服！"),_c('br'),_c('br'),_vm._v(" 「哇！今天妳這樣幫我慶祝生日，我要怎樣報答妳啊！」我開玩笑地問她。只見她聽完我說的話後，臉上的笑容收斂了一下。"),_c('br'),_c('br'),_vm._v(" 「有點冷耶！」她說。"),_c('br'),_c('br'),_vm._v(" 於是我把在腳邊的棉被拉上來蓋到腰間的位置。"),_c('br'),_c('br'),_vm._v(" 「可不可以﹍﹍」她轉頭看著我說「可不可以讓我靠在你的懷中，就今晚。」"),_c('br'),_c('br'),_vm._v(" 我看了她一下，她的眼神竟然在這時候顯的十分脆弱，我探尋了那脆弱的深處一會兒，然後微笑著說：「好吧！大哥我今天就以身相許了！」"),_c('br'),_c('br'),_vm._v(" 她看著我呵呵地笑了出來，我伸過右手把她擁入我的懷中，她的臉龐就靠在我的右肩下。接下來，我們開始聊到彼此的小時候，從國小、國中、高中﹍﹍一路說過來，她一邊說著，一邊不停用手向天花板比著，好像那團透明的空氣中，可以看見她腦中的畫面般。而我，卻從她的臉中看見自己十年未動的那泓淡藍的情感。突然，她停下先前的話題，伸手指著電腦螢幕右下方的時間，顯示著AM 12:05。"),_c('br'),_c('br'),_vm._v(" 「十二點五分了。」她說。"),_c('br'),_c('br'),_vm._v(" 「嗯！二月七號過去了。」我說。"),_c('br'),_c('br'),_vm._v(" 「喂！」她叫了我一聲。我低下頭看著她，她也正看著我，在那深深的眸中。「Birthday Kiss!」她說完就把她的唇緊緊貼上我的唇。而我，則把懷中的她抱得更緊。今晚，她依偎在我的懷抱中慢慢地睡著了，我看著她沈睡的臉，脖子下感受著她鼻息的溫度，空氣中正瀰漫著她身上的香味。而那夜我的生日願望是短短的一句---讓她知道我愛她！"),_c('br'),_c('br'),_vm._v(" 十四."),_c('br'),_vm._v(" 隔天醒來，她已經不見了，全部屬於她的東西都不見了，只有書桌上放著我的電腦和一封信，以及壓在信下的十萬元日幣，還有她留在我身上的香味。我打開信封，拿出淡藍的信紙讀著。"),_c('br'),_c('br'),_vm._v(" 『嘿！大睡豬，你可醒了啊！起來時你一定發現我不見了，事實上，我已經在五點坐飛機走了！』我看看手錶，現在五點半了。"),_c('br'),_c('br'),_vm._v(" 『別看錶了！你一定來不及追我的，你很糟糕喔！一個女人從你懷中離開都沒警覺，這樣不行喔！你對我一定有很多疑問，現在我一一解答吧！事實上，我留在日本原來主要是找我老爸，但他做了一些令我生氣的事，我一氣之下就離家出走啦！那時剛好手邊有你的名片，所以就找上你了，事實上你住的飯店是我叔父的，飯店中大概職位高一點的主管都認識我，所以一開始我是騙你什麼被搶之類的事，你先別生氣喔！看完信再說。"),_c('br'),_c('br'),_vm._v(" 所以和你在一起的兩個禮拜，我幾乎沒花到你給我的十萬日幣，因為，我叔父全部會幫我出錢，但你的十萬日幣，我還是拿走了，現在在你桌上的，是我另外領出來的，為什麼特地拿走你的十萬日幣，是想﹍記住遇過你這樣的人，為我做過這些事，現在看到那筆錢，心中還有一份感動呢！"),_c('br'),_c('br'),_vm._v(" 昨天，你生日，也就是二月七日，為什麼我會知道呢！這是因為我們第一次見面時你在飛機把記事本給我看時，我瞄到你的生日，這令我很吃驚喔！因為我的生日是二月八日，居然可以看到比我早一天生日的人，所以一直印象深刻。你的禮物，也就是在電腦上的那個，可是花了我一個禮拜的功夫呢！我要找攝影師替我照相，找電腦技術人員幫我做處理，才變成昨晚你看到的樣子呢！還有，那首曲子可是我自己譜的喔！那一個禮拜下來很忙呢！所以別以為我只是每天騎著腳踏車亂逛。"),_c('br'),_c('br'),_vm._v(" 記得昨晚，也不是昨晚啦！反正是十二點零五分的時候，我吻了你嗎？因為那時候正是我出生的那一刻呢！二十六年前的那一刻，我出生了，為什麼想吻你，因為﹍﹍二十六年來，我終於遇見一個真正令我心動的人，我怎麼能不把初吻獻給他呢！而今天，也就是我的生日，我許下的願望是-----讓你知道我愛著你（呵！我只「寫」出來，應該還有效吧！）你會不會覺得這願望很傻呢！對了！別以為我每天都比你晚起喔！其實我每天都比你早很多就起床了，我總是坐在床邊看著你睡覺時的臉，但怎麼也無法將你的睡臉和那下流的色狼聯想在一起，呵！別生氣喔！不過，說實在的，我有時也會克制自己別一直看著你，不然，我會忍不住過去吻你的臉。寫到這裡，不曉得怎麼開始回想起我們相遇的種種﹍﹍飛機上幫你找筆、你抱著我跑進醫院、午後的公園，我們同坐在一張長椅上。還有，甚至連被你看到我穿內衣的樣子，現在竟有點懷念起來了，對了！這可是本小姐第一次讓別人看到我的胴體呢！真的好多東西都值的我去懷念，記得你用食指接住了我的眼淚、騎著腳踏車載我的那段上坡、細雨中漫步的竹林，現在手長上似乎還能感受到你牽住我時的溫度呢！還有，一輩子不會忘記，昨晚你吻我時的那股溫柔﹍﹍"),_c('br'),_c('br'),_vm._v(" 說真的，假如昨晚你給我的盒子內是戒指，假如你問我願不願意嫁給你，或許你不會相信，但我真的會答應嫁給你，真的，想嫁給你。四點半了！我想我必須走了，你剛才說了一句「別走！」把我嚇了一跳，原來你是在說夢話，但終究，我還是要走啊！我想，如果未來，我們能再見面，不知會是在哪裡，是在台北的某個街，或是在飛機上，或是在這擁有你我記憶的飯店﹍﹍，但我想，假如我們真有一天能再面對面的見面，我對你說的第一句話不是「嗨！」而是﹍﹍﹍﹍﹍我愛你！"),_c('br'),_c('br'),_vm._v(" PS：我想，我沒有欠你任何錢吧！我可不能壞了我家從不欠錢的傳統喔！"),_c('br'),_c('br'),_vm._v(" 再PS：記得你說過，從不跟女友以外的人發生親密關係，那昨晚我吻了你，那麼，現在我又是你的什麼呢？^^\""),_c('br'),_c('br'),_vm._v(" 好了！不寫了，再寫下去我就要去補妝了！我可是功率良好的眼淚製造機呢！我想，就再見了！"),_c('br'),_c('br'),_vm._v(" 十五."),_c('br'),_vm._v(" 「渾蛋！」我邊說邊拭去模糊了視線的淚水「妳還欠我醫藥費呢！」"),_c('br'),_c('br'),_vm._v(" 我征征地站在書桌前，窗外吹進來的風，告訴我今天的清晨，比較冷﹍﹍三個月過去了，我從日本回來也兩個月了，我和她的事一直沒辦法忘記，我把這件事寫了下來，當成一篇小說，題目取叫「故事」。沒錯啊！那是一件發生在我身上的故事，那個人，那件事﹍﹍然後，我用「逍風」這暱稱把這小說公布在網路上。"),_c('br'),_c('br'),_vm._v(" 今天，五月的深夜，我有點無聊，上網到聊天室中晃著，不久，螢幕上出現一排字﹍﹍"),_c('br'),_c('br'),_vm._v(" 怡瑜：逍風，安！"),_c('br'),_c('br'),_vm._v(" 逍風：怡瑜，安！"),_c('br'),_c('br'),_vm._v(" 逍風：怡瑜？？？？"),_c('br'),_c('br'),_vm._v(" 怡瑜：記得我嗎？日本？飯店？溫泉？：）"),_c('br'),_c('br'),_vm._v(" 逍風：是妳啊！妳回台灣了！：）"),_c('br'),_c('br'),_vm._v(" 怡瑜：風~~三八喔！把我們的事放到網路上，要給稿費~~）"),_c('br'),_c('br'),_vm._v(" 逍風：還說呢！妳還欠我醫藥費！"),_c('br'),_c('br'),_vm._v(" 怡瑜：那點小錢！﹍﹍好吧！小妹我請你吃飯，算賠你了！^_^"),_c('br'),_c('br'),_vm._v(" 逍風：喂！怡瑜！有件事告訴妳！"),_c('br'),_c('br'),_vm._v(" 怡瑜：幹嘛？？？"),_c('br'),_c('br'),_vm._v(" 逍風：我愛妳﹍﹍﹍"),_c('br'),_c('br'),_vm._v(" 怡瑜：呵﹍﹍^^\""),_c('br'),_c('br'),_vm._v(" 我的雙手離開鍵盤，眼睛看著她在螢幕上的笑臉，思維陷入了回憶，回憶那張笑臉及那個我愛的人。此時，她的笑臉彷彿透過螢幕傳來，她的笑聲隨著窗外的風鈴響起﹍﹍"),_c('br'),_c('br'),_vm._v(" 風從窗口徐徐吹了進來，輕輕地拂了我的臉，而有一股熟悉的香味，夾伴在這五月的風中。腦中，回想起那一夜電腦螢幕上的字：???? ---------思念?? 是從回憶深處伴著微風而來的熟悉香味。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-8",modifiers:{"accordion-8":true}}],attrs:{"block":"","variant":""}},[_vm._v("放風箏的自由")])],1),_c('b-collapse',{attrs:{"id":"accordion-8","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 「有人說大多數的女人，都是先愛上愛情，才愛上男人的。」妳說。"),_c('br'),_c('br'),_vm._v(" 「那妳是哪種女人？」我問。"),_c('br'),_c('br'),_vm._v(" 「我是先愛上了自由，才愛上愛情的。」妳回答。"),_c('br'),_c('br'),_vm._v(" 「我要的愛情就像放風箏，你是放風箏的人，我就是那風箏。"),_c('br'),_c('br'),_vm._v(" 風箏是屬於廣大天空的，是自由不受拘束的，"),_c('br'),_c('br'),_vm._v(" 但是，只要你需要我，一收線，我就會回到你身邊。"),_c('br'),_c('br'),_vm._v(" 只想把風箏放在身邊的男人，風箏也就不是風箏了。」妳說。"),_c('br'),_c('br'),_vm._v(" 因此，愛上妳，我學會不嫉妒，"),_c('br'),_c('br'),_vm._v(" 因為我不能成為妳口中「自私的只想綁住女人」的男人。"),_c('br'),_c('br'),_vm._v(" 所以，我必須在看著妳和另一個男人親暱談笑時，適時的微笑。"),_c('br'),_c('br'),_vm._v(" 儘管我的心已經像一條絞乾的手帕，被擰了再擰，扭了再扭，也不敢喊痛。"),_c('br'),_c('br'),_vm._v(" 儘管我的嘴角像掛了千金重的砝碼，也得費盡心力的揚?一點笑意。"),_c('br'),_c('br'),_vm._v(" 然後說：「原來是妳的高中同學啊！難怪你們的感情那麼好。」"),_c('br'),_c('br'),_vm._v(" 「是啊！」妳還給了我一個天使般的燦爛笑容。"),_c('br'),_c('br'),_vm._v(" 愛上妳，我學會不擔憂，因為我不能成為「不信任妳」的男人。"),_c('br'),_c('br'),_vm._v(" 所以，在深夜一兩點，妳終於回家時，"),_c('br'),_c('br'),_vm._v(" 我不能問妳：「妳到底去了那裡？怎麼現在才回來？」"),_c('br'),_c('br'),_vm._v(" 儘管我剛剛是那樣的坐立難安，緊抱著時鐘望著窗外開門與關門，"),_c('br'),_c('br'),_vm._v(" 儘管我恐懼的滿腦是妳車禍血流滿地的情景，"),_c('br'),_c('br'),_vm._v(" 還想著如果只是妳和男孩子玩得太晚了就好了。!"),_c('br'),_c('br'),_vm._v(" 但是，我只是坐在沙發上拿著報紙，對著妳笑：「妳一定累了。早點睡吧！」"),_c('br'),_c('br'),_vm._v(" 謝謝妳，因為我愛上妳，我才能成為最自由的男人。"),_c('br'),_c('br'),_vm._v(" 不必擔心妳會抓著我聞身上是?有女人的香水味，不必編出任何晚歸的藉口，"),_c('br'),_c('br'),_vm._v(" 我可以大大方方的和過去的女朋友喝茶，"),_c('br'),_c('br'),_vm._v(" 因為妳說：「男人也可以有女性朋友。」"),_c('br'),_c('br'),_vm._v(" 但是我心裡卻想著：不知妳現在和哪個男生在一起?？"),_c('br'),_c('br'),_vm._v(" 我也可以和公司客戶應酬到深夜，"),_c('br'),_c('br'),_vm._v(" 因為妳說：「我相信你，這是你的工作。」"),_c('br'),_c('br'),_vm._v(" 但是我心裡焦慮著：不知妳回到家了沒？"),_c('br'),_c('br'),_vm._v(" 然而，我必須記著：妳是風箏，一個自由的風箏，我不能成為綁住妳的男人。"),_c('br'),_c('br'),_vm._v(" 今晚，我的車子故障了，半夜三點才回到家。"),_c('br'),_c('br'),_vm._v(" 我看到了妳，淚流滿面，焦慮不安的妳，是我從沒有見過的。"),_c('br'),_c('br'),_vm._v(" 妳撲進我懷裡，哽咽地直說："),_c('br'),_c('br'),_vm._v(" 「嚇死我了，你到哪裡去了？連通電話也不打回來，"),_c('br'),_c('br'),_vm._v(" 我打電話問遍你所有的朋友，可是……」然後妳又哭了。"),_c('br'),_c('br'),_vm._v(" 我心疼地摟住妳，?怪這些話怎麼如此熟悉，這也是我每天每天都想對妳講的話啊！"),_c('br'),_c('br'),_vm._v(" 「我不曉得妳會這麼擔心。」我說。"),_c('br'),_c('br'),_vm._v(" 「我當然擔心，我擔心死了，因為你是我最愛的人啊！"),_c('br'),_c('br'),_vm._v(" 為什麼，你從來不擔心我，不問我去哪裡了呢？」妳說。"),_c('br'),_c('br'),_vm._v(" 我愕然了。「我以為妳喜歡自由。」"),_c('br'),_c('br'),_vm._v(" 妳好委屈的!望著我。"),_c('br'),_c('br'),_vm._v(" 「我是喜歡自由啊！"),_c('br'),_c('br'),_vm._v(" 但是飛得再高再遠的風箏，也有想休息的時候。"),_c('br'),_c('br'),_vm._v(" 你只會放風箏，卻忘了收風箏。"),_c('br'),_c('br'),_vm._v(" 有你的支持，休息過後的風箏才能飛得更高更遠啊！"),_c('br'),_c('br'),_vm._v(" 難道你忍心讓風箏一個人孤獨的在天上飛？」"),_c('br'),_c('br'),_vm._v(" 我抱著妳，突然間明白自己是世界上最傻的放風箏的人。"),_c('br'),_c('br'),_vm._v(" 放風箏的自由，我現在才懂。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-9",modifiers:{"accordion-9":true}}],attrs:{"block":"","variant":""}},[_vm._v("蝴蝶情書")])],1),_c('b-collapse',{attrs:{"id":"accordion-9","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 那年的冬令自強活動---雪山營隊第二梯次，我認識了淑芬、阿德。我從沒5天4夜的活動會讓我們結下長長久久的緣份。"),_c('br'),_c('br'),_vm._v(" 淑芬念輔英護校、阿德念的是那時候還沒升格改制的屏東農專。我們念不同的學校、卻格外的有話聊大概是因為那次淑芬在活動中扭傷了腳。我跟阿德輪流揹她所揹出來的情誼。然而我們三人顯然都不滿足僅於友情的程度，紛紛擾擾的情愫在我們之間產生。簡單來說就是淑芬喜歡我、我喜歡阿德、阿德喜歡淑芬。"),_c('br'),_c('br'),_vm._v(" 阿德是一隻黑黑壯壯的熊、來自彰化農家，家裡世代務農、很老實很呆的一隻可愛土熊。他黑壯老實、卻有一副憨憨的娃娃臉、再戴上一副古板老氣的黑框眼鏡、說有多號呆就有多號呆。一般女孩子都不會喜歡這種型的男孩子，但我就是喜歡。不過，在那青澀的年代再那個尚未解嚴的時代氛圍裡，同性戀不只是道德的譴責而已。"),_c('br'),_c('br'),_vm._v(" 活動結束、我們三人留下聯絡電話開始我們的長遠友誼。淑芬是女孩子、我是不能說、阿德是不敢說，即使我們的感情隨著頻繁的聚會而起了質變我們三人卻都沒有開口。直到我們都快畢業。阿德打電話要我幫他作一些東西。到了阿德的住處，果然是農藝科的人住的房間。許許多多的植物、很多昆蟲標本、培養盒、養殖槽。他的桌上擺了一個剛修好漆的木框要我幫忙作個襯底。"),_c('br'),_c('br'),_vm._v(" 「這要幹嘛的？」我不解的問著"),_c('br'),_c('br'),_vm._v(" 「蝴蝶的標本箱」"),_c('br'),_c('br'),_vm._v(" 「要送給淑芬的？」"),_c('br'),_c('br'),_vm._v(" 他立刻漲紅了臉、尷尬的點頭"),_c('br'),_c('br'),_vm._v(" 「我沒有喔!」 我沒好氣的問著"),_c('br'),_c('br'),_vm._v(" 「有阿！做好了」"),_c('br'),_c('br'),_vm._v(" 他拿出一個小很多、裡面只有三隻蝴蝶的標本盒"),_c('br'),_c('br'),_vm._v(" 「哇！！你大小眼喔、嘖嘖！！」我笑著說"),_c('br'),_c('br'),_vm._v(" 他頑皮的伸伸舌頭。"),_c('br'),_c('br'),_vm._v(" 我到布莊去挑了一塊暗紅的絨布襯在箱底，然後在框沿畫了一些簡單的紋飾。"),_c('br'),_c('br'),_vm._v(" 「好漂亮！！不愧是美工科的。」"),_c('br'),_c('br'),_vm._v(" 他驚喜的誇耀著卻沒有察覺我心裡的黯然。然後他開始將一隻隻撒過防腐粉的蝴蝶、小心翼翼的釘上去。一隻、兩隻、三隻、等到第四隻。我立刻辨識出這不是普通的標本箱。每一隻蝴蝶都藏著私密的戀語。"),_c('br'),_c('br'),_vm._v(" 「阿德-----你這呆子！！ 何不乾脆用說的！！這樣暗示她會知道才有鬼。」於是、我冷冷的說，你這樣子她還是不會知道的。"),_c('br'),_c('br'),_vm._v(" 「你看得出來？！」他驚訝莫名"),_c('br'),_c('br'),_vm._v(" 「我們來打賭！！你覺得她多久才能看出來？」"),_c('br'),_c('br'),_vm._v(" 「可是、你不就一下子看出來了？」（笨胖子！！那是因為我喜歡你！！）"),_c('br'),_c('br'),_vm._v(" 他還是呆呆的釘完最後一隻蝴蝶。兩天後、在例行的聚會中。淑芬收到這份禮物、只有微笑的謝謝說著這些蝴蝶好漂亮。"),_c('br'),_c('br'),_vm._v(" 這個賭注一過就是12年。淑芬畢業沒多久、就到台北醫院實習，沒多久、就嫁給駐院醫師。他的醫生老公非常爭氣、不久就開始掛牌行醫。在三重是頗為有名的小兒科。淑芬先生娘生涯過的順心又優雅。而笨笨呆呆胖阿德，回到彰化老家繼承農莊生活，不過、他也頗為努力，在農政月刊上老是看見他又發明什麼新的截肢移植栽種技術，不然就是什麼十大農村青年。而他也沒忘記寄來一箱箱他努力的成果分享給我。"),_c('br'),_c('br'),_vm._v(" 收到他的喜帖當然也就很自然的邀淑芬，不過、淑芬剛好要出國，所以、最後只有我去吃他的喜酒。新娘是外籍新娘。鄉下不好擇偶吧。喜宴結束、他雖有醉意、但仍提了兩箱蘋果給我，託我拿一箱給淑芬。還 忘 不 了 她 嗎......................."),_c('br'),_c('br'),_vm._v(" 我隔了幾天去三重找淑芬，發福的淑芬 笑吟吟的迎接著我，她寶貝小女兒真是秀麗可愛。豪華的客廳、有的只是名酒櫥櫃、音響、室內設計師挑選的現代畫。"),_c('br'),_c('br'),_vm._v(" 「你在找什麼？」她端來一杯茶"),_c('br'),_c('br'),_vm._v(" 「阿德以前不是有送你一個蝴蝶標本？」"),_c('br'),_c('br'),_vm._v(" 「喔！那個呀！！沒地方掛、我收到櫃子去了喔.........」"),_c('br'),_c('br'),_vm._v(" (收到櫃子去了.............)我請她拿來給我看看怎麼啦！！她進去儲物間找了好一陣子，終於翻出那個發霉的蝴蝶標本。我看著這個標本、呆了好一陣子說:「淑芬、妳以前知道阿德在喜歡妳嗎？」她愣了一下"),_c('br'),_c('br'),_vm._v(" 「嗯、有感覺、可是他都沒說」"),_c('br'),_c('br'),_vm._v(" 「嗯、如果他那個時候跟妳告白、妳會接受嗎？」"),_c('br'),_c('br'),_vm._v(" 「哎呀！！多久的事了！！還提」(沈默了好一會)"),_c('br'),_c('br'),_vm._v(" 「不會吧、因為........ 我知道她想說什麼」"),_c('br'),_c('br'),_vm._v(" 「淑芬....這個蝴蝶標本、其實是一封情書......」我開始指給她看第一隻蝴蝶的翅膀的紋路，因為圖地反轉的配置，形成一個不明顯的小寫i字。"),_c('br'),_vm._v(" 第二隻則是大寫的L。"),_c('br'),_vm._v(" 第三隻是o"),_c('br'),_vm._v(" 第四隻v"),_c('br'),_vm._v(" 第五隻則是e"),_c('br'),_vm._v(" 第六隻是y"),_c('br'),_vm._v(" 第七隻是另一種o形圖案的蝴蝶"),_c('br'),_vm._v(" U則是第八隻，雖然因為色彩紛雜、其實並不好辨識但如果稍微留意、卻是可以看得出來..........."),_c('br'),_c('br'),_vm._v(" 淑芬很快就紅了眼睛，她突然跑進去廚房洗那些阿德託我帶給她的蘋果，磨蹭了一 會。她坐在廚房的高腳椅上，拿起一粒蘋果，咬了起來、清脆的咬聲，迴響在午後的寧靜廚房裡。這蘋果好甜呢！ 她笑著、淚水卻臉頰快速滑下，連掩飾都來不及。"),_c('br'),_c('br'),_vm._v(" 是阿! 我能輕易解讀這八隻字母蝴蝶，然而這卻不是給我的。但擁有這傾慕的證據的人，卻12年後才能在別人的指示下獲得解答。這8隻蝴蝶只有三隻是台灣原產，其他都是源自非洲或印度、美洲。阿德在圖鑑上看到後，就想盡辦法跟原產的國家的蝴蝶同好會購買或是交換。有些大學的研究所堅持不賣，他只好走遍中央山脈去尋找對方指定的台灣特有蝴蝶來交換。這八隻蝴蝶耗費他兩年多時間為的只是 一句 i love you。然而 淑芬卻不能懂得。"),_c('br'),_c('br'),_vm._v(" 能懂你的 你未必愛 你所愛的 卻未必懂你"),_c('br'),_c('br'),_vm._v(" 情愛有時真是一種最殘忍的食物鏈。"),_c('br'),_c('br'),_c('span',{staticStyle:{"font-style":"italic"}},[_vm._v("青山不問凡塵事,綠水何曾說是非~~")])])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-10",modifiers:{"accordion-10":true}}],attrs:{"block":"","variant":""}},[_vm._v("珍惜身邊的人")])],1),_c('b-collapse',{attrs:{"id":"accordion-10","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 一頓歷時89分鐘，價值274元的晚餐。一對男女戀人步出餐廳。男的吃這頓飯吃得好辛苦，因他85分鐘都用了來想著公司的那份計劃書該怎樣做，幸好女的一點也不發覺他心不在焉。"),_c('br'),_c('br'),_vm._v(" 女的對男的說：「現在不算太晚，你不用送我了，我自己回家吧。」明天就要交計劃書的男友，”立刻”答：「好呀!那你小心點了,回家致電給我吧! 再見。」"),_c('br'),_c('br'),_vm._v(" 男的飛快回家，去埋首他的工作，他不停的做直致電話響起。他看看鐘原來已經半夜三時許，是誰這麼晚來電？他接過電話，原來是他的未來外母：「我女兒現在還沒回家，你不是和她去了吃飯嗎？她怎樣了，我好怕......」男的才記起女的說回到家後會致電他報平安，但現在已經夜半二、三時她為什麼還不回家。男的心亂如麻,最後還是報警求助 。"),_c('br'),_c('br'),_vm._v(" 去到警局，警員問他：「她失蹤時的衣著是？」男的：「這...她......想不起來...」警員：「不打緊，你先放鬆一下，那衣服的顏色總會記得吧？藍色？紅色？」男的：「我...我只想著份計劃書，吃飯也低下頭...我不曾看過她的...」 警員：「她的髮型呢？長髮？短髮？」男的：「我跟她一起很多年了...我所以...怎麼她的東西我一點也想不起...」警員：「你說她是你女友，你們最近很少見面嗎？怎會連髮型也不知道，那她有帶首飾，手錶嗎？」男的也是無言以對。"),_c('br'),_c('br'),_vm._v(" 離開警局，男的覺得很驚訝，驚訝他對女友的”不上心”。多年來他已把愛情的感覺當成一種習慣，就連對他的女友也”習以為常”，已經不把她放在心上了。已過了三天，女的還是音訊全無，這三天男的除了擔心女友的安危，就是不停的想：「她的髮型、她的衣著，我怎會不知道的！我一定要記起來！」"),_c('br'),_c('br'),_vm._v(" 這夜，男的經過一條幽黑、灰暗的長街，竟看見女的就站在長街的盡頭。男的高興得跑向她，想緊緊的擁抱她。但走到女友的面前他就改變了主意，他雙手按著女友的肩膊，說：「等一下，先不要動。讓我看清楚妳，妳的頭髮、妳的衣著，我要好好的記著。回想起那晚，其實是妳發現我還有工作在身，想我早點回家工作而叫我不用送妳的。一直以來只有妳還關心我的感受，而我卻不理會妳。但不要緊，我已知道應怎樣去愛妳、珍惜妳。相信我，妳的一切一切今後都會常在我心中的。」"),_c('br'),_c('br'),_vm._v(" 女的：「你終於都懂得理會我的感受，懂得珍惜我了...可惜已經太遲......」 男的忽然醒過來，原來剛才的是一場夢。 夢醒後男的努力去想，夢中女友的衣著、髮型，但還是記不起來....."),_c('br'),_c('br'),_vm._v(" 隔天，男的終於再見到他的女友，就是在警員帶他到殮房認屍的時候......"),_c('br'),_c('br'),_vm._v(" 望著躺在停屍間的女友，男的哭著說：「我終於知道妳的髮型，衣著，我現在才真正的看著你。我會記著的，永遠都會記著的..."),_c('br'),_c('br'),_vm._v(" 我剛想到要珍惜妳... 妳醒醒呀... 不要就這樣離開我好嗎......」  男的剛學會珍惜，卻已後悔莫及。現在他除了哭泣，就什麼都做不了......"),_c('br'),_c('br'),_vm._v(" 也許你常發現我一直凝望著你！你問我看什麼呢？  我笑笑的回答....沒什麼，只是想將你的一切記在心裏，在見不到你時細細回味.......... ")])])],1)],1)],1)],1)])]),_c('div',{staticClass:"frame"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s2",attrs:{"id":"s2","name":"s2"}},[_vm._v("新女性健康保養")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-11",modifiers:{"accordion-11":true}}],attrs:{"block":"","variant":""}},[_vm._v("女性的健康知識")])],1),_c('b-collapse',{attrs:{"id":"accordion-11","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 資料來源：女性諮詢健康調查，美國國家女性健康資源中心"),_c('br'),_c('br'),_vm._v(" 什麼是美國女性希望去做，而能增進她們健康的事？答案並不是買更多的健康保險，或是購買一大堆健康食品；根據近期發表的一項調查顯示，她們想要的是減輕壓力，以及擁有更多自己的時間。根據美國國家女性健康資源中心調查顯示，就女性的認知而言，為什麼她的健康會改變，這是因為生活型態的因素改變了，像是壓力指數的改變。"),_c('br'),_c('br'),_vm._v(" 研究報告指出，從這項研究可相當清楚地得知，65歲以下的特定女性，對工作所賦予責任感、競爭力，和家庭生活都會感到壓力，而限制了她們的時間，並磨損了她們的精力。報告同時指出，就大部份女性而言，她們對影響健康的因素都能充份的瞭解，並瞭解改善她們健康的基本步驟，然而卻少有女性化想法為行動。"),_c('br'),_c('br'),_vm._v(" 在美國全國性的調查中，10名女性中將近有4名表示，改善整體健康的方法中，降低她們每天的壓力會是她們的第一選擇；調查中差不多比例的女性表示，能有更多的時間照顧自己會是首選。調查列表中還有幾項：擁有更多的金錢來運用在健康保養的消費，而且能有更好的健康保險。調查中其他的資料指出，即使女性認為她們的時間不夠用，也不會盡可能地安排時間，去作例行性的看診。"),_c('br'),_c('br'),_vm._v(" 僅40%的女性表示，她們會例行性地帶著一些問題或疑慮去找醫師諮詢；部份醫師希望她們能這麼做，因為例行性看診，在門診侯診人數多時，基本上不會超過8至10分鐘，所以不會浪費她們太多時間。美國國家女性健康資源中心主席Amy Niles指出，時間問題則更是複雜，我們相信，事實上是女性根本不打算來看診；該中心為非營利團體，對1,005名18歲以上的女性作電話調查。除了報告中明顯的壓力指數，半數女性指出她們曾經採取主動的步驟來降低她們的壓力，而有15%表示她們曾看過心理醫師。"),_c('br'),_c('br'),_vm._v(" 關於健康議題，女性如何獲得正確資訊的調查結果是綜合的；80%至90%的女性意識到每年要作篩檢的重要性，像是巴氏子宮癌檢驗法和骨盆腔檢驗；也有許多女性認知到經常性篩檢的重要性，像是檢查血中膽固醇的濃度，和乳房X光攝影。"),_c('br'),_c('br'),_vm._v(" 報告顯示，大約比半數多一點的女性，認為心臟病是女性死亡率的首位；不過，僅三分之一的女性知道年長的女性應該作骨密度測試，以評估骨關節炎所帶來的骨折風險。"),_c('br'),_c('br'),_vm._v(" 白種和非裔女性中，有超過60%的人表示，她們對自家的家族病史很清楚，這是許多專家認為控制疾病風險的關鍵因素；但是僅半數的西裔女性，和三分之一的亞洲女性，清楚自身的家族病史。同時，有60%的女性向研究人員表示，她們自認健康狀況是「好」或「非常好」，而有93%的女性對自己的孩子持同樣的看法。"),_c('br'),_c('br'),_vm._v(" 耶魯大學婦產科教授Mary Jane Minkin醫師指出，這些數據被高估了，她注意到大約四分之一的成人有吸菸的習慣，而有三分之一的人是肥胖的。他表示，任何吸菸者或肥胖的人士，都不會是健康的。"),_c('br'),_c('br'),_vm._v(" Minkin醫師指出，一項調查結果顯示，女性大多被認為有良好的健康情況，就意味著有健康的家庭；這是以精神上的健康狀態、身體活動的狀態，以及沒有慢性病侵擾所作的評估。她表示，許多時候，我敦促著病友，告訴她，如果她不照顧好自己，她就會真的病得嚴重，也就沒有辦法照顧其他人。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-22",modifiers:{"accordion-22":true}}],attrs:{"block":"","variant":""}},[_vm._v("如何保養皮膚？")])],1),_c('b-collapse',{attrs:{"id":"accordion-22","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 要自己的皮膚滋潤、細膩、柔嫩、富有彈性，但有些人的皮膚則不盡如人意，顯得暗黑粗糙。分析原因為：遺傳因素及疾病影響外，還與後天不善於保養有密切關係。皮膚保養主要注意以下兩方面的內容：少食肉類食品，多吃新鮮的蔬菜。"),_c('br'),_c('br'),_vm._v(" 一、少食肉類食品"),_c('br'),_c('br'),_vm._v(" 蛋白質是構成人體細胞的主要成分，一旦長期缺乏蛋白質，人的皮膚則失去彈性，膚面粗糙，提早老化，蛋白質是人類必不可少的營養物質。許多人把肉類視攝取蛋白質的重要來源，其實不儘然。因為肉類及、蝦、蟹的蛋白質，對膚質較敏感的人很容易引起皮膚過敏。"),_c('br'),_c('br'),_vm._v(" 其蛋白質、脂肪在人體內分解過程中，產生硫酸、乳酸等酸性物質，使血液呈酸性狀態，而這些酸性物質對皮膚和內臟均有強烈的刺激性，妨礙皮膚的正常代謝，影響皮膚的健美。讓皮膚的含水量維持在10%左右，可延緩皮膚衰老，改變皮膚粗糙狀況。青少年時期則應適當多吃一些新鮮的肉類，攝入發育階段所必需的動物蛋白。"),_c('br'),_c('br'),_vm._v(" 二、多吃新鮮蔬菜"),_c('br'),_c('br'),_vm._v(" 皮膚的粗糙通常是血液酸性偏高的結果，而新鮮蔬菜裏的鹼性無機鹽如鈣、鈉、鉀、鎂等含量較高，經常不斷地吃新鮮蔬菜，能使體內鹼性物質充足，使體內的酸性物質迅速中和成無毒的化合物排出體外，血液維持在比較理想的弱鹼性狀態中，保持皮膚的光滑滋潤。"),_c('br'),_c('br'),_vm._v(" 清潔皮膚的作用何為？"),_c('br'),_c('br'),_vm._v(" 皮膚是美容的基礎，而臉部的保養在美容中佔有極重要的位置，面部清潔是皮膚保養的第一步。"),_c('br'),_c('br'),_vm._v(" 面部皮膚暴露在空氣中，空氣中含有污物、塵埃、細菌等，自然附著於皮膚表面，加上自身分泌的油脂、汗液、死細胞等，這些因素會影響皮膚正常生理功能的發揮，甚至引起皮膚感染，發生痤瘡等皮膚病。由此可見，皮膚清潔是非常重要的。"),_c('br'),_c('br'),_vm._v(" 潔膚的目的主要有如下幾個方面："),_c('br'),_c('br'),_vm._v(" 清除皮膚表面的污垢及皮膚分泌物，保持汗腺、皮脂腺分泌物排出暢通，防止細菌感染；潔膚可使皮膚得到放鬆、休息，以便充分發揮皮膚的生理功能，潔膚可調節皮膚的pH值,使其恢復正常的酸鹼度。除了用普通的方法進行皮膚的清潔以外，還可以用營養潔面的方法，常用的有蜂蜜、醋、芹菜、黃瓜、雞蛋、西瓜皮、番茄、粗鹽等。"),_c('br'),_c('br'),_vm._v(" 妙用蔬果美容"),_c('br'),_c('br'),_vm._v(" 每個人都渴望擁有水水嫩嫩的肌膚，所以在化妝品上的花費上是絕不吝嗇的。然而，如果懂得妙用蔬果美容，是可以美得自然又經濟的。"),_c('br'),_c('br'),_vm._v(" 以下介紹和皮膚保養有關的六種蔬菜水果，供愛美的朋友參考："),_c('br'),_c('br'),_vm._v(" 一、檸檬："),_c('br'),_c('br'),_vm._v(" 檸檬中含有大量的檸檬酸，可與皮膚表面及頭髮表面的鹼中和，使皮膚和頭髮能順利生長。而檸檬還含有大量的維生素A，維生素A是皮膚不可缺少的營養成分，缺乏維生素A的人，皮膚和黏膜的抵抗力衰弱，並顯得乾燥、粗裂。"),_c('br'),_c('br'),_vm._v(" 對於皮膚乾燥的人來說，用檸檬汁擦臉，可使維生素A通過皮膚吸收，讓皮膚看起來光潔細膩。"),_c('br'),_c('br'),_vm._v(" 二、木瓜："),_c('br'),_c('br'),_vm._v(" 木瓜含木瓜脢，根據試驗，其表皮內側具有保護、滋潤皮膚功效。常用木瓜擦臉能減緩皮膚老化，使皮膚潔淨、細嫩。"),_c('br'),_c('br'),_vm._v(" 三、西瓜："),_c('br'),_c('br'),_vm._v(" 將西瓜皮切成塊狀，直接塗抹臉部，反覆揉搓，後用清水洗淨，再塗一點美容霜收斂皮膚，經常使用可使皮膚保持細嫩潔白。"),_c('br'),_c('br'),_vm._v(" 四、番茄："),_c('br'),_c('br'),_vm._v(" 番茄含有豐富的維生素C，將番茄切碎，置於碗中，用湯匙擠壓成液汁，最好加入少許蜂蜜，塗抹於臉部及雙手、手臂。時常塗抹，能消除皺紋，可使肌膚潔白留香。"),_c('br'),_c('br'),_vm._v(" 五、黃瓜："),_c('br'),_c('br'),_vm._v(" 黃瓜不僅可收斂及消除皮膚皺紋，且更可使暗沈的皮膚光潔細膩。方法是把黃瓜切成小塊，擠汁。用棉花蘸汁塗抹臉部，尤其是皺紋多處要抹得多些，經常擦拭效果更佳。"),_c('br'),_c('br'),_vm._v(" 六、洋蔥："),_c('br'),_c('br'),_vm._v(" 洋蔥含有大量的維生素Ｃ，而且還含有一種能抵抗癩皮病的維生素，它能促進表皮細胞對血液中氧的吸收，增強修復損傷細胞的能力，從而使皮膚保持光潔、細緻。因此經常食用洋蔥，對皮膚保健相當有助益。"),_c('br'),_c('br'),_vm._v(" 蔬果中豐富的維生素Ｃ和膠原蛋白的生成具有極密切的關係，此外，維生素Ｃ能中斷黑色素生成，減少皮膚黑色素沈澱，加快皮膚的的還原變白，所以蔬果是天然的美容保養品。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-33",modifiers:{"accordion-33":true}}],attrs:{"block":"","variant":""}},[_vm._v("讓美白效果極大化")])],1),_c('b-collapse',{attrs:{"id":"accordion-33","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 根據一項在韓國所做的研究調查顯示，有一半以上的亞洲女性有斑點的困擾，而超過30％的亞洲女性擁有膚色黯沈的問題。針對肌膚製造過量的黑色素，引發的斑點惱人現象，VICHY薇姿特別擷取「麴酸」與「維他命C」兩種美白主流成份，推出高效能性的「BI-WHITE美白淡斑修護系列」，讓雙重美白的修護效果，一次解決臉上斑點與膚色黯沈的問題。"),_c('br'),_c('br'),_vm._v(" 此外，為了確保純維他命C跟麴酸的活性和穩定性，VICHY還運用獨特的「三重乳化科技」，透過毫微膠囊，使細小的美白分子能更深入肌膚，並逐漸釋放於細胞的基底層，因而能夠發揮起最大的美白效用。值得一提的是，「BI-WHITE美白淡斑修護系列」，亦維持於中性的PH質，可降低對於肌膚的刺激，其親膚性極高的植物性副配方，也讓美白護膚的過程更為溫和無負擔。"),_c('br'),_c('br'),_vm._v(" 時序進入盛夏，面對節節高升的紫外線指數，讓偏愛白皙膚色的亞洲女性，又進入捍衛膚色的高峰期。"),_c('br'),_c('br'),_vm._v(" 跳脫一般保養品的侷限，芙蓓森以自然植物水果為基調，推出以「甜橙純精油」為主角的美白系列三款組合。芙蓓森大量運用含豐富維他命C的甜橙純精油，搭配上喚采保濕調理水中調和，轉換出一款具有美白抗皺、規律臉部油脂分泌效果的化妝水。其亦與喚采平衡╱滋養霜相結合，使喚采平衡╱滋養霜搖身一變成為可減少臉部皺紋、淨白肌膚的美白聖品。此外，芙蓓森還將甜橙純精油和甜杏仁基底油相融合，形成能有促進血液循環、強化肌膚細胞再生能力的身體護膚單品。使看似平凡卻擁有多種維生素A、B 群、C、礦物質的甜橙，在今年夏天為你徹底呵護起細緻脆弱的肌膚。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-44",modifiers:{"accordion-44":true}}],attrs:{"block":"","variant":""}},[_vm._v("天天喝蔬果汁 1個月胖1公斤")])],1),_c('b-collapse',{attrs:{"id":"accordion-44","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 天天喝一瓶市售的盒裝蔬果汁，不僅無法防癌，連喝一個月會胖一公斤！現代人生活忙碌，果汁市場搭上「蔬果579」、「天天五蔬果」的便車，推出大量的蔬果汁產品。營養師指出，市售蔬果汁每100cc約50卡，天天喝、連喝一個月，會胖一公斤。"),_c('br'),_c('br'),_vm._v(" 「滿足一日所需」、「蔬果579，健康天天有」等廣告詞，引發營養界大老的質疑，群起澄清。台北醫學大學保健營養學系教授楊淑惠指出，市售蔬果汁強調的579，指的是種類而非份數。但是蔬果要防癌，是指每天吃至少五分量的蔬果，一分蔬菜約一個拳頭大小。"),_c('br'),_c('br'),_vm._v(" 而市售蔬果汁盒的標示，讓專家也看得一頭霧水。"),_c('br'),_c('br'),_vm._v(" 台灣癌症基金會副董事長彭汪嘉康也指出，市售蔬果汁很難判斷到底有幾分蔬果，一瓶蔬果汁能提供多少蔬果？是否滿足一日所需？根本沒有明確標示。"),_c('br'),_c('br'),_vm._v(" 市售蔬果汁的廣告常標榜「健康」形象，但是營養師不以為然。營養師推算，市面宣稱100%的蔬果汁，平均每100cc約40卡到50卡，每天喝下500cc的蔬果汁，就攝取了將近一碗飯的熱量！也等同於三分之二杯珍珠奶茶。"),_c('br'),_c('br'),_vm._v(" 癌症基金會營養師吳秋玲推算，人攝取7700大卡會增加一公斤，也就是如果天天喝一杯號稱100%的蔬果汁，連續喝一個月，就會胖一公斤，蔬果汁的熱量不可輕忽。但市售蔬果汁純汁含量大約只有5%到10%，其餘的都是糖水、色素和香料。"),_c('br'),_c('br'),_vm._v(" 就算宣稱是100%的蔬果汁，也多使用濃縮果汁再還原，加工過程中許多營養素都被破壞了。因此營養師強調，愛喝市售盒裝蔬果汁，小心喝到的根本只是香料與色素調和的糖水果汁。 ")])])],1)],1)],1)],1)])]),_c('div',{staticClass:"frame"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s3",attrs:{"id":"s3","name":"s3"}},[_vm._v("新女性工作")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-1a",modifiers:{"accordion-1a":true}}],attrs:{"block":"","variant":""}},[_vm._v("如何改善職場中的差別待遇")])],1),_c('b-collapse',{attrs:{"id":"accordion-1a","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 雖然憲法明文揭示「中華民國人民，無分男女在法律上一律平等。」第十五條規定「人民之生存權、工作權及財產權，應予保障。」，第一五二條並規定「人民具有工作能力者，國家應予以適當之工作機會。」可惜這些符合兩性平等、保障人民權利的規定並未落實於現實生活中，尤其是就業市場。"),_c('br'),_c('br'),_vm._v(" 女性在就業市場上遭受性別歧視之案例層出不窮，且由來已久，只是以往受歧視之女性只能以宿命論之，逆來順受，甚少會出面指控或申訴，即便要申訴，亦投訴無門。"),_c('br'),_c('br'),_vm._v(" 迄至民國76年8月爆發國父紀念館57位女性員工及高雄市立文化中心44位女性員工，因於招考時與館方切結約定，凡是女性員工年滿三十歲，或是結婚、懷孕就自動離職，而被迫離職之事件，類似這樣的「單身條款」在當年其實非常普遍，只是此次女性員工願意集體出面申訴，因此在婦女新知等婦女團體之聲援下，要求國父紀念館及高雄市立文化中心廢止此不合理規定，並要教育部下令所有文化機構廢除此種「單身條款」之不合理規定，且召開記者會，呼籲社會正視婦女在工作職場上所受到之不平等待遇。婦女團體於聲援的過程中，卻發現國內並沒有相關法律可以規範這種不合理的性別歧視。"),_c('br'),_c('br'),_vm._v(" 然而，早在1975年聯合國婦女會議時，即決議將1975-1985年訂為「聯合國婦女十年」，要求各會員國應在十年內廢除一切歧視女性的法律，並積極制定法律及政策以保障女性的權益。因此，聯合國會員國即陸續以法律保障兩性平等工作權。例如，英國在1975年制定「性別差別禁止法」，義大利在1977年制定關於男女勞動平等待遇法，法國1983年修改勞動法典以保障女性工作權，日本1985年制定「男女僱用機會均等法」等等。我國自1971年退出聯合國，因此未受到該「聯合國婦女十年」及「消除一切形式歧視女性之公約」之拘束。"),_c('br'),_c('br'),_vm._v(" 為保障女性平等工作權益，婦女新知基金會乃由本人召集了涂秀蕊、陳美玲、劉志鵬、潘正芬等律師於七十六年著手起草了「男女工作平等法」草案，於七十九年送進立法院，企盼上開憲法所保障之權益能真正落實至女性身上。可惜上開草案冰凍於立法院長達十一年，立法委員不斷以各種理由杯葛，如：行政院無相對版本等，或因國會遊說法的通過，第三屆所有未完成黨政協商的法案，都必須在新任期開始後重新提案，草案歸零。尤有甚者，企業界更將「男女工作平等法」(即兩性工作平等法之前身)與「消費者保護法」、「環境保護法」等列為「企業出走的十大惡法」之一，堅決反對制定該法，致使法案一再延宕，然而也正因為該法的擬定才讓職場上的性別歧視現象獲得檢討與改進的機會，如：於八十一年通過就業服務法，於第五條規定「為保障國民就業機會平等，雇主對求職人或所僱用員工，不得以種族、階級、語言、思想、宗教、黨派、籍貫、性別、容貌、五官、殘障或以往工會會員身分為由，予以歧視。」並於第六十二條規定違反第五條規定者處新台幣三千元以上三萬元以下罰鍰。司法院、考試院及行政院等各部會先後提出「女性公務人員育嬰期間申請留職停薪處理原則」、「男性公務人員兩天陪產假之規定」。社會大眾也開始注意職場性騷擾、懷孕歧視的嚴重性，這部法案歷經十五年終於通過。"),_c('br'),_c('br'),_vm._v(" 三、執法落實"),_c('br'),_c('br'),_vm._v(" 徒法不足以自行，法令的落實需要民眾的了解與運用，以及政府機關的貫徹執行才能讓良法美意發揮最大效用。若大家對女性工作權益無法予以正視，或仍無法與女性平起平坐，成為工作夥伴，甚或仍認為養育兒女，家務工作是女人天職，女人只有在孩子已有妥善的照顧後才能外出工作，則女性的工作權益永難獲得保障，為使兩性工作平等法的精神得以落實，應從教育、法律、社會層面採取必要措施。"),_c('br'),_c('br'),_vm._v(" (一)教育層面的必要措施"),_c('br'),_c('br'),_vm._v(" 女性在工作職場上受到歧視，最主要來自於傳統「男主外，女主內」、「生育是女人的天職」的影響，而這些觀念實源自於封建制度與宗法制度，即將整個社會分為公、私領域，公領域有封建制度，國王、諸侯、卿大夫、士，君君臣臣，層級排比清楚，以使政權得以世襲；在私的領域有所謂「宗法制度」，後為「家族制度」，即以家父長為本位，以上祭祖先，下承煙祀，所有成員均為家屬。為了「承煙祀」即傳宗接代，因是父系社會，當然是傳夫家的宗，接夫家的代，但男人不會生育，只有靠女人，因此在周公制禮作樂的時代，男女命運自出生的那一刻即被嚴苛的決定，男孩將來負有傳承的使命，出將入相，光宗耀祖，女孩終究要嫁入夫家，為夫家傳宗接代，相夫教子當個賢妻良母。公私領域的嚴格劃分，成就了男人的事業，掌握了國家資源，成為資源分配者；而女人在私領域內，不眠不休操持家務，照顧兒女，美其名為愛的勞務，但實質淪為經濟的依附者，次等的人格者，即使封建解體、家族制度崩潰，工業革命結果，小家庭制度取代大家庭，而家庭經濟由「男主外，女主內」變成「雙薪家庭」，以維持家庭開銷，但傳統的女性屬於家庭的觀念並沒有突破性的改變，誠如P. Freire對文化階層化的觀察："),_c('br'),_c('br'),_vm._v(" 「文化的征服，往往導致被入侵者喪失其文化自信心，他們會開始仿效征服者的價值、標準、和目標。文化入侵若要成功，最重要的就是使被入侵者相信其內在的劣根性。文化入侵一方面是宰制的手段，另方面則是宰制的結果。因此，帶有支配特性的文化活動，除了是刻意計畫出來的，在另一層意義上也就是壓迫性事實的產物。」(註一)"),_c('br'),_c('br'),_vm._v(" 在各教育、文化、媒體仍不斷塑造「母性天職」，及女人未照顧好家庭，是其未盡職，形成職業婦女的焦慮，以及女性相信自己在「男性擅長」的領域是天生較差的，這般自我貶抑，透過教育系統不僅內化到女性的價值觀中，更進一步強化了父權社會的威權性。在父權社會的馴化機制與教育／家庭系統的價值內化下，社會上的職業分野有著嚴重的性別階層化的區隔，依生物發展法則─「惟有高度變異的族群，才有可能在大自然的考驗下存活某幾類物種，藉此銜接族群」，父權社會獨尊男性與異性戀的價值觀，而貶抑／壓迫／消滅其他性別認同的可能性，無異是將社會高度同質化，也就大大地減損了國家／企業的競爭力。惟有打破性別刻板化的現況，才能在國家／企業發展中，創造出盡可能的變異性，以在國際競爭的嚴苛考驗中，提升生存與發展的可能性。(註二)"),_c('br'),_c('br'),_vm._v(" 因此要真正落實兩性工作平等，政府應採取下列具體措施："),_c('br'),_c('br'),_vm._v(" 1.隨時檢視並改善所有教科書中性別刻板化的教材。"),_c('br'),_c('br'),_vm._v(" 2.不斷訓練教師，各行各業的人去刻板化印象。"),_c('br'),_c('br'),_vm._v(" 3.檢視所有公務部門性別比例的分布現象，並予以鼓勵、調整。"),_c('br'),_c('br'),_vm._v(" 4.舉辦各種女性領導人才訓練，以培訓女性進入高階領導階層。"),_c('br'),_c('br'),_vm._v(" 5.以性別角度檢視所有公共設施、職業場所，是否構成一個友善、安全的工作環境。"),_c('br'),_c('br'),_vm._v(" 6.不斷宣導兩性平權，打破公、私領域界線，提倡男女在公領域之分工、資源之共享，私領域家務育兒之分工、責任共擔。"),_c('br'),_c('br'),_vm._v(" 7.全面檢討所有招募廣告，是否蘊含性別歧視，並予以改善。"),_c('br'),_c('br'),_vm._v(" (二)法律層面的必要措施："),_c('br'),_c('br'),_vm._v(" 觀念的改變，文化規範的改變是最深遠且最主要的，但亦是最緩慢的，而法律制度的建立是最立即的，未必最有效，但絕對有催化觀念改變及文化規範改變的力量。茲「兩性工作平等法」正式實施，藉由法律的強制力，使人為的性別歧視如招募、甄試、進用、分發、配置、考績、陞遷、薪資報酬、退休、資遣、離職、解僱及雇主為受僱者舉辦或提供各項教育、訓練或其他類似活動，或福利措施等均不得再因性別而有差別待遇。"),_c('br'),_c('br'),_vm._v(" 又性騷擾問題由來已久，且不斷困擾女性，尤其是工作場所中之性騷擾，其所侵害的不僅是女性的人格與尊嚴，更是假藉性騷擾而改變其工作條件，使得女性只有屈從一途，因此有加以規範之必要，且性騷擾之問題，實為性別歧視的問題，即一性對另一性之不尊重，始有冒犯之舉動，故在「兩性工作平等法」中乃特別予以規範："),_c('br'),_c('br'),_vm._v(" 1.交換式性騷擾："),_c('br'),_c('br'),_vm._v(" 禁止雇主對受僱者或求職者為明示或暗示之性要求、具有性意味或性別歧視之言詞或行為，作為勞務契約成立、存續、變更或分發、配置、報酬、考績、陞遷、降調、獎懲等之交換條件。"),_c('br'),_c('br'),_vm._v(" 2.敵意工作環境之性騷擾："),_c('br'),_c('br'),_vm._v(" 防治工作場所性騷擾的另一個積極性的做法是要建立一個友善的工作場所。由於工作場所的性別關係即是反映社會的性別權力關係，工作場所的性騷擾也正是反映社會性別權力的不平等，是一種就業性別歧視。即男性為了展現或維護男性的優勢地位，意圖以各種不受歡迎之性要求，或具有性意味或性別歧視的言詞、圖片、動作或手勢，拒絕受僱者進入職場，或使其難堪無法在工作職場繼續待下去，而損害其工作權益，此均係製造女性受僱者的敵意工作環境。然而，此類屬於敵意環境的性騷擾卻完全無法引用刑法條文加以處罰。因此，在本法中也明文規定雇主應盡積極防止之責任。"),_c('br'),_c('br'),_vm._v(" 法律通過後，為使該法得以真正落實，更應採取下列具體措施："),_c('br'),_c('br'),_vm._v(" 1.政府機關率先實施「兩性工作平等法」，並建立各種申訴管道。"),_c('br'),_c('br'),_vm._v(" 2.勞工行政主管機關應督促公民營機關、事業單位於送核之「工作規則」中加入落實「兩性工作平等法」之必要事項及申訴管道之建立。"),_c('br'),_c('br'),_vm._v(" 3.審視申訴管道有無兼顧勞、僱雙方及性別平衡原則，以及保密原則。"),_c('br'),_c('br'),_vm._v(" 4.宣導各地「就業歧視評議委員會」及「兩性工作平等委員會」之申訴電話號碼，使受害者有申訴管道。"),_c('br'),_c('br'),_vm._v(" 5.對企業體不斷宣導就業性別歧視之概念，以免觸法。"),_c('br'),_c('br'),_vm._v(" 6.宣導何謂就業歧視，以及如何保存證據、申訴、鼓勵更多受歧視者勇於站出來申訴。"),_c('br'),_c('br'),_vm._v(" 7.強化各地「就業歧視評議委員會」及「兩性工作平等委員會」之職能，發揮糾正性別歧視之功能。"),_c('br'),_c('br'),_vm._v(" (三)社會層面的必要措施："),_c('br'),_c('br'),_vm._v(" 如何讓女性將育兒的重任卸下，實為女性邁向工作平等的關鍵。"),_c('br'),_c('br'),_vm._v(" 按嬰兒是民族命脈的延續，兒童是國家未來的主人翁，生育照顧健康的下一代，是國家的社會投資，是全民的使命，並非單純女性的責任！因此，憲法第一五六條前段特別規定「國家為奠定民族生存發展之基礎，應保護母性。」而所謂「保護母性」，並非對女性的特別禮遇，而係基於保護民族幼苗，而對於懷孕婦女予以照顧，使其安心、安全地孕育健康的下一代，並於胎兒出生後，給予嬰兒、兒童最適切之撫育。因此，其孕育、培育過程之成本及責任應由家庭、社會、國家共同承擔，而非由女性單獨背負！"),_c('br'),_c('br'),_vm._v(" 何況根據科學家之最新發現，「腦力即國力，三歲奠定一生基礎，從出生到三、四歲，是人類腦部活動最密集的時期，這段嬰幼年，正是奠定一生智力、情緒與性格的關鍵階段。」「一旦錯過良機，整個社會都將付出沈重的代價」，因為根據統計發現在嬰兒期沒被好好照顧的人，長大後成為犯罪者的機率，五倍於接受良好學前教育的人。因此現在世界各先進國家均開始撥出大筆預算，減輕家庭照顧幼兒的負擔，讓國家的新血輪，在最重要的發展時期，得到最好的照顧。(註三)但是反觀國內的幼兒照顧與教育，仍是每個家庭一肩挑的沈重負擔，更是懷孕女性永無止境的夢魘，甚至懷孕歧視在社會上仍到處可見。"),_c('br'),_c('br'),_vm._v(" 因此「兩性工作平等法」規定應由國家立法透過社會保險方式發放育嬰留職停薪津貼，使育兒責任由全民共同承擔。又因育兒與賺錢是家庭之分工，是夫妻協調之結果，不應再限於「男主外，女主內」；且根據心理學者之研究顯示，父親參與育兒工作較多的孩子，語言表達能力、解決問題能力及社交能力，均較沒有父親參與的領先二至六個月，更足見育兒不再是女性的責任，更是男女兩性的共同責任！且觀去年喧騰一時的國王企鵝孵蛋，更是自然界兩性分擔育兒責任的最佳典範，當母企鵝產完卵，為了讓母企鵝恢復體力，公企鵝會自告奮勇的來幫忙孵卵；而母企鵝也會很領情的去吃吃東西、游游泳、舒活舒活筋骨。大約三星期多的時間，母企鵝的體力恢復了，會回來接替孵卵的工作。讓公企鵝再去補充體力。整個孵卵的時間通常需要五十八天。(註四)因此育嬰休假、育嬰減少工時、家庭照顧休假均應由男女兩性共同享有；因此乃規定育嬰假、育嬰減少工時，彈性工時、家庭照顧假、生理假、產假、陪產假，並規定國家對於因結婚、懷孕、生產或育兒而退職之女性受雇者應採取再就業之輔助，並鼓勵雇主再僱用。及規定僱用受僱者250人以上之企業設置托兒設施或提供托兒措施，使受僱者無後顧之憂，工作情緒穩定，進而提升工作效能。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-2a",modifiers:{"accordion-2a":true}}],attrs:{"block":"","variant":""}},[_vm._v("女性工作者的保障")])],1),_c('b-collapse',{attrs:{"id":"accordion-2a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 不知道妳是否因為結婚、懷孕或生產而辭去了妳原來的工作？如果是這樣的話，不知道妳是否打算在孩子長大後重回就業市場？或者妳正咬牙撐做一個蠟燭兩頭燒的職業婦女？這種情境真的是女人在面對工作與家庭時的辛酸與無奈。"),_c('br'),_c('br'),_vm._v(" 台灣的婦女就業政策，有著先天不良，後天又失調的體質，因為就業婦女同時有著身為女性以及身為受雇者的雙重弱勢，在這種「爹不疼、娘不愛」情況下，我們的婦女就業相關法令，真的是缺乏完善的規定與保障。"),_c('br'),_c('br'),_vm._v(" 一、勞動基準法"),_c('br'),_c('br'),_vm._v(" 勞動基準（簡稱勞基法）是規範最低勞動條件的法令。所謂的勞動條件，就是每個人在找工作時所關心的那些問題，例如每天工作幾小時、每週放假幾天、年假有多少、加班費怎麼算、以及資遣、退休金的發放等。"),_c('br'),_c('br'),_vm._v(" 在勞基法中，大部份條文是沒有性別之分的，也就是同時適用於男性員工與女性員工。不過，其中有些修文規定女性之「特別待遇」，並且在討論女工與童工的專章中有規定產假等母性保護條款。但是，目前並不是所有的上班族都適用勞基法，千萬不要以為有勞保就有勞基法。"),_c('br'),_c('br'),_vm._v(" 二、就業服務法"),_c('br'),_c('br'),_vm._v(" 就業服務法的第五條內容，是所謂的反岐視條款，規定「為保障國民就業機會平等，雇主對求職人或所顧用員工，不得以種族、階級、語言、思想、宗教、黨派、籍貫、性別、容貌、五官、殘障或以往工會會員身份為由，予以歧視。」"),_c('br'),_c('br'),_vm._v(" 但是，在就業服務法裡的規定，如果歧視成立，公司會被處罰三千至三萬元。因此，即使提出申訴，經評議委員會認定歧視成立，充其量也只是公司被罰三萬元。在兩性工作平等法尚未通過之前，女性在就業上想要要求平等保障，大概只能仰賴這部輕描淡寫的就業服務法了。"),_c('br'),_c('br'),_vm._v(" 三、台北市職場性騷擾防治要點"),_c('br'),_c('br'),_vm._v(" 八十七年十月三日由台北市政府通過的「台北市職場性騷擾防治要點」，是目前國內唯一規範的職場性騷擾，而且被通過的法令。要點中規定任何人不得在工作場所張貼或擺設黃色書報刊，並且不得有因性別差異而有侮辱、蔑視或歧視之態度及行為。當然，與性有關的不適當、不悅或冒犯性質的語言、身體碰觸或性要求；以及以性活動或與性有關之行為作為交換報償之承諾更是被禁止。"),_c('br'),_c('br'),_vm._v(" 但是，由於要點的法律位階太低，沒有罰則，也沒有更高層次的法源作為處罰依據，因此，其功能大概也是在於宣導與教育，對於深受性騷擾之苦的女性（或男性），所能幫助的有限。 ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-3a",modifiers:{"accordion-3a":true}}],attrs:{"block":"","variant":""}},[_vm._v("職場生存法則-衡量個人成熟度的四個指標")])],1),_c('b-collapse',{attrs:{"id":"accordion-3a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("作者／杜書伍(聯強國際集團總裁兼執行長)")]),_c('p',[_vm._v(" 我們經常會說一個人「夠不夠成熟」、「火候深不深」，但到底怎樣才算是「夠成熟」、「有火候」呢？這是一個相當難以評量的問題，因為成熟、火候只能以意會，而沒有數字化的指標可以衡量，不像身高、體重一量便知。但是，一個人從最基本的人際關係好不好、處事態度健不健康，到能否正確地決斷事物等等，都與其成熟度有關。因此，如何衡量一個人（包括自己）的成熟度，是一件重要的事。對此，我認為至少可以從四個方面來評量。 "),_c('br'),_c('br'),_vm._v(" 一、單面向V.S.多面向 "),_c('br'),_c('br'),_vm._v(" 一個人在成長過程中，對於周遭事物的考量，最先大多只能專注於一件事物，然後逐漸進展到能夠同時兼顧多種事物。大部分人都曾看過這樣的現象，一個小孩子在餐桌上伸手要夾較遠處的菜時，經常會不經意地碰翻了靠近自己身前的杯子。之所以如此，是因為當小孩子想要伸手穿過杯子去夾菜時，腦海中便只專注於要去夾的這盤菜，而無視於杯子的存在，因此很容易就把杯子碰翻了。隨著年齡漸長，小孩逐漸學會同時注意到二件、三件不同的事物之後，這種現象發生的機會也跟著降低。 "),_c('br'),_c('br'),_vm._v(" 在工作能力的培養上，也常見類似的情況。有些人總是習慣於單點思考，在考量一件事物時，往往只能注意到其中的一個單點，而無法同時顧及周遭相關的事物，使得思考、決斷的品質相當低落，掛一漏萬。而一個成熟、有火候的人，則會把各種相關的事物一起納進來做整體的考量，例如，思考這件事情如果這樣做，其他相關的事物會產生什麼連帶的變化；或是如果在另一個部分做出哪些配合，可以是整體效果更好等等，如此同時考量多重不同面向，所做出來判斷決策的品質也會較佳。如果一個人能同時兼顧到更多相關的面向，則我們可以說這個人越成熟、火候越深。 "),_c('br'),_c('br'),_vm._v(" 二、有形V.S.無形 "),_c('br'),_c('br'),_vm._v(" 人在判斷外界事物的價值時，只有先意識到事物的存在，才有可能進一步體認到它的價值。有些事物是有形的，只要看到、聽到，就能意識到它的存在；但無形的事物卻看不到、聽不到，只能靠著「想像」來感知它的存在。一個人如果只能用眼睛、耳朵來意識事物的存在，而無法用想像、體會的方式來感知事物，便無法體認到無形事物所具有的價值。然而，無形事物所能產生的價值，卻永遠大於有形的事物，因此，一個人從只能看到有形價值，進一步到能夠體認無形價值的存在，便反映出這個人的成熟度與火候。 "),_c('br'),_c('br'),_vm._v(" 舉個例子來說，一般人就業時，所獲得的薪資是有形的，工作的學習成長卻是無形的，而一個環境能帶給個人能力更大的成長，與更好的發揮，個人得到的成就也因此更高，所以其價值遠高於有形的薪資。 "),_c('br'),_c('br'),_vm._v(" 三、短期V.S.長期 "),_c('br'),_c('br'),_vm._v(" 耕耘與收穫之間，通常會牽涉到時間長短的問題。有些事物在投入之後，很快就能得到回收，不過，其效益持續的時間也比較短暫，效益反映結束之後，必須再次投入，才能再產生效益。另外也有一些事物是需要長期的耕耘，而且在短期內不見得可以看到明顯的效果，但是，其效益卻是源遠流長地延續發酵，且時間越久，產生效益的放大倍數越大，所能得到的整體成效也會因此百倍於短期效益。 "),_c('br'),_c('br'),_vm._v(" 但投入之後可以馬上回收，表面上看起來是一件相當誘人的事情，許多人都會被此吸引，因此在思考與決策上便容易傾向於注重眼前立即的效益，卻未能重視、或沒有耐性從事長期的耕耘，形成所謂的「短視」。而一個成熟的人，則能夠忍受短期效益不明顯的現象，而選擇長期耕耘，追求長遠的效益，我們說一個人「有遠見」，意思便在於此。 "),_c('br'),_c('br'),_vm._v(" 四、相對V.S.絕對 "),_c('br'),_c('br'),_vm._v(" 世界上所有事物幾乎可以說都是相對的，而非絕對。比方說，好與壞便是相對比較之下所得到的結果，當我們說一件事物是「好的」，指的是「與另一事物比較之後，相對的好」，而非「絕對的好」。換句話說，好與壞之間，並非100分與０分的截然二分。「好」有可能70分的好，也可能是90分的好；「壞」則可能是59分的壞，或20分的壞。 "),_c('br'),_c('br'),_vm._v(" 但一個不成熟的人，會習慣於用絕對性的角度來判斷事物，只有「好」與「壞」的絕對二分法。在這種情形下，由於缺乏相對比較的判別標準，因此無法分辨出真正的好與壞，也不知道好有多好？壞有多壞？而一個成熟的人，對於事物則懂得用相對的邏輯來看待，而擁有較精準的判斷力。 "),_c('br'),_c('br'),_vm._v(" 綜合上述四個指標，當我們在判斷一個人的成熟度時，可以從他的行事作風來觀察其思考、決斷一件事物時，是否能經常兼顧到多個面向、是否能看到無形的價值、是否注重長遠的效益，以及對事物的判斷是否能用相對的邏輯，藉此來衡量。因此，一個人的成熟度夠不夠、火候深不深，就看他對於上述事物養成多深的認知，以及多牢固的習慣。 ")])])],1)],1)],1)],1)])]),_c('div',{staticClass:"frame"},[_c('div',{staticClass:"scrollspy-example",attrs:{"data-bs-spy":"scroll","data-bs-target":"#list-example","data-bs-offset":"0","tabindex":"0"}},[_c('h2',{ref:"s4",attrs:{"id":"s4","name":"s4"}},[_vm._v("新女性理財")]),_c('div',{staticClass:"accordion",attrs:{"role":"tablist"}},[_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-11a",modifiers:{"accordion-11a":true}}],attrs:{"block":"","variant":""}},[_vm._v("女性的私房理財秘笈")])],1),_c('b-collapse',{attrs:{"id":"accordion-11a","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("現代女性的私房理財秘笈～文/黃琡珺")]),_c('p',[_vm._v(" 古時候的女性在嫁為人婦後就在夫家操持家務，經濟來源仰賴丈夫供給，為保障自己未來生活、或是為了增加安全感，多多少少會想辦法摳出一些小錢來存私房錢。"),_c('br'),_c('br'),_vm._v(" 近年來女性受?育機會提高，收入也大為提升， 女性擁有工作自主權之後，存私房錢，不僅只是為了多一份安全感， 更希望擁有金錢自主的權利， 即使是純家庭主婦，也開始要求從事家務的適當報酬， 總是希望有獨立自支配的金錢以備不時之需， 也不需在經濟上看另一半的臉色。"),_c('br'),_c('br'),_vm._v(" 想要有財務獨立自主的空間， 私房錢可別真的只是「存」者、「藏」者， 畢竟物價也會有上漲的時候，回想一下， 今年以來油價、天然氣，糖及相關的食品飲料，這些和生活息息相關的民生物資都喊漲， 如果光會守財而不會理財，錢也有可能愈存愈薄呢！"),_c('br'),_c('br'),_vm._v(" 投資海外共同基金倒不失為私房錢理財的好方法， 尤其女性朋友上班講求工作績效表現， 回家可能還要負擔家庭管理的情況下，應該也沒有多餘的時間好好鑽研私房錢如何「煉金」， 交由專業經理人代為操盤、由信譽佳的基金公司提供您理財服務，而且透過定期定額投資海外共同基金，每月只要三、五千元起的小錢就可以投資，還可以透過金融機構指定每月自動扣款投資金額，讓您再忙也可以利用定期投資的方式為自己攢點私房錢。"),_c('br'),_c('br'),_vm._v(" 為了跳脫區域國界與產業類股的限制，使資產配置更具彈性，不妨以均衡佈局全球股市來做為投資首選。"),_c('br'),_c('br'),_vm._v(" 若以代表全球股市的摩根士丹利世界指數為例，過去10年以台幣計價的年平均報酬率達.47%， 累積報酬率達147.32%(過去三年月報酬率標準差(非年化)原幣計價的波動風險為2.38%，理柏資訊，以上均統計至2006/11/30，過去績效非對未來投資績效之保證)，全球化的長期投資確實有讓私房錢成長的機會，您私房錢理財的好伙伴。"),_c('br'),_c('br'),_vm._v(" 以往女性偷偷存錢很辛苦，然而，現代夫妻多為雙薪家庭，財務多半各自獨立，所以除了共同負擔家庭支出外，現代夫妻要存私房錢，倒也可以不用太偷偷摸摸，對於私房錢也應該給予更廣泛的定義，雖說私房錢最好是自己存自己用，不過，行有餘力時也可以做為子女教育或家用的補貼，或是家務緊急開支的小金庫。"),_c('br'),_c('br'),_vm._v(" 至於單身女性要不要存私房錢？當然更要存嘍!!女性朋友不論是將來嫁作人婦或是享受單身貴族，一輩子都要擁有自己寵愛自己的能力！! ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-22a",modifiers:{"accordion-22a":true}}],attrs:{"block":"","variant":""}},[_vm._v("女性理財是一種趨勢")])],1),_c('b-collapse',{attrs:{"id":"accordion-22a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('p',[_vm._v(" 雖然女性在生理上、體力上相較男性還是有先天的弱勢，但在很多職業領域並不影響女性工作表現，相反的，女性朋友細心、耐心、執著堅韌的特質，更有助於許多女性在工作領域上取得令人矚目的成就。而女性在投資理財方面，我們也可以透過媒體，看到很多成功的典範，建議妳不妨善用女性細心、耐心、執著堅韌的特質，幫助於妳實行理財計劃，達成理財目標，提供幾個理財方向供妳參考。"),_c('br'),_c('br'),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("(1)努力學習理財，為自己量身打造理財計劃：")]),_c('br'),_c('br'),_vm._v(" 如何理財使資產增值？理財的方法與途徑有哪些？這當中的訣竅和學問，建議妳可以透過書籍的閱讀、網路的查詢或是尋求專業投資顧問與理財專家的協助，可以了解理財相關知識與觀念、學習別人成功理財的方法，並依自己財務需求來設立短期、中期、長期的目標，計劃得越周密，就不會在將來的需要用錢的時候不知所措。"),_c('br'),_c('br'),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("(2)節流為理財之本：")]),_c('br'),_c('br'),_vm._v(" 投資理財總得要一些本錢，理性消費是累積本錢的必要態度。購物是許多女性的喜好、也是弱點，所以應該控制自己不要被購物情緒左右，使辛勤換來的收入輕易地付諸東流。一些小小花費，可以在積少成多、聚沙成塔之下，帶給資產負面影響；相反的，儲蓄與投資也能發揮積少成多、聚沙成塔的效果，讓妳的資產增值。建議每月將薪資的固定比例提撥在儲蓄及投資，運用女性細心的特質做好帳簿管理，以了解每月收支及投資帳戶的變動狀況。"),_c('br'),_c('br'),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("(3)投資、提升專業開財源：")]),_c('br'),_c('br'),_vm._v(" 從求職網站可以發現上班族兼職者不在少數，不過妳若沒有過人的精力與良好的時間管理，還是選擇進修充實本業，做為提升薪資的籌碼，並透過適當的投資管道投資來增加收入，一方面可以增加理財資本；另一方面，不論是工作能力或理財能力也都可以藉此得到鍛鍊。"),_c('br'),_c('br'),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("(4)定期定額、全球化投資：")]),_c('br'),_c('br'),_vm._v(" 女性理財有時會帶有情緒性或恐懼感，導致不敢投資或非理性的殺進殺出。建議妳透過定期定額投資海外共同基金，由專業理財顧問服務，找到適合自己投資的基金類型，讓基金經理人視市場及景氣變化為妳操盤，避免妳的情緒影響投資判斷，也可以在全球化分散投資風險原則下，降低投資恐懼。善用女性耐心的特質，以定期定額的方式長期投資，相信在計劃、紀律的投資下能及早實現妳的理財目標。"),_c('br'),_c('br'),_vm._v(" 女性理財已經是一種趨勢，用智慧理財更是女性朋們享受金錢應該有的態度，也能讓妳在享受人生之餘沒有後顧之憂喔！ ")])])],1)],1)],1),_c('b-card',{staticClass:"mb-1",attrs:{"no-body":""}},[_c('b-card-header',{staticClass:"p-1",attrs:{"header-tag":"header","role":"tab"}},[_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.accordion-33a",modifiers:{"accordion-33a":true}}],attrs:{"block":"","variant":""}},[_vm._v("測試你的發財欲望有多高")])],1),_c('b-collapse',{attrs:{"id":"accordion-33a","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-card-text',[_c('h4',[_vm._v("引用自:微風論壇／由Tommy1024編輯")]),_c('p',[_vm._v(" 「我想要賺錢！很多很多的錢！」有多少人在頭腦中會有這樣強烈的念頭呢？"),_c('br'),_vm._v(" 在當今社會，沒錢雖說不至於真會寸步難行，但的確苦惱著很多的人。"),_c('br'),_c('br'),_vm._v(" 測試看看，你的發財欲望高嗎？"),_c('br'),_c('br'),_vm._v(" 題目：一個垂暮的老人獨自站在高樓的窗前眺望窗外繁華的街道，你猜他在看